import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import * as PropTypes from 'prop-types';
import t from '../../../../lang/t';
import { addressSlice } from '../../../../store/slices/address';
import HelperService from '../../../../services/HelperService';
import NotificationService from '../../../../services/NotificationService';
import RequestService from '../../../../services/RequestService';
import DeleteModal from '../../../../modals/DeleteModal/DeleteModal';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import RiskScoreCell from '../../../../components/RiskScoreCell/RiskScoreCell';
import Table from '../../../../components/Table/Table';
import WalletsList from '../../../../components/WalletsList';
import {addressLink} from '../../../../utils/cryptoLinkGenerator';
import NotificationButton from '../../../../components/Table/components/NotificationButton';

const { setQuarantineAddress, setRiskAddress } = addressSlice.actions;

const AddressesTab = ({ onCheck, onWithdraw, riskAddress, merchantId, needUpdateAddresses, setNeedUpdateAddresses, calculateBalances, openModalEditDescription }) => {
    const [isLoading, setLoading] = useState(false);
    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState([{ 'Type': 3, 'Column': 'MerchantId', 'Items': [merchantId] }]);
    const [allAddresses, setAllAddresses] = useState([]);
    const [balancesData, setBalancesData] = useState({ sum: 0, list: [] });

    const [selectedItems, setSelectedItems] = useState([]);

    const cryptocurrencies = useSelector(state => state.cryptocurrency.cryptocurrencies);
    const quotes = useSelector(state => state.cryptocurrency.quotes);
    const quarantineAddress = useSelector((state) => state['address'].quarantineAddress);
    const dispatch = useDispatch();

    const [selectedWalletIndex, setSelectedWalletIndex] = useState(null);
    const [isHiddeAddresses, setIsHiddeAddresses] = useState(true)
    
    const updateBalancesData = () => {
        const balancesData = calculateBalances(allAddresses, quotes, cryptocurrencies);
        setBalancesData(balancesData);
    }

    useEffect(() => {
        updateBalancesData();
    }, [allAddresses, quotes]); // eslint-disable-line react-hooks/exhaustive-deps

    const fetchAllWalletsAddresses = async () => {
        const response = await RequestService.GetAddresses(1, 10000, '', { by: 'Id', desc: true }, filters);

        if (response.Status === 'Success') {
            setAllAddresses(response.Addresses);
        }
    };

    useEffect(() => {
        fetchAllWalletsAddresses().then();
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    const columns = [
        {
            Field: 'Balance',
            Title: t('Balance'),
            Width: '140px',
            Filter: {
                Type: 2
            },
            Cell: (data) => (
                <div className="tableBodyItemCellBalance" style={{'pointerEvents':'none'}} onClick={() => {data['LastCheckDate'] ? dispatch(setRiskAddress(data)) : onCheck(data['Address'])}}>
                    <Icon
                        type={Icons[data['Currency']]}
                        width={16}
                        height={16}
                    />
                    <p>
                        <b>
                            { HelperService.FormatCrypto(data['Balance'], data['Currency'] === 'USDT-TRC20' && 2) }
                            { data['Currency'] === 'USDT-TRC20' ? <br/> : <>&nbsp;</> }
                            { data['Currency'] }
                        </b>
                        <span>
                            { HelperService.FormatFiat(data['BalanceUSD']) }
                        </span>
                    </p>
                </div>
            ),
        },
        {
            Field: 'RiskScore',
            Title: t('Risk level'),
            Description: t('The indicator of the legality of funds on the crypto address. The higher the percentage, the worth.'),
            Width: '180px',
            Cell: (data) => (
                <RiskScoreCell
                    address={data}
                    riskScore={data['RiskScore']}
                    lastCheckDate={data['LastCheckDate']}
                    onCheck={() => onCheck(data)}
                    onQuarantine={() => dispatch(setQuarantineAddress(data))}
                />
            ),
        },
        {
            Field: 'AdditionalInfo',
            Title: t('Description'),
            Description: t('The description that was written when creating the crypto address.'),
            Width: '160px',
            Filter: {
                Type: 1
            },
            Cell: (data) => (
                <div className="commentBox">
                    <span title={data['AdditionalInfo']}>{ data['AdditionalInfo'] }</span>
                    <Icon
                        type={Icons.EDIT_PEN}
                        width={16}
                        height={16}
                        onClick={() => openModalEditDescription(data)}
                    />
                </div>
            )
        },
        {
            Field: 'Address',
            Title: t('Crypto address'),
            Width: '247px',
            Filter: {
                Type: 1
            },
            Cell: (data) => (
                <div className="tableBodyItemCellAddress">
                    <a href={addressLink(data.Currency, data.Address)} 
                        target="_blank" rel="noreferrer"
                        className="tableBodyItemCellAddress__link"
                        data-content-start={data.Address.substring(0, data.Address.length/2|0)}
                        data-content-end={data.Address.substring(data.Address.length/2|0)}
                    >
                    </a>
                    <NotificationButton data={data.Address} text={t('Copied')} />
                </div>
            ),
        },
        {
            Width: '16px',
            Cell: (data) => (
                <div className="tableBodyItemCellActions">
                    <Icon
                        type={Icons.WITHDRAW}
                        width={16}
                        height={16}
                        onClick={() => onWithdraw(data['Currency'], data['MerchantId'], data['Address'])}
                    />
                </div>
            ),
        }
    ];

    const onPaginate = async (tablePage = page, tableCount = count, tableSearch = search, tableSort = sort, tableFilters = filters) => {
        setLoading(true);
        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        const response = await RequestService.GetAddresses(tablePage, tableCount, tableSearch, tableSort, tableFilters, isHiddeAddresses);

        setSelectedItems(new Array(response.Total).fill(false));
        setTotal(response['Total']);
        setData(response['Addresses']);

        setLoading(false);
    };

    useEffect(() => {
        onPaginate().then();
    }, [needUpdateAddresses, isHiddeAddresses]); // eslint-disable-line react-hooks/exhaustive-deps

    const onQuarantine = async () => {
        const response = await RequestService.QuarantineAdd(quarantineAddress['Id']);
        dispatch(setQuarantineAddress(null));

        if (response['Status'] !== 'Success') {
            NotificationService.Push(t('Failed to add to quarantine'), true);
            return;
        }

        NotificationService.Push(t('Successfully quarantined'), false);
        onPaginate().then();
        fetchAllWalletsAddresses().then();
    };

    const isActiveRowCallback = (row) => riskAddress ? riskAddress.Id === row.Id : false;

    const walletsListProps = { sourcePage: 'wallets', data: balancesData, onPaginate, onWithdraw, page, count, search, sort, filters,
     merchantId: Number(merchantId), selectedWalletIndex, setSelectedWalletIndex };

    const tableProps = { isLoading, columns, page, count, search, sort, filters, total, data, onPaginate, isActiveRowCallback, selectedItems, setSelectedItems,
        setNeedUpdateAddresses, isHiddeAddresses, setIsHiddeAddresses, balancesData, selectedWalletIndex };

    return (
        <div className="tabContent">
            <WalletsList {...walletsListProps} />
            <div className="tableWrapper">
                <Table id="merchantAddressesTable" { ...tableProps } />
            </div>
            {quarantineAddress && (
                <DeleteModal
                    title={t('Adding to quarantine')}
                    subtitle={t('Are you sure you want to quarantine the address?')}
                    description={quarantineAddress['Address']}
                    onAccept={onQuarantine}
                    onClose={() => dispatch(setQuarantineAddress(null))}
                />
            )}
        </div>
    )
};

AddressesTab.defaultProps = {
    riskAddress: null,
};

AddressesTab.propTypes = {
    onCheck: PropTypes.func.isRequired,
    onWithdraw: PropTypes.func.isRequired,
    merchantId: PropTypes.string.isRequired,
    riskAddress: PropTypes.object,
};

export default AddressesTab;