import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import SettingsModal from './components/SettingsModal';
import TableControls from './components/TableControls';
import TableContent from './components/TableContent';
import './Table.scss';

const Table = ({ id, className, setNeedUpdateAddresses, isLoading, columns, page, count, search, sort, filters, 
    datesRange, total, data, onPaginate, selectedItems, setSelectedItems, isActiveRowCallback, isHiddeAddresses,
    setIsHiddeAddresses, noFilters, noPagitations, uploadedData, balancesData, selectedWalletIndex }) => {

    const [isTableSettings, setIsTableSettings] = useState(false);
    const [tableSettings, setTableSettings] = useState([]);

    useEffect(() => {
        let tableSettings = JSON.parse(localStorage.getItem(id));

        if (tableSettings !== null) {
            tableSettings = tableSettings.filter(item => {
                if (item.title === 'Select') {
                    setIsHiddeAddresses(item.isVisible);
                    return false;
                }
                    return true;
            })
        } else {
            tableSettings = [];

            for (let i = 0; i < columns.length; i++) {
                if (!columns[i].Title) {
                    continue;
                }

                tableSettings.push({ title: columns[i].Title.props.id, isVisible: true });
            }
        }

        setTableSettings(tableSettings);
    }, [id, columns, setIsHiddeAddresses]);

    if (tableSettings.length === 0 || columns.length === 0) {
        return null;
    }

    const modalProps = { id, setIsTableSettings, tableSettings, setTableSettings, isHiddeAddresses, setIsHiddeAddresses }
    const controlsProps = { id, page, count, search, sort, filters, datesRange, total, onPaginate, setIsTableSettings, noFilters, noPagitations, uploadedData }
    const contentProps = { setNeedUpdateAddresses, columns, data, page, count, search, sort, filters, total, isLoading,
        tableSettings, onPaginate, selectedItems, setSelectedItems, isActiveRowCallback, balancesData, selectedWalletIndex
    };

    return (
        <div id={id} className={classNames('table', className)}>
            {isTableSettings && <SettingsModal {...modalProps} />}
            <TableControls {...controlsProps} />
            <TableContent {...contentProps} />
        </div>
    );
};

Table.defaultProps = {
    id: null,
    className: null,
    isLoading: false,
    columns: [],
    actions: [],
    total: 0,
    data: [],
    onPaginate: () => { },
    isActiveRowCallback: () => { },
};

Table.propTypes = {
    id: PropTypes.string.isRequired,
    className: PropTypes.string,
    isLoading: PropTypes.bool.isRequired,
    columns: PropTypes.arrayOf(PropTypes.object).isRequired,
    actions: PropTypes.arrayOf(PropTypes.object).isRequired,
    total: PropTypes.number.isRequired,
    data: PropTypes.arrayOf(PropTypes.object).isRequired,
    onPaginate: PropTypes.func.isRequired,
    isActiveRowCallback: PropTypes.func.isRequired,
};

export default Table;
