import React, { useEffect } from 'react';
import t from '../../../../lang/t';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import Button from '../../../../components/Button/Button';
import { useDispatch, useSelector } from 'react-redux';
import { appSlice } from '../../../../store/slices';
import RootModalModel from '../../../../models/root-modal';
import RequestService from '../../../../services/RequestService';

import './BackupSettings.scss';

const BackupSettings = () => {
    const dispatch = useDispatch();
    const lastUpdate = useSelector((state) => state?.app?.backupLastUpdate);
    const date = lastUpdate !== null ? new Date(lastUpdate + 'Z') : null;
    const isShowDateInfo = Object.prototype.toString.call(date) === "[object Date]";

    const onOpenBackupSettings = () => {
        dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
            id: 'Backup',
            className: 'BackupModal',
            title: t('Backup settings'),
            tag: 'backup',
            isOpen: true,
            outsideParams: {},
        })))
    };

    useEffect(() => {
        const getBackupInfo = async () => {
            const responseBackupInfo = await RequestService.GetWalletLastBackupInfo();
            if (responseBackupInfo && responseBackupInfo.Status === 'Success') {
                dispatch(appSlice.actions.setBackupLastUpdate(responseBackupInfo?.Date));
            }
        };
        getBackupInfo();
      }, [dispatch]);
      
    return (
        <div className="BackupSettings">
            <div className="profileModalTabSettingsBackup">
                <h4>
                    <Icon type={Icons.MORE} width={21} height={21}/>
                    <span>{t('Backup')}</span>
                </h4>
            </div>
            <div className="BackupSettings__LastUpdate">
                {isShowDateInfo ? (<div className="LastUpdate__Date">
                        <div className="Date__Title">
                            {t('Last backup date')}:
                        </div>
                        <div className="Date__Info">
                            {date.toLocaleString()}
                        </div>
                    </div>) :
                    (<div className="LastUpdate__Notify">
                        <div className="Notify__Title">
                            {t('No backup was made')}
                        </div>
                    </div>)}
            </div>
            <div className="BackupSettings__Buttons">
                <Button onClick={onOpenBackupSettings}>{t('Set up backup')}</Button>
            </div>
            <div className="profileModalTabSettingsSeparator"/>
        </div>
    );
};

export default BackupSettings;
