import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useIntl from 'react-intl/lib/src/components/useIntl';
import Input from '../../../../../../components/Input/Input';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import RiskIcon from '../RiskIcon';
import { withdrawalSlice } from '../../../../../../store/slices';

import './ReceiverAddress.scss';

const ReceiverAddress = () => {
    const dispatch = useDispatch();
    const address = useSelector((state) => state?.withdrawal?.manualWithdrawal?.receiverAddress);
    const setAddress = (value) => dispatch(withdrawalSlice.actions.setReceiverAddressWithdrawalModel(value));
    const modal = useSelector((state) => state?.app?.modal);
    const intl = useIntl();

    const onChange = (e) => setAddress(e.target.value);

    const doCheck = (address) => {
        const { onCheck } = modal?.outsideParams;
        if (onCheck && typeof onCheck === 'function') onCheck(address);
    };

    return (
        <div className="ReceiverAddress">
            <Input
                id="receiver-address-input"
                type="text"
                label={intl.formatMessage({ id: 'Recipient address' })}
                placeholder={intl.formatMessage({ id: 'Insert address' })}
                value={address}
                onChange={onChange}
            />
            {address && false && <>
                <div className="risk-status">
                    <RiskIcon type="unchecked"/>
                    -
                </div>
                <Icon className="check-icon" type={Icons.CHECK} width={18} height={18} onClick={() => doCheck(address)}/>
            </>}
        </div>
    );
};

export default ReceiverAddress;
