import React, { useState, useEffect, useRef } from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import t from '../../lang/t';
import Icon, { Icons } from '../Icon/Icon';
import './Select.scss';

const Select = ({ id, className, isMulti, label, options, value, disabled, excludeActiveOption, onChange, ...props }) => {
    const ref = useRef(null);

    const [isOpen, setIsOpen] = useState(false);

    const isOptionActive = (option) => {
        return isMulti ? value.findIndex((item) => item.value === option.value) !== -1 : option.value === value.value;
    };

    const onOptionClick = (option) => {
        if (isMulti) {
            const newValue = Object.assign([], value);
            let optionIndex = -1;
            newValue.forEach((v, index) => {
                if(v.value === option.value) optionIndex = index;
            });

            if (optionIndex === -1) {
                newValue.push(option);
            } else {
                newValue.splice(optionIndex, 1);
            }

            onChange(newValue);
        } else {
            onChange(option);
        }

        setIsOpen(false);
    };

    const onClickOutside = (e) => {
        if (ref.current && ref.current.contains && !ref.current.contains(e.target)) {
            if (isOpen) setIsOpen(false);
        }
    };

    const onOpen = () => () => setIsOpen(false);

    useEffect(() => {
        document.addEventListener('click', onOpen, true);
        return () => {
            document.removeEventListener('click', onOpen, true);
        };
    }, []);

    useEffect(() => {
        document.addEventListener('click', onClickOutside, true);
        return () => {
            document.removeEventListener('click', onClickOutside, true);
        };
    });

    return (
        <div ref={ref} className={classNames('select', className)} onClick={(e) => e.stopPropagation()}>
            <span onClick={() => disabled ? null : setIsOpen(value => !value)}>
                {isMulti ? (value.length === 0 ? t('Not selected') : t('Selected {count}', { count: value.length })) : value ? value.label : ''}
            </span>
            <label htmlFor="">{label}</label>
            <Icon type={Icons.SELECT_ARROW} width={16} height={16}
                  onClick={() => disabled ? null : setIsOpen(value => !value)}/>
            {isOpen &&
            <ul>
                {options.filter((option) => {
                    return excludeActiveOption ? (option.value !== value.value) : true
                }).map((option, index) => {
                    return (
                        <li key={index} className={isOptionActive(option) ? 'active' : null}
                            onClick={() => onOptionClick(option)}>{option.label}</li>
                    );
                })}
            </ul>}
        </div>
    );
};

Select.defaultProps = {
    id: null,
    className: null,
    isMulti: false,
    label: '',
    options: [],
    value: null,
    disabled: false,
    excludeActiveOption: false,
    onChange: () => {
    }
};

Select.propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    isMulti: PropTypes.bool,
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    options: PropTypes.arrayOf(Object),
    value: PropTypes.any,
    disabled: PropTypes.bool,
    excludeActiveOption: PropTypes.bool,
    onChange: PropTypes.func
};

export default Select;
