import React from 'react';
import * as PropTypes from 'prop-types';
import './RiskIcon.scss';

const RiskIcon = ({ type }) => {
    const appearance = {
        low: {
            fill: '#18AA6E',
        },
        medium: {
            fill: '#EACF45',
        },
        high: {
            fill: '#FF6161'
        },
        unchecked: {
            fill: '#C4C4C4',
        },
    };
    const colors = appearance[type];

    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="none"
            viewBox="0 0 16 16"
            className="RiskIcon"
        >
            <path
                fill={colors.fill}
                fillOpacity="0.4"
                fillRule="evenodd"
                d="M8 14A6 6 0 108 2a6 6 0 000 12zm0 2A8 8 0 108 0a8 8 0 000 16z"
                clipRule="evenodd"
            />
            <path
                fill={colors.fill}
                d="M9.512 4.035a.258.258 0 01.117.3l-.928 3.017h1.713a.258.258 0 01.188.434L6.477 12.17a.258.258 0 01-.434-.253L6.97 8.9H5.258a.258.258 0 01-.188-.435L9.195 4.08a.258.258 0 01.317-.046z"
            />
        </svg>
    );
};

RiskIcon.defaultProps = {
    type: 'unchecked',
};

RiskIcon.propTypes = {
    type: PropTypes.oneOf(['low', 'medium', 'high', 'unchecked']),
};

export default RiskIcon;