import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import t from '../../lang/t';
import NotificationService from '../../services/NotificationService';
import StorageService from '../../services/StorageService';
import RequestService from '../../services/RequestService';
import DeleteModal from '../../modals/DeleteModal/DeleteModal';
import MerchantsHead from './components/MerchantsHead';
import MerchantModal from './components/MerchantsModal';
import MerchantsTable from './components/MerchantsTable';
import BalanceSheetTab from './components/BalanceSheetTab';

import './Merchants.scss';
import { useSelector } from 'react-redux';

const Merchants = () => {
    const { tab } = useParams();
    const [isLoading, setIsLoading] = useState(false);

    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState([]);

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);

    const [id, setId] = useState(0);
    const [name, setName] = useState('');
    const [link, setLink] = useState('');
    const [isReusable, setIsReusable] = useState(false);

    const [isNameError, setIsNameError] = useState(false);

    const modal = useSelector((state) => state?.app?.modal);

    const onPaginate = async (tablePage = page, tableCount = count, tableSearch = search, tableSort = sort, tableFilters = filters) => {
        setIsLoading(true);

        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        const response = await RequestService.GetMerchants(tablePage, tableCount, tableSearch, tableSort, tableFilters);

        setTotal(response['Total']);
        setData(response['Merchants']);

        setIsLoading(false);
    };

    useEffect(() => {
        onPaginate(page, count, search, sort, filters)
    }, [modal]) // eslint-disable-line react-hooks/exhaustive-deps

    const onCreateModalOpen = () => {
        setIsNameError(false);
        setIsModalOpen(true);
    };

    const onEditModalOpen = (data) => {
        setIsNameError(false);

        setId(data['Id']);
        setName(data['Name'] ?? '');
        setLink(data['CallBackLink'] ?? '');
        setIsReusable(data['IsReusableMode'] ?? false);

        setIsModalOpen(true);
    };

    const onModalClose = () => {
        setId(0);
        setName('');
        setLink('');
        setIsReusable(false);

        setIsModalOpen(false);
        setIsDeleteModalOpen(false);
    };

    const onMerchant = async () => {
        setIsNameError(false);

        const response = id
            ? await RequestService.UpdateMerchant(id, name, link, isReusable)
            : await RequestService.CreateMerchant(name, link, isReusable);

        if (response['Status'] !== 'Success') {
            setIsNameError(true);
            return;
        }

        setId(0);
        setName('');
        setLink('');
        setIsReusable(false);

        setIsModalOpen(false);
        await onPaginate(page, count, search, sort, filters);

        NotificationService.Push(id ? t('Merchant edited') : t('Merchant added'), false);

        StorageService.OnMerchants().then();
        StorageService.GetMerchantsList().then();
    };

    const onDeleteModalOpen = (data) => {
        setId(data['Id']);
        setName(data['Name'] ?? '');

        setIsDeleteModalOpen(true);
    };

    const onDelete = async () => {
        const response = await RequestService.DeleteMerchant(id);

        if (response.Status !== 'Success') {
            NotificationService.Push(t('Unable to delete merchant'), true);
            return;
        }

        setIsDeleteModalOpen(false);
        await onPaginate(page, count, search, sort, filters);

        StorageService.OnMerchants().then();
        StorageService.GetMerchantsList().then();
    };

    const merchantsModalProps = { id, name, setName, link, setLink, isReusable, setIsReusable, isNameError, onMerchant, onModalClose };

    const merchantTableProps = { isLoading, page, count, search, sort, filters, total, data, onPaginate, onEditModalOpen, onDeleteModalOpen };

    //todo translate
    return (
        <div className="merchants">
            {isModalOpen && (
                <MerchantModal { ...merchantsModalProps } />
            )}
            {isDeleteModalOpen && (
                <DeleteModal
                    title={t('Deleting a merchant')}
                    subtitle={t('Confirm merchant deletion')}
                    description={name}
                    onAccept={onDelete}
                    onClose={onModalClose}
                />
            )}
            <MerchantsHead onCreateModalOpen={onCreateModalOpen} tab={tab} />
            {tab === undefined &&
                <div className="table-wrapper">
                    <MerchantsTable { ...merchantTableProps } />
                </div>
            }
            {tab === 'balance-sheet' &&
                <BalanceSheetTab />
            }

        </div>
    );
};

export default Merchants;
