import React, { useState } from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../lang/t';
import config from '../../AppConfig';
import Button from '@mui/material/Button';
import ButtonIcon from '../../components/ButtonIcon';
import DeleteModal from '../../modals/DeleteModal/DeleteModal';
import NotificationService from '../../services/NotificationService';
import RequestService from '../../services/RequestService';
import AdministratorsTable from './components/AdministratorsTable';
import AdministratorsModal from './components/AdministratorsModal';
import PageHeadTabs from '../../components/PageHeadTabs';
import './Administrators.scss';

const { routes } = config;

const Administrators = () => {
    const [isLoading, setIsLoading] = useState(false);
    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState([]);

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);

    const [id, setId] = useState(0);
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [rights, setRights] = useState([]);
    const [merchants, setMerchants] = useState([]);

    const [isEmailError, setIsEmailError] = useState(false);

    const intl = useIntl();

    const tabOptions = [{to: routes.administrators, value: t('Administrators')}];

    const onPaginate = async (tablePage = page, tableCount = count, tableSearch = search, tableSort = sort, tableFilters = filters) => {
        setIsLoading(true);

        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        const response = await RequestService.GetAdministrators(tablePage, tableCount, tableSearch, tableSort, tableFilters);
        setTotal(response.Total);
        setData(response.Admins);

        setIsLoading(false);
    };

    const onCreateModalOpen = () => {
        setIsEmailError(false);
        setIsModalOpen(true);
    };

    const onEditModalOpen = (data) => {
        setIsEmailError(false);
        setId(data.Id);
        setName(data.Name ?? '');
        setEmail(data.Email ?? '');
        setRights(data.Rights.map((right) => ({ label: right.Name, value: right.Id })) ?? []);
        setMerchants(data.AvailableMerchants.map((merchant) => ({ label: merchant.Name, value: merchant.Id })) ?? []);
        setIsModalOpen(true);
    };

    const onModalClose = () => {
        setId(0);
        setName('');
        setEmail('');
        setPassword('');
        setRights([]);
        setMerchants([]);

        setIsModalOpen(false);
        setIsDeleteModalOpen(false);
    };

    const isAdministrator = () => {
        return id ? name && email : name && email && password;
    };

    const onAdministrator = async () => {
        setIsEmailError(false);

        const rightsData = rights.map((right) => right.value);
        const merchantsData = merchants.map((merchant) => merchant.value);

        let response;
        if (id) {
            response = await RequestService.UpdateAdministrator(id, name, email, rightsData, merchantsData);
        } else {
            response = await RequestService.CreateAdministrator(name, email, password, rightsData, merchantsData);
        }
        if (response.Status !== 'Success') {
            setIsEmailError(true);
            return;
        }

        setId(0);
        setName('');
        setEmail('');
        setPassword('');
        setRights([]);
        setMerchants([]);

        setIsModalOpen(false);
        await onPaginate(page, count, search, sort, filters);

        NotificationService.Push(id ? t('Administrator edited') : t('Administrator added'), false);
    };

    const onDeleteModalOpen = (data) => {
        setId(data.Id);
        setName(data.Name ?? '');
        setIsDeleteModalOpen(true);
    };

    const onDelete = async () => {
        const response = await RequestService.DeleteAdministrator(id);
        if (response.Status !== 'Success') {
            NotificationService.Push(t('Remove administrator is impossible'), true);
            return;
        }
        setId(0);
        setName('');
        setIsDeleteModalOpen(false);
        await onPaginate(page, count, search, sort, filters);
    };


    const modalProps = {
        id, name, setName, email, setEmail, password, setPassword, rights, setRights,
        merchants, setMerchants, onModalClose, onAdministrator, isEmailError, isAdministrator
    };
    const tableProps = { isLoading, page, count, search, sort, filters, total, data, onPaginate, onEditModalOpen, onDeleteModalOpen };

    return (
        <div className="administrators">
            {isModalOpen && <AdministratorsModal {...modalProps} />}
            {isDeleteModalOpen &&
                <DeleteModal title={intl.formatMessage({ id: 'Removing an administrator' })} subtitle={intl.formatMessage({ id: 'Confirm administrator removal' })}
                    description={name} onAccept={onDelete} onClose={onModalClose} />}
            <div className="administratorsHead">
                <PageHeadTabs tabOptions={tabOptions} />
                <div className="administratorsHeadContainer">
                    <Button variant="contained" className="administratorsHeadContainerAdd text-initial text-padding" onClick={onCreateModalOpen}>
                        <ButtonIcon type="administrator" />
                        <span>{t('Add administrator')}</span>
                    </Button>
                    {/*<Button className="administratorsHeadContainerReference">*/}
                    {/*    <Icon type={Icons.REFERENCE} width={32} height={32}/>*/}
                    {/*</Button>*/}
                </div>
            </div>
            <AdministratorsTable {...tableProps} />
        </div>
    );
};
export default Administrators;
