import React, {useEffect, useState} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { appSlice } from '../../../../store/slices';
import t from '../../../../lang/t';
import Textarea from '../../../../components/Textarea/Textarea';

import './EditDescription.scss';
import RequestService from '../../../../services/RequestService';
import NotificationService from '../../../../services/NotificationService';
import RootModalModel from '../../../../models/root-modal';

const EditDescription = ({handleClose}) => {

    const modal = useSelector((state) => state?.app?.modal);
    const data = modal.outsideParams.data;
    const dispatch = useDispatch();

    const [description, setDescription] = useState(data.AdditionalInfo);

    useEffect(() => {
        dispatch(appSlice.actions.setLoaderStatus(false));
    }, [dispatch])

    const onChangeDescription = e => {
        setDescription(e);
    }

    const handleSave = async () => {
        const response = await RequestService.UpdateDescription(data.Id, description);

        dispatch(appSlice.actions.updateModalStructure(new RootModalModel({ ...modal, isOpen: false})))

        if (response.Status !== 'Success') {
            NotificationService.Push(t('An error occurred while changing the description'), true);
        } else {
            NotificationService.Push(t('Description was edited'), false)
        }

        modal.outsideParams.setNeedUpdateAddresses(prev => !prev)
    }

    return (
        <>
            <Textarea
                label={t('Description cripto address')}
                value={description ? description : ''}
                onChange={(e) => onChangeDescription(e.target.value)}
            />
            <div className="block-btn">
                <button className="default light" onClick={handleClose}>{t('Cancel')}</button>
                <button className="default" onClick={handleSave}>{t('Save')}</button>
            </div>
        </>
    )
}

export default EditDescription;
