const SubscribeService = {
    Actions: {
        NOTIFICATION: 'NOTIFICATION'
    },
    Subscribers: {},

    Subscribe: (action, callback) => {
        if (!SubscribeService.Subscribers[action]) {
            SubscribeService.Subscribers[action] = [];
        }

        SubscribeService.Subscribers[action].push(callback);
    },
    Action: (action, data) => {
        for (let i = 0; i < SubscribeService.Subscribers[action].length; i++) {
            SubscribeService.Subscribers[action][i](data);
        }
    }
};
export default SubscribeService;