import BaseModel from './base';
import Config from '../AppConfig';

export default class VersionInfoModel extends BaseModel {
    constructor(initData) {
        super();

        this.version = Config.version || '1.0.0';
        this.hash = '';
        this.date = '';
        this.formatedVersion = '';

        this.copyFrom(initData);
        this.init(initData);
        this.formatVersion();
    }

    init = (initData) => {
        if (!initData) return;
        const { commit = { hash: '', shortHash: '', date: '', message: '' } } = initData;
        const date = !!commit?.date ? new Date(commit?.date) : '';

        this.hash = commit?.shortHash;
        this.date = this.formatDate(date);
    };

    formatDate = (date, splitSymbol = '.', splitDate = 10) => {
        if (typeof date !== 'object') return date;
        const yyyy = date.getFullYear();
        let dd = date.getDate();
        let mm = date.getMonth() + 1;

        if (dd < splitDate) {
            dd = '0' + dd;
        }

        if (mm < splitDate) {
            mm = '0' + mm;
        }

        return dd + splitSymbol + mm + splitSymbol + yyyy;
    };

    formatVersion = (splitSymbol = '-') => {
        if (!this.version || !this.hash || !this.date) return;
        this.formatedVersion = `${this.version}${splitSymbol}${this.hash}${splitSymbol}${this.date}`;
    };
}
