import BaseModel from './base';
import CryptocurrencyModel from './cryptocurrency';
import { transactionTypes } from '../containers/RootModal/components/Withdrawal/components/AdditionalDropdown/component/TransactionCommission/transaction-types';
import HelperService from '../services/HelperService';
import Types from '../classes/Types';

export default class WithdrawalFormModel extends BaseModel {
    constructor(initData) {
        super();

        this.currency = new CryptocurrencyModel({});
        this.merchant = [];
        this.addresses = [];
        this.riskLevel = {};
        this.fiatAmount = 0;
        this.cryptoAmount = 0;
        this.commissionType = HelperService.HasLocalStorageProperty(Types.localStorageCommissionTypeName) ? transactionTypes[HelperService.GetLocalStorageProperty(Types.localStorageCommissionTypeName)] : transactionTypes.medium;
        this.commission = 0;
        this.commissionPerKb = 0;
        this.commissionFiat = 0;
        this.comment = '';
        this.confirmation = false;
        this.receiverAddress = '';
        this.isInit = false;
        this.availableBalance = 0;
        this.commissionsBySpeed = {
            fast: 0,
            medium: 0,
            slow: 0,
        };
        this.transactionSize = 0;

        this.copyFrom(initData);
    }
}
