import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import t from '../../../../lang/t';
import { appSlice } from '../../../../store/slices';
import RootModalModel from '../../../../models/root-modal';
import RequestService from '../../../../services/RequestService';
import Icon, { Icons } from '../../../../components/Icon/Icon';
// import Tooltip from '../../../../components/Tooltip/Tooltip';
import NotificationService from '../../../../services/NotificationService';
import ReceivingCurrencySelect from './components/ReceivingCurrencySelect';
import ReceivingMerchantSelect from './components/ReceivingMerchantSelect';
import ReceivingAddressesSelect from './components/ReceivingAddressesSelect';
import ReceivingResults from './components/ReceivingResults';
import Button from '@mui/material/Button';

import './Receiving.scss';

// const PAID_CREATION_CURRENCIES = ['UDST (TRC)'];

const Receiving = () => {
    const dispatch = useDispatch();
    const [additionallyOpen, setAdditionallyOpen] = useState(false);

    const modal = useSelector((state) => state.app.modal);
    const merchants = useSelector((state) => state?.merchant.merchants);
    const [selectedCurrency, setSelectedCurrency] = useState(modal?.outsideParams?.currency);
    const [selectedMerchant, setSelectedMerchant] = useState(merchants && (merchants.find(mrch =>  mrch.Id === modal?.outsideParams?.merchantId) || merchants[0]));
    const [selectedAddress, setSelectedAddress] = useState(null);
    const [allAddresses, setAllAddresses] = useState([]);
    const [filteredAddresses, setFilteredAllAddresses] = useState([]);
    const [addressesCountInMerchants, setAddressesCountInMerchants] = useState({});
    const [createdAddress, setCreatedAddress] = useState(null);
    const [isLoading, setIsLoading] = useState(false);

    const filterAddress = ({ addresses = allAddresses, merchant = selectedMerchant, currency = selectedCurrency }) => {
        setFilteredAllAddresses(addresses.filter(address => address.MerchantId === merchant.Id && address.Currency === currency));
    };

    const updateAddressesCountInMerchants = ({ addresses = allAddresses, currency = selectedCurrency }) => {
        const addressesCountInMerchants = merchants.reduce((acc, merchant) => { acc[merchant.Id] = 0; return acc }, {});
        addresses.forEach(address => { if (address.Currency === currency) { addressesCountInMerchants[address.MerchantId]++ } });
        setAddressesCountInMerchants(addressesCountInMerchants);
    };

    const selectMerchant = (merchant) => {
        if (merchant !== selectedMerchant) {
            setSelectedMerchant(merchant);
            setSelectedAddress(null);
            filterAddress({ merchant });
        }
    }

    const selectCurrency = (currency) => {
        if (currency !== selectedCurrency) {
            setSelectedCurrency(currency);
            setSelectedAddress(null);
            filterAddress({ currency });
            updateAddressesCountInMerchants({ currency });
        }
    }

    const toggleAdditionally = () => {
        setAdditionallyOpen(!additionallyOpen);
        setSelectedAddress(null);
    };

    const fetchAllAddresses = () => {
        Promise.all([
            RequestService.GetAddresses(1, 10000, '', { by: 'Id', desc: true }, []),
            RequestService.GetQuarantine(1, 10000, '', { by: 'Id', desc: true }, []),
        ]).then((address) => {
            const allAddresses = address.filter(address => address.Status === 'Success').map(address => address.Addresses).flat();
            setAllAddresses(allAddresses);
            const sortedAddresses = allAddresses.sort((a, b) => b.Balance - a.Balance);
            filterAddress({ addresses: sortedAddresses });
            updateAddressesCountInMerchants({ addresses: allAddresses });
        })
    }

    useEffect(() => {
        fetchAllAddresses();
    }, []) // eslint-disable-line react-hooks/exhaustive-deps

    const getRequisites = async () => {
        if (selectedAddress) {
            setCreatedAddress(selectedAddress);
        } else {
            setIsLoading(true);
            const response = await RequestService.CreateAddress(selectedCurrency, selectedMerchant.Id);

            setIsLoading(false);

            if (response.Status === 'Success') {
                modal?.outsideParams?.onPaginate();
                setCreatedAddress({ ...response, MerchantId: selectedMerchant.Id, MerchantName: selectedMerchant.Name, Currency: selectedCurrency });
            }

            if (response.Status === 'Error') {
                NotificationService.Push(t('Failed to create address'), true);
                return;
            }
        }
    }

    const finishReceiving = () => {
        dispatch(appSlice.actions.updateModalStructure(new RootModalModel({ ...modal, isOpen: false })))
    };

    return (
        <div className="Receiving">
            {!createdAddress ?
                <div className="Receiving__settings">
                    <ReceivingCurrencySelect
                        selectedCurrency={selectedCurrency}
                        setSelectedCurrency={selectCurrency}
                        currencyBalances={modal?.outsideParams?.currencyBalances} />
                    <ReceivingMerchantSelect
                        selectedMerchant={selectedMerchant}
                        setSelectedMerchant={selectMerchant}
                        addressesCountInMerchants={addressesCountInMerchants} />
                    <div className="Receiving__additionally">
                        <div className="additionally__toggle-wrapper">
                            <button className={classNames('additionally__toggle', { open: additionallyOpen })} onClick={toggleAdditionally}>
                                <div className="additionally__toggle-inside">
                                    {t('Additionally')}
                                    <Icon className="dropdown-icon" type={Icons.SELECT_ARROW} width={24} height={24} />
                                </div>
                            </button>
                        </div>
                        {additionallyOpen &&
                            <ReceivingAddressesSelect
                                selectedAddress={selectedAddress}
                                setSelectedAddress={setSelectedAddress}
                                addresses={filteredAddresses}
                                placeholder={'Crypto address to receive'} />
                        }
                        {additionallyOpen ?
                            <div className="Receiving__creating-info">
                                {t('If the address is not selected, it will be created automatically on the selected merchant')}
                            </div> :
                            <div className="Receiving__creating-info">
                                <span>{t('To receive funds to an axisting merchants and address, click "Additionally", or continue to receive a new address for')} </span>
                                <Link to={`/merchants/${selectedMerchant?.Id}`} className="Receiving__merchant-link" onClick={finishReceiving}>
                                    {selectedMerchant?.Name}
                                </Link>
                            </div>
                        }
                        {/* {PAID_CREATION_CURRENCIES.includes(selectedCurrency) &&
                            <div className="creation-paid">
                                <div className="creation-paid__label">
                                    <span>Плата за создание криптоадреса</span>
                                    <Tooltip id="creation-paid-tooltip" text="Создание криптоадресов на блокчейне TRC на криптовалюте USDT платное и составляет ...." />
                                </div>
                                <ReceivingAddressesSelect
                                    selectedAddress={selectedPaidAddress}
                                    setSelectedAddress={setSelectedPaidAddress}
                                    addresses={allAddresses}
                                    placeholder={'Криптоадрес для списания комиссии'} />
                            </div>
                        } */}
                        <div className="Receiving__send">
                            <Button className="Receiving__send-button" disabled={isLoading} variant="contained" onClick={getRequisites}>{t('Receive the details')}</Button>
                        </div>
                    </div>
                </div> :
                <ReceivingResults createdAddress={createdAddress} isNew={!!selectedAddress} finishReceiving={finishReceiving} />}
        </div>
    );
};

export default Receiving;
