import React, {useEffect, useState} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { CurrencySelect, MerchantSelect, RiskSelect, AdditionalDropdown, Switcher } from './components';
import RequestService from '../../../../services/RequestService';
import { appSlice } from '../../../../store/slices';
import Types from '../../../../classes/Types';
import { useIntl } from 'react-intl';
import t from '../../../../lang/t';
import Input from '../../../../components/Input/Input';
import Textarea from '../../../../components/Textarea/Textarea';
import Button from '../../../../components/Button/Button';

import './AutoWithdrawal.scss';
import NotificationService from '../../../../services/NotificationService';
import RootModalModel from '../../../../models/root-modal';
import Switch from '../../../../components/Switch/Switch';
import Tooltip from '../../../../components/Tooltip/Tooltip';

const AutoWithdrawal = () => {
    const modal = useSelector((state) => state?.app?.modal);
    const modalData = modal.outsideParams.data;
    const merchantId = modal.outsideParams.MerchantId;
    const dispatch = useDispatch();

    const [balances, setBalances] = useState([]);
    const cryptocurrencies = useSelector((state) => state?.cryptocurrency?.cryptocurrencies);
    const merchants = useSelector((state) => state?.merchant.merchants);

    const { autoWithdrawTypes, autoWithdrawCommissionSpeedTypes, autoWithdrawQualities } = Types;
    const intl = useIntl();

    const translatedAutoWithdrawTypes = autoWithdrawTypes.map((type) => ({ ...type, label: intl.formatMessage({ id: type.label }) }));
    const translatedAutoWithdrawCommissionSpeedTypes = autoWithdrawCommissionSpeedTypes.map((type) => ({ ...type, label: intl.formatMessage({ id: type.label }) }));

    const [data, setData] = useState({
        Type: translatedAutoWithdrawTypes[0],
        MerchantId: merchants[0],
        CryptoCurrency: cryptocurrencies[0],
        MinimalWithdrawalAmount: '0',
        WithdrawalAddresses: '',
        ExcludedAddresses: [],
        CommissionType: translatedAutoWithdrawCommissionSpeedTypes[0],
        MinAmountVerifyAddress: '0',
        MinAddressQuality: autoWithdrawQualities[0],
        IsEnable: true
    });

    const isValid = () => +data.MinimalWithdrawalAmount > 0 && !!data.WithdrawalAddresses && data.MinAmountVerifyAddress !== '' && +data.MinAmountVerifyAddress >= 0;

    const onChange = async (field, value) => {
        const newData = Object.assign({}, data);
        newData[field] = value;
        setData(newData);
        if (field === 'MerchantId') {
            getBalances(value.Id);
        }
    };

    const getBalances = async (merchantId) => {
        dispatch(appSlice.actions.setLoaderStatus(true));
        const response = await RequestService.GetBalances([merchantId]);

        if (response.Status === 'Success') {
            setBalances(response.Balance);
        };
        dispatch(appSlice.actions.setLoaderStatus(false));
    }

    const onCreate = async () => {
        data.Type = data.Type.value;
        data.MerchantId = data.MerchantId.Id;
        data.CryptoCurrency = data.CryptoCurrency.Symbol;
        data.WithdrawalAddresses = data.WithdrawalAddresses.split('\n');
        data.CommissionType = data.CommissionType.value;
        data.MinAddressQuality = data.MinAddressQuality.value;

        let response;

        if (modalData) {
            response = await RequestService.UpdateAutoWithdrawal(data);
        } else {
            response = await RequestService.CreateAutoWithdrawal(data);
        }

        dispatch(appSlice.actions.updateModalStructure(new RootModalModel({ ...modal, isOpen: false, outsideParams: { reloadTable: true }})))

        if (response.Status !== 'Success') {
            NotificationService.Push(
                data.Id ? t('An error occurred while changing the Auto withdrawal') : t('An error occurred while creating the Auto withdrawal'),
                true
            );
            return;
        };

        NotificationService.Push(
            data.Id ? t('Auto withdrawal edited') : t('Auto withdrawal successfully created'),
            false
        );
    };

    useEffect(() => {
        let defaultMerchantId = merchants[0]?.Id;
        if (modalData !== null) {
            defaultMerchantId = merchants.filter((item) => item.Id === modalData.MerchantId)[0].Id;
            const newData = {
                Id: modalData.Id,
                Type: translatedAutoWithdrawTypes.filter((item) => item.value === modalData.Type)[0],
                MerchantId: defaultMerchantId,
                CryptoCurrency: cryptocurrencies.filter((item) => item.Symbol === modalData.CryptoCurrency)[0],
                MinimalWithdrawalAmount: modalData.MinimalWithdrawalAmount,
                WithdrawalAddresses: modalData.WithdrawalAddresses.join('\n'),
                ExcludedAddresses: [],
                CommissionType: autoWithdrawCommissionSpeedTypes.filter((item) => item.label === modalData.CommissionType)[0],
                MinAmountVerifyAddress: '0',
                MinAddressQuality: autoWithdrawQualities.filter((item) => item.value === modalData.MinAddressQuality)[0],
                IsEnable: modalData.IsEnable
            }
            setData(newData)        
        }
        
        getBalances(defaultMerchantId);
        
        if(merchantId) {
            onChange('MerchantId', merchantId)
        }

    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    const focusHandle = (e) => {
        if (+e.target.value === 0) onChange('MinimalWithdrawalAmount', '');
    };

    return (
        <div className="Withdrawal">
            <Switcher
                arrayButton={translatedAutoWithdrawTypes}
                data={data}
                dataType='Type'
                onChange={onChange}
                tooltip={t('Options to withdraw funds to crypto withdrawal addresses. \n\n Looped - the withdrawal will held to each address each time when minimal amount is reached. \n\n Single-time - the withdrawal will start only once to each withdraw address when minimal amount will be reached, then the automatic withdrawal will be turned off.')}
            />
            <CurrencySelect
                data={data}
                balances={balances}
                onChange={onChange}
                cryptocurrencies={cryptocurrencies}
                disabled={modalData !== null ? true : false}
            />
            <MerchantSelect
                merchants={merchants}
                data={data}
                onChange={onChange}
                disabled={modalData !== null ? true : false}
            />
            <RiskSelect disabled />
            
            <Input type="text" label={t('Min. withdrawal amount')} placeholder={intl.formatMessage({ id: 'Min. withdrawal amount' })}
                value={data.MinimalWithdrawalAmount.toString()}
                onFocus={focusHandle}
                onChange={e => {
                    const valueChecked = e.target.value.replace(/,/, '.').replace(/^\./, '0.').replace(/^0\d/, e => '0.' + e.slice(1));
                    if (valueChecked.match(/^(\d*\.{0,1}\d{0,8}$)/)){
                        onChange('MinimalWithdrawalAmount', valueChecked)
                    }
                }}
            />

            <div className="autoWithdrawModalAddresses">
                <Textarea label={t('Crypto withdrawal addresses')} value={data.WithdrawalAddresses}
                    placeholder={intl.formatMessage({ id: 'Crypto withdrawal addresses (each address on a new line)' })}
                    onChange={(e) => onChange('WithdrawalAddresses', e.target.value)} />
                <p>
                    {t('Number of withdrawal crypto addresses')}:&nbsp;
                    {data.WithdrawalAddresses ? data.WithdrawalAddresses.split('\n').length : 0}
                </p>
                <Tooltip className="textarea-tooltip" text={t('Crypto addresses for funds withdrawal')} />
            </div>

            <div className="autoWithdrawModalActive">
                <Switch id="autoWithdrawModalActive" checked={data.IsEnable}
                    onChange={() => onChange('IsEnable', !data.IsEnable)} />
                <span>{t('Disable / Enable')}</span>
            </div>

            <AdditionalDropdown
                arrayButton={translatedAutoWithdrawCommissionSpeedTypes}
                data={data}
                dataType='CommissionType'
                onChange={onChange}
            />
            <Button className="autoWithdrawal-send" disabled={!isValid()} onClick={onCreate}>
                {modalData !== null ? t('Change') : t('Create')}
            </Button>
        </div>
    );
};

export default AutoWithdrawal;