import React, { useState } from 'react';
import PropTypes from 'prop-types';
import t from '../../lang/t';
import Modal from '../../components/Modal/Modal';
import InputFile from '../../components/InputFile/InputFile';
import Progress from '../../components/Progress/Progress';
import Button from '../../components/Button/Button';
import './ImportModal.scss';

const ImportModal = ({ onClose }) => {
    const [isImport, setIsImport] = useState(false);

    const [files, setFiles] = useState(null);
    const [progress, setProgress] = useState(0);

    const onImport = () => {
        const interval = setInterval(() => {
            setProgress(prev => {
                if (prev >= 100) {
                    setProgress(0);
                    setIsImport(false);

                    clearInterval(interval);
                    return prev;
                }

                return prev + 10;
            });
        }, 1000);
        setIsImport(true);
    };

    return (
        <div className="importantModal">
            <Modal title={t('System import')} description={!isImport
                ? t('Restoring a system from an image')
                : t('Please wait, this may take some time')} onClose={!isImport ? onClose : () => {}}>
                {!isImport &&
                <InputFile id="importantModalInputFile" files={files} onChange={(files) => setFiles(files)}/>}
                {isImport && <Progress progress={progress}/>}
                {!isImport && <p>{t('Select a previously exported file for system recovery')}</p>}
                {isImport && <p>{t('Upon completion of the restoration, the system will be logged out')}</p>}
                <Button disabled={files === null || files.length === 0 || isImport} onClick={onImport}>{t('Reestablish')}</Button>
            </Modal>
        </div>
    );
};

ImportModal.defaultProps = {
    onClose: () => {}
};

ImportModal.propTypes = {
    onClose: PropTypes.func.isRequired
};

export default ImportModal;