import { useEffect } from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import { useSelector } from 'react-redux';
import { settings } from '../settings';
import HelperService from '../../../../../services/HelperService';

const useGetBalances = ({ setData, datesRange, uploadedData }) => {
    const intl = useIntl();
    const lang = useSelector((state) => state?.app?.lang);
    const merchants = useSelector((state) => state?.merchant.merchants);

    const merchantsById = merchants.reduce((acc, merchant) => { acc[merchant.Id] = merchant.Name; return acc; }, {});

    const dataFilter = (data) => {
        return data.map((d) => {
            const {
                MerchantId = '',
                Currency = '',
                BalanceBegin = 0,
                TotalIn = 0,
                TotalOut = 0,
                BalanceEnd = 0,
            } = d;
            return {
                [intl.formatMessage({ id: 'Merchant' })]: merchantsById[MerchantId],
                [intl.formatMessage({ id: 'Currency' })]: Currency,
                [intl.formatMessage({ id: 'Time periond' })]: `${datesRange?.[0].toLocaleDateString()} - ${datesRange?.[1].toLocaleDateString()}`,
                [intl.formatMessage({ id: 'Balance at the beginning' })]: BalanceBegin,
                [intl.formatMessage({ id: 'Total IN' })]: TotalIn,
                [intl.formatMessage({ id: 'Total OUT' })]: TotalOut,
                [intl.formatMessage({ id: 'Balance at the end' })]: BalanceEnd,
            };
        });

    };

    useEffect(() => {       
        setData(dataFilter(uploadedData));

        const fromDate = HelperService.formatDate(datesRange?.[0], '-');
        const toDate = HelperService.formatDate(datesRange?.[1], '-', 10, 'T23:59:59.99999');
        settings.uploadSettings.historyTable.fileName = `${settings.uploadSettings.historyTable.fileBasis}${fromDate ? `-${intl.formatDate(fromDate)}` : ``}${toDate ? `-${intl.formatDate(toDate)}` : ``}`;
    
    }, [lang, uploadedData]); // eslint-disable-line react-hooks/exhaustive-deps

};

export default useGetBalances;
