import GitInfo from 'react-git-info/macro';
import store from '../store';
import { appSlice } from '../store/slices';
import VersionInfoModel from '../models/version-info';
import Types from '../classes/Types';

const HelperService = {
    FormatNumber: (number) => {
        return new Intl.NumberFormat('ru-RU', {
            maximumFractionDigits: 8
        }).format(number)
    },
    FormatFiat: (number, fractionDigits) => {
        return '$ ' + new Intl.NumberFormat('ru-RU', {
            minimumFractionDigits: fractionDigits || 2,
            maximumFractionDigits: fractionDigits || 2,
        }).format(number);
    },
    FormatFiatWithoutCurrency: (number) => {
        return new Intl.NumberFormat('ru-RU', {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2
        }).format(number);
    },
    FormatCrypto: (number, fractionDigits) => {
        return new Intl.NumberFormat('ru-RU', {
            minimumFractionDigits: fractionDigits || 8,
            maximumFractionDigits: fractionDigits || 8
        }).format(number);
    },
    FormatTime: (number) => {
        return new Intl.NumberFormat('ru-RU', {
            minimumIntegerDigits: 2
        }).format(number);
    },
    FormatPercent: (number) => {
        return new Intl.NumberFormat('ru-RU', {
            maximumFractionDigits: 4
        }).format(number);
    },
    GetVersionInfo: () => {
        const gitInfo = GitInfo();
        const model = new VersionInfoModel({ ...gitInfo });
        store.dispatch(appSlice.actions.setVersionInfo(model));
    },
    SetLanguageToStorage: (lang = 'en') => {
        localStorage.setItem(Types.localStorageLanguageName, lang);
    },
    GetLanguageFromStorage: () => {
        return localStorage.getItem(Types.localStorageLanguageName);
    },
    HasLanguageInStorage: () => {
        return !!(localStorage.getItem(Types.localStorageLanguageName));
    },
    SetTokenToStorage: (token = '') => {
        sessionStorage.setItem(Types.sessionStorageTokenName, token);
    },
    GetTokenFromStorage: () => {
        return sessionStorage.getItem(Types.sessionStorageTokenName);
    },
    RemoveTokenFromStorage: () => {
        sessionStorage.removeItem(Types.sessionStorageTokenName);
    },
    SetLocalStorageProperty: (name, value) => {
        if (!name) return;
        const property = typeof value === 'object' ? JSON.stringify(value) : value;
        localStorage.setItem(name, property);
    },
    HasLocalStorageProperty: (name) => {
        if (!name) return;
        return !!(localStorage.getItem(name));
    },
    GetLocalStorageProperty: (name) => {
        if (!name || !HelperService.HasLocalStorageProperty(name)) return null;
        return localStorage.getItem(name);
    },
    RemoveLocalStorageProperty: (name) => {
        if (!name) return;
        localStorage.removeItem(name);
    },
    formatDate: (date, splitSymbol = '.', splitDate = 10, postFix = '') => {
        if (!date || typeof date !== 'object') return date;
        const yyyy = date.getFullYear();
        let dd = date.getDate();
        let mm = date.getMonth() + 1;

        if (dd < splitDate) {
            dd = '0' + dd;
        }

        if (mm < splitDate) {
            mm = '0' + mm;
        }

        return yyyy + splitSymbol + mm + splitSymbol + dd + postFix;
    },
};
export default HelperService;
