import React from 'react';
import * as PropTypes from 'prop-types';
import t from '../../../../../../lang/t';
import { stepTypes } from './step-types';
import {
    AdditionalDropdown,
    AddressesSelect,
    AmountInputs,
    CurrencySelect,
    MerchantSelect,
    ReceiverAddress,
    RiskSelect,
    ConfirmationCurrency,
    ConfirmationMerchant,
    ConfirmationRisk,
    ConfirmationAddress,
    ConfirmationCommission,
    ConfirmationComment,
    ConfirmationReceiverAddress,
    ConfirmationButtonsGroup,
    TransactionError,
    TransactionSuccess,
} from '../index';
import Button from '@mui/material/Button';
import { appSlice } from '../../../../../../store/slices';
import { useSelector, useDispatch } from 'react-redux';
import RootModalModel from '../../../../../../models/root-modal';

const StepRenderer = ({ step, setConfirmStep }) => {
    const roundCrypto = (value = 0) => Math.round(+value * 1e8) / 1e8;
    const model = useSelector((state) => state?.withdrawal?.manualWithdrawal);
    const activeAddresses = useSelector((state) => state?.withdrawal?.withdrawalFilteredList);
    const activeAddressesToShow = activeAddresses.filter((add) => add.isShow);
    const isValid = model?.currency && model?.merchant && model?.riskLevel && model?.receiverAddress && (+model?.availableBalance > 0) && (+model?.cryptoAmount > 0) && (roundCrypto(model?.cryptoAmount) <= roundCrypto(+model?.availableBalance)) && (activeAddressesToShow.length > 0);

    const components = {
        [stepTypes.send.name]: (
            <div className={`Step-${stepTypes.send.name}`}>
                <CurrencySelect/>
                <MerchantSelect/>
                <RiskSelect disabled />
                <AddressesSelect/>
                <AmountInputs/>
                <ReceiverAddress/>
                <AdditionalDropdown/>
                <div className="Withdrawal__Send">
                    <Button variant="contained" onClick={setConfirmStep} disabled={!isValid}>{t('Send')}</Button>
                </div>
            </div>
        ),
        [stepTypes.confirm.name]: (
            <div className={`Step-${stepTypes.confirm.name}`}>
                <ConfirmationCurrency/>
                <ConfirmationMerchant/>
                <ConfirmationRisk/>
                <ConfirmationAddress/>
                <ConfirmationReceiverAddress />
                <ConfirmationCommission/>
                <ConfirmationComment/>
                <ConfirmationButtonsGroup/>
            </div>
        ),
        [stepTypes.success.name]: (
            <div className={`Step-${stepTypes.success.name}`}>
                <TransactionSuccess/>
            </div>
        ),
        [stepTypes.error.name]: (
            <div className={`Step-${stepTypes.error.name}`}>
                <TransactionError/>
            </div>
        ),
    };
    return components[step];
};

const WithdrawalStepper = ({ step }) => {
    const dispatch = useDispatch();
    const modal = useSelector((state) => state?.app?.modal);
    const setConfirmStep = () => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        ...modal,
        title: t('Confirm sending'),
        outsideParams: {
            ...modal.outsideParams,
            step: stepTypes.confirm.name,
        },
    })));
    return (
        <div className="WithdrawalStepper">
            <StepRenderer step={step} setConfirmStep={setConfirmStep}/>
        </div>
    );
};

WithdrawalStepper.defaultProps = {
    step: stepTypes.send,
};

WithdrawalStepper.propTypes = {
    step: PropTypes.string,
};

export default WithdrawalStepper;
