import React from 'react';
import { useSelector } from 'react-redux';
import t from '../../../../../../lang/t';
import './ConfirmationReceiverAddress.scss';

const ConfirmationReceiverAddress = () => {
    const receiverAddress = useSelector((state) => state?.withdrawal?.manualWithdrawal?.receiverAddress);
    return (
        <div className="ConfirmationReceiverAddress">
            <div className="ConfirmationReceiverAddress__Label">
                {t('Recipient address')}
            </div>
            <div className="ConfirmationReceiverAddress__Container">
                <div className="address">{receiverAddress}</div>
            </div>
        </div>
    );
};

export default ConfirmationReceiverAddress;
