import React from 'react';
import t from '../../../../lang/t';
import { tabTypes } from '../../checkModalTypes';

const TabSwitcher = ({ tab, onChangeTab }) => {
    const isSingleCheck = tab === tabTypes.singleCheck.key;
    const isMultiCheck = tab === tabTypes.multiCheck.key;

    return (
        <ul className="checkModalTabs">
            <li className={isSingleCheck ? 'active' : ''} onClick={() => onChangeTab(tabTypes.singleCheck.key)}>
                {t('One time check')}
            </li>
            <li className={isMultiCheck ? 'active' : ''} onClick={() => onChangeTab(tabTypes.multiCheck.key)}>
                {t('Bulk check')}
            </li>
        </ul>
    );
};

export default TabSwitcher;
