import React, { useEffect, useRef } from 'react';
import * as PropTypes from 'prop-types';
import classNames from 'classnames';
import Icon, { Icons } from '../Icon/Icon';
import './SideModal.scss';

const SideModal = ({ isOpen, className, title, children, onClose }) => {
    const modalRef = useRef(null);

    useEffect(() => {
        const handleClick = (event) => {
            let hasNotification = document.getElementsByClassName('notificationSuccess')?.length;
            
            if (isOpen && (!modalRef.current || !modalRef.current.contains(event.target)) && !hasNotification) {
                onClose();
            }
        };

        document.getElementById('root').addEventListener('click', handleClick);

        return () => {
            document.getElementById('root').removeEventListener('click', handleClick);
        }
    }, [modalRef, onClose, isOpen]);

    return (
        <div
            className={classNames('sideModal', isOpen ? 'sideModalOpen' : null, className)}
            ref={modalRef}
        >
            <div className="sideModalHeader">
                {title && <h2>{title}</h2>}
                <Icon type={Icons.CROSS} width={32} height={32} onClick={onClose}/>
            </div>
            <div className="sideModalBody">{children}</div>
        </div>
    );
};

SideModal.defaultProps = {
    isOpen: false,
    className: null,
    title: null,
    children: null,
    onClose: () => {}
};

SideModal.propTypes = {
    isOpen: PropTypes.bool.isRequired,
    className: PropTypes.string,
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    children: PropTypes.node.isRequired,
    onClose: PropTypes.func.isRequired
};

export default SideModal;