import { useEffect } from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import RequestService from '../../../../../services/RequestService';
import { useSelector } from 'react-redux';
import { settings } from '../settings';
import Types from '../../../../../classes/Types';
import HelperService from '../../../../../services/HelperService';

const { statusesById } = Types;

const useGetTransactions = ({ setData, datesRange }) => {
    const intl = useIntl();
    const lang = useSelector((state) => state?.app?.lang);
    const cryptocurrencies = useSelector(state => state.cryptocurrency.cryptocurrencies);

    const currencyByIds = cryptocurrencies.reduce((acc, currency) => {acc[currency.Symbol] = currency.Name; return acc;}, {});

    const dataFilter = (data) => {
        return data.filter((d) => d?.Status !== Types.statusesByName.Failed).map((d) => {
            const {
                Date = '',
                Cryptocurrency = '',
                Status = '',
                Type = '',
                TxId = '',
                MerchantName = '',
                InitiatorType = '',
                InitiatorId = '',
                Commission = 0,
                Amount = 0,
                SenderAddresses = [],
                DestinationAddress = '',
                AddressAdditionalInfo = '',
                Comment = '',
            } = d;
            return {
                [intl.formatMessage({ id: 'Date' })]: intl.formatDate(Date),
                [intl.formatMessage({ id: 'Time' })]: intl.formatTime(Date),
                [intl.formatMessage({ id: 'Currency' })]: currencyByIds[Cryptocurrency],
                [intl.formatMessage({ id: 'Transaction status' })]: Status ? intl.formatMessage({ id: statusesById[Status].name }) : '-',
                [intl.formatMessage({ id: 'Transaction amount\n(minus commissions)' })]: ((Type === 2 || Type === 4) ? '- ' : '') + (Cryptocurrency === 'USDT-TRC20' ? Amount.toFixed(8) : (Amount - Commission).toFixed(8)),
                [intl.formatMessage({ id: 'Type' })]: (Type === 1) ? intl.formatMessage({ id: 'Receipt of funds' }) : (Type === 2) ? intl.formatMessage({ id: 'Withdrawal' }) : (Type === 3) ? intl.formatMessage({ id: 'Odd money Receipt' }) : intl.formatMessage({ id: 'Odd money Withdrawal' }),
                [intl.formatMessage({ id: 'Transaction link' })]: TxId,
                [intl.formatMessage({ id: 'Merchant' })]: MerchantName,
                [intl.formatMessage({ id: 'Initiator' })]: `${intl.formatMessage({id: InitiatorType})} ${InitiatorId || ''}`,
                [intl.formatMessage({ id: 'Transaction fee' })]: (Commission > 0 ? '- ' : '') + (+Commission).toFixed(8),
                [intl.formatMessage({ id: 'Transaction amount\n(without commission deduction)' })]: ((Type === 2 || Type === 4) ? '- ' : '') + (+Amount).toFixed(8),
                [intl.formatMessage({ id: "Sender's address" })]: SenderAddresses.join('\n'),
                [intl.formatMessage({ id: 'Recipient address' })]: DestinationAddress,
                [intl.formatMessage({ id: 'System description' })]: AddressAdditionalInfo,
                [intl.formatMessage({ id: 'User comment' })]: Comment,
            };
        });
    };

    useEffect(() => {
        const fetchData = async () => {
            const fromDate = HelperService.formatDate(datesRange?.[0], '-');
            const toDate = HelperService.formatDate(datesRange?.[1], '-', 10, 'T23:59:59.99999');

            const responseTransactions = await RequestService.GetTransactions(
                1,
                999999,
                '',
                {
                    by: 'Id',
                    desc: true
                },
                [],
                fromDate,
                toDate,
            );
            setData(dataFilter(responseTransactions['Transactions'] || []));
            settings.uploadSettings.historyTable.fileName = `${settings.uploadSettings.historyTable.fileBasis}${fromDate ? `-${intl.formatDate(fromDate)}` : ``}${toDate ? `-${intl.formatDate(toDate)}` : ``}`;
        };
        setTimeout(() => fetchData().then(), 0);
    }, [lang, datesRange]); // eslint-disable-line react-hooks/exhaustive-deps

};

export default useGetTransactions;
