import { useEffect, useState } from 'react';
import t from '../../../../lang/t';
import Button from "../../../../components/Button/Button";
import Input from "../../../../components/Input/Input";
import NotificationService from "../../../../services/NotificationService";
import RequestService from '../../../../services/RequestService';
import TwoFaIcon from "../TwoFaIcon";
// TO DO: to generate QR on front
// import QRCode from 'react-qr-code';

const StepTwo = ({onGoToSecondStep, qrVisibel, setQrVisibel, previousStep}) => {

    const [twoFaQr, setTwoFaQr] = useState('');
    const [twoFaKey, setTwoFaKey] = useState('');

    useEffect(() => {
        const fetchData = async () => {
            const response = await RequestService.EnableTwoFa();
            setTwoFaQr(response['QrCodeImageUrl']);
            setTwoFaKey(response['ManualEntrySetupCode']);
        }
        fetchData();
      }, []);

    const handleChangeQrVisibel = () => qrVisibel ? setQrVisibel(false) : setQrVisibel(true);

    const copyTwoFA = async () => {
        navigator.clipboard.writeText(twoFaKey)

        NotificationService.Push(t('Copied'), false, 'white');
    }

    return (
        <div className="twoFaActivateModalStep1">
            <div className="twoFaActivateModalStep1Container">
                <div className="twoFaActivateModalStep1ContainerContent">
                    <Input value={twoFaKey} disabled={true}/>
                    <div onClick={() => copyTwoFA()} className="copyTwoFA"><TwoFaIcon type="svgCopy" /></div>
                    <div className="openQR"  onClick={handleChangeQrVisibel}><TwoFaIcon type="svgQr" /></div>
                    <div className={qrVisibel ? 'modal-qr active' : 'modal-qr'}>
                        {/* TO DO: to generate QR on front */}
                        {/* <QRCode value={twoFaKey} size={198}/> */}
                        <img src={twoFaQr} alt="QR"/>
                    </div>
                </div>
            </div>
            <div className="twoFaActivateModalStep1Buttons">
                <Button color="light" onClick={() => previousStep(0)}><TwoFaIcon type="arrowLeft" /> {t('Previous step')}</Button>
                <Button onClick={onGoToSecondStep}>{t('Next step')} <TwoFaIcon type="arrowRight" /></Button>
            </div>
        </div>
    )
}

export default StepTwo;