import React from 'react';
import t from '../../../../lang/t';
import Icon, { Icons } from '../../../../components/Icon/Icon';

const CheckError = () => (
    <div className="checkModalError">
        <Icon
            type={Icons.WARNING}
            width={24}
            height={24}
        />
        <span>
            {t('Failed to check, please try again later')}
        </span>
    </div>
);

export default CheckError;
