import React from 'react';
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';
import Tooltip from '../../components/Tooltip/Tooltip';
import './PageHeadTabs.scss';

const PageHeadTabs = ({tabOptions}) => {

    return (
        <ul className="PageHeadTabs">
            {tabOptions.map((tab, index) => (
                <li className={classNames('PageHeadTabs__tab', tabOptions.length === 1 && 'no-highLight', tab.value.props.id === 'Quarantine' && 'quarantine-disabled')} key={index}>
                    {tab.value.props.id === 'Quarantine'
                        ? <span>{tab.value}</span>
                        : <NavLink to={tab.to} isActive={tab.isActive}>{tab.value}</NavLink>
                    }
                    {tab.tooltip && <Tooltip text={tab.tooltip} />}
                </li>
            ))}
        </ul>
    );
};
export default PageHeadTabs;
