import React, { useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import t from '../../../../../../lang/t';
import HelperService from '../../../../../../services/HelperService';
import RequestService from '../../../../../../services/RequestService';
import { withdrawalSlice } from '../../../../../../store/slices';
import Types from '../../../../../../classes/Types';
import './ConfirmationCommission.scss';

const ConfirmationCommission = () => {
    const dispatch = useDispatch();
    const activeCommission = useSelector((state) => state?.withdrawal?.manualWithdrawal?.commissionType);
    const activeCurrency = useSelector((state) => state?.withdrawal?.manualWithdrawal?.currency);
    const commission = useSelector((state) => state?.withdrawal?.manualWithdrawal?.commission);
    const commissionFiat = useSelector((state) => state?.withdrawal?.manualWithdrawal?.commissionFiat);
    const zeroCommission = 0;

    const getQuotes = useCallback(async () => {
        const quotes = await RequestService.GetQuotes();
        const currentQuote = quotes?.Quotes?.find((quote) => quote?.Cryptocurrency === activeCurrency.Symbol);
        const price = currentQuote?.Price;
        const fiatCommission = (+commission * price).toFixed(2);
        dispatch(withdrawalSlice.actions.setCommissionFiatAmountWithdrawalModel(fiatCommission));
    }, [activeCurrency.Symbol, commission, dispatch]);

    useEffect(() => {
        if (!commissionFiat) getQuotes();
    }, [commission, commissionFiat, getQuotes]);

    return (
        <div className="ConfirmationCommission">
            <div className="ConfirmationCommission__Label">
                {t('Transaction fee ver2')}
            </div>
            {+commission === zeroCommission && <div className="ConfirmationCommission__Label additional">
                {t('Commission will be calculated automatically')}
            </div>}
            {!Types.currencyWithoutAutoCommission.includes(activeCurrency.Symbol) && (
                <>
                    <div className="ConfirmationCommission__Labels">
                        {+commission !== zeroCommission && <div className="label crypto-value">
                            <div className="value">{HelperService.FormatCrypto(commission, activeCurrency?.Symbol === 'USDT-TRC20' && 2)}</div>
                            <div className="currency">{activeCurrency?.Symbol}</div>
                        </div>}
                        {+commission !== zeroCommission && <div className="label speed">
                            {activeCommission?.subLabel} {t('Speed')}
                        </div>}
                    </div>
                    <div className="ConfirmationCommission__Details">
                        {+commission !== zeroCommission && <div className="detail usd-value">
                            <div className="value">{HelperService.FormatFiat(commissionFiat)}</div>
                        </div>}
                        {+commission !== zeroCommission && <div className="detail speed">{activeCommission?.time}</div>}
                    </div>
                </>
            )}
        </div>
    );
};

export default ConfirmationCommission;
