import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../lang/t';
import Input from '../Input/Input';
import Select from '../Select/Select';
import Icon, { Icons } from '../Icon/Icon';
import './Filter.scss';

const FilterNumberTypes = [
    {
        label: t('Equals'),
        value: 2
    },
    {
        label: t('More'),
        value: 3
    },
    {
        label: t('Less'),
        value: 1
    }
];

const Filter = ({ type, field, items, value, onFilter }) => {
    const [isFilter, setIsFilter] = useState(false);

    const [currentValue, setCurrentValue] = useState({});
    const [timer, setTimer] = useState(0);

    const intl = useIntl();

    const onFilterClick = () => {
        setIsFilter(prev => !prev);

        if (!isFilter && type === 1) {
            setTimeout(() => document.getElementById('filterInput').focus(), 1);
        }

        if (value.Column) {
            onFilter({ Column: field });
        }
    };

    const onFilterSearch = (text) => {
        clearTimeout(timer);

        setCurrentValue({ Type: type, Column: field, Search: [text] });
        setTimer(setTimeout(() => onFilter({ Type: type, Column: field, Search: [text] }), 1000));
    };
    const onFilterNumber = (numberType, number) => {
        clearTimeout(timer);

        const nextValue = {
            Type: type,
            Column: field,
            FiltrationType: numberType ? numberType : FilterNumberTypes[0].value,
            Number: number ? number : ''
        };

        setCurrentValue(nextValue);

        if (+number) {
            setTimer(setTimeout(() => onFilter(nextValue), 1000));
        }
    };

    const getFilterSelect = () => {
        return currentValue.Items ? currentValue.Items.map((value) => {
            return items.find((item) => item.value === value);
        }) : [];
    };
    const onFilterSelect = (options) => {
        clearTimeout(timer);

        const nextValue = {
            Type: type,
            Column: field,
            Items: options.map((option) => option.value)
        };

        setCurrentValue(nextValue);
        setTimer(setTimeout(() => onFilter(nextValue), 1000));
    };

    useEffect(() => {
        setCurrentValue(value);
    }, [value]);
    useEffect(() => {
        document.body.addEventListener('mousedown', () => setIsFilter(false));
    }, []);

    return (
        <div className="filter" onMouseDown={(e) => e.stopPropagation()}
             onClick={(e) => e.stopPropagation()}>
            <Icon className={value.Column ? 'filterReset' : ''} type={!value.Column ? Icons.FILTER : Icons.FILTER_RESET}
                  width={16} height={16} onClick={onFilterClick}/>
            {isFilter && type === 1 &&
            <div className="filterSearch">
                <Input id="filterInput" placeholder={intl.formatMessage({ id: 'Search...' })} value={currentValue.Search ? currentValue.Search[0] : ''}
                       onChange={(e) => onFilterSearch(e.target.value)}/>
            </div>}
            {isFilter && type === 2 &&
            <div className="filterNumber">
                <Select options={FilterNumberTypes} onChange={(option) => onFilterNumber(option.value, currentValue.Number)}
                        value={currentValue.FiltrationType
                            ? FilterNumberTypes.find(
                                (item) => item.value === currentValue.FiltrationType)
                            : FilterNumberTypes[0]}/>
                <Input id="filterInput" type="number" placeholder={intl.formatMessage({ id: 'Number...' })} value={currentValue.Number ?? ''}
                       onChange={(e) => onFilterNumber(currentValue.FiltrationType, e.target.value)}/>
            </div>}
            {isFilter && type === 3 &&
            <div className="filterSelect">
                <Select isMulti options={items} value={getFilterSelect()} onChange={onFilterSelect}/>
            </div>}
            {isFilter && type === 4 &&
            <div className="filterDate">

            </div>}
        </div>
    );
};

Filter.defaultProps = {
    type: 1,
    items: [],
    value: {},
    onFilter: () => {}
};

Filter.propTypes = {
    type: PropTypes.oneOf([1, 2, 3, 4]).isRequired,
    items: PropTypes.arrayOf(PropTypes.object),
    value: PropTypes.object.isRequired,
    onFilter: PropTypes.func.isRequired
};

export default Filter;