import { createSlice } from '@reduxjs/toolkit';
import SelfAdminModel from '../../../models/self-admin';
import RightsModel from '../../../models/rights';
import { LOCALES } from '../../../lang';

const initialState = {
    self: new SelfAdminModel(),
    rights: [],
    admins: [],
};

export const adminSlice = createSlice({
    name: 'admin',
    initialState,
    reducers: {
        getAllAdmins: (state, action) => {
            const { payload: admins = [] } = action;
            state.admins = admins;
        },
        getSelfAdmin: (state, action) => {
            const { payload: self = {} } = action;
            state.self = new SelfAdminModel(self);
        },
        getRights: (state, action) => {
            const { payload: rights = [] }  = action;

            if(Array.isArray(rights) && rights.length > 0) {
                state.rights = rights.map((right) => (new RightsModel(right)));
            }
        },
        updateLanguage: (state, action) => {
            const { payload = LOCALES.en }  = action;

            state.self = new SelfAdminModel({ ...state.self, Language: payload })
        },
    },
});
