import RequestService from './RequestService';
import store from '../store';
import { appSlice, adminSlice, cryptocurrencySlice, merchantSlice } from '../store/slices';

const StorageService = {
    System: {},
    Self: {},
    Rights: [],
    Cryptos: [],
    Merchants: [],

    Initialize: async () => {
        // Select license information;
        const systemResponse = await RequestService.GetSystem();

        if (systemResponse['Status'] !== 'Success') {
            return;
        }

        StorageService.System = systemResponse['LicenseData'];
        store.dispatch(appSlice.actions.getLicense(systemResponse['LicenseData']));

        // Select information about self admin;
        const selfResponse = await RequestService.GetSelf();

        if (selfResponse['Status'] !== 'Success') {
            return;
        }

        StorageService.Self = selfResponse['Data'];
        store.dispatch(adminSlice.actions.getSelfAdmin(selfResponse['Data']));

        // Select rights information;
        const rightsResponse = await RequestService.GetRights();

        if (rightsResponse['Status'] !== 'Success') {
            return;
        }

        StorageService.Rights = rightsResponse['Rights'].map((item) => {
            return { label: item['Name'], value: item['Id'] };
        });
        store.dispatch(adminSlice.actions.getRights(rightsResponse['Rights']));

        // Select crypto currencies list;
        const cryptosResponse = await RequestService.GetCryptos();

        if (cryptosResponse['Status'] !== 'Success') {
            return;
        }

        StorageService.Cryptos = cryptosResponse['Cryptocurrencies'].map((item) => {
            return { id: item['Id'], label: item['Name'], value: item['Symbol'] };
        });
        store.dispatch(cryptocurrencySlice.actions.getAllCryptocurrencies(cryptosResponse['Cryptocurrencies']));

        // Select merchants list;
        await StorageService.GetMerchantsList();
    },
    GetMerchantsList: async () => {
        const merchantsResponse = await RequestService.GetAllMerchants();

        if (merchantsResponse['Status'] !== 'Success') {
            return;
        }

        StorageService.Merchants = merchantsResponse['Merchants'].map((item) => {
            return { label: item['Name'], value: item['Id'] };
        });
        store.dispatch(merchantSlice.actions.getAllMerchants(merchantsResponse['Merchants']));
    },
    OnMerchants: async () => {
        const merchantsResponse = await RequestService.GetAllMerchants();

        if (merchantsResponse['Status'] !== 'Success') {
            return;
        }

        StorageService.Merchants = merchantsResponse['Merchants'].map((item) => {
            return { label: item['Name'], value: item['Id'] };
        });
    }
};
export default StorageService;
