import React from 'react';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import t from '../../../../lang/t';
import Button from '../../../../components/Button/Button';
import { GasStation } from './components';
import { useDispatch, useSelector } from 'react-redux';
import { appSlice } from '../../../../store/slices';
import RootModalModel from '../../../../models/root-modal';

import './GasStationSettings.scss';
import Tooltip from '../../../../components/Tooltip/Tooltip';

const GasStationSettings = () => {
    const dispatch = useDispatch();
    const tronRootAccount = useSelector((state) => state?.app?.tronRootAccount);

    const toggleGasStationModal = () => {
        dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
            id: 'GasStation',
            className: 'GasStationModal',
            title: tronRootAccount?.isAvailable ? t('Edit gas station') : t('Add gas station'),
            tag: 'gasStation',
            isOpen: true,
            outsideParams: {},
        })))
    };

    return (
        <div className="GasStationSettings">
            <div className="profileModalTabSettingsGasStation">
                <h4>
                    <Icon type={Icons.GAS_STATION} width={21} height={21}/>
                    <span>{t('Gas station settings')}</span>
                    <Tooltip text={t("Setting parent's address for the coins it requires. Transaction fees will be deducted from this address.")}>
                        {' '}
                    </Tooltip>
                </h4>
            </div>
            <div className="GasStationSettings__Stations">
                { tronRootAccount?.isAvailable && <GasStation balance={tronRootAccount?.Balance} onOpenEditModal={toggleGasStationModal} /> }
            </div>
            <div className="GasStationSettings__Buttons">
                <Button className="light" onClick={toggleGasStationModal}>{tronRootAccount?.isAvailable ? t('Edit') : t('Add')}</Button>
            </div>
            <div className="profileModalTabSettingsSeparator"/>
        </div>
    );
};

export default GasStationSettings;
