import { appSlice } from './app';
import { adminSlice } from './admin';
import { merchantSlice } from './merchant';
import { cryptocurrencySlice } from './cryptocurrency';
import { addressSlice } from './address';
import { withdrawalSlice } from './withdrawal';
import { transactionSlice } from './transaction';
import { paymentSlice } from './payment';


export {
    appSlice,
    adminSlice,
    merchantSlice,
    cryptocurrencySlice,
    addressSlice,
    withdrawalSlice,
    transactionSlice,
    paymentSlice,
};

export const rootReducer = {
    app: appSlice.reducer,
    admin: adminSlice.reducer,
    merchant: merchantSlice.reducer,
    cryptocurrency: cryptocurrencySlice.reducer,
    address: addressSlice.reducer,
    withdrawal: withdrawalSlice.reducer,
    transaction: transactionSlice.reducer,
    payment: paymentSlice.reducer,
};
