import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import t from '../../lang/t';
import Icon, { Icons } from '../Icon/Icon';
import './InputFile.scss';

const InputFile = ({ id, className, files, onChange }) => {
    return (
        <div id={id} className={classNames('inputFile', className)}>
            <input id={'inputFile-' + id} type="file"
                   onChange={(e) => onChange(e.target.files)}/>
            <label htmlFor={'inputFile-' + id}>
                <span>{files === null || files.length === 0 ? t('Select a file') : files[0]['name']}</span>
                <Icon type={Icons.EXPORT} width={24} height={24}/>
            </label>
        </div>
    );
};

InputFile.defaultProps = {
    id: null,
    className: null,
    files: null,
    onChange: () => {}
};

InputFile.propTypes = {
    id: PropTypes.string.isRequired,
    className: PropTypes.string,
    files: PropTypes.object,
    onChange: PropTypes.func
};

export default InputFile;