import React from 'react';
import t from '../../../../lang/t';
import useIntl from 'react-intl/lib/src/components/useIntl';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import Input from '../../../../components/Input/Input';

const SingleCheckTab = ({ address, setAddress, addressPercent, isChecked }) => {
    const intl = useIntl();

    return (
        <div className="checkModalInput">
            <Input
                type="text"
                label={t('Enter crypto address')}
                placeholder={intl.formatMessage({ id: 'Enter crypto address' })}
                value={address}
                onChange={(e) => setAddress(e.target.value)}
            />
            {isChecked && (
                <div className="checkModalInputRisk">
                    <Icon
                        className={addressPercent <= 25 ? 'low' : addressPercent <= 74 ? 'middle' : 'high'}
                        type={Icons.RISK}
                        width={16}
                        height={16}
                    />
                    <span>
                    {`${addressPercent}%`}
                </span>
                </div>
            )}
        </div>
    )
};

export default SingleCheckTab;
