import React from 'react';
import t from '../../../../../../lang/t';
import NotificationService from '../../../../../../services/NotificationService';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import {addressLink} from '../../../../../../utils/cryptoLinkGenerator';

import './ReceivingAddressField.scss';

const ReceivingAddressField = ({ createdAddress }) => {

    return (
        <div className="ReceivingAddressField">
            <div className="ReceivingAddressField__title">
            {t('Your {currency} address', { currency: createdAddress.Currency })}
            </div>
            <span className="ReceivingAddressField__value">
                <a href={addressLink(createdAddress.Currency, createdAddress.Address)} target="_blank" rel="noreferrer">{createdAddress.Address}</a>
            </span>
            <div className="ReceivingAddressField__copy">
                <Icon
                    type={Icons.COPY}
                    width={16}
                    height={16}
                    onClick={() => navigator.clipboard.writeText(createdAddress.Address).then(() => NotificationService.Push(t('Copied'), false))}
                />
            </div>
        </div>
    );
};

export default ReceivingAddressField;
