import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import './Switch.scss';

const Switch = ({ id, className, checked, disabled, onChange }) => {
    return (
        <div id={id} className={classNames('switch', className)}>
            <input id={'switch-' + id} type="checkbox" checked={checked} disabled={disabled} onChange={onChange}/>
            <label htmlFor={'switch-' + id}>
                <div/>
            </label>
        </div>
    );
};

Switch.defaultProps = {
    id: null,
    className: null,
    checked: false,
    disabled: false,
    onChange: () => {}
};

Switch.propTypes = {
    id: PropTypes.string.isRequired,
    className: PropTypes.string,
    checked: PropTypes.bool.isRequired,
    disabled: PropTypes.bool,
    onChange: PropTypes.func
};

export default Switch;