import React from 'react';
import { useSelector } from 'react-redux';
import t from '../../../../../../lang/t';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import RiskIcon from '../RiskIcon';
import './ConfirmationAddress.scss';
import { riskTypes } from '../RiskSelect/riskTypes';
import { FixedSizeList } from 'react-window';

const listOptions = {
    height: 240,
    itemSize: 20,
    className: 'ConfirmationAddress__List',

};

const ConfirmationAddress = () => {
    const activeAddresses = useSelector((state) => state?.withdrawal?.withdrawalFilteredList);
    const modal = useSelector((state) => state?.app?.modal);
    const activeAddressesToShow = activeAddresses.filter((add) => add.isShow);

    const getRiskPercent = (score = 0) => {
        const percent = parseInt((score * 100).toString());
        return percent > 0 ? `${percent}%` : `-`;

    };

    const doCheck = (address) => {
        const { onCheck } = modal?.outsideParams;
        if (onCheck && typeof onCheck === 'function') onCheck(address);
    };

    return (
        <div className="ConfirmationAddress">
            <div className="ConfirmationAddress__Label">
                {t('From crypto addresses')}
            </div>
            {/*<div className="ConfirmationAddress__List">
                {Array.isArray(activeAddressesToShow) && activeAddressesToShow.length > 0 && activeAddressesToShow.map((add) => (
                    <li key={add?.Id} className="item">
                        <div className="address">{add?.Address}</div>
                        <div className="risk">
                            <div className="risk-icon"><RiskIcon type={add?.riskName}/></div>
                            <div className="risk-wrapper">
                                <div className="risk-percent">{getRiskPercent(add?.RiskScore)}</div>
                                {add?.riskName === riskTypes.unchecked.name && false &&
                                <div className="risk-check" onClick={() => {
                                    doCheck(add?.Address)
                                }}>
                                    <Icon className="check-icon" type={Icons.CHECK} width={18} height={18}/></div>}
                            </div>
                        </div>
                    </li>
                ))}
            </div>*/}
            <FixedSizeList
                {...listOptions}
                itemData={activeAddressesToShow}
                itemCount={activeAddressesToShow.length}
            >
                {({ data, index, style }) => (<div style={style}>
                    <li key={data[index]?.Id} className="item">
                        <div className="address">{data[index]?.Address}</div>
                        <div className="risk">
                            <div className="risk-icon"><RiskIcon type={data[index]?.riskName}/></div>
                            <div className="risk-wrapper">
                                <div className="risk-percent">{getRiskPercent(data[index]?.RiskScore)}</div>
                                {data[index]?.riskName === riskTypes.unchecked.name && false &&
                                <div className="risk-check" onClick={() => {
                                    doCheck(data[index]?.Address)
                                }}>
                                    <Icon className="check-icon" type={Icons.CHECK} width={18} height={18}/></div>}
                            </div>
                        </div>
                    </li>
                </div>)}
            </FixedSizeList>
        </div>
    );
};

export default ConfirmationAddress;
