import React from 'react';
import { useSelector } from 'react-redux';
import t from '../../../../../../lang/t';
import CurrencyIcon from '../CurrencyIcon';

import './ConfirmationCurrency.scss';
import HelperService from '../../../../../../services/HelperService';

const ConfirmationCurrency = () => {
    const activeCurrency = useSelector((state) => state?.withdrawal?.manualWithdrawal?.currency);
    const fiatAmount = useSelector((state) => state?.withdrawal?.manualWithdrawal?.fiatAmount);
    const cryptoAmount = useSelector((state) => state?.withdrawal?.manualWithdrawal?.cryptoAmount);

    return (
        <div className="ConfirmationCurrency">
            <div className="ConfirmationCurrency__Labels">
                <div className="label amount">
                    {t('Sending amount')}
                </div>
                <div className="label usd-amount">
                    <div className="value">{HelperService.FormatFiat(fiatAmount)}</div>
                </div>
            </div>
            <div className="ConfirmationCurrency__Details">
                <div className="detail crypto-currency">
                    <div className="currency-icon">
                        <CurrencyIcon name={activeCurrency?.Name} />
                    </div>
                    <div className="currency-name">{activeCurrency?.Name}</div>
                </div>
                <div className="detail crypto-amount">
                    <div className="value">{HelperService.FormatCrypto(cryptoAmount, activeCurrency?.Symbol === 'USDT-TRC20' && 2)}</div>
                    <div className="currency">{activeCurrency?.Symbol}</div>
                </div>
            </div>
        </div>
    );
};

export default ConfirmationCurrency;