import React, { useState, useCallback } from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import Select from '../../../Select/Select';
import Icon, { Icons } from '../../../Icon/Icon';
import t from '../../../../lang/t';
import HelperService from '../../../../services/HelperService';
import DateRangePicker from '../../../DateRangePicker';
import TableUpload from '../TableUpload';
import './TableControls.scss';

const TableCountOptions = [
    {
        label: 10,
        value: 10
    },
    {
        label: 20,
        value: 20
    },
    {
        label: 50,
        value: 50
    }
];

const TableControls = ({ id, page, count, search, sort, filters, datesRange, total, onPaginate, setIsTableSettings, noFilters, noPagitations, uploadedData }) => {
    const [tableSearch, setTableSearch] = useState('');
    const intl = useIntl();

    const onTablePaginateClick = useCallback((isNext) => {
        if (isNext) {
            if ((page - 1) * count + count < total) {
                onPaginate(page + 1, count, search, sort, filters, datesRange);
            }
        } else {
            if ((page - 1) * count + 1 !== 1) {
                onPaginate(page - 1, count, search, sort, filters, datesRange);
            }
        }
    }, [page, count, search, sort, filters, total, onPaginate, datesRange]);

    const handleChangeSearch = (value) => {
        setTableSearch(value);
        onPaginate(1, count, value, sort, filters, datesRange);
    };

    const handleDateFromChange = (from) => {
        if (HelperService.formatDate(from) === HelperService.formatDate(datesRange[0])) return;
        const to = (datesRange[1] && from > datesRange[1]) ? from : datesRange[1];
        onPaginate(1, count, search, sort, filters, [from, to]);
    };

    const handleDateToChange = (to) => {
        if (HelperService.formatDate(to) === HelperService.formatDate(datesRange[1])) return;
        const from = (datesRange[0] && datesRange[0] > to) ? to : datesRange[0];
        onPaginate(1, count, search, sort, filters, [from, to]);
    };

    return (
        <div className="tableControls">
            <div className="tableControlsFilters">
                {!noFilters &&
                    <>
                        {datesRange &&
                            <DateRangePicker
                                datesRange={datesRange}
                                handleDateFromChange={handleDateFromChange}
                                handleDateToChange={handleDateToChange}
                            />
                        }
                        <div className="tableControlsFiltersSearch">
                            <Icon type={Icons.SEARCH} width={24} height={24} />
                            <input
                                type="text"
                                placeholder={intl.formatMessage({ id: 'Search' })}
                                value={tableSearch}
                                onChange={(e) => handleChangeSearch(e.target.value)}
                            />
                            <Icon className="tableControlsFiltersSearchClear" type={Icons.CROSS} width={16} height={16}
                                onClick={() => handleChangeSearch('')} />
                        </div>
                    </>
                }
            </div>
            <div className="tableControlsPagination">
                {!noPagitations &&
                    <>
                        <div className="tableControlsPaginationCount">
                            <label htmlFor="">{t('Lines per page:')}</label>
                            <Select options={TableCountOptions} value={{ label: count, value: count }}
                                onChange={(option) => onPaginate(Math.min(page, Math.ceil(total / option.value)), option.value)} />
                        </div>
                        <span className="tableControlsPaginationTotal">
                            {total !== 0 ? (page - 1) * count + 1 : 0}
                            &nbsp;-&nbsp;
                            {(page - 1) * count + count > total ? total : (page - 1) * count + count}
                            &nbsp;{t('from')}&nbsp;
                            {total}
                        </span>
                        <div className="tableControlsPaginationArrows">
                            <Icon className={(page - 1) * count + 1 === 1 ? 'disabled' : null}
                                type={Icons.NAVIGATE_ARROW} width={32} height={32}
                                onClick={() => onTablePaginateClick(false)} />
                            <Icon className={(page - 1) * count + count >= total ? 'disabled' : null}
                                type={Icons.NAVIGATE_ARROW} width={32} height={32}
                                onClick={() => onTablePaginateClick(true)} />
                        </div>
                    </>
                }
                <TableUpload tableId={id} datesRange={datesRange} uploadedData={uploadedData} filters={filters}/>
                <Icon className="tableControlsPaginationSettings" type={Icons.SETTINGS} width={24} height={24}
                    onClick={() => setIsTableSettings(true)} />
            </div>
        </div>
    );
};

export default TableControls;
