import BaseModel from './base';
import { LOCALES } from '../lang';

export default class SelfAdminModel extends BaseModel {
    constructor(initData) {
        super();

        this.AvailableMerchants = [];
        this.Email = '';
        this.Id = 0;
        this.Is2FaEnabled = false;
        this.Language = LOCALES.en;
        this.Name = '';
        this.Notifications = null;
        this.NotifyAboutAutoWithdraw = false;
        this.NotifyAboutDeposit = false;
        this.NotifyAboutWithdraw = false;
        this.Rights = [];

        this.copyFrom(initData);
    }
}
