import React from 'react';
import { useDispatch } from 'react-redux';
import t from '../../lang/t';
import { addressSlice } from '../../store/slices/address';
import Icon, { Icons } from '../Icon/Icon';
import { getRiskLevelByRiskScore } from '../../utils';
import './RiskScoreCell.scss';

const RiskScoreCell = ({ address, isQuarantine, riskScore, lastCheckDate, onCheck, onQuarantine }) => {
    const dispatch = useDispatch();

    const riskLevelType = getRiskLevelByRiskScore(lastCheckDate ? riskScore : null);

    const showRiskModal = () => {
        const { setRiskAddress } = addressSlice.actions;
        dispatch(setRiskAddress(address));
    };

    return (
        <div className="riskScoreCell">
            <div className="riskScoreCellInfo">
                <div>
                    <Icon className={riskLevelType.color} type={Icons.RISK} width={16} height={16}/>
                    <span>{!lastCheckDate ? '-' : (riskScore * 100) + ' %'}</span>
                </div>
                {lastCheckDate && <p>{t('on')} {new Date(lastCheckDate).toLocaleDateString()}</p>}
            </div>
            <div className="riskScoreCellActions" style={{'pointerEvents':'none', 'opacity':'.3'}}>
                {(isQuarantine || (lastCheckDate && riskScore > 0.74)) && (
                    <Icon
                        className="riskScoreCellActionsQuarantine"
                        type={Icons.QUARANTINE}
                        width={16}
                        height={16}
                        onClick={onQuarantine}
                        tooltip={address.IsOnQuarantine ? t('Remove address from quarantine') : t('Add address to quarantine')}
                    />
                )}
                <Icon
                    className="riskScoreCellActionsCheck"
                    type={lastCheckDate ? Icons.RELOAD : Icons.CHECK}
                    width={16}
                    height={16}
                    onClick={onCheck}
                    tooltip={t('$2.50 will be deducted from your BitHide account for checking.')}
                />
                <Icon
                    className="riskScoreCellActionsEye"
                    type={Icons.EYE}
                    width={16}
                    height={16}
                    onClick={showRiskModal}
                    disabled={!lastCheckDate}
                />
            </div>
        </div>
    );
};
export default RiskScoreCell;