import Types from '../classes/Types';

export const getRiskLevelByRiskScore = (score) => {
    const { riskLevelsMap } = Types;

    const lowRiskType = riskLevelsMap.get('Low');
    const middleRiskType = riskLevelsMap.get('Middle');
    const highRiskType = riskLevelsMap.get('High');
    const uncheckedRiskType = riskLevelsMap.get('Unchecked');

    if (typeof score !== 'number') return uncheckedRiskType;

    if (score <= lowRiskType.riskBreakpoint) return lowRiskType;
    if (score <= middleRiskType.riskBreakpoint) return middleRiskType;
    if (score <= highRiskType.riskBreakpoint) return highRiskType;

    return uncheckedRiskType;
};