import React from 'react';
import { Link } from 'react-router-dom';
import * as PropTypes from 'prop-types';
import t from '../../../../lang/t';
import Button from '../../../../components/Button/Button';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import PageHeadTabs from '../../../../components/PageHeadTabs';
import config from '../../../../AppConfig';
import ButtonMUI from '@mui/material/Button';
import ButtonIcon from '../../../../components/ButtonIcon';

const { subRoutesGenerator } = config;


const HeadButtons = ({ name, setIsCheckModal, setIsModalOpen, tab, merchantId }) => {

    const tabOptions = [
        {
            to: subRoutesGenerator.merchantsId(merchantId),
            value: t('Wallets'),
            isActive: () => (tab === undefined || tab === 'wallets'),
        },
        {
            to: subRoutesGenerator.merchantsIdTab(merchantId, 'quarantine'),
            value: t('Quarantine'),
        },
        {
            to: subRoutesGenerator.merchantsIdTab(merchantId, 'history'),
            value: t('Transaction History'),
        },
    ];

    return (
        <div className="merchantHead">
            <div className="merchantHeader">
                <Link to="/merchants/" className="backButton"><Icon type={Icons.LONGBACKARROW} width={24} height={24} /></Link>
                <h2>
                    {t('Merchant')}
                    {`: ${name}`}
                </h2>
            </div>
            <div className="merchantActions">
                <PageHeadTabs tabOptions={tabOptions} />
                <div className="merchantHeadButtons">
                    <ButtonMUI variant="contained" color="secondary" className="fundsHeadButtonsCheck text-initial text-padding" onClick={() => setIsCheckModal(true)} disabled={true}>
                        <ButtonIcon/>
                        <span>{t('Check crypto addresses')}</span>
                    </ButtonMUI>
                    <Button onClick={() => setIsModalOpen(true)}>
                        <Icon type={Icons.SETTINGS} width={32} height={32} />
                    </Button>
                </div>
            </div>
        </div>
    )
};

HeadButtons.propTypes = {
    name: PropTypes.string.isRequired,
    setIsCheckModal: PropTypes.func.isRequired,
    setIsModalOpen: PropTypes.func.isRequired,
};

export default HeadButtons;
