// const TwoFaIcon = ({ type }) => (type === 'google' ? <GooglePlayIcon/> : <AppStoreIcon/>);
const TwoFaIcon = ({ type }) => {
    switch(type) {
        case 'google':
            return <GooglePlayIcon/>
        case 'stepOne':
            return <StepOne/>
        case 'stepTwo':
            return <StepTwo/>
        case 'stepThree':
            return <StepThree/>
        case 'arrowRight':
            return <ArrowRight/>
        case 'arrowLeft':
            return <ArrowLeft/>
        case 'svgQr':
            return <SvgQr/>
        case 'svgCopy':
            return <SvgCopy/>
        default :
            return <AppStoreIcon/>
    }
};

TwoFaIcon.defaultProps = {
    type: 'google',
};

TwoFaIcon.propTypes = {
    type: PropTypes.string,
    // type: PropTypes.oneOf(['google', 'appStore', 'stepOne']),
};

export default TwoFaIcon;
