import React from 'react';
import t from '../../../../lang/t';
import HelperService from '../../../../services/HelperService';
import Checkbox from '../../../../components/Checkbox/Checkbox';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import {addressLink} from '../../../../utils/cryptoLinkGenerator';

const MultiCheckTab = ({ isChecked, addresses, setAddresses, merchants }) => {
    const isAddressChecked = (address) => addresses.findIndex((item) => item['Id'] === address['Id']) !== -1;

    const isMerchantChecked = (merchant) => {
        for (let i = 0; i < merchant.Addresses.length; i++) {
            if (!isAddressChecked(merchant.Addresses[i])) return false;
        }
        return true;
    };

    const isTotalChecked = () => {
        let addressesCount = 0;
        merchants.forEach((item) => addressesCount += item['Addresses'].length);
        return addressesCount === addresses.length;
    };

    const onAddressCheck = (address) => {
        const newAddresses = Object.assign([], addresses);
        const addressIndex = newAddresses.findIndex((item) => item['Id'] === address['Id']);

        if (addressIndex === -1) {
            newAddresses.push(address);
        } else {
            newAddresses.splice(addressIndex, 1);
        }

        setAddresses(newAddresses);
    };

    const onMerchantCheck = (merchant) => {
        const newAddresses = Object.assign([], addresses);

        if (isMerchantChecked(merchant)) {
            for (let i = 0; i < merchant.Addresses.length; i++) {
                const addressIndex = newAddresses.findIndex(
                    (address) => address['Id'] === merchant.Addresses[i]['Id']);

                if (addressIndex === -1) {
                    continue;
                }

                newAddresses.splice(addressIndex, 1)
            }
        } else {
            for (let i = 0; i < merchant.Addresses.length; i++) {
                const addressIndex = newAddresses.findIndex(
                    (address) => address['Id'] === merchant.Addresses[i]['Id']);

                if (addressIndex !== -1) {
                    continue;
                }

                newAddresses.push(merchant.Addresses[i])
            }
        }

        setAddresses(newAddresses);
    };

    const onTotalCheck = () => {
        if (isTotalChecked()) {
            setAddresses([]);
        } else {
            const newAddresses = [];

            for (let i = 0; i < merchants.length; i++) {
                for (let j = 0; j < merchants[i]['Addresses'].length; j++) {
                    newAddresses.push(merchants[i]['Addresses'][j]);
                }
            }

            setAddresses(newAddresses);
        }
    };

    return (
        <div className="checkModalMulti">
            {(!isChecked && addresses.length === 0) && <p>{t('Choose crypto addresses')}</p>}
            {(!isChecked && addresses.length !== 0) && (
                <p>
                    {t('Selected:')}
                    &nbsp;
                    <span>{addresses.length} {t('Crypto addresses')}</span>
                </p>
            )}
            {isChecked && <p>{t('Verification result')}</p>}
            <div className="checkModalMultiSelect">
                {!isChecked && merchants.length !== 0 && (
                    <div className="checkModalMultiSelectMerchant">
                        <div className="checkModalMultiSelectMerchantContainer" style={{ marginBottom: 0 }}>
                            <Checkbox
                                id="total"
                                checked={isTotalChecked()}
                                onChange={() => onTotalCheck()}
                            />
                            <b>{t('Select all')}</b>
                        </div>
                    </div>
                )}
                {merchants.map((item, merchantIndex) => (
                    <div key={merchantIndex} className="checkModalMultiSelectMerchant">
                        <div className="checkModalMultiSelectMerchantContainer">
                            {!isChecked && (
                                <Checkbox
                                    id={'merchant' + merchantIndex}
                                    checked={isMerchantChecked(item)}
                                    onChange={() => onMerchantCheck(item)}
                                />
                            )}
                            <b>{item['MerchantName']}</b>
                        </div>
                        <div className="checkModalMultiSelectMerchantAddresses">
                            {item['Addresses'].map((address, addressIndex) => (
                                <div key={addressIndex} className="checkModalMultiSelectMerchantAddressesAddress">
                                    <div className="checkModalMultiSelectMerchantAddressesAddressLeft">
                                        {!isChecked && (
                                            <Checkbox
                                                id={'address' + address['Id']}
                                                checked={isAddressChecked(address)}
                                                onChange={() => onAddressCheck(address)}
                                            />
                                        )}
                                        <div>
                                            <b>{`${HelperService.FormatCrypto(address['Balance'], address['Cryptocurrency'] === 'USDT-TRC20' && 2)} ${address['Cryptocurrency']}`}</b>
                                            <span>
                                                {/* {address['Address']} */}
                                                <a href={addressLink(address['Cryptocurrency'], address['Address'])} target="_blank" rel="noreferrer">
                                                    {address['Address']}
                                                </a>
                                            </span>
                                        </div>
                                    </div>
                                    {(isChecked && address['Percent'] >= 0) && (
                                        <div className="checkModalMultiSelectMerchantAddressesAddressRight">
                                            <Icon
                                                className={address['Percent'] <= 25 ? 'low' : address['Percent'] <= 74 ? 'middle' : 'high'}
                                                type={Icons.RISK}
                                                width={16}
                                                height={16}
                                            />
                                            <span>{address['Percent']}%</span>
                                        </div>
                                    )}
                                </div>
                            ))}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default MultiCheckTab;
