import React, { useState, useEffect } from 'react';
import RequestService from '../../../services/RequestService';
import Connection from './components/Connection';
import Logs from './components/Logs';
// import Risks from './components/Risks';
import Transactions from './components/Transactions';
import './MainTabMain.scss';

const MainTabMain = () => {
    const [status, setStatus] = useState(null);
    // const [risks, setRisks] = useState(null);

    const [logs, setLogs] = useState([]);
    const [transactions, setTransactions] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const responseStatus = await RequestService.GetStatus();
            setStatus(responseStatus['Network']);

            // const responseRisks = await RequestService.GetRisks();
            // setRisks(responseRisks);

            const responseTransactions = await RequestService.GetTransactions(1, 10, '', { by: 'Id', desc: true }, []);
            setTransactions(responseTransactions['Transactions']);
        };

        const fetchLogs = async () => {
            const responseLogs = await RequestService.GetLogs();
            setLogs(responseLogs['ActivityEvents']);
        };

        const intervat = setInterval(() => {
            fetchLogs();
        }, 600000);

        fetchData().then();
        fetchLogs();

        return () => clearInterval(intervat);
    }, []);

    return (
        <div className="mainTabMain">
            <Connection status={status} />
            {/* Uncomment when you need the risk block on the main */}
            {/* <Risks risks={risks} /> */}
            <Logs logs={logs.slice(0, 50)} />
            <Transactions transactions={transactions} />
        </div>
    );
};

export default MainTabMain;
