import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import classNames from 'classnames';
import Icon, { Icons } from '../Icon/Icon';
import SubscribeService from '../../services/SubscribeService';
import NotificationService from '../../services/NotificationService';
import './Notifications.scss';

const Notifications = ({ isAuth }) => {
    const [notifications, setNotifications] = useState([]);

    const modalIsOpen = useSelector((state) => state?.app?.modal.isOpen);

    useEffect(() => {
        SubscribeService.Subscribe(SubscribeService.Actions.NOTIFICATION, setNotifications);
    }, []);

    let className = notifications[notifications.length - 1]?.className

    return (
        <div className={classNames('notifications', isAuth ? 'notificationsAuth' : '', className)}>
            {notifications.length !== 0 &&
            <div className={classNames('notification',
                notifications[notifications.length - 1].isError ? 'notificationError' : 'notificationSuccess',
                modalIsOpen && 'opaque'
            )}>
                <Icon type={Icons.CROSS} width={14} height={14} className="notificationClose"
                      onClick={() => NotificationService.Remove(notifications[notifications.length - 1])}
                      />
                <span>{notifications[notifications.length - 1].text}</span>
            </div>}

            {/*{notifications.map((notification, index) => {*/}
            {/*    return (*/}
            {/*        <div key={index} className={classNames('notification',*/}
            {/*            notification.isError ? 'notificationError' : 'notificationSuccess'*/}
            {/*        )}>*/}
            {/*            <Icon type={Icons.CROSS} width={14} height={14} className="notificationClose"*/}
            {/*                  onClick={() => NotificationService.Remove(notification)}/>*/}
            {/*            <span>{notification.text}</span>*/}
            {/*        </div>*/}
            {/*    );*/}
            {/*})}*/}
        </div>
    );
};
export default Notifications;