import TwoFaIcon from "../TwoFaIcon";

const CLASSES_STEPS = ["iconStep", "iconStep stepTwo", "iconStep stepThree"];

const IconStep = ({step}) => {

    return (
        <div className={CLASSES_STEPS[step]}>
            <TwoFaIcon type="stepOne" />
            <hr/>
            <TwoFaIcon type="stepTwo" />
            <hr/>
            <TwoFaIcon type="stepThree" />
        </div>
    )
}

export default IconStep;