import React from 'react';
import t from '../../../../lang/t';
import useIntl from 'react-intl/lib/src/components/useIntl';
import Button from '../../../../components/Button/Button';
import Form from '../../../../components/Form/Form';
import Input from '../../../../components/Input/Input';
import Modal from '../../../../components/Modal/Modal';
import Checkbox from '../../../../components/Checkbox/Checkbox';

const MerchantModal = (props) => {
    const { id, name, setName, link, setLink, isReusable, setIsReusable, isNameError, onMerchant, onModalClose } = props;

    const intl = useIntl();

    const isMerchant = () => !!name;

    //todo translate
    return (
        <Modal
            className="merchantsModal"
            title={!id ? t('Add merchant') : t('Edit merchant')}
            onClose={onModalClose}
        >
            <Form onSubmit={onMerchant}>
                <Input
                    type="name"
                    label={isNameError ? t('Title exists') : t('Merchant name')}
                    placeholder={intl.formatMessage({ id: 'Merchant name' })}
                    value={name}
                    required
                    error={isNameError}
                    onChange={(e) => setName(e.target.value)}
                />
                <Input
                    type="text"
                    label={t('Merchant backlink')}
                    placeholder={intl.formatMessage({ id: 'Merchant backlink' })}
                    value={link}
                    onChange={(e) => setLink(e.target.value)}
                    tooltip={'The link to which notifications will be sent when funds are received'}
                />
                <Checkbox
                    id="merchantsModalIsReusable"
                    className="merchantsModalIsReusable"
                    checked={isReusable}
                    onChange={() => setIsReusable((prev) => !prev)}
                    label={'Use static addresses'}
                    tooltip={'Whether the merchant is allowed to create static addresses. Addresses to which you can pay an unlimited number of times'}
                />
                <Button
                    type="submit"
                    disabled={!isMerchant()}
                >
                    { !id ? t('Create') : t('Change') }
                </Button>
            </Form>
        </Modal>
    );
};

export default MerchantModal;
