import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../../../../../../../lang/t';
import Tooltip from '../../../../../../../../components/Tooltip/Tooltip';
import Input from '../../../../../../../../components/Input/Input';
import Typography from '@mui/material/Typography';
import { transactionTypes } from './transaction-types';
import classNames from 'classnames';
import { withdrawalSlice } from '../../../../../../../../store/slices';
import RequestService from '../../../../../../../../services/RequestService';
import Types from '../../../../../../../../classes/Types';
import HelperService from '../../../../../../../../services/HelperService';

import './TransactionCommission.scss';


function debounce(func, timeout = 500) {
    let timer;
    return (...args) => {
        clearTimeout(timer);
        timer = setTimeout(() => {
            func.apply(this, args);
        }, timeout);
    };
}

const TransactionCommission = () => {
    const [commissionFiat, setCommissionFiat] = useState(0);
    const dispatch = useDispatch();
    const intl = useIntl();
    const activeCommission = useSelector((state) => state?.withdrawal?.manualWithdrawal?.commissionType);
    const activeCurrency = useSelector((state) => state?.withdrawal?.manualWithdrawal?.currency);
    const commission = useSelector((state) => state?.withdrawal?.manualWithdrawal?.commission);
    const commissionPerKb = useSelector((state) => state?.withdrawal?.manualWithdrawal?.commissionPerKb);
    const commissionsBySpeed = useSelector((state) => state?.withdrawal?.manualWithdrawal?.commissionsBySpeed);
    const transactionSize = useSelector((state) => state?.withdrawal?.manualWithdrawal?.transactionSize);
    const transactions = Object.values(transactionTypes);
    const onChangeCommission = (commission) => {
        dispatch(withdrawalSlice.actions.setCommissionWithdrawalModel(commission));
        HelperService.SetLocalStorageProperty(Types.localStorageCommissionTypeName, commission?.name);
    };

    const getQuotes = async () => {
        const quotes = await RequestService.GetQuotes();
        const currentQuote = quotes?.Quotes?.find((quote) => quote?.Cryptocurrency === activeCurrency.Symbol);
        const price = currentQuote?.Price;
        const fiatCommission = (+commission * price).toFixed(2);
        setCommissionFiat(fiatCommission);
        dispatch(withdrawalSlice.actions.setCommissionFiatAmountWithdrawalModel(fiatCommission));
    };

    const getQuotesWithDebounce = debounce(getQuotes);

    const onChange = (e) => {
        const { value } = e.target;
        const valueChecked = value.replace(/,/g, '.');
        e.target.value = valueChecked;
        const maxLength = 11;
        const isValid = valueChecked.match(/^(\d*\.{0,1}\d{0,8}$)/);
        if (isValid && valueChecked.length <= maxLength) {
            if (transactionSize) {
                const commissionTypes = Object.keys(commissionsBySpeed)
                    .map((key) => ({ type: key, fee: commissionsBySpeed[key] }))
                    .sort((a, b) => a.fee - b.fee);
                let type = commissionTypes[0]?.type;
                commissionTypes.forEach((speed) => {
                    if (+valueChecked > speed?.fee) type = speed?.type;
                });
                onChangeCommission(transactionTypes[type]);
            }
            dispatch(withdrawalSlice.actions.setCommissionAmountWithdrawalModel(valueChecked));
        }
    };

    useEffect(() => {
        getQuotesWithDebounce();
    }, [commission]); // eslint-disable-line react-hooks/exhaustive-deps

    if (Types.currencyWithoutAutoCommission.includes(activeCurrency.Symbol)) {
        return (
            <div className="TransactionCommission">
                <div className="TransactionCommission__Title">
                    <Typography variant="h5">{t('Transaction fee ver2')}</Typography>
                    <span className="title-tooltip">
                    <Tooltip id="transaction-commission-tooltip"
                             text={t('The amount of commission that will be paid to the blockchain for the transaction. The speed of the transaction depends on the size of the amount')}/>
                    </span>
                </div>
                <div
                    className="TransactionCommission__Subtitle">{t('Commission will be calculated automatically')}</div>
            </div>
        );
    }

    return (
        <div className="TransactionCommission">
            <div className="TransactionCommission__Title">
                <Typography variant="h5">{t('Transaction fee ver2')}</Typography>
                <span className="title-tooltip">
                    <Tooltip id="transaction-commission-tooltip"
                             text={t('The amount of commission that will be paid to the blockchain for the transaction. The speed of the transaction depends on the size of the amount')}/>
                </span>
            </div>
            <div className="TransactionCommission__Tabs">
                {Array.isArray(transactions) && transactions.length > 0 && transactions.map((transaction) => (
                    <button key={transaction.id} onClick={() => onChangeCommission(transaction)}
                            className={classNames('commission-tab', { active: transaction.id === activeCommission.id })}>{transaction.label}</button>
                ))}
            </div>
            <div className="TransactionCommission__Input">
                <div className="form-group">
                    <div className="form-field">
                        <Input id="commission-input" type="text"
                               label={`${intl.formatMessage({ id: 'Enter fee in' })} ${activeCurrency.Symbol}`}
                               value={commission.toString()}
                               onChange={onChange}/>
                        <div className="input-rate">
                            <div className="value">{HelperService.FormatFiat(commissionFiat)}</div>
                        </div>
                    </div>
                    <div className="input-label">{activeCommission.title}</div>
                    {!!transactionSize && <div className="input-label">{t('Transaction fee per 1Kb')} { HelperService.FormatCrypto(commissionPerKb) } { activeCurrency?.Symbol }</div>}
                </div>
            </div>
        </div>
    );
};

export default TransactionCommission;
