import RequestService from '../../../services/RequestService';
import { addressSlice, appSlice, withdrawalSlice } from '../../slices';
import { riskTypes } from '../../../containers/RootModal/components/Withdrawal/components/RiskSelect/riskTypes';
import WithdrawalFormModel from '../../../models/withdrawal-form';
import Types from '../../../classes/Types';
import { batch } from 'react-redux';

function getMerchantAddresses(store, action) {
    if (action.payload && action.payload.tag === 'withdrawal') {
        const state = store.getState();
        const cryptoCurrencies = state?.cryptocurrency?.cryptocurrencies;
        const merchants = state?.merchant?.merchants;
        const sendAddress = state?.withdrawal?.manualWithdrawal?.sendAddress;
        const isFetchedData = state?.withdrawal?.isFetchedData;
        const promises = [];

        if (Array.isArray(cryptoCurrencies) && Array.isArray(merchants) && !isFetchedData) {
            promises.push(RequestService.GetAddresses(1, 999999, '', { by: 'Id', desc: true }, []));
            promises.push(RequestService.GetQuarantine(1, 999999, '', { by: 'Id', desc: true }, []));

            Promise.all(promises).then((address) => {
                if (!address.length) return;
                const allAddress = address.filter((add) => (Object.keys(add).length > 0));
                let addressArray = [];
                allAddress.forEach((add) => {
                    if (add.Addresses) addressArray = [...addressArray, ...add.Addresses];
                });

                // Filter zero balance
                addressArray = addressArray.filter(add => add?.Balance > 0);

                store.dispatch(addressSlice.actions.getAllAddresses(addressArray));

                if (sendAddress) {
                    store.dispatch(withdrawalSlice.actions.setAddressesWithdrawalModel(addressArray.map((add) => ({
                        ...add,
                        isShow: (add.Address === sendAddress),
                        riskName: setAddressRisk(add),
                    }))));
                    store.dispatch(withdrawalSlice.actions.filterAddressesWithdrawalModel(null));
                } else {
                    store.dispatch(withdrawalSlice.actions.setAddressesWithdrawalModel(addressArray.map((add) => ({
                        ...add,
                        isShow: true,
                        riskName: setAddressRisk(add),
                    }))));
                    store.dispatch(withdrawalSlice.actions.filterAddressesWithdrawalModel(null));
                }
            }).finally(() => {
                store.dispatch(appSlice.actions.setLoaderStatus(false));
            });
        }
    }
}

function setWithdrawalFilters(store, action) {
    if (action.payload && action.payload.tag === 'withdrawal') {
        const { sendAddress = null, sendCurrency = null, sendMerchant = null } = action.payload?.outsideParams;
        const isHasDefaultParams = !!sendAddress && !!sendCurrency && !!sendMerchant;
        const state = store.getState();
        const cryptocurrencies = state?.cryptocurrency?.cryptocurrencies;
        const merchants = state?.merchant?.merchants;
        const riskLevel = riskTypes.all;
        const isFetchedData = state?.withdrawal?.isFetchedData;

        if (isFetchedData) return;

        if (isHasDefaultParams) {
            const currency = cryptocurrencies.find((crr) => crr.Symbol === sendCurrency);
            const merchant = [{...merchants.find((mrch) => mrch.Id === sendMerchant), isShow: true}];

            store.dispatch(withdrawalSlice.actions.setManualWithdrawalModel(new WithdrawalFormModel({
                currency,
                merchant,
                riskLevel,
                sendAddress,
            })));
        } else {
            const currency = sendCurrency ? cryptocurrencies.find((crt) => crt.Symbol === sendCurrency) : cryptocurrencies[0];
            const merchant = sendMerchant ? [{...merchants.find((mrch) => mrch.Id === sendMerchant), isShow: true}] : [{...merchants[0], isShow: true}];

            store.dispatch(withdrawalSlice.actions.setManualWithdrawalModel(new WithdrawalFormModel({
                currency,
                merchant,
                riskLevel,
            })));
        }
    }
}

function clearFormData(store, action) {
    if (action.payload && action.payload.tag === 'withdrawal' && !action.payload.isOpen) {
        store.dispatch(withdrawalSlice.actions.clearFormData(null));
    }
}

function setAddressesFiltering(store, action) {
    const model = store.getState().withdrawal.manualWithdrawal;
    if (action.type === 'withdrawal/setCurrencyWithdrawalModel' && JSON.stringify(model?.currency) === JSON.stringify(action?.payload)) return;
    if (action.type === 'withdrawal/setMerchantWithdrawalModel' && JSON.stringify(model?.merchant) === JSON.stringify(action?.payload)) return;
    if (action.type === 'withdrawal/setRiskWithdrawalModel' && JSON.stringify(model?.riskLevel) === JSON.stringify(action?.payload)) return;

    setTimeout(() => store.dispatch(withdrawalSlice.actions.filterAddressesWithdrawalModel(null)), 0);
}

function setAddressRisk(address) {

    if (address.RiskScore > 0 && address.RiskScore <= 0.25) {
        return riskTypes.low.name;
    }

    if (address.RiskScore > 0.25 && address.RiskScore <= 0.74) {
        return riskTypes.medium.name;
    }

    if (address.RiskScore > 0.74) {
        return riskTypes.high.name;
    }

    if (address.RiskScore <= 0) {
        return riskTypes.unchecked.name;
    }
}

async function getCommission(store) {
    setTimeout(async () => {
        const { withdrawal = {} } = store.getState();
        const { manualWithdrawal = {}, withdrawalFilteredList = [] } = withdrawal;
        const { currency, cryptoAmount, commissionType } = manualWithdrawal;
        const addressesToShow = withdrawalFilteredList.filter((add) => add.isShow).map((add) => add?.Address);
        const isReadyToFetch = currency && (!Types.currencyWithoutAutoCommission.includes(currency?.Symbol));

        const resetCommission = () => {
            batch(() => {
                store.dispatch(withdrawalSlice.actions.setTransactionSizeWithdrawalModel(0));
                store.dispatch(withdrawalSlice.actions.setCommissionBySpeedWithdrawalModel({
                    slow: 0,
                    medium: 0,
                    fast: 0
                }));
                store.dispatch(withdrawalSlice.actions.setCommissionAmountWithdrawalModel(0));
            });
        };

        if (Types.currencyWithoutAutoCommission.includes(currency?.Symbol)) {
            resetCommission();
            return;
        }

        if (isReadyToFetch) {
            const commission = await RequestService.CalculateCommissionWithdrawal(currency?.Symbol, addressesToShow, cryptoAmount);

            if (commission && commission?.Status === Types.requestStatuses.success) {
                let commissionTypes = {};
                const { TransactionSize: transactionSize = 0, CommissionsBySpeed: commissionsBySpeed } = commission?.CommissionInfo;

                if (Array.isArray(commissionsBySpeed) && commissionsBySpeed.length > 0) {
                    commissionsBySpeed.forEach((speed) => {
                        commissionTypes[`${speed?.Speed.toLowerCase()}`] = speed?.Fee ? speed?.Fee : 0;
                    });
                }

                if (transactionSize && Object.keys(commissionTypes).length > 0) {
                    batch(() => {
                        store.dispatch(withdrawalSlice.actions.setTransactionSizeWithdrawalModel(transactionSize));
                        store.dispatch(withdrawalSlice.actions.setCommissionBySpeedWithdrawalModel(commissionTypes));
                        store.dispatch(withdrawalSlice.actions.setCommissionAmountWithdrawalModel(commissionTypes[commissionType?.name]));
                    });
                }
            } else {
                resetCommission();
            }

        }

    }, 0);
}

function calculateCommissionPerKb(store, action) {
    setTimeout(() => {
        const { withdrawal = {} } = store.getState();
        const { manualWithdrawal = {} } = withdrawal;
        const { commission = 0, transactionSize = 0 } = manualWithdrawal;
        const commissionPerKb = +commission / (+transactionSize / 1024);
        const formattedCommission = (isNaN(commissionPerKb)) ? 0 : commissionPerKb;
        store.dispatch(withdrawalSlice.actions.setCommissionByKbWithdrawalModel(formattedCommission));
    }, 0);
}

export const manualWithdrawalMiddleware = (store) => (next) => (action) => {
    switch (action.type) {
        case 'app/updateModalStructure':
            setWithdrawalFilters(store, action);
            getMerchantAddresses(store, action);
            clearFormData(store, action);
            break;
        case 'withdrawal/setCurrencyWithdrawalModel':
        case 'withdrawal/setRiskWithdrawalModel':
        case 'withdrawal/setMerchantWithdrawalModel':
            setAddressesFiltering(store, action);
            break;
        case 'withdrawal/setCryptoAmountWithdrawalModel':
        case 'withdrawal/filterAddressesWithdrawalModel':
        case 'withdrawal/selectAddressesWithdrawalModel':
            getCommission(store).then();
            break;
        case 'withdrawal/setCommissionAmountWithdrawalModel':
        case 'withdrawal/setCommissionWithdrawalModel':
            calculateCommissionPerKb(store, action);
            break;
        default:
            break;
    }
    return next(action);
};
