import React from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import Modal from '../../../../components/Modal/Modal';
import Form from '../../../../components/Form/Form';
import Input from '../../../../components/Input/Input';
import Select from '../../../../components/Select/Select';
import Button from '../../../../components/Button/Button';
import StorageService from '../../../../services/StorageService';

const AdministratorsModal = (props) => {
    const { id, name, setName, email, setEmail, password, setPassword, rights, setRights,
        merchants, setMerchants, onModalClose, onAdministrator, isEmailError, isAdministrator } = props;

    const intl = useIntl();

    return (
        <Modal className="administratorsModal" title={intl.formatMessage({ id: !id ? 'Add administrator' : 'Administrator editing' })}
            onClose={onModalClose}>
            <Form onSubmit={onAdministrator}>
                <Input type="name" label={intl.formatMessage({ id: 'Name *' })} placeholder={intl.formatMessage({ id: 'Name *' })} value={name} required
                    onChange={(e) => setName(e.target.value)} />
                <Input type="email" label={intl.formatMessage({ id: isEmailError ? 'Email already exist' : 'Email *' })}
                       placeholder={intl.formatMessage({ id: 'Email *' })} value={email}
                       required error={isEmailError} onChange={(e) => setEmail(e.target.value)} />
                {!id &&
                    <Input type="password" label={intl.formatMessage({ id: 'Password *' })}
                           placeholder={intl.formatMessage({ id: 'Password *' })} value={password} required
                           onChange={(e) => setPassword(e.target.value)} />}
                <Select isMulti={true} label={intl.formatMessage({ id: 'Permissions' })} options={StorageService.Rights} value={rights} className="disabled" disabled
                    onChange={(options) => setRights(options)} />
                <Select isMulti={true} label={intl.formatMessage({ id: 'Access to merchants' })} options={StorageService.Merchants} value={merchants}
                    onChange={(options) => setMerchants(options)} />
                <Button type="submit" disabled={!isAdministrator()}>{intl.formatMessage({ id: !id ? 'Create' : 'Change' })}</Button>
            </Form>
        </Modal>
    );
};

export default AdministratorsModal;