import React, { useState, useRef, useEffect } from 'react';
import { useSelector } from 'react-redux';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../../../../../lang/t';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import Typography from '@mui/material/Typography';
import Input from '../../../../../../components/Input/Input';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import classNames from 'classnames';
import HelperService from '../../../../../../services/HelperService';

import './ReceivingCurrencySelect.scss';

const ReceivingCurrencySelect = ({ selectedCurrency, setSelectedCurrency, currencyBalances }) => {
    const ref = useRef(null);
    const intl = useIntl();

    const [dropdownStatus, setDropdownStatus] = useState(false);
    const [searchValue, setSearchValue] = useState('');

    const cryptocurrencies = useSelector((state) => state?.cryptocurrency?.cryptocurrencies);

    const [filteredCurrencies, setFilteredCurrencies] = useState(cryptocurrencies);
    const activeCurrency = cryptocurrencies?.find(cryptocurrency => cryptocurrency.Symbol === selectedCurrency) || cryptocurrencies[0];

    const onSelectItem = (currency) => {
        setSelectedCurrency(currency.Symbol);
        setDropdownStatus(false);
    };

    const onClickOutside = (e) => {
        if (ref.current && ref.current.contains && !ref.current.contains(e.target)) {
            if (dropdownStatus) setDropdownStatus(false);
        }
    };

    useEffect(() => {
        document.addEventListener('click', onClickOutside, true);
        return () => {
            document.removeEventListener('click', onClickOutside, true);
        };
    });

    const onChangeSearch = (e) => {
        const { value = '' } = e.target;
        const RGX = new RegExp(`${value}`, 'i');
        const filteredCurrencies = !!value ? cryptocurrencies.filter(currency => RGX.test(currency.Name)) : cryptocurrencies;

        setSearchValue(value);
        setFilteredCurrencies(filteredCurrencies);
    };

    return (
        <div ref={ref} className="ReceivingCurrencySelect">
            <div className="click-zone" onClick={() => setDropdownStatus(!dropdownStatus)} />
            {activeCurrency && Icons[activeCurrency.Symbol] && <Icon type={Icons[activeCurrency.Symbol]} width={24} height={24} />}
            <div className="currency-name">
                <Typography variant="h4">{activeCurrency?.Name}</Typography>
            </div>
            <Icon className="dropdown-icon" type={Icons.SELECT_ARROW} width={16} height={16} />
            {dropdownStatus && <div className="dropdown">
                <div className="dropdown__search">
                    <div className="search-title">
                        {t('Select crypto currency')}
                    </div>
                    <Input type="search" placeholder={intl.formatMessage({ id: 'To find crypto currency' })} value={searchValue} onChange={onChangeSearch} />
                </div>
                <List component="nav" className="dropdown__list">
                    {(Array.isArray(filteredCurrencies) && filteredCurrencies.length > 0) && filteredCurrencies.map((currency) => (
                        <ListItemButton key={currency.Id}
                            className={classNames({ active: activeCurrency.Id === currency.Id })}
                            onClick={() => onSelectItem(currency)}>
                            <div className="dropdown__item item">
                                <div className="item__currency">
                                    {Icons[currency.Symbol] && <Icon type={Icons[currency.Symbol]} width={16} height={16} />}
                                    <div className="item__currency-name">
                                        <Typography variant="h4">{currency.Name}</Typography>
                                    </div>
                                </div>
                                <div className="item__value">
                                    <div className="item__value-number">
                                        {currency.Symbol in currencyBalances
                                            ? HelperService.FormatCrypto(currencyBalances[currency.Symbol], currency.Symbol === 'USDT-TRC20' && 2)
                                            : '-'}
                                    </div>
                                    <div className="item__value-currency">{currency.Symbol}</div>
                                </div>
                            </div>
                        </ListItemButton>
                    ))}
                </List>
            </div>}
        </div>
    );
};

export default ReceivingCurrencySelect;