import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import useIntl from 'react-intl/lib/src/components/useIntl';
import { appSlice } from '../../store/slices';
import t from '../../lang/t';
import config from '../../AppConfig';
import NotificationService from '../../services/NotificationService';
import RequestService from '../../services/RequestService';
import HelperService from '../../services/HelperService';
import StorageService from '../../services/StorageService';
import Button from '../../components/Button/Button';
import Form from '../../components/Form/Form';
import Icon, { Icons } from '../../components/Icon/Icon';
import Input from '../../components/Input/Input';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import LanguageSelector from '../../components/LanguageSelector';
import Types from "../../classes/Types";

import './Login.scss';

const { setToken } = appSlice.actions;
const { routes, defaults } = config;
const RegExps = {
    protocol: /^https?:\/\//,
    domain: /(?:[a-z0-9](?:[a-z0-9-]{0,61}[a-z0-9])?\.)+[a-z0-9][a-z0-9-]{0,61}[a-z0-9]/,
    port: /:[1-9][0-9]*$/,
    localhost: /localhost|127\.\d{1,3}\.\d{1,3}\.\d{1,3}/,
};
const urlDefaults = {
    protocol: `${defaults.protocol}://`,
    port: `:${defaults.port}`,
};

const Login = () => {
    const dispatch = useDispatch();
    const intl = useIntl();

    const isFetching = useSelector((state) => state?.app?.isFetchingLogin);
    const setIsFetching = (status) => {
        dispatch(appSlice.actions.setFetchingLoginStatus(status));
    };

    const [isError, setIsError] = useState(false);
    const [isNew, setIsNew] = useState(false);
    const [is2FA, setIs2FA] = useState(false);

    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');
    const [twoFactorCode, setTwoFactorCode] = useState(['', '', '', '', '', '']);
    const localStorageSaveDomain = HelperService.HasLocalStorageProperty(Types.localStorageIsRememberDomainName) ? (HelperService.GetLocalStorageProperty(Types.localStorageIsRememberDomainName) !== 'false') : true;
    const [saveDomain, setSaveDomain] = useState(localStorageSaveDomain);
    const localStorageDomain = (HelperService.HasLocalStorageProperty(Types.localStorageDomainName) && saveDomain) ? HelperService.GetLocalStorageProperty(Types.localStorageDomainName) : '';
    const [domain, setDomain] = useState(localStorageDomain);
    const [isDomainError, setDomainError] = useState(false);
    const [, setDomainTouched] = useState(false);

    const onLogin = async (email, password, twoFactorCode) => {
        const loginResponse = await RequestService.Login(email, password, twoFactorCode);

        if (!loginResponse) return;

        if (loginResponse['Status'] === 'Error') {
            return loginResponse;
        }

        RequestService.Token = loginResponse['Token'];
        RequestService.OnLogout = () => {
            HelperService.RemoveTokenFromStorage();
            dispatch(setToken(null));
        };

        dispatch(setToken(loginResponse['Token']));
        HelperService.SetTokenToStorage(loginResponse['Token']);

        await StorageService.Initialize();
        return loginResponse;
    };

    const onChangeTwoFactor = async (key, code) => {
        const prevTwoFactorCode = Object.assign([], twoFactorCode);

        if (code === 'Backspace' && key !== 0 && prevTwoFactorCode[key] === '') {
            prevTwoFactorCode[key - 1] = '';
            setTwoFactorCode(prevTwoFactorCode);

            document.getElementById('loginTwoFactorInput' + key).focus();
            return;
        } else if (code === 'Backspace') {
            prevTwoFactorCode[key] = '';
            setTwoFactorCode(prevTwoFactorCode);
        }


        if (+code !== 0 && !+code) {
            return;
        }

        prevTwoFactorCode[key] = code;
        setTwoFactorCode(prevTwoFactorCode);

        if (key < 5 && code.length !== 0) {
            document.getElementById('loginTwoFactorInput' + (key + 2)).focus();
        } else if (key === 5) {
            const loginResponse = await onLogin(login, password, prevTwoFactorCode.join(''));

            if (loginResponse['Status'] !== 'Success') {
                setTwoFactorCode(['', '', '', '', '', '']);

                NotificationService.Push(t('Incorrect code'), true);
                document.getElementById('loginTwoFactorInput1').focus();
            }
        }
    };

    const getMatches = (matches) => matches ? matches[0] : null;
    const fillMatches = (matches, name) => matches ? matches : urlDefaults[name];

    const onSubmit = async () => {
        if(isDomainError) return;

        setIsFetching(true);
        setIsError(false);

        if(saveDomain) {
            HelperService.SetLocalStorageProperty(Types.localStorageDomainName, domain);
        }

        let _protocol = domain.match(RegExps.protocol);
        let _domain = domain.match(RegExps.domain) || domain.match(RegExps.localhost);
        let _port = domain.match(RegExps.port);

        _protocol = getMatches(_protocol);
        _domain = getMatches(_domain);
        _port = getMatches(_port);

        _protocol = fillMatches(_protocol, 'protocol');
        _port = fillMatches(_port, 'port');

        const apiUrl = `${_protocol}${_domain}${_port}/`;

        HelperService.SetLocalStorageProperty(Types.localStorageDomainServerName, apiUrl);

        config.SetConfig('apiUrl', apiUrl);

        const loginResponse = await onLogin(login, password);

        if(!loginResponse) {
            NotificationService.Push(t('Network error'), true);
            dispatch(appSlice.actions.setFetchingLoginStatus(false));
            return;
        }

        if (loginResponse['Status'] !== 'Success') {
            if (loginResponse['ErrorCode'] === 'UserNotFound' || loginResponse['ErrorCode'] === 'AutorizationFailed') {
                setIsError(true);
                NotificationService.Push(t('Incorrect email or password'), true);
            } else if (loginResponse['ErrorMessage'] === '') {
                // TODO: Update after API update;
                setIsNew(true);
            } else if (loginResponse['ErrorCode'] === 'InvalidParam') {
                setIs2FA(true);
                document.getElementById('loginTwoFactorInput1').focus();
            }
        }

        setIsFetching(false);
    };

    const onChangeDomain = (e) => {
        setDomain(e.target.value);
    };

    const onSaveDomain = (e) => {
        const { checked } = e.target;
        setSaveDomain(checked);
        HelperService.SetLocalStorageProperty(Types.localStorageIsRememberDomainName, checked);
    };

    const onSaveDomainTouched = (status) => {
        const reg = new RegExp(RegExps.domain);
        const regLocal = new RegExp(RegExps.localhost);
        if (status) {
            setDomainError(!reg.test(domain) && !regLocal.test(domain));
        } else {
            setDomainError(false);
        }
        setDomainTouched(status);
    };

    return (
        <div className="login">
            <Icon type={Icons.LOGO_MIN} width={72} height={72} className="loginLogo" />
            {!isNew && !is2FA &&
            <Form className="loginForm" onSubmit={onSubmit}>
                <h2 className="loginTitle">{t('Authorization')}</h2>
                <div className="auth-domain">
                    <Input
                        type="text"
                        label={intl.formatMessage({ id: 'Authorization domain' })}
                        placeholder={intl.formatMessage({ id: 'Authorization domain' })}
                        required
                        error={isDomainError}
                        value={domain}
                        onChange={onChangeDomain}
                        onFocus={() => onSaveDomainTouched(false)}
                        onBlur={() => onSaveDomainTouched(true)}
                    />
                    <FormControlLabel control={<Checkbox checked={saveDomain} onChange={onSaveDomain} size="small" />} label={intl.formatMessage({ id: 'Remember' })} />
                </div>
                <Input type="email" label='Email' placeholder={intl.formatMessage({ id: 'Email' })} required value={login} error={isError}
                       onChange={(e) => setLogin(e.target.value)} />
                <Input type="password" label={intl.formatMessage({ id: 'Password' })} placeholder={intl.formatMessage({ id: 'Password' })} required value={password} error={isError}
                       onChange={(e) => setPassword(e.target.value)} />
                <Button type="submit" disabled={isFetching}>{t('Login')}</Button>
                <div className="loginForm__footer">
                    <Link to={routes.forgot} className="forgotPassword">{t('Forgot your password?')}</Link>
                    <LanguageSelector />
                </div>
            </Form>}
            {isNew && !is2FA &&
            <div className="loginNew">
                <Icon type={Icons.NEW} width={84} height={84} />
                <h2 className="loginTitle">{t('You signed in from a new device')}</h2>
                <p className="loginDescription">{t('Authorization code has been sent to your email')}</p>
            </div>}
            {is2FA &&
            <Form className="loginTwoFactor" onSubmit={onSubmit}>
                <h2 className="loginTitle">{t('Two-factor authentication')}</h2>
                <p className="loginDescription">{t('Enter your code from the Google Authenticator')}</p>
                <div className="loginTwoFactorInputContainer">
                    <Input id="loginTwoFactorInput1" type="text" value={twoFactorCode[0]} required
                           onKeyDown={(e) => onChangeTwoFactor(0, e.key)} />
                    <Input id="loginTwoFactorInput2" type="text" value={twoFactorCode[1]} required
                           onKeyDown={(e) => onChangeTwoFactor(1, e.key)} />
                    <Input id="loginTwoFactorInput3" type="text" value={twoFactorCode[2]} required
                           onKeyDown={(e) => onChangeTwoFactor(2, e.key)} />
                    <Input id="loginTwoFactorInput4" type="text" value={twoFactorCode[3]} required
                           onKeyDown={(e) => onChangeTwoFactor(3, e.key)} />
                    <Input id="loginTwoFactorInput5" type="text" value={twoFactorCode[4]} required
                           onKeyDown={(e) => onChangeTwoFactor(4, e.key)} />
                    <Input id="loginTwoFactorInput6" type="text" value={twoFactorCode[5]} required
                           onKeyDown={(e) => onChangeTwoFactor(5, e.key)} />
                </div>
                <p className="loginTwoFactorSupport">{t('To disable two-factor authentication contact support')}</p>
            </Form>}
        </div>
    );
};
export default Login;
