import { createSlice } from '@reduxjs/toolkit';
import LicenseModel from '../../../models/license';
import RiskModel from '../../../models/risk';
import RootModalModel from '../../../models/root-modal';
import VersionInfoModel from '../../../models/version-info';
import { LOCALES } from '../../../lang';
import HelperService from '../../../services/HelperService';
import BackupModel from '../../../models/backup';
import TronRootAccountModel from '../../../models/tron-root-account';

const initialState = {
    token: null,
    license: new LicenseModel(),
    risks: null,
    modal: new RootModalModel(),
    versionInfo: new VersionInfoModel(),
    lang: HelperService.HasLanguageInStorage() ? HelperService.GetLanguageFromStorage() : LOCALES.en,
    backup: new BackupModel({}),
    backupLastUpdate: null,
    isFetchingLogin: false,
    tronRootAccount: null,
};


export const appSlice = createSlice({
    name: 'app',
    initialState,
    reducers: {
        setToken: (state, action) => {
            const { payload: token = null } = action;
            state.token = token;
        },
        getLicense: (state, action) => {
            const { payload: license = {} } = action;
            state.license = new LicenseModel(license);
        },
        getRisks: (state, action) => {
            const { payload: risks = {} } = action;
            const riskKeys = Object.keys(risks);
            if (Array.isArray(riskKeys) && riskKeys.length > 0) {
                let generatedObject = {};
                riskKeys.forEach((key) => {
                    generatedObject[key] = new RiskModel(risks[key]);
                });
                state.risks = generatedObject;
            }
        },
        updateModalStructure: (state, action) => {
            const { payload: model = {} } = action;
            state.modal = new RootModalModel(model);
        },
        setVersionInfo: (state, action) => {
            state.versionInfo = action.payload;
        },
        setLoaderStatus: (state, action) => {
            const { payload: status = false } = action;
            state.modal = new RootModalModel({ ...state.modal, isShowLoader: status });
        },
        setLanguage: (state, action) => {
            const { payload: lang = LOCALES.en } = action;
            state.lang = lang;
        },
        setBackupModel: (state, action) => {
            const { payload: model } = action;
            const { encryptionPassword = '' } = model;
            const testEXP = /^[A-Za-z0-9]*$/;
            const testLength = 20;
            let error = !testEXP.test(encryptionPassword) || (encryptionPassword.toString().length < testLength);
            error = model.encryptionPasswordIsTouched ? error : false;
            state.backup = new BackupModel({ ...model, encryptionPasswordError: error });
        },
        setBackupLastUpdate: (state, action) => {
            const { payload: update } = action;
            state.backupLastUpdate = update;
        },
        setFetchingLoginStatus: (state, action) => {
            state.isFetchingLogin = action.payload;
        },
        setTronRootAccount: (state, action) => {
            state.tronRootAccount = new TronRootAccountModel(action.payload);
        },
    },
});
