import React from 'react';
import { useParams } from 'react-router-dom';
import t from '../../lang/t';
import config from '../../AppConfig';
import MainTabMain from './MainTabMain';
import MainTabHistory from './MainTabHistory';
import PageHeadTabs from '../../components/PageHeadTabs';

import './Main.scss';

const { routes } = config;

const Main = () => {
    const { tab } = useParams();

    const tabOptions = [
        {
            to: routes.main,
            value: t('Dark wing'),
            isActive: () => (tab !== 'history'),
        },
        // {
        //     to: '/profile/history',
        //     isActive: () => (tab === 'history'),
        //     value: t('History of account BitHide'),
        // },
    ];

    return (
        <div className="main">
            <div className="mainHead">
                <PageHeadTabs tabOptions={tabOptions} />
            </div>
            {tab !== 'history' && <MainTabMain/>}
            {tab === 'history' && <MainTabHistory/>}
        </div>
    );
};
export default Main;