import React, { useState, useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import classNames from 'classnames';
import t from '../../lang/t';
import Types from '../../classes/Types';
import { addressSlice } from '../../store/slices/address';
import { getRiskLevelByRiskScore } from '../../utils';
import HelperService from '../../services/HelperService';
import RequestService from '../../services/RequestService';
import ButtonMUI from '@mui/material/Button';
import Copy from '../../components/Copy/Copy';
import Icon, { Icons } from '../../components/Icon/Icon'
import ButtonIcon from '../../components/ButtonIcon';
import SideModal from '../../components/SideModal/SideModal';
import Tooltip from '../../components/Tooltip/Tooltip';

import './RiskModal.scss';

const { setCheckAddressData, setQuarantineAddress } = addressSlice.actions;
const { signalsTypesMap, signalsFieldsMap } = Types;

const RiskModal = ({ onCheck, showRemoveQuarantine }) => {
    const [isPercent, setIsPercent] = useState(false);

    const address = useSelector((state) => state.address.riskAddress);
    const checkData = useSelector((state) => state.address.checkAddressData);
    const riskLevels = useSelector((state) => state.app.license.RiskLevels);
    const dispatch = useDispatch();

    const { location: { pathname } } = useHistory();

    const riskLevelType = getRiskLevelByRiskScore(address ? address['RiskScore'] : null);

    const onClose = useCallback(() => {
        const { setRiskAddress } = addressSlice.actions;
        dispatch(setRiskAddress(null));
    }, [dispatch]);

    const handleCheck = () => {
        onCheck(address);
        onClose();
    };

    const handleQuarantine = () => {
        dispatch(setQuarantineAddress(address));
        onClose();
    };

    const getRiskLevelData = useCallback((riskLevel, signals, balance = 0) => {
        const data = {};
        Object.entries(riskLevels).filter((i) => i[1] === riskLevel).forEach((i) => {
            const value = signals[i[0]];
            if (value) data[i[0]] = value;
        });
        return Object.entries(data).map((i) => ({
            title: signalsFieldsMap.get(i[0]).title,
            amount: balance * i[1],
            percent: i[1] * 100,
        })).filter((i) => i.amount);
    }, [riskLevels]);

    useEffect(() => {
        const fetchData = async () => {
            if (!address) {
                dispatch(setCheckAddressData(null));
                return;
            }

            dispatch(setCheckAddressData(null));

            const response = await RequestService.GetCheckAddress(address['Address']);

            if (response['Status'] !== 'Success') {
                dispatch(setCheckAddressData(null));
                return;
            }

            const signals = response['Data'].Signals;
            if (!signals) {
                dispatch(setCheckAddressData(null));
                return;
            }

            const lowType = signalsTypesMap.get('Low').type;
            const mediumType = signalsTypesMap.get('Medium').type;
            const highType = signalsTypesMap.get('High').type;
            const uncategorizedType = signalsTypesMap.get('Uncategorized').type;

            const balance = address['Balance'];

            const checkDateResult = {
                [lowType]: getRiskLevelData(lowType, signals, balance),
                [mediumType]: getRiskLevelData(mediumType, signals, balance),
                [highType]: getRiskLevelData(highType, signals, balance),
                [uncategorizedType]: getRiskLevelData(uncategorizedType, signals, balance),
            };

            dispatch(setCheckAddressData(checkDateResult));
        };

        if (address) {
            fetchData();
        }
    }, [address, getRiskLevelData, dispatch]);

    useEffect(() => {
        onClose();
    }, [pathname, onClose]);

    const renderRiskGroup = (riskType) => {
        if (!checkData[riskType].length) return null;
        return (
            <div key={riskType} className={classNames('riskModalSource', 'riskModalSource' + riskType)}>
                <div className="riskModalSourceTitle">
                    <Icon type={Icons.RISK} width={28} height={28} className="riskIcon" />
                    {(riskType === signalsTypesMap.get('Low').type) && (
                        <span>
                            {t('Minimum risk level')}
                            <Tooltip text={t('Cryptocurrency is not involved in criminal or risky activities')} />
                        </span>
                    )}
                    {(riskType === signalsTypesMap.get('Medium').type) && (
                        <span>
                            {t('Medium risk')}
                            <Tooltip text={t('Cryptocurrency is participating in risky activities')} />
                        </span>
                    )}
                    {(riskType === signalsTypesMap.get('High').type) && (
                        <span>
                            {t('High risk')}
                            <Tooltip text={t('Cryptocurrency is involved in illegal activities')} />
                        </span>
                    )}
                    {(riskType === signalsTypesMap.get('Uncategorized').type) && (
                        <span>
                            {t('Undefined source')}
                            <Tooltip text={t('Cryptocurrency with undefined risk level')} />
                        </span>
                    )}
                </div>
                {checkData[riskType].map((item, index) => {
                    return (
                        <div key={index} className="riskModalSourceItem">
                            <p><i/>{item['title']}</p>
                            {isPercent && (
                                <span>{HelperService.FormatPercent(item['percent'])}&nbsp;<small>%</small></span>
                            )}
                            {!isPercent && (
                                <span>{HelperService.FormatCrypto(item['amount'], address['Currency'] === 'USDT-TRC20' && 2)}&nbsp;<small>{address['Currency']}</small></span>
                            )}
                        </div>
                    );
                })}
                <div className="riskModalSourceDelimiter"/>
                <div className="riskModalTotal">
                    <p>{t('Total:')}</p>
                    {isPercent &&
                    <b>
                        {HelperService.FormatPercent(checkData[riskType].reduce((prev, current) => prev + current['percent'], 0))}
                        &nbsp;<small>%</small>
                    </b>}
                    {!isPercent &&
                    <b>
                        {HelperService.FormatCrypto(checkData[riskType].reduce((prev, current) => prev + current['amount'], 0), address['Currency'] === 'USDT-TRC20' && 2)}
                        &nbsp;<small>{address['Currency']}</small>
                    </b>}
                </div>
            </div>
        )
    };

    return (
        <SideModal className="riskModal" title={t('Information')} isOpen={!!address} onClose={onClose}>
            {!!address &&
            <>
                <div className="riskModalAddress">
                    <div className="riskModalRow">
                        <div className="riskModalCol">
                            <Copy text={address['Address']}/>
                            <span className="riskModalColLabel">{t('Crypto address')}</span>
                        </div>
                    </div>
                    <div className="riskModalRow">
                        <div className="riskModalCol">
                            <b>
                                {HelperService.FormatCrypto(address['Balance'], address['Currency'] === 'USDT-TRC20' && 2)}
                                &nbsp;<small>{address['Currency']}</small>
                            </b>
                            <span className="riskModalColLabel">{t('Amount')}</span>
                        </div>
                        <div className="riskModalCol">
                            <div className="riskModalRisk">
                                <Icon className={riskLevelType.color} type={Icons.RISK} width={16} height={16}/>

                                <span>{(address['RiskScore'] * 100) + ' %'}</span>
                            </div>
                            <span className="riskModalColLabel">{t('Risk level')}</span>
                        </div>
                    </div>
                    <ButtonMUI variant="contained" color="secondary" className="riskModalCheck text-initial" onClick={handleCheck}>
                        <ButtonIcon />
                        <span>{t('Verify crypto address again')}</span>
                    </ButtonMUI>
                    <span className="riskModalCheckDate">
                        {t('Date of last check:')}{` ${new Date(address['LastCheckDate']).toLocaleDateString()}`}
                    </span>
                    {(riskLevelType.riskLevel === 'High' || showRemoveQuarantine) && (
                        <ButtonMUI variant="contained" color="error" className="riskModalQuarantine text-initial" onClick={handleQuarantine}>
                            {t(showRemoveQuarantine ? 'To remove from quarantine' : 'Send to Quarantine')}
                        </ButtonMUI>
                    )}
                </div>
                <div className="riskModalSourcesHead">
                    <h3>{t('Sources of funds')}</h3>
                    <div className="riskModalSourcesHeadContainer">
                        <Icon className={isPercent ? '' : 'active'} type={Icons.COIN} width={16} height={16}
                              onClick={() => setIsPercent(false)}/>
                        <Icon className={isPercent ? 'active' : ''} type={Icons.PERCENT} width={16} height={16}
                              onClick={() => setIsPercent(true)}/>
                    </div>
                </div>
                <div className="riskModalSources">
                    {checkData ? Object.keys(checkData).map(renderRiskGroup) : null}
                </div>
            </>}
        </SideModal>
    );
};
export default RiskModal;
