import React, { useState, useRef, useEffect } from 'react';
import classNames from 'classnames';
import './AutoScalingInput.scss';

const AutoScalingInput = ({ onChange, value, name }) => {
    const inputRef = useRef(null);
    const spanRef = useRef(null);
    const offset = 0;
    const [content, setContent] = useState(value);
    const [width, setWidth] = useState(0);

    useEffect(() => {
        if (spanRef && spanRef.current) setWidth((spanRef.current?.offsetWidth + offset));
    }, [content]);

    useEffect(() => {
        setContent(value);
    }, [value])

    const changeHandler = (e) => {
        const { value } = e.target;
        const valueChecked = value.replace(/,/g, '.');
        e.target.value = valueChecked;
        const maxLength = 24;
        const regExps = {
            fiat: /^(\d*\.{0,1}\d{0,2}$)/,
            crypto: /^(\d*\.{0,1}\d{0,8}$)/,
        };

        const isValid = valueChecked.match(regExps[name]);
        if (isValid && valueChecked.length <= maxLength) {
            setContent(valueChecked);
            onChange(e);
        }
    };

    const focusHandle = (e) => {
        if (+e.target.value === 0 || e.target.value === '0.') {
            e.target.value = '';
            setContent('');
            onChange(e);
        }
    };

    return (
        <div className="AutoScalingInput">
            <span className="hide" ref={spanRef}>{content}</span>
            <input
                ref={inputRef}
                type="text"
                autoComplete="off"
                name={name}
                value={content}
                className={classNames('auto-scale-input', { underline: !content })} style={{ width }}
                onChange={changeHandler}
                onFocus={focusHandle}
            />
        </div>
    );
};

export default AutoScalingInput;
