import BaseModel from './base';

export default class TronRootAccountModel extends BaseModel {
    constructor(initData) {
        super();

        this.OwnerAddress = '';
        this.OwnerPrivateKey = '';
        this.Balance = '';
        this.isAvailable = false;

        this.copyFrom(initData);
        this.setAccountStatus(initData);
    }

    setAccountStatus(initData) {
        this.isAvailable = !!(initData?.OwnerAddress && initData?.OwnerPrivateKey);
    }
}
