import React, { useState, useEffect } from 'react';
import { Redirect, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import t from '../../lang/t';
import { addressSlice } from '../../store/slices/address';
import config from '../../AppConfig';
import NotificationService from '../../services/NotificationService';
import RequestService from '../../services/RequestService';
import DeleteModal from '../../modals/DeleteModal/DeleteModal';
import RiskModal from '../../modals/RiskModal/RiskModal';
import WalletsList from '../../components/WalletsList/WalletsList';
import QuarantineTable from './components/QuarantineTable';

import './Quarantine.scss';

const { routes } = config;
const { setQuarantineAddress } = addressSlice.actions;

const Quarantine = ({ onCheck, onWithdraw, calculateBalances, needUpdateQuarantine, setNeedUpdateQuarantine }) => {
    const { addresses } = useParams();

    const [balancesData, setBalancesData] = useState({ sum: 0, list: [] });

    const [balances, setBalances] = useState([]);
    const [isLoading, setLoading] = useState(false);
    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState([]);
    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [selectedItems, setSelectedItems] = useState([]);
    const [redirectToFunds, setRedirectToFunds] = useState(false);

    const cryptocurrencies = useSelector(state => state.cryptocurrency.cryptocurrencies);
    const quotes = useSelector(state => state.cryptocurrency.quotes);
    const quarantineAddress = useSelector((state) => state.address.quarantineAddress);
    const merchants = useSelector((state) => state?.merchant.merchants);
    const dispatch = useDispatch();

    useEffect(() => {
        if (!addresses) return;

        setPage(1);
        setFilters([{ 'Type': '1', 'Column': 'Address', 'Search': addresses.split(',') }]);
        setRedirectToFunds(true);
    }, [addresses]);

    const updateBalancesData = (balances) => {
        const balancesData = calculateBalances(balances, quotes, cryptocurrencies);
        setBalancesData(balancesData);
    }
    
    useEffect(() => {
        updateBalancesData(balances);
    }, [quotes]); // eslint-disable-line react-hooks/exhaustive-deps

    const GetBalances = async () => {
        const merchantIds = merchants.map(merchant => merchant.Id);
        const response = await RequestService.GetBalances(merchantIds, true);

        if (response.Status === 'Success') {
            setBalances(response.Balance);
            updateBalancesData(response.Balance);
        }
    };

    useEffect(() => {
        GetBalances();
    }, []); // eslint-disable-line react-hooks/exhaustive-deps

    const onPaginate = async (tablePage = page, tableCount = count, tableSearch = search, tableSort = sort, tableFilters = filters) => {
        setLoading(true);

        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        const response = await RequestService.GetQuarantine(tablePage, tableCount, tableSearch, tableSort, tableFilters);

        setTotal(response.Total);
        setData(response.Addresses);

        setLoading(false);
        setNeedUpdateQuarantine(false);
    };

    useEffect(() => {
        if (needUpdateQuarantine) onPaginate();
    }, [needUpdateQuarantine]); // eslint-disable-line react-hooks/exhaustive-deps

    const onQuarantine = async () => {
        const response = await RequestService.QuarantineRemove(quarantineAddress.Id);
        dispatch(setQuarantineAddress(null));
        if (response.Status !== 'Success') {
            NotificationService.Push(t('Unable to remove from quarantine'), true);
            return;
        }
        NotificationService.Push(t('Successfully removed from quarantine'), false);
        onPaginate();
        GetBalances();
    };

    if (redirectToFunds) {
        return (
            <Redirect to={routes.funds} />
        );
    }

    const walletsListProps = { sourcePage: 'quarantine', data: balancesData, onPaginate, onWithdraw, page, count, search, sort, filters };

    const quarantineTableProps = { isLoading, page, count, search, sort, filters, total, data, selectedItems, setSelectedItems, onWithdraw, onCheck, onPaginate };

    return (
        <div className="quarantine">
            <RiskModal onCheck={onCheck} showRemoveQuarantine={true}/>
            {quarantineAddress && (
                <DeleteModal
                    title={t('Remove from quarantine')}
                    subtitle={t('Are you sure you want to remove the address from quarantine?')}
                    description={quarantineAddress.Address}
                    currency={quarantineAddress.Currency}
                    onAccept={onQuarantine}
                    onClose={() => dispatch(setQuarantineAddress(null))}
                />
            )}
            <WalletsList {...walletsListProps} />
            <QuarantineTable {...quarantineTableProps} />
        </div>
    );
};
export default Quarantine;