import React from 'react';
import * as PropTypes from 'prop-types';
import classNames from 'classnames';
import './Button.scss';

const Button = ({ id, className, type, color, onClick, disabled, children, ...props }) => {
    const handleClick = (e) => {
        e.stopPropagation();
        onClick();
    };

    return (
        <button id={id} className={classNames(color, className)} type={type} onClick={handleClick} disabled={disabled}
                {...props}>
            {children}
        </button>
    );
};

Button.defaultProps = {
    id: null,
    className: null,
    type: 'button',
    color: 'default',
    children: null,
    disabled: false,
    onClick: () => {}
};

Button.propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    type: PropTypes.oneOf(['button', 'submit']),
    color: PropTypes.oneOf(['default', 'light', 'red', 'green']),
    children: PropTypes.node,
    disabled: PropTypes.bool,
    onClick: PropTypes.func
};

export default Button;