import { createSlice, current } from '@reduxjs/toolkit';
import WithdrawalFormModel from '../../../models/withdrawal-form';
import { riskTypes } from '../../../containers/RootModal/components/Withdrawal/components/RiskSelect/riskTypes';

const initialState = {
    autoWithdrawals: [],
    isFetchedData: false,
    manualWithdrawal: new WithdrawalFormModel(),
    withdrawalFilteredList: [],
    withdrawalSearchList: [],
    lastTransaction: [],
};

export const withdrawalSlice = createSlice({
    name: 'withdrawal',
    initialState,
    reducers: {
        getAllAutoWithdrawals: (state, action) => {
            const { payload: autoWithdrawals = [] } = action;
            state.autoWithdrawals = autoWithdrawals;
        },
        setManualWithdrawalModel: (state, action) => {
            const { payload: model = {} } = action;
            state.manualWithdrawal = new WithdrawalFormModel(model);
        },
        setCurrencyWithdrawalModel: (state, action) => {
            const { payload: currency = null } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, currency });
        },
        setMerchantWithdrawalModel: (state, action) => {
            let { payload: merchant = null } = action;
            if (!Array.isArray(merchant)) merchant = [merchant];
            merchant = merchant.filter(item => item.isShow)
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, merchant });
        },
        setRiskWithdrawalModel: (state, action) => {
            const { payload: riskLevel = null } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, riskLevel });
        },
        setAddressesWithdrawalModel: (state, action) => {
            const { payload: addresses = [] } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, addresses });
            state.withdrawalFilteredList = addresses;
            state.withdrawalSearchList = addresses;
            state.isFetchedData = true;
        },
        filterAddressesWithdrawalModel: (state, action) => {
            const { addresses, currency, merchant, riskLevel } = state.manualWithdrawal;
            let filteredAddress = addresses.filter((add) => (!add.IsHidden && add.Currency === currency.Symbol && (riskLevel.name === riskTypes.all.name ? true : add.riskName === riskLevel.name)));
            filteredAddress = filteredAddress.filter(add => merchant.filter(merch => add.MerchantId === merch.Id ? true : false).length > 0 ? true : false );

            const withdrawalFilteredList = Object.assign([], current(state.withdrawalFilteredList));

            filteredAddress = filteredAddress.map(add1 => {
                const index = withdrawalFilteredList.findIndex(add2 => add2.Id === add1.Id);
                return index !== -1 ? {...add1, isShow: withdrawalFilteredList[index].isShow} : {...add1, isShow: true};
            });

            state.withdrawalFilteredList = filteredAddress;
            state.withdrawalSearchList = filteredAddress;
            const availableBalance = state.withdrawalFilteredList.filter((add) => add.isShow).reduce((acc, add) => (acc + add.Balance), 0);
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, availableBalance });
        },
        selectAddressesWithdrawalModel: (state, action) => {
            const { payload: address } = action;
            const filter = (add) => add.Id === address.Id ? { ...add, isShow: !add.isShow } : add;

            state.withdrawalFilteredList = state.withdrawalFilteredList.map((add) => filter(add));
            state.withdrawalSearchList = state.withdrawalSearchList.map((add) => filter(add));
            const availableBalance = state.withdrawalFilteredList.filter((add) => add.isShow).reduce((acc, add) => (acc + add.Balance), 0);
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, availableBalance });
        },
        selectAllAddressesWithdrawalModel: (state, action) => {
            const isShow = state.withdrawalFilteredList.some(add => !add.isShow);
            const filter = (add) => ({ ...add, isShow });

            state.withdrawalFilteredList = state.withdrawalFilteredList.map((add) => filter(add));
            state.withdrawalSearchList = state.withdrawalSearchList.map((add) => filter(add));
            const availableBalance = state.withdrawalFilteredList.filter((add) => add.isShow).reduce((acc, add) => (acc + add.Balance), 0);
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, availableBalance });
        },
        searchAddressesWithdrawalModel: (state, action) => {
            const { payload: search } = action;
            const RGX = new RegExp(`${search}`, 'i');
            state.withdrawalSearchList = !!search ? state.withdrawalSearchList.filter((add) => RGX.test(add.Address)) : state.withdrawalFilteredList;
        },
        setFiatAmountWithdrawalModel: (state, action) => {
            const { payload: amount = 0 } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, fiatAmount: amount });
        },
        setCryptoAmountWithdrawalModel: (state, action) => {
            const { payload: amount = 0 } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, cryptoAmount: amount });
        },
        setReceiverAddressWithdrawalModel: (state, action) => {
            const { payload: receiverAddress = '' } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, receiverAddress });
        },
        setCommissionWithdrawalModel: (state, action) => {
            const { payload: commissionType = {} } = action;
            state.manualWithdrawal = new WithdrawalFormModel({
                ...state.manualWithdrawal,
                commissionType,
                commission: state.manualWithdrawal.commissionsBySpeed[commissionType.name],
            });
        },
        setCommissionAmountWithdrawalModel: (state, action) => {
            const { payload: commission = 0 } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, commission });
        },
        setCommissionFiatAmountWithdrawalModel: (state, action) => {
            const { payload: commissionFiat = 0 } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, commissionFiat });
        },
        setCommissionByKbWithdrawalModel: (state, action) => {
            const { payload: commissionPerKb = 0 } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, commissionPerKb });
        },
        setCommentWithdrawalModel: (state, action) => {
            const { payload: comment } = action;
            state.manualWithdrawal = new WithdrawalFormModel({ ...state.manualWithdrawal, comment });
        },
        toggleConfirmationWithdrawalModel: (state, action) => {
            state.manualWithdrawal = new WithdrawalFormModel({
                ...state.manualWithdrawal,
                confirmation: !state.manualWithdrawal.confirmation
            });
        },
        setCommissionBySpeedWithdrawalModel: (state, action) => {
            const { payload } = action;
            state.manualWithdrawal = new WithdrawalFormModel({
                ...state.manualWithdrawal,
                commissionsBySpeed: payload,
            });
        },
        setTransactionSizeWithdrawalModel: (state, action) => {
            const { payload } = action;
            state.manualWithdrawal = new WithdrawalFormModel({
                ...state.manualWithdrawal,
                transactionSize: payload,
            });
        },
        setFetchDataStatus: (state, action) => {
            const { payload: status = false } = action;
            state.isFetchedData = status;
        },
        clearFormData: (state) => {
            state.isFetchedData = false;
            state.manualWithdrawal = new WithdrawalFormModel();
            state.withdrawalFilteredList = [];
            state.withdrawalSearchList = [];
            state.lastTransaction = [];
        },
        setLastTransaction: (state, action) => {
            const { payload: lastTransaction = [] } = action;
            state.lastTransaction = lastTransaction;
        },
    },
});
