import React, { useState, useEffect } from 'react';
import CryptoJs from 'crypto-js';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../lang/t';
import DepositModal from '../DepositModal/DepositModal';
import Icon, { Icons } from '../../components/Icon/Icon';
import Tooltip from '../../components/Tooltip/Tooltip';
import Button from '../../components/Button/Button';
import Select from '../../components/Select/Select';
// import HelperService from '../../services/HelperService';
import StorageService from '../../services/StorageService';
import './ProfileModalTabProfile.scss';

const ProfileModalTabProfile = ({onLogout}) => {
    const [devicePrints, setDevicePrints] = useState('');
    const [isDepositModal, setIsDepositModal] = useState(false);

    const [commissionCryptos, setCommissionCryptos] = useState([]);
    const [commissionCrypto, setCommissionCrypto] = useState(null);

    const intl = useIntl();

    useEffect(() => {
        const fetchData = async () => {
            const commissionCryptos = StorageService.System['CommissionSettings']?.map((item) => {
                return { label: item['Currency'], value: item['Currency'], data: item };
            });

            setCommissionCrypto(commissionCryptos?.[0]);
            setCommissionCryptos(commissionCryptos);
        };
        fetchData();
    }, []);
    useEffect(() => {
        let prints = CryptoJs.MD5(window.navigator.userAgent).toString().toUpperCase().substr(0, 20);
        let formattedPrints = '';

        for (let i = 0; i < prints.split('').length; i++) {
            formattedPrints += prints[i];

            if ((i + 1) % 5 === 0 && i !== 19) {
                formattedPrints += '-';
            }
        }

        setDevicePrints(formattedPrints);
    }, []);

    //todo translate
    return (
        <div className="profileModalTabProfile">
            {isDepositModal &&
            <DepositModal onClose={() => setIsDepositModal(false)}/>}
            <h3>{StorageService.Self['Name']}</h3>
            <Tooltip className="profileModalTabProfileIp" text={t('Device ID that keeps your account secure.')}>
                {t('Device Fingerprint:')}
                &nbsp;
                {devicePrints}
            </Tooltip>
            <div className="profileModalTabProfileBalance">
                <div className="profileModalTabProfileBalanceHead">
                    <h4>{t('Your BitHide')}</h4>
                    <span className={StorageService.System['Status'] ? 'active' : 'inactive'}>
                        {StorageService.System['Status'] ? t('Active') : t('Inactive')}
                    </span>
                </div>
                <div className="profileModalTabProfileBalanceBody">
                    <Tooltip text={t('The amount of funds currently on your BitHide account to pay for the plan.')}>
                        {t('BitHide account balance')}
                    </Tooltip>
                    {/* <p>{HelperService.FormatFiat(StorageService.System['Balance'])}</p> */}
                    <p>{t('N/A')}</p>
                </div>
                {false &&
                <Button color="green" onClick={() => setIsDepositModal(true)}>
                    <Icon type={Icons.WALLET} width={24} height={24}/>
                    <span>{t('Top up balance')}</span>
                </Button>}
            </div>
            <div className="profileModalTabProfileSeparator"/>
            <div className="profileModalTabProfilePlan">
                <h5>{t('Your plan')}</h5>
                <div className="profileModalTabProfileRow">
                    <Tooltip position="left" text={t('The cost of a single check for a crypto address risk level.')}>
                        {t('The cost of verifying a crypto address')}
                    </Tooltip>
                    {/* <p>{HelperService.FormatFiat(StorageService.System['CheckAddressCost'])}</p> */}
                    <p>{t('N/A')}</p>
                </div>
                <div className="profileModalTabProfileRow">
                    <Tooltip position="left" text={t('The minimum amount of service charges that must be paid. In case of incomplete payment of the amount, the balance will be deducted from the BitHide account balance.')}>
                        {t('Minimum monthly payment')}
                    </Tooltip>
                    <div>
                        {/* <p>{HelperService.FormatFiat(StorageService.System['MinRequiredPayment'])}</p> */}
                        <p>{t('N/A')}</p>
                        {/*<span>Списание 21.02.2022</span>*/}
                    </div>
                </div>
                <div className="profileModalTabProfileRow">
                    <Tooltip position="left" text={t('The number of merchants available for creation in the BitHide system')}>
                        {t('Available number of merchants')}
                    </Tooltip>
                    {/* <p>{HelperService.FormatNumber(StorageService.System['MaxMerchantsCount'])}</p> */}
                    <p>{t('N/A')}</p>
                </div>
                <div className="profileModalTabProfileRow">
                    <Tooltip position="left" text={t('The monthly payment amount for the one merchant maintenance')}>
                        {t('Merchant\'s monthly cost')}
                    </Tooltip>
                    {/* <p>{HelperService.FormatFiat(StorageService.System['MerchantPricePerMonth'])}</p> */}
                    <p>{t('N/A')}</p>
                </div>
                {commissionCryptos?.length !== 0 &&
                <>
                    <h5>{t('Service fee')}</h5>
                    <Select className="profileModalTabProfileRowCurrency" label={intl.formatMessage({ id: 'Select crypto currency' })} disabled
                            options={commissionCryptos} value={commissionCrypto}
                            onChange={(value) => setCommissionCrypto(value)}/>
                    {commissionCrypto &&
                    <div className="profileModalTabProfileRow">
                        <Tooltip position="left" text={t('The minimum fee that will be charged for each incoming transaction to your wallet.')}>
                            {t('Minimum fee for incoming transaction')}
                        </Tooltip>
                        {/* <p>
                            {HelperService.FormatCrypto(commissionCrypto.data['MinCommission'], commissionCrypto.label === 'USDT-TRC20' && 2)}
                            &nbsp;{commissionCrypto.label}
                        </p> */}
                        <p>{t('N/A')}</p>
                    </div>}
                    {commissionCrypto &&
                    <div className="profileModalTabProfileRow">
                        <Tooltip position="left" text={t('The commission percentage that will be charged for each incoming transaction to your wallet, if it is more than the minimum fee.')}>
                            {t('Incomin transaction fee')}
                        </Tooltip>
                        {/* <p>{HelperService.FormatNumber(commissionCrypto.data['CommissionPercentage'])}%</p> */}
                        <p>{t('N/A')}</p>
                    </div>}
                </>}
            </div>
            <div className="profileModalTabLogout">
                <Button className="profileModalTabLogoutBtn" onClick={onLogout}><Icon type={Icons.LOGOUT} width={20} height={20}/>{t('Sign Out')}</Button>
            </div>
        </div>
    );
};
export default ProfileModalTabProfile;