import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../../../../../../../lang/t';
import Typography from '@mui/material/Typography';
import Tooltip from '../../../../../../../../components/Tooltip/Tooltip';
import Input from '../../../../../../../../components/Input/Input';
import { withdrawalSlice } from '../../../../../../../../store/slices';
import './TransactionComment.scss';

const TransactionComment = () => {
    const dispatch = useDispatch();
    const intl = useIntl();
    const comment = useSelector((state) => state?.withdrawal?.manualWithdrawal?.comment);
    const onChange = (e) => { dispatch(withdrawalSlice.actions.setCommentWithdrawalModel(e.target.value)) };

    return (
        <div className="TransactionComment">
            <div className="TransactionComment__Title">
                <Typography variant="h5">{t('Comment')}</Typography>
                <Tooltip id="transaction-comment-tooltip" text={t('The comment is optional, but allows you to refine the transaction on request. Will be displayed in transaction history')} />
            </div>
            <div className="TransactionComment__Input">
                <Input type="text" label={t('Enter a description')} placeholder={intl.formatMessage({ id: 'Enter a description' })} value={comment} onChange={onChange} maxLength={2000} />
            </div>
        </div>
    );
};

export default TransactionComment;