import React, { useState, useEffect } from 'react';
import t from '../../lang/t';
import Icon, { Icons } from '../../components/Icon/Icon';
import Select from '../../components/Select/Select';
import Input from '../../components/Input/Input';
import StorageService from '../../services/StorageService';
import RequestService from '../../services/RequestService';
import './CalcModal.scss';

const CalcModal = ({ onClose }) => {
    const [currencies, setCurrencies] = useState([]);
    const [quotesTimeout, setQuotesTimeout] = useState(0);

    const [fromCurrency, setFromCurrency] = useState(null);
    const [from, setFrom] = useState('');

    const [toCurrency, setToCurrency] = useState(null);
    const [to, setTo] = useState('');

    const onChangeCurrency = (currency, isTo) => {
        isTo
            ? fromCurrency === currency ? onExchange() : setToCurrency(currency)
            : toCurrency === currency ? onExchange() : setFromCurrency(currency);

        if ((isTo && fromCurrency !== currency) || (!isTo && toCurrency !== currency)) {
            onChangeAmount(from, false, isTo ? fromCurrency : currency, isTo ? currency : toCurrency);
        }
    };
    const onChangeAmount = async (amount, isTo, from = fromCurrency, to = toCurrency) => {
        isTo ? setTo(amount) : setFrom(amount);

        if (!+amount) {
            isTo ? setFrom(amount) : setTo(amount);
            return;
        }

        clearTimeout(quotesTimeout);
        const timeout = setTimeout(async () => {
            const quotes = await RequestService.GetQuotes();

            const fromQuote = quotes['Quotes'].find(
                (item) => item['Cryptocurrency'] === from.value);

            const toQuote = quotes['Quotes'].find(
                (item) => item['Cryptocurrency'] === to.value);

            if (!fromQuote || !toQuote) {
                isTo
                    ? setFrom((fromQuote
                        ? (amount / fromQuote['Price']).toFixed(from.value === 'USDT-TRC20' ? 2 : 8)
                        : (amount * toQuote['Price']).toFixed(2)))
                    : setTo((fromQuote
                        ? (amount * fromQuote['Price']).toFixed(2)
                        : (amount / toQuote['Price']).toFixed(to.value === 'USDT-TRC20' ? 2 : 8)));
            } else {
                isTo
                    ? setFrom((amount * toQuote['Price'] / fromQuote['Price']).toFixed(from.value === 'USDT-TRC20' ? 2 : 8))
                    : setTo((amount * fromQuote['Price'] / toQuote['Price']).toFixed(to.value === 'USDT-TRC20' ? 2 : 8));
            }
        }, 500);
        setQuotesTimeout(timeout);
    };

    const onExchange = () => {
        setFromCurrency(toCurrency);
        setFrom(to);

        setToCurrency(fromCurrency);
        setTo(from);
    };

    useEffect(() => {
        const listenerCallback = () => onClose();
        document.body.addEventListener('mousedown', listenerCallback);

        return () => {
            document.body.removeEventListener('mousedown', listenerCallback);
        }
    }, [onClose]);
    useEffect(() => {
        const fetchData = async () => {
            const response = StorageService.Cryptos.map((item) => {
                return { label: item['value'], value: item['value'] };
            });
            response.unshift({ label: 'USD', value: 'USD' });

            setFromCurrency(response[1]);
            setToCurrency(response[0]);

            setCurrencies(response);
        };
        fetchData();
    }, []);

    return (
        <div className="calcModal" onMouseDown={(e) => e.stopPropagation()}
             onClick={(e) => e.stopPropagation()} >
            <h4>{t('Cryptocurrency calculator')}</h4>
            <div className="calcModalConvert">
                <div className="calcModalConvertItem">
                    <Select options={currencies} value={fromCurrency}
                            onChange={(option) => onChangeCurrency(option, false)}/>
                    <Input type="number" value={from} onChange={(e) => onChangeAmount(e.target.value, false)}/>
                </div>
                <Icon type={Icons.EXCHANGE} width={24} height={24} onClick={onExchange}/>
                <div className="calcModalConvertItem">
                    <Select options={currencies} value={toCurrency}
                            onChange={(option) => onChangeCurrency(option, true)}/>
                    <Input type="number" value={to} onChange={(e) => onChangeAmount(e.target.value, true)}/>
                </div>
            </div>
            {/*<div className="calcModalCourses">*/}
            {/*    <div className="calcModalCoursesCourse">*/}
            {/*        <p>*/}
            {/*            <b>BTC / USD</b>*/}
            {/*            <span>Binance</span>*/}
            {/*            <span>14:34</span>*/}
            {/*        </p>*/}
            {/*        <div>*/}
            {/*            <b>37 745.07 USD</b>*/}
            {/*            <p className="calcModalCoursesCourseUp">*/}
            {/*                <Icon type={Icons.ARROW} width={16} height={16}/>*/}
            {/*                <span>-2.09%</span>*/}
            {/*            </p>*/}
            {/*        </div>*/}
            {/*    </div>*/}
            {/*    <div className="calcModalCoursesCourse">*/}
            {/*        <p>*/}
            {/*            <b>BTC / USD</b>*/}
            {/*            <span>Binance</span>*/}
            {/*            <span>14:34</span>*/}
            {/*        </p>*/}
            {/*        <div>*/}
            {/*            <b>37 745.07 USD</b>*/}
            {/*            <p className="calcModalCoursesCourseDown">*/}
            {/*                <Icon type={Icons.ARROW} width={16} height={16}/>*/}
            {/*                <span>-2.09%</span>*/}
            {/*            </p>*/}
            {/*        </div>*/}
            {/*    </div>*/}
            {/*    <div className="calcModalCoursesCourse">*/}
            {/*        <p>*/}
            {/*            <b>BTC / USD</b>*/}
            {/*            <span>Binance</span>*/}
            {/*            <span>14:34</span>*/}
            {/*        </p>*/}
            {/*        <div>*/}
            {/*            <b>37 745.07 USD</b>*/}
            {/*            <p className="calcModalCoursesCourseUp">*/}
            {/*                <Icon type={Icons.ARROW} width={16} height={16}/>*/}
            {/*                <span>-2.09%</span>*/}
            {/*            </p>*/}
            {/*        </div>*/}
            {/*    </div>*/}
            {/*</div>*/}
        </div>
    );
};
export default CalcModal;