import React from 'react';
import { useSelector } from 'react-redux';
import t from '../../../../../../lang/t';
import './ConfirmationMerchant.scss';

const ConfirmationMerchant = () => {
    const activeMerchant = useSelector((state) => state?.withdrawal?.manualWithdrawal?.merchant);
    return (
        <div className="ConfirmationMerchant">
            <div className="ConfirmationMerchant__Label">
                {t('From the merchant')}
            </div>
            <div className="ConfirmationMerchant__Value">
                {activeMerchant?.Name}
            </div>
        </div>
    );
};

export default ConfirmationMerchant;