import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Icon, { Icons } from '../Icon/Icon';
import './Modal.scss';

const Modal = ({ className, title, twoFaStep, description, children, onClose }) => {
    return (
        <div className={classNames('modal', className)} onMouseDown={onClose}>
            <div className="modalContainer" onMouseDown={(e) => e.stopPropagation()}>
                <div className="modalContainerHeader">
                    <Icon type={Icons.CROSS} width={32} height={32} onClick={onClose}/>
                    {title && <h2>{title}</h2>}
                    {twoFaStep && twoFaStep}
                    {description && <p>{description}</p>}
                </div>
                <div className="modalContainerBody">{children}</div>
            </div>
        </div>
    );
};

Modal.defaultProps = {
    className: null,
    title: null,
    description: null,
    children: null,
    onClose: () => {}
};

Modal.propTypes = {
    className: PropTypes.string,
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    description: PropTypes.string,
    children: PropTypes.node.isRequired,
    onClose: PropTypes.func.isRequired
};

export default Modal;