import React from 'react';
import { useSelector } from 'react-redux';
import t from '../../../../../../lang/t';
import './ConfirmationRisk.scss';

const ConfirmationRisk = () => {
    const activeRiskLevel = useSelector((state) => state?.withdrawal?.manualWithdrawal?.riskLevel);
    return (
        <div className="ConfirmationRisk">
            <div className="ConfirmationRisk__Label">
                {t('By the risk level')}
            </div>
            <div className="ConfirmationRisk__Value">
                {activeRiskLevel?.label}
            </div>
        </div>
    );
};

export default ConfirmationRisk;