import React, { useState, useRef, useEffect } from 'react';
import t from '../../../../lang/t';
import TextField from '@mui/material/TextField';
import ButtonMUI from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import { useSelector, useDispatch } from 'react-redux';
import { appSlice } from '../../../../store/slices';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
import NotificationService from '../../../../services/NotificationService';
import generator from 'generate-password';
import classNames from 'classnames';

import './Backup.scss';
import RequestService from '../../../../services/RequestService';

const BootstrapTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} arrow classes={{ popper: className }}/>
))(({ theme }) => ({
    [`& .${tooltipClasses.arrow}`]: {
        color: '#EFEFEF',
    },
    [`& .${tooltipClasses.tooltip}`]: {
        backgroundColor: '#ffffff',
        color: '#464646',
        fontSize: theme.typography.pxToRem(12),
        border: '1px solid #EFEFEF',
        boxShadow: '0 2px 8px rgba(0, 0, 0, 0.1)',
    },
}));

const BackupIcon = ({ type = 'copy' }) => {
    const icons = {
        copy: (<svg
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            fill="none"
            viewBox="0 0 16 16"
        >
            <g fill="#0D62B5" clipPath="url(#clip0_5988_41295)">
                <path d="M11.273 0H2.545c-.803 0-1.454.65-1.454 1.455v10.181h1.454V1.455h8.728V0z"/>
                <path
                    d="M13.455 2.91h-8C4.65 2.91 4 3.56 4 4.363v10.181C4 15.35 4.65 16 5.455 16h8c.803 0 1.454-.65 1.454-1.454V4.364c0-.804-.65-1.455-1.454-1.455zm0 11.636h-8V4.364h8v10.181z"/>
            </g>
            <defs>
                <clipPath id="clip0_5988_41295">
                    <path fill="#fff" d="M0 0H16V16H0z"/>
                </clipPath>
            </defs>
        </svg>),
        eye: (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="none"
                viewBox="0 0 16 16"
            >
                <path
                    fill="#0455A3"
                    d="M8 2.667c-3.333 0-6.18 2.073-7.333 5 1.153 2.926 4 5 7.333 5s6.18-2.074 7.333-5c-1.153-2.927-4-5-7.333-5zM8 11a3.335 3.335 0 010-6.667A3.335 3.335 0 018 11zm0-5.333c-1.107 0-2 .893-2 2 0 1.106.893 2 2 2s2-.894 2-2c0-1.107-.893-2-2-2z"
                />
            </svg>),
        key: (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="none"
                viewBox="0 0 16 16"
            >
                <path fill="#9FA3A6" d="M11 6a1 1 0 100-2 1 1 0 000 2z"/>
                <path
                    fill="#9FA3A6"
                    d="M7.5 12v-.5h1A.5.5 0 009 11v-1h1a4 4 0 10-3.838-2.87L2.292 11a1 1 0 00-.292.707V13a1 1 0 001 1h2a1 1 0 001-1v-.5h1a.5.5 0 00.5-.5zM7 6a3 3 0 113 3H8.5a.5.5 0 00-.5.5v1H7a.5.5 0 00-.5.5v.5h-1a.5.5 0 00-.5.5v1H3v-1.293l4.089-4.089a.5.5 0 00.113-.534C7.072 6.748 7 6.384 7 6z"
                />
            </svg>
        ),
        dropBox: (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="25"
                height="24"
                fill="none"
                viewBox="0 0 25 24"
            >
                <path
                    fill="#0061FF"
                    d="M12.032 5.76L6.016 9.6l6.016 3.84-6.016 3.84L0 13.408l6.016-3.84L0 5.76l6.016-3.84 6.016 3.84zM5.984 18.496L12 14.656l6.016 3.84L12 22.336l-6.016-3.84zm6.048-5.088l6.016-3.84-6.016-3.808 5.984-3.84 6.016 3.84-6.016 3.84 6.016 3.84-6.016 3.84-5.984-3.872z"
                />
            </svg>
        ),
    };

    return icons[type];
};

const Backup = () => {
    const [saveButtonStatus, setSaveButtonStatus] = useState(true);
    const dispatch = useDispatch();
    const backupModel = useSelector((state) => state?.app?.backup);
    const modal = useSelector((state) => state?.app?.modal);
    const encryptionPasswordRef = useRef(null);
    const {
        encryptionPassword,
        encryptionPasswordType,
        encryptionPasswordError,
        encryptionPasswordIsTouched,
        signInStatus,
        saveStatus,
        authUrl,
        opts
    } = backupModel;

    const onChangeEncryptionPassword = (e) => {
        const newBackupModel = {
            ...backupModel,
            encryptionPassword: e.target.value,
            encryptionPasswordIsTouched: (e.target.value?.length >= 20),
        };
        dispatch(appSlice.actions.setBackupModel(newBackupModel));
    };

    const onChangeEncryptionType = () => {
        const newEncryptionType = encryptionPasswordType === 'password' ? 'text' : 'password';
        const newBackupModel = {
            ...backupModel,
            encryptionPasswordType: newEncryptionType,
        };
        dispatch(appSlice.actions.setBackupModel(newBackupModel));
    };

    const onCopyEncryptionPassword = async () => {
        await navigator.clipboard.writeText(encryptionPassword);
        NotificationService.Push(t('Copied'), false, 'white');
    };

    const onGenerateEncryptionPassword = async () => {
        const newBackupModel = {
            ...backupModel,
            encryptionPassword: generator.generate(opts),
        };
        dispatch(appSlice.actions.setBackupModel(newBackupModel));
    };

    const saveBackupSettings = async () => {
        setSaveButtonStatus(false);
        const request = await RequestService.SetWalletBackupSettings(encryptionPassword);
        setTimeout(() => {
            setSaveButtonStatus(true);
            if (request && request?.Status === 'Success') {
                const message = saveStatus ? t('Changed') : t('Saved');
                NotificationService.Push(message, false, 'white');
                dispatch(appSlice.actions.setBackupModel({ ...backupModel, saveStatus: true }));
            }
        }, 1000);
    };

    const checkSettingsStep = (e) => {
        e.preventDefault();
        if (encryptionPasswordRef && encryptionPasswordRef.current) {
            encryptionPasswordRef.current.focus();
        }
    };

    const onEncryptionPasswordTouch = () => {
        dispatch(appSlice.actions.setBackupModel({ ...backupModel, encryptionPasswordIsTouched: true }));
    };

    const closeModal = (e) => {
        dispatch(appSlice.actions.updateModalStructure({ ...modal, isOpen: false }));
    };

    useEffect(() => {
        if (encryptionPasswordRef && encryptionPasswordRef.current && !encryptionPassword) {
            encryptionPasswordRef.current.focus();
        }
    }, [encryptionPasswordRef, encryptionPassword]);

    return (
        <div className="Backup">
            <div className="Backup__SecurityPassword">
                <div className="SecurityPassword__Title">
                    <h3>{t('Create a backup encryption password.')}</h3>
                </div>
                <div className="Body">
                    <div className="SecurityPassword__Subtitle">
                        <h4>{t('Encryption password')}</h4>
                    </div>
                    <div className="SecurityPassword__Form">
                        <div className="Form__Field">
                            <TextField
                                className="Form__SecurityPassword"
                                id="security-password"
                                label={encryptionPasswordError ? t('Error') : ''}
                                variant="outlined"
                                size="small"
                                fullWidth
                                autoComplete="new-password"
                                error={encryptionPasswordError}
                                type={encryptionPasswordType}
                                value={encryptionPassword}
                                onChange={onChangeEncryptionPassword}
                                onBlur={onEncryptionPasswordTouch}
                                inputRef={encryptionPasswordRef}
                            />
                            <div className="Form__Copy" onClick={onCopyEncryptionPassword}>
                                <BackupIcon/>
                            </div>
                            <div className="Form__Show" onClick={onChangeEncryptionType}>
                                <BackupIcon type="eye"/>
                            </div>
                            <BootstrapTooltip title={t('Generate a password.')}>
                                <div className="Form__Generate" onClick={onGenerateEncryptionPassword}>
                                    <BackupIcon type="key"/>
                                </div>
                            </BootstrapTooltip>
                        </div>
                        <p className={classNames("Form__HelpText", { error: encryptionPasswordError })}>{t('The password must be at least 20 characters, contain Latin A-a, numbers')}</p>
                    </div>
                    <ButtonMUI
                        className="Account__Button Save"
                        variant="contained"
                        onClick={saveBackupSettings}
                        disabled={!encryptionPassword || encryptionPasswordError || !saveButtonStatus || !encryptionPasswordIsTouched}
                    >{saveStatus ? t('Change') : t('Save')}</ButtonMUI>
                </div>
            </div>
            <div className="Backup__Account">
                <div className="Account__Title">
                    <h3>{t('Enter your Dropbox username and password. Backup files will be uploaded to it.')}</h3>
                </div>
                <div className="Body">
                    <div className="Account__Subtitle">
                        <h4>{t('Dropbox account')}</h4>
                    </div>
                    <div className="Account__Buttons">
                        <ButtonMUI className="Account__Button SignIn done" variant="outlined" disabled><BackupIcon
                            type="dropBox"/> {signInStatus ? t('Signed in') : t('You are not sign in')}</ButtonMUI>
                        {saveStatus ? (<a className="Account__Button SignIn"
                                          href={authUrl} target="_blank"
                                          onClick={closeModal}
                                          rel="noreferrer"
                        >
                            <BackupIcon
                                type="dropBox"/>&nbsp;{signInStatus ? t('Change the Dropbox account') : t('Sign in to your Dropbox account')}
                        </a>) : (
                            <div className="Account__Button SignIn disabled"
                               onClick={checkSettingsStep}
                            >
                                <BackupIcon
                                    type="dropBox"/>&nbsp;{signInStatus ? t('Change the Dropbox account') : t('Sign in to your Dropbox account')}
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Backup;
