const addresessLinks = {
    "USDT-TRC20": "https://tronscan.org/#/address/",
    "BTC": "https://www.blockchain.com/btc/address/"
}

const addressLink = (currency, address) => {
    return addresessLinks[currency] + address;
};

const transactionLinks = {
    "USDT-TRC20": "https://tronscan.org/#/transaction/",
    "BTC": "https://www.blockchain.com/ru/btc/tx/"
}

const transactionLink = (currency, address) => {
    return transactionLinks[currency] + address;
};

export {addressLink, transactionLink}