import React from 'react';
import { useSelector } from 'react-redux';
import t from '../../../../../../lang/t';
import './ConfirmationComment.scss';

const ConfirmationComment = () => {
    const comment = useSelector((state) => state?.withdrawal?.manualWithdrawal?.comment);
    return (
        <div className="ConfirmationComment">
            <div className="ConfirmationComment__Label">{t('Comment')}</div>
            <div className="ConfirmationComment__Comment">{comment || '-'}</div>
        </div>
    );
};

export default ConfirmationComment;