import React, { useState } from 'react';
import t from '../../../lang/t';
import Table from '../../../components/Table/Table';
import HelperService from '../../../services/HelperService';
import RequestService from '../../../services/RequestService';
import './MainTabHistory.scss';

const MainTabHistory = () => {
    const [isLoading, setIsLoading] = useState(false);
    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState({});

    const [payments, setPayments] = useState(0);
    const [commissions, setCommissions] = useState(0);
    const [checks, setChecks] = useState(0);

    const columns = [
        {
            Title: t('Payment ID'),
            Description: t('Column description'),
            Width: '130px',
            Field: 'Id'
        },
        {
            Title: t('Transaction ID'),
            Description: t('Column description'),
            Width: '170px',
            Field: 'ExternalId'
        },
        {
            Title: t('Merchant'),
            Description: t('Column description'),
            Width: '130px',
            Field: 'MerchantTitle'
        },
        {
            Title: t('Payment type'),
            Description: t('Column description'),
            Width: '140px',
            Field: 'Type'
        },
        {
            Title: t('Payment currency'),
            Description: t('Column description'),
            Width: '170px',
            Field: 'Currency'
        },
        {
            Title: t('Payment amount'),
            Description: t('Column description'),
            Width: '160px',
            Field: 'Amount',
            Cell: (data) => {
                return (
                    <span>
                        {data['Currency'] === 'USD'
                            ? HelperService.FormatFiat(data['Amount'])
                            : HelperService.FormatCrypto(data['Amount'], data['Currency'] === 'USDT-TRC20' && 2)}
                    </span>
                );
            }
        },
        {
            Title: t('Rate at the time of payment'),
            Description: t('Column description'),
            Width: '220px',
            Field: 'Course',
            Cell: (data) => {
                return (
                    <span>{HelperService.FormatFiat(data['Course'])}</span>
                );
            }
        },
        {
            Title: t('Payment amount in USD'),
            Description: t('Column description'),
            Width: '210px',
            Field: 'CourseAmount',
            Cell: (data) => {
                return (
                    <span>{HelperService.FormatFiat(data['CourseAmount'])}</span>
                );
            }
        },
        {
            Title: t('Date of payment'),
            Description: t('Column description'),
            Width: '150px',
            Field: 'Date'
        }
    ];
    const onPaginate = async (tablePage = page, tableCount = count, tableSearch = search, tableSort = sort, tableFilters = filters) => {
        setIsLoading(true);

        const response = await RequestService.GetPayments(tablePage, tableCount, tableSearch, tableSort, tableFilters);

        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        setTotal(response['Total']);
        setData(response['Payments']);

        setPayments(response['PaymentsAmount']);
        setCommissions(response['CommissionsAmount']);
        setChecks(response['ChecksAmount']);

        setIsLoading(false);
    };

    return (
        <div className="mainTabHistory">
            <div className="mainTabHistorySummary">
                <div className="mainTabHistorySummaryItem">
                    <span>{t('Top up your USDT account balance')}</span>
                    <p>{HelperService.FormatFiat(payments)}</p>
                </div>
                <div className="mainTabHistorySummarySeparator">
                    <div/>
                </div>
                <div className="mainTabHistorySummaryItem">
                    <span>{t('USD transaction fee')}</span>
                    <p>{HelperService.FormatFiat(commissions)}</p>
                </div>
                <div className="mainTabHistorySummarySeparator">
                    <div/>
                </div>
                <div className="mainTabHistorySummaryItem">
                    <span>{t('Purchase of crypto address verification')}</span>
                    <p>{HelperService.FormatFiat(checks)}</p>
                </div>
                <div className="mainTabHistorySummarySeparator">
                    <div/>
                    <div/>
                </div>
                <div className="mainTabHistorySummaryItem">
                    <span>{t('Balance for the USDT period')}</span>
                    <p>{HelperService.FormatFiat(payments - commissions - checks)}</p>
                </div>
            </div>
            <Table id="accountHistoryTable" isLoading={isLoading} columns={columns} page={page} count={count}
                   search={search} sort={sort} filters={filters} total={total} data={data} onPaginate={onPaginate}/>
        </div>
    );
};
export default MainTabHistory;