import { createSlice } from '@reduxjs/toolkit';
import CryptocurrencyModel from '../../../models/cryptocurrency';

const initialState = {
    cryptocurrencies: [],
    quotes: [],
};

export const cryptocurrencySlice = createSlice({
    name: 'cryptocurrency',
    initialState,
    reducers: {
        getAllCryptocurrencies: (state, action) => {
            const { payload: cryptocurrencies = [] } = action;
            if (Array.isArray(cryptocurrencies) && cryptocurrencies.length > 0) {
                state.cryptocurrencies = cryptocurrencies.map((currency) => new CryptocurrencyModel(currency));
            }
        },
        getQuotes: (state, action) => {
            const { payload: quotes = [] } = action;
            state.quotes = quotes;
        },
    },
});
