import { createSlice } from '@reduxjs/toolkit';
import MerchantModel from '../../../models/merchant';

const initialState = {
    merchants: [],
};

export const merchantSlice = createSlice({
    name: 'merchant',
    initialState,
    reducers: {
        getAllMerchants: (state, action) => {
            const { payload: merchants = [] } = action;
            if(Array.isArray(merchants) && merchants.length > 0) {
                state.merchants = merchants.map((merchant) => new MerchantModel(merchant));
            }
        },
    },
});
