import React, { useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import t from '../../lang/t';
import Icon, { Icons } from '../Icon/Icon';
import Tooltip from '../Tooltip/Tooltip';
import HelperService from '../../services/HelperService';
import './Input.scss';

const Input = ({ id, className, type, label, placeholder, required, disabled, value, error, onChange, onKeyDown, onBlur, onFocus, tooltip, maxLength, autoComplete }) => {
    const [inputType, setInputType] = useState(type === 'number' ? 'text' : type === 'name' ? 'text' : type);

    const onInputChange = (e) => {
        if (type === 'name' && !/[a-z|а-я \d]/i.test(e.target.value[e.target.value.length - 1])) {
            return false;
        }

        onChange(e);
    };

    return (
        <div className={classNames('input', className, error ? 'inputError' : '', { search:  type === 'search'})}>
            {type === 'search' && <Icon type={Icons.SEARCH} width={18} height={18} className="search-icon"/>}
            <input id={id} type={inputType} placeholder={placeholder} required={required} disabled={disabled}
                   value={type === 'number' && inputType === 'text' ? HelperService.FormatNumber(value) : value}
                   onChange={onInputChange} onKeyDown={onKeyDown}
                   onFocus={(e) => {setInputType(type === 'password' ? inputType : type); onFocus(e)}}
                   onBlur={(e) => {setInputType(type === 'number' ? 'text' : type === 'password' ? inputType : type); onBlur(e)}}
                   maxLength={maxLength || 'auto'} autoComplete={autoComplete} />
            <label htmlFor={id}>{label}</label>
            {type === 'password' &&
            <Icon type={Icons.EYE} width={16} height={16}
                  className={classNames('inputPasswordEye', inputType === 'text' ? 'inputPasswordEyeActive' : '')}
                  onClick={() => setInputType((prev) => prev === 'text' ? 'password' : 'text')}/>}
            {tooltip && <Tooltip id={id + '-tooltip'} className="input-tooltip" text={t(tooltip)} />}
        </div>
    );
};

Input.defaultProps = {
    id: null,
    className: null,
    type: 'text',
    label: '',
    placeholder: '',
    required: false,
    disabled: false,
    value: null,
    error: false,
    onChange: () => {},
    onKeyDown: () => {},
    onFocus: () => {},
    onBlur: () => {},
    autoComplete: '',
};

Input.propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    type: PropTypes.oneOf(['text', 'name', 'email', 'password', 'number', 'search']),
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    placeholder: PropTypes.string,
    required: PropTypes.bool,
    disabled: PropTypes.bool,
    value: PropTypes.string,
    error: PropTypes.bool,
    onChange: PropTypes.func,
    onKeyDown: PropTypes.func,
    onFocus: PropTypes.func,
    onBlur: PropTypes.func,
    autoComplete: PropTypes.string,
};

export default Input;
