import React, { useEffect, useState } from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import Modal from '../../../Modal/Modal';
import Button from '../../../Button/Button';
import Icon, { Icons } from '../../../Icon/Icon';
import t from '../../../../lang/t';
import { DragAndDrop, DragAndDropItem, Reorder } from '../../../DragAndDrop/DragAndDrop';
import './SettingsModal.scss';
import { Switch } from '@mui/material';

const SettingsModal = ({ id, tableSettings, setIsTableSettings, setTableSettings, isHiddeAddresses, setIsHiddeAddresses }) => {
    const intl = useIntl();
    const [hideSwitcher, setHideSwitcher] = useState(isHiddeAddresses)
    const tablesWithAddresses = ['merchantAddressesTable', 'addressesTable']

    useEffect(() => {
        let store = JSON.parse(localStorage.getItem(id));
        if (store !== null) {
            store.map((item) => {
                if(item.title === 'Select') setHideSwitcher(item.isVisible);
                return null;
            })
        }
    }, [id])

    const onChangeTableSettings = (item, isVisible) => {
        const newTableSettings = Object.assign([], tableSettings);
        const itemIndex = newTableSettings.indexOf(item);

        newTableSettings[itemIndex].isVisible = isVisible;
        setTableSettings(newTableSettings);
    };

    const onDragTableSettings = (result) => {
        if (!result.destination) {
            return;
        }

        setTableSettings(Reorder(tableSettings, result.source.index, result.destination.index));
    };

    const onSaveTableSettings = () => {
        const storage = setIsHiddeAddresses ? [{title: 'Select', isVisible: hideSwitcher}, ...tableSettings] : tableSettings;
        localStorage.setItem(id, JSON.stringify(storage));
        setIsHiddeAddresses && setIsHiddeAddresses(hideSwitcher);
        setIsTableSettings(false);
    };

    return (
        <Modal className="tableSettingsModal" title={intl.formatMessage({ id: 'Table setup' })} onClose={() => setIsTableSettings(false)}>

            {tablesWithAddresses.map((item, index) => {
                if (item === id) {
                    return (<div className="tableSettingsModalHiddenCripto" key={index}>
                        <span>{t('Show hidden crypto addresses')}</span>
                        <Switch
                            checked={hideSwitcher}
                            onChange={() => setHideSwitcher(!hideSwitcher)}
                        />
                    </div>)
                }
                return null;
            })
            }

            <ul className="tableSettingsModalHeader">
                <li style={{ width: '50%' }}>{t('Column name')}</li>
                <li style={{ width: '40%' }}>{t('Display')}</li>
                <li style={{ width: '10%' }}>{t('Hide')}</li>
            </ul>
            <div className="tableSettingsModalBody">
                <DragAndDrop onDragEnd={onDragTableSettings}>
                    {tableSettings.map((item, index) => {
                        return (
                            <DragAndDropItem key={index} index={index}>
                                <div className="tableSettingsModalBodyItem">
                                    <div style={{ width: '50%' }} className="tableSettingsModalBodyItemName">
                                        <Icon type={Icons.MOVE} width={16} height={16} />
                                        <span>{t(item['title'])}</span>
                                    </div>
                                    <div className="tableSettingsModalBodyItemCell" style={{ width: '40%' }}>
                                        <div className="tableSettingsModalBodyItemRadio"
                                            onClick={() => onChangeTableSettings(item, true)}>
                                            {item.isVisible && <div />}
                                        </div>
                                    </div>
                                    <div className="tableSettingsModalBodyItemCell" style={{ width: 'calc(10% + 8px)' }}>
                                        <div className="tableSettingsModalBodyItemRadio"
                                            onClick={() => onChangeTableSettings(item, false)}>
                                            {!item.isVisible && <div />}
                                        </div>
                                    </div>
                                </div>
                            </DragAndDropItem>
                        );
                    })}
                </DragAndDrop>
            </div>
            <Button onClick={onSaveTableSettings}>{t('Save')}</Button>
        </Modal>
    );
};

export default SettingsModal;