import React from 'react';
import t from '../../lang/t';
import Modal from '../../components/Modal/Modal';
import Button from '../../components/Button/Button';
import { addressLink } from '../../utils/cryptoLinkGenerator';
import './DeleteModal.scss';

const DeleteModal = ({ title, subtitle, description, onClose, onAccept, currency }) => {
    return (
        <Modal className="deleteModal" title={title} onClose={onClose}>
            <p className="deleteModalSubtitle">{subtitle}</p>
            {description && <p className="deleteModalDescription"><a href={addressLink(currency, description)} target="_blank" rel="noreferrer" onClick={(e) => e.preventDefault()}>{description}</a></p>}
            <div className="deleteModalButtons">
                <Button color="red" onClick={onClose}>{t('Cancel')}</Button>
                <Button onClick={onAccept}>{t('Confirm')}</Button>
            </div>
        </Modal>
    );
};

export default DeleteModal;
