import React, { useState, useEffect } from 'react';
import t from '../../../lang/t';
import NotificationService from '../../../services/NotificationService';
import RequestService from '../../../services/RequestService';
import DeleteModal from '../../../modals/DeleteModal/DeleteModal';
import AutoWithdrawalsTable from './components/AutoWithdrawalsTable';

import './FundsAutoWithdrawals.scss';

import { useSelector } from 'react-redux';

const FundsAutoWithdrawals = ({ openModalAutoWithdrawal }) => {
    const [isLoading, setIsLoading] = useState(false);
    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState([]);

    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
    const [autoWithdrawal, setAutoWithdrawal] = useState(null);

    const reloadTable = useSelector((state) => state?.app?.modal?.outsideParams?.reloadTable);

    const onPaginate = async (tablePage, tableCount, tableSearch = search, tableSort = sort, tableFilters = filters) => {
        setIsLoading(true);

        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        const response = await RequestService.GetAutoWithdrawals(tablePage, tableCount, tableSearch, tableSort, tableFilters);

        setIsLoading(false);

        if (response.Status !== 'Success') {
            NotificationService.Push(t('Failed to load Auto Withdrawals'), true);
            return;
        }

        setTotal(response.Total);
        setData(response.AutoWithdrawals);        
    };

    const onModalClose = () => {
        setIsDeleteModalOpen(false);
    };

    useEffect(() => {
        if (reloadTable) onPaginate(autoWithdrawal ? page : 1, count, search, sort, filters);
    }, [reloadTable]); // eslint-disable-line react-hooks/exhaustive-deps

    const onSwitch = async (autoWithdrawal) => {
        autoWithdrawal.IsEnable = !autoWithdrawal.IsEnable;
        const response = await RequestService.UpdateAutoWithdrawal(autoWithdrawal);

        if (response.Status === 'Error') {
            NotificationService.Push(autoWithdrawal.IsEnable ? t('Failed to switch on auto withdrawal') : t('Failed to switch off auto withdrawal'), true);
            return;
        }

        const newData = Object.assign([], data);
        const index = newData.findIndex((item) => item.Id === autoWithdrawal.Id);

        newData[index] = autoWithdrawal;
        setData(newData);

        NotificationService.Push(autoWithdrawal.IsEnable ? t('The auto withdrawal is switched on successfully') : t('The auto withdrawal is switched off successfully'), false);
    };

    const onDelete = async () => {
        const response = await RequestService.DeleteAutoWithdrawal(autoWithdrawal.Id);

        if (response.Status === 'Error') {
            NotificationService.Push(t('Failed to delete auto withdrawal'), true);
            return;
        }

        onPaginate(page, count, search, sort, filters);
        onModalClose();

        NotificationService.Push(t('Auto withdrawal removed'), false);
    };

    const tableProps = { isLoading, page, count, search, sort, filters, total, data, onPaginate, onSwitch, setAutoWithdrawal, setIsDeleteModalOpen, openModalAutoWithdrawal };

    return (
        <div className="fundsAutoWithdrawals">
            {isDeleteModalOpen &&
                <DeleteModal
                    title={t('Removing automatic withdrawal')}
                    subtitle={t('Confirm auto withdrawal deletion')}
                    onAccept={onDelete}
                    onClose={onModalClose}
                />
            }
            <AutoWithdrawalsTable {...tableProps} />
        </div>
    );
};
export default FundsAutoWithdrawals;