import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { appSlice } from '../../../../../../store/slices';
import t from '../../../../../../lang/t';
import TransactionIcon from '../TransactionIcon';
import { NavLink } from 'react-router-dom';
import './TransactionSuccess.scss';


const TransactionSuccess = () => {
    const dispatch = useDispatch();
    const modal = useSelector((state) => state?.app?.modal);
    const closeModal = () => dispatch(appSlice.actions.updateModalStructure({ ...modal, isOpen: false }));

    return (
        <div className="TransactionSuccess">
            <div className="TransactionSuccess__Icon">
                <TransactionIcon type="yes"/>
            </div>
            <div className="TransactionSuccess__Title">
                {t('Transactions queued for withdrawal.')}
            </div>
            <div className="TransactionSuccess__Label">
                {t('Successful transactions will be shown in')} <NavLink to="/funds/history" onClick={closeModal}>{t('Transaction History.')}</NavLink>
            </div>
            <div className="TransactionSuccess__Transaction"/>
        </div>
    );
};

export default TransactionSuccess;
