import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import t from '../../lang/t';
import useIntl from 'react-intl/lib/src/components/useIntl';
import NotificationService from '../../services/NotificationService';
import StorageService from '../../services/StorageService';
import RequestService from '../../services/RequestService';
import { tabTypes } from './merchantTypes';
import AddressesTab from './components/AddressesTab';
import HistoryTab from './components/HistoryTab';
import QuarantineTab from './components/QuarantineTab';
import Form from '../../components/Form/Form';
import Input from '../../components/Input/Input';
import Modal from '../../components/Modal/Modal';
import Checkbox from '../../components/Checkbox/Checkbox';
import Button from '../../components/Button/Button';
import RiskModal from '../../modals/RiskModal/RiskModal';
import CheckModal from '../../modals/CheckModal/CheckModal';
import ReceiveModal from '../../modals/ReceiveModal/ReceiveModal';
import HeadButtons from './components/HeadButtons';

import './Merchant.scss';
import { appSlice } from '../../store/slices/app';
import { cryptocurrencySlice } from '../../store/slices/cryptocurrency';
import RootModalModel from '../../models/root-modal';
import { stepTypes } from '../RootModal/components/Withdrawal/components/WithdrawalStepper/step-types';

const Merchant = () => {
    const dispatch = useDispatch();
    const { merchantId, tab } = useParams();
    const intl = useIntl();

    const [isCheckModal, setIsCheckModal] = useState(false);
    const [checkAddress, setCheckAddress] = useState(null);

    const [isReceiveModal, setIsReceiveModal] = useState(false);

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isNameError, setIsNameError] = useState(false);

    const [name, setName] = useState('');
    const [callBackLink, setCallBackLink] = useState('');
    const [isReusableMode, setIsReusableMode] = useState(false);

    const [needAddressesUpdate, setNeedUpdateAddresses] = useState(false);
    const [needQuarantineUpdate, setNeedUpdateQuarantine] = useState(false);

    const riskAddress = useSelector((state) => state['address'].riskAddress);

    const onCheck = (address) => {
        setIsCheckModal(true);
        setCheckAddress(address);
    };

    const activeMerchantId = isNaN(Number(merchantId)) ? null : Number(merchantId);

    const openModal = (currency = null, merchant = activeMerchantId, address = null) => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        id: 'Withdrawal',
        className: 'WithdrawalModal',
        title: t('Send'),
        tag: 'withdrawal',
        isOpen: true,
        isShowLoader: true,
        outsideParams: {
            step: stepTypes.send.name,
            sendCurrency: currency,
            sendMerchant: merchant,
            sendAddress: address,
            onCheck,
        },
    })));

    const openModalEditDescription = (data = null) => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        id: 'ModalEditDescription',
        className: 'ModalEditDescription',
        title: t('Edit description'),
        tag: 'editDescription',
        isOpen: true,
        isShowLoader: true,
        outsideParams: {
            data: data,
            setNeedUpdateAddresses: setNeedUpdateAddresses
        },
    })));

    const onWithdraw = (currency, merchant, address) => {
        openModal(currency, merchant, address);
    };

    const onClose = () => {
        setIsCheckModal(false);
        setCheckAddress(null);

        setIsReceiveModal(false);
    };

    const isMerchant = () => {
        return name && callBackLink;
    };

    const onMerchant = async () => {
        setIsNameError(false);
        let response = await RequestService.UpdateMerchant(merchantId, name, callBackLink, isReusableMode);

        if (response['Status'] !== 'Success') {
            setIsNameError(true);
            return;
        }

        setIsModalOpen(false);
        NotificationService.Push(t('Merchant edited'), false);

        StorageService.OnMerchants().then();
    };

    useEffect(() => {
        const fetchQuotes = async () => {
            const response = await RequestService.GetQuotes();
            if (response.Status !== 'Success') {
                NotificationService.Push(t('Failed to load currency quotes'), true);
                return;
            }
            dispatch(cryptocurrencySlice.actions.getQuotes(response.Quotes));
        }
        fetchQuotes()
    }, [dispatch])

    useEffect(() => {
        const fetchData = async () => {
            const merchantResponse = await RequestService.GetMerchant(merchantId);

            setName(merchantResponse['Merchant']['Name']);
            setCallBackLink(merchantResponse['Merchant']['CallBackLink']);
            setIsReusableMode(merchantResponse['Merchant']['IsReusableMode']);
        };
        fetchData().then();
    }, [merchantId]);
    
    const calculateBalances = (allAddresses, quotes, cryptocurrencies) => {
        const currencyQuotes = quotes.reduce((acc, currency) => { acc[currency.Cryptocurrency] = currency.Price; return acc }, {});

        const balances = {sum : 0, list: cryptocurrencies.map(({Id, Name, Symbol}) => ({Id, Name, Symbol, quotation: currencyQuotes[Symbol], Amount: 0, AmountUSD: 0}))};

        allAddresses.forEach(address => {
            balances.sum += address.BalanceUSD;

            let currencyIndex = balances.list.findIndex(wallet => wallet.Id === address.CurrencyId);
            balances.list[currencyIndex].Amount += address.Balance;
            balances.list[currencyIndex].AmountUSD += address.BalanceUSD;
        })
        balances.list = balances.list.sort((a, b) => a.BalanceUSD - b.BalanceUSD);
        return balances;
    };

    //todo translate
    return (
        <div className="merchant">
            <RiskModal onCheck={onCheck} />
            {isCheckModal && (
                <CheckModal
                    checkAddress={checkAddress}
                    onClose={onClose}
                    setNeedUpdate={tab === 'quarantine' ? setNeedUpdateQuarantine : setNeedUpdateAddresses}
                />
            )}
            {isReceiveModal && (
                <ReceiveModal
                    merchant={merchantId}
                    onClose={onClose}
                />
            )}
            {isModalOpen && (
                <Modal
                    className="merchantsModal"
                    title={t('Edit merchant')}
                    onClose={() => setIsModalOpen(false)}
                >
                    <Form onSubmit={onMerchant}>
                        <Input
                            type="name"
                            label={isNameError ? t('Title exists') : t('Merchant name')}
                            placeholder={intl.formatMessage({ id: 'Merchant name' })}
                            value={name}
                            error={isNameError}
                            onChange={(e) => setName(e.target.value)}
                            required
                        />
                        <Input
                            type="text"
                            label={t('Merchant backlink')}
                            placeholder={intl.formatMessage({ id: 'Merchant backlink' })}
                            value={callBackLink}
                            onChange={(e) => setCallBackLink(e.target.value)}
                            required
                            tooltip={'The link to which notifications will be sent when funds are received'}
                        />
                        <Checkbox
                            id="merchantsModalIsReusable"
                            className="merchantsModalIsReusable"
                            checked={isReusableMode}
                            onChange={() => setIsReusableMode((prev) => !prev)}
                            label={'Use static addresses'}
                            tooltip={'Whether the merchant is allowed to create static addresses. Addresses to which you can pay an unlimited number of times'}
                        />
                        <Button
                            type="submit"
                            disabled={!isMerchant()}
                        >
                            {t('Change')}
                        </Button>
                    </Form>
                </Modal>
            )}
            <HeadButtons
                name={name}
                setIsCheckModal={setIsCheckModal}
                setIsModalOpen={setIsModalOpen}
                tab={tab}
                merchantId={merchantId}
            />
            {(tab === tabTypes.addresses.key) && (
                <AddressesTab
                    onCheck={onCheck}
                    onWithdraw={onWithdraw}
                    riskAddress={riskAddress}
                    merchantId={merchantId}
                    needUpdateAddresses={needAddressesUpdate}
                    setNeedUpdateAddresses={setNeedUpdateAddresses}
                    calculateBalances={calculateBalances}
                    openModalEditDescription={openModalEditDescription}
                />
            )}
            {(tab === tabTypes.quarantine.key) && (
                <QuarantineTab
                    onCheck={onCheck}
                    onWithdraw={onWithdraw}
                    riskAddress={riskAddress}
                    merchantId={merchantId}
                    needUpdate={needQuarantineUpdate}
                    setNeedUpdate={setNeedUpdateQuarantine}
                    calculateBalances={calculateBalances}
                />
            )}
            {(tab === tabTypes.history.key) && (
                <HistoryTab merchantId={merchantId} />
            )}
        </div>
    );
};

export default Merchant;
