import BaseModel from './base';

export default class LicenseModel extends BaseModel {
    constructor(initData) {
        super();
        this.Balance = 0;
        this.CheckAddressCost = 0;
        this.CommissionSettings = [];
        this.MaxAmountOfDebt = 0;
        this.MaxMerchantsCount = 0;
        this.MerchantPricePerMonth = 0;
        this.MinRequiredPayment = 0;
        this.NextPaymentDate = 0;
        this.Status = 1;

        this.copyFrom(initData);
    }
}
