import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import t from '../../lang/t';
import { appSlice } from '../../store/slices';
import { cryptocurrencySlice } from '../../store/slices/cryptocurrency';
import { stepTypes } from '../RootModal/components/Withdrawal/components/WithdrawalStepper/step-types';
import config from '../../AppConfig';
import StorageService from '../../services/StorageService';
import RootModalModel from '../../models/root-modal';
import RequestService from '../../services/RequestService';
import ButtonMUI from '@mui/material/Button';
import ButtonIcon from '../../components/ButtonIcon';
import NotificationService from '../../services/NotificationService';
import CheckModal from '../../modals/CheckModal/CheckModal';
import WithdrawModal from '../../modals/WithdrawModal/WithdrawModal';
import ReceiveModal from '../../modals/ReceiveModal/ReceiveModal';
import Wallets from '../Wallets/Wallets';
import Quarantine from '../Quarantine/Quarantine';
import FundsMain from './FundsMain';
import FundsAutoWithdrawals from './FundsAutoWithdrawals';
import PageHeadTabs from '../../components/PageHeadTabs';

import './Funds.scss';

const { subRoutesGenerator } = config;

const Funds = () => {
    const dispatch = useDispatch();
    const { tab } = useParams();

    const [isCheckModal, setIsCheckModal] = useState(false);
    const [checkAddress, setCheckAddress] = useState(null);

    const [isSendModal, setIsSendModal] = useState(false);
    const [sendCurrency, setSendCurrency] = useState(null);
    const [sendMerchant, setSendMerchant] = useState(null);
    const [sendAddress, setSendAddress] = useState(null);
    const [needUpdateAddresses, setNeedUpdateAddresses] = useState(false);
    const [needUpdateQuarantine, setNeedUpdateQuarantine] = useState(false);

    const [isReceiveModal, setIsReceiveModal] = useState(false);

    const tabOptions = [
        {
            to: subRoutesGenerator.fundsTab('wallets'),
            value: t('Wallets'),
            isActive: () => (tab === undefined || tab === 'wallets'),
        },
        {
            to: subRoutesGenerator.fundsTab('quarantine'),
            value: t('Quarantine'),
        },
        {
            to: subRoutesGenerator.fundsTab('withdrawals'),
            value: t('Auto Withdrawals'),
        },
        {
            to: subRoutesGenerator.fundsTab('history'),
            value: t('Transaction History'),
        },
    ];

    useEffect(() => {
        const fetchQuotes = async () => {
            const response = await RequestService.GetQuotes();
            if (response.Status !== 'Success') {
                NotificationService.Push(t('Failed to load currency quotes'), true);
                return;
            }
            dispatch(cryptocurrencySlice.actions.getQuotes(response.Quotes));
        }
        fetchQuotes()
    }, [dispatch])

    const onCheck = (address) => {
        setIsCheckModal(true);
        setCheckAddress(address);
    };

    const openModal = (currency = null, merchant = null, address = null) => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        id: 'Withdrawal',
        className: 'WithdrawalModal',
        title: t('Send'),
        tag: 'withdrawal',
        isOpen: true,
        isShowLoader: true,
        outsideParams: {
            step: stepTypes.send.name,
            sendCurrency: currency,
            sendMerchant: merchant,
            sendAddress: address,
            onCheck,
        },
    })));

    const openModalAutoWithdrawal = (data = null) => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        id: 'AutoWithdrawal',
        className: 'AutoWithdrawalModal',
        title: !data ? t('Create auto withdrawal') : t('Edit auto withdrawal'),
        tag: 'autoWithdrawal',
        isOpen: true,
        isShowLoader: true,
        outsideParams: {
            data: data,
        },
    })));
    
    const openModalEditDescription = (data = null) => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        id: 'ModalEditDescription',
        className: 'ModalEditDescription',
        title: t('Edit description'),
        tag: 'editDescription',
        isOpen: true,
        isShowLoader: true,
        outsideParams: {
            data: data,
            setNeedUpdateAddresses: setNeedUpdateAddresses
        },
    })));

    const onWithdraw = (currency, merchant, address) => {
        /*setIsSendModal(true);
        setSendCurrency(currency);
        setSendMerchant(merchant);
        setSendAddress(address);*/
        openModal(currency, merchant, address);
    };

    const onClose = () => {
        setIsCheckModal(false);
        setCheckAddress(null);

        setIsSendModal(false);
        setSendCurrency(null);
        setSendMerchant(null);
        setSendAddress(null);

        setIsReceiveModal(false);
    };

    const calculateBalances = (balances, quotes, cryptocurrencies) => {
        const currencyQuotes = quotes.reduce((acc, currency) => { acc[currency.Cryptocurrency] = currency.Price; return acc }, {});
        const currencyIdentifies = cryptocurrencies.reduce((acc, currency) => { acc[currency.Symbol] = currency; return acc }, {});

        const balancesData = {
            sum : balances.reduce((acc, balance) => acc += balance.AmountUSD, 0),
            list: balances.map(balance => (
                {...balance, ...currencyIdentifies[balance.Currency], quotation: currencyQuotes[balance.Currency]}
            )),
        };
        balancesData.list = balancesData.list.sort((a, b) => a.BalanceUSD - b.BalanceUSD);
        return balancesData;
    }

    return (
        <div className="funds">
            {isCheckModal &&
            <CheckModal checkAddress={checkAddress} onClose={onClose} setNeedUpdate={tab === 'quarantine' ? setNeedUpdateQuarantine : setNeedUpdateAddresses} />}
            {isSendModal &&
            <WithdrawModal withdrawCurrency={sendCurrency} withdrawMerchant={sendMerchant} withdrawAddress={sendAddress}
                           onClose={onClose}/>}
            {isReceiveModal && <ReceiveModal onClose={onClose}/>}
            <div className="fundsHead">
                <PageHeadTabs tabOptions={tabOptions} />
                {tab !== 'withdrawals' &&
                <div className="fundsHeadButtons">
                    <ButtonMUI variant="contained" color="secondary" className="fundsHeadButtonsCheck text-initial text-padding" onClick={() => setIsCheckModal(true)} disabled={true}>
                        <ButtonIcon/>
                        <span>{t('Check crypto addresses')}</span>
                    </ButtonMUI>
                    {/*{StorageService.Merchants.length !== 0 &&*/}
                    {/*<Button className="fundsHeadButtonsReceive" onClick={() => setIsReceiveModal(true)}>*/}
                    {/*    <Icon type={Icons.RECEIVE} width={24} height={24}/>*/}
                    {/*    <span>{t('To receive')}</span>*/}
                    {/*</Button>}*/}
                    {/*<Button>*/}
                    {/*    <Icon type={Icons.REFERENCE} width={32} height={32}/>*/}
                    {/*</Button>*/}
                </div>}
                {tab === 'withdrawals' &&
                <div className="fundsHeadButtons">
                    {StorageService.Merchants.length !== 0 &&
                    <ButtonMUI variant="contained" className="fundsHeadButtonsAutoWithdrawal text-initial text-padding" onClick={() => openModalAutoWithdrawal()}>
                        <ButtonIcon type="autoWithdrawal"/>
                        <span>{t('Create an auto withdrawal')}</span>
                    </ButtonMUI>}
                    {/*<Button>*/}
                    {/*    <Icon type={Icons.REFERENCE} width={32} height={32}/>*/}
                    {/*</Button>*/}
                </div>}
            </div>
            {(tab === undefined || tab === 'wallets') &&
            <Wallets
                onCheck={onCheck}
                onWithdraw={onWithdraw}
                calculateBalances={calculateBalances}
                needUpdateAddresses={needUpdateAddresses}
                setNeedUpdateAddresses={setNeedUpdateAddresses}
                openModalEditDescription={openModalEditDescription} />}
            {tab === 'quarantine' &&
            <Quarantine
                onCheck={onCheck}
                onWithdraw={onWithdraw}
                calculateBalances={calculateBalances}
                needUpdateQuarantine={needUpdateQuarantine}
                setNeedUpdateQuarantine={setNeedUpdateQuarantine} />}
            {/* {tab === 'withdrawals' && <FundsAutoWithdrawals openModalAutoWithdrawal={openModalAutoWithdrawal} isCreateModalOpen={isAutoWithdrawModal} onClose={() => setIsAutoWithdrawModal(false)}/>} */}
            {tab === 'withdrawals' && <FundsAutoWithdrawals openModalAutoWithdrawal={openModalAutoWithdrawal}/>}
            {/* Оставлено только на время разработки. Удалить когда станет неактуально */}
            {(tab === 'funds' || tab === 'oldQuarantine' || tab === 'history') && <FundsMain onCheck={onCheck} onWithdraw={onWithdraw} />}
        </div>
    );
};
export default Funds;
