import BaseModel from './base';

export default class CryptocurrencyModel extends BaseModel {
    constructor(initData) {
        super();

        this.Addresses = null;
        this.Created = '';
        this.Id = 0;
        this.Modified = '';
        this.Name = '';
        this.Symbol = '';
        this.Transactions = null;

        this.copyFrom(initData);
    }
}
