import React from 'react';
import t from '../../../../../lang/t';

const Logs = ({ logs }) => {
    if (!logs.length) return null;

    return (
        <div className="mainTabMainLogs">
            <h4>{t('Dark Wing Activity')}</h4>
            <div className="mainTabMainLogsContainer">
                <div className="mainTabMainLogsContainer__table-wrapper">
                    <table>
                        <thead>
                            <tr>
                                <th className="date-column">{t('Date')}</th>
                                <th className="event-column">{t('Event')}</th>
                                <th className="details-column">{t('Details')}</th>
                            </tr>
                        </thead>
                        <tbody>
                            {logs.map((log, index) => {
                                const timeEdge = log.EventTime.indexOf('T');
                                const time = `${log.EventTime.substring(timeEdge + 1)} 
                                    ${log.EventTime.substring(timeEdge -2, timeEdge)}.${log.EventTime.substring(timeEdge-5, timeEdge-3)}`;
                                return (
                                <tr key={index}>
                                    <td>{time}</td>
                                    <td>{log.EventType}</td>
                                    <td>{log.EventDetails.replaceAll('BlackCloak', 'DarkWing')}</td>
                                </tr>
                            )})}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
};

export default Logs;
