import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import t from '../../../../lang/t';
import { addressSlice } from '../../../../store/slices/address';
import config from '../../../../AppConfig';
import HelperService from '../../../../services/HelperService';
import NotificationService from '../../../../services/NotificationService';
import StorageService from '../../../../services/StorageService';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import RiskScoreCell from '../../../../components/RiskScoreCell/RiskScoreCell';
import Table from '../../../../components/Table/Table';
import {addressLink} from '../../../../utils/cryptoLinkGenerator';

const { subRoutesGenerator } = config;
const { setQuarantineAddress, setRiskAddress } = addressSlice.actions;

const QuarantineTable = (props) => {
    const { isLoading, page, count, search, sort, filters, total, data, selectedItems, setSelectedItems, onWithdraw, onCheck, onPaginate } = props;

    const riskAddress = useSelector((state) => state['address'].riskAddress);
    const dispatch = useDispatch();

    //todo translate
    const columns = [
        {
            Field: 'Balance',
            Title: t('Balance'),
            Width: '140px',
            Filter: {
                Type: 2,
            },
            Cell: (data) => (
                <div className="tableBodyItemCellBalance" style={{'pointerEvents':'none'}} onClick={() => {data['LastCheckDate'] ? dispatch(setRiskAddress(data)) : onCheck(data['Address'])}}>
                    <Icon
                        type={Icons[data['Currency']]}
                        width={16}
                        height={16}
                    />
                    <p>
                        <b>
                            { HelperService.FormatCrypto(data['Balance'], data['Currency'] === 'USDT-TRC20' && 2) }
                            { data['Currency'] === 'USDT-TRC20' ? <br/> : <>&nbsp;</> }
                            { data['Currency'] }
                        </b>
                        <span>
                            { HelperService.FormatFiat(data['BalanceUSD']) }
                        </span>
                    </p>
                </div>
            ),
        },
        {
            Field: 'RiskScore',
            Title: t('Risk level'),
            Description: t('The indicator of the legality of funds on the crypto address. The higher the percentage, the worth.'),
            Width: '180px',
            Filter: {
                Type: 2,
            },
            Cell: (data) => (
                <RiskScoreCell
                    address={data}
                    isQuarantine
                    riskScore={data['RiskScore']}
                    lastCheckDate={data['LastCheckDate']}
                    onCheck={() => onCheck(data)}
                    onQuarantine={() => dispatch(setQuarantineAddress(data))}
                />
            ),
        },
        {
            Field: 'MerchantId',
            Title: t('Merchant'),
            Description: t('The name of the merchant to which the crypto address belongs to'),
            Width: '120px',
            Filter: {
                Type: 3,
                Items: StorageService.Merchants,
            },
            Cell: (data) => (
                <Link
                    to={subRoutesGenerator.merchantsId(data['MerchantId'])}
                    className="tableBodyItemCellLink"
                >
                    { data['MerchantName'] }
                </Link>
            ),
        },
        {
            Field: 'AdditionalInfo',
            Title: t('Description'),
            Description: t('The description that was written when creating the crypto address.'),
            Width: '160px',
            Filter: {
                Type: 1,
            },
            Cell: (data) => (
                <div className="commentBox">
                    { data['AdditionalInfo'] }
                </div>
            ),
        },
        {
            Field: 'Address',
            Title: t('Crypto address'),
            Width: '247px',
            Filter: {
                Type: 1,
            },
            Cell: (data) => (
                <div className="tableBodyItemCellAddress">
                    <a href={addressLink(data.Currency, data.Address)} 
                        target="_blank" rel="noreferrer"
                        className="tableBodyItemCellAddress__link"
                        data-content-start={data.Address.substring(0, data.Address.length/2|0)}
                        data-content-end={data.Address.substring(data.Address.length/2|0)}
                    >
                    </a>
                    <Icon
                        type={Icons.COPY}
                        width={16}
                        height={16}
                        onClick={() => navigator.clipboard.writeText(data.Address).then(() => NotificationService.Push(t('Copied'), false))}
                    />
                </div>
            ),
        },
        {
            Width: '16px',
            Cell: (data) => (
                <div className="tableBodyItemCellActions">
                    <Icon
                        type={Icons.WITHDRAW}
                        width={16}
                        height={16}
                        onClick={() => onWithdraw(data['Currency'], data['MerchantId'], data['Address'])}
                    />
                </div>
            ),
        }
    ];

    const isActiveRowCallback = (row) => riskAddress ? riskAddress.Id === row.Id : false;

    const tableProps = { isLoading, columns, page, count, search, sort, filters, total, data, onPaginate, selectedItems, setSelectedItems, isActiveRowCallback };

    return (
        <div className="tableWrapper">
            <Table id="quarantineTable" { ...tableProps } />
        </div>
    );
};

export default QuarantineTable;
