import React, { useState, useRef, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../../../../../lang/t';
import { withdrawalSlice } from '../../../../../../store/slices';
import CurrencyIcon from '../CurrencyIcon';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import Typography from '@mui/material/Typography';
import Input from '../../../../../../components/Input/Input';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import classNames from 'classnames';
import HelperService from '../../../../../../services/HelperService';

import './CurrencySelect.scss';

const CurrencySelect = () => {
    const dispatch = useDispatch();
    const intl = useIntl();
    const cryptocurrencies = useSelector((state) => state?.cryptocurrency?.cryptocurrencies);
    const activeCurrency = useSelector((state) => state?.withdrawal?.manualWithdrawal?.currency);
    const availableBalance = useSelector((state) => state?.withdrawal?.manualWithdrawal?.availableBalance);
    const addresses = useSelector((state) => state?.withdrawal?.manualWithdrawal?.addresses);
    const ref = useRef(null);
    const [localCurrencies, setLocalCurrencies] = useState(cryptocurrencies);
    const [dropdownStatus, setDropdownStatus] = useState(false);
    const [searchValue, setSearchValue] = useState('');

    const onClickOutside = (e) => {
        if (ref.current && ref.current.contains && !ref.current.contains(e.target)) {
            if (dropdownStatus) setDropdownStatus(false);
        }
    };

    useEffect(() => {
        document.addEventListener('click', onClickOutside, true);
        return () => {
            document.removeEventListener('click', onClickOutside, true);
        };
    });

    const onSelectItem = (currency) => {
        dispatch(withdrawalSlice.actions.setCurrencyWithdrawalModel(currency));
        setDropdownStatus(false);
    };

    const onChangeSearch = (e) => {
        const { value = '' } = e.target;
        const RGX = new RegExp(`${value}`, 'i');
        const filteredLocalCurrencies = !!value ? localCurrencies.filter((currency) => RGX.test(currency.Name)) : cryptocurrencies;

        setSearchValue(value);
        setLocalCurrencies(filteredLocalCurrencies);
    };

    const getAvailableAmount = (currency) => {
        const addressesByCurrency = addresses.filter((add) => add?.CurrencyId === currency?.Id);
        return HelperService.FormatCrypto(addressesByCurrency.reduce((acc, add) => (acc += add?.Balance), 0), currency.Symbol === 'USDT-TRC20' && 2);
    };

    return (
        <div ref={ref} className="CurrencySelect">
            <div className="Body">
                <div className="Body__Currency" onClick={() => setDropdownStatus(true)}>
                    <div className="Currency__Icon">
                        <CurrencyIcon name={activeCurrency.Name}/>
                    </div>
                    <div className="Currency__Name">
                        <Typography variant="h4">{activeCurrency.Name}</Typography>
                        <Icon type={Icons.SELECT_ARROW} width={16} height={16}/>
                    </div>
                </div>
                <div className="Body__Amount">
                    <div className="Amount__Title">
                        <Typography variant="caption">{t('Available')}</Typography>
                    </div>
                    <div className="Amount__Value">
                        <span className="Value__Number">
                            <Typography variant="body2">{HelperService.FormatCrypto(availableBalance, activeCurrency.Symbol === 'USDT-TRC20' && 2)}</Typography>
                        </span>
                        <span className="Value__Currency">
                            <Typography variant="caption">{activeCurrency.Symbol}</Typography>
                        </span>
                    </div>
                </div>
            </div>
            {dropdownStatus && <div className="Dropdown">
                <div className="Dropdown__Search">
                    <div className="Search__Title">
                        {t('Select crypto currency')}
                    </div>
                    <div className="Search__Input">
                        <Input type="search" placeholder={intl.formatMessage({ id: 'To find crypto currency' })} value={searchValue}
                               onChange={onChangeSearch}/>
                    </div>
                </div>
                <List component="nav" className="Dropdown__List">
                    {(Array.isArray(localCurrencies) && localCurrencies.length > 0) && localCurrencies.map((currency) => (
                        <ListItemButton key={currency.Id}
                                        className={classNames({ active: activeCurrency.Id === currency.Id })}
                                        onClick={() => onSelectItem(currency)}>
                            <div className="List__Item">
                                <div className="Item__Currency">
                                    <div className="Currency__Icon">
                                        <CurrencyIcon name={currency.Name}/>
                                    </div>
                                    <div className="Currency__Name">
                                        <Typography variant="h4">{currency.Name}</Typography>
                                    </div>
                                </div>
                                <div className="Item__Value">
                                    <div
                                        className="Value__Number">{getAvailableAmount(currency)}</div>
                                    <div className="Value__Currency">{currency.Symbol}</div>
                                </div>
                            </div>
                        </ListItemButton>
                    ))}
                </List>
            </div>}
        </div>
    );
};

export default CurrencySelect;
