import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import t from '../../lang/t';
import Modal from '../../components/Modal/Modal';
import './TwoFaActivateModal.scss';
import StepOne from './components/StepOne/StepOne';
import StepTwo from './components/StepTwo/StepTwo';
import StepThree from './components/StepThree/StepThree';
import IconStep from './components/IconStep/IconStep';

const TwoFaActivateModal = ({ onAccept, onClose }) => {
    const [step, setStep] = useState(0);
    const [twoFaError, setTwoFaError] = useState(false);
    const [next, setNext] = useState('');
    const [qrVisibel, setQrVisibel] = useState(false);


    useEffect(() => {
        setNext('active');
    }, [])

    const onGoToFirstStep = async () => {
        setNext('close');

        setTimeout(() => {
            setNext('hidden');
        }, 500);

        setTimeout(() => {
            setStep(1);
            setNext('active');
        }, 530);
    };

    const onGoToSecondStep = async () => {
        setNext('close');

        setTimeout(() => {
            setNext('hidden');
        }, 500);

        setTimeout(() => {
            setStep(2);
            setNext('active');
            setTimeout(() => document.getElementById('twoFaCode1').focus(), 1);
        }, 530);
    };

    const previousStep = async pStep => {
        setNext('');

        setTimeout(() => {
            setNext('close-hidden');
        }, 500);

        setTimeout(() => {
            setStep(pStep);
            setNext('active');
        }, 530);
    }

    return (
        <div className="twoFaActivateModal" onClick={() => qrVisibel ? setQrVisibel(false) : ''}>
            <Modal className={next} title={t('Two-factor authentication')}
                twoFaStep={<IconStep step={step} />}
                description={step === 0
                    ? t('Download and install Google Authenticator')
                    : step === 1
                        ? t('Scan this QR code in the Google Authenticator app')
                        : !twoFaError ? t('Enter your code from the Google Authenticator') : ''
                } onClose={onClose}>
                {step === 0 && <StepOne onGoToFirstStep={onGoToFirstStep} />}
                {step === 1 && <StepTwo onGoToSecondStep={onGoToSecondStep} qrVisibel={qrVisibel} setQrVisibel={setQrVisibel}  previousStep={previousStep} />}
                {step === 2 && <StepThree onAccept={onAccept} previousStep={previousStep} twoFaError={twoFaError} setTwoFaError={setTwoFaError} /> }
            </Modal>
        </div>
    );
};

TwoFaActivateModal.defaultProps = {
    onAccept: () => {},
    onClose: () => {}
};

TwoFaActivateModal.propTypes = {
    onAccept: PropTypes.func,
    onClose: PropTypes.func
};

export default TwoFaActivateModal;
