import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from './../../lang/t';
import Modal from '../../components/Modal/Modal';
import Input from '../../components/Input/Input';
import Icon, { Icons } from '../../components/Icon/Icon';
import HelperService from '../../services/HelperService';
import RequestService from '../../services/RequestService';
import './DepositModal.scss';

const DepositModal = ({ onClose }) => {
    const [checkAmount, setCheckAmount] = useState(0);
    const [commissionAmount, setCommissionAmount] = useState(0);

    const [amount, setAmount] = useState('');
    const [addresses, setAddresses] = useState([]);

    const intl = useIntl();

    const onChangeAmount = (e) => {
        setAmount(e.target.value);
    };

    useEffect(() => {
        const fetchData = async () => {
            const response = await RequestService.GetPayment();

            setCheckAmount(response.Data['CheckAmount']);
            setCommissionAmount(response.Data['CommissionAmount']);

            setAmount((+response.Data['CheckAmount'] + +response.Data['CommissionAmount']).toString());
            setAddresses(response.Data['Addresses']);
        };
        fetchData();
    }, []);

    return (
        <div className="depositModal">
            <Modal title={t('Top up BitHide balance')} onClose={onClose}>
                <div className="depositModalPrevious">
                    <div>
                        <span>{t('Verification costs for the last 30 days:')}</span>
                        <p>{HelperService.FormatFiat(checkAmount)}</p>
                    </div>
                    <div>
                        <span>{t('Fee value for the last 30 days:')}</span>
                        <p>{HelperService.FormatFiat(commissionAmount)}</p>
                    </div>
                </div>
                <div className="depositModalRecommended">
                    <span>{t('Recommended top-up amount')}</span>
                    <p>{HelperService.FormatFiat(checkAmount + commissionAmount)}</p>
                </div>
                <div className="depositModalAmount">
                    <p>{t('Deposit amount in USDT')}</p>
                    <Input type="number" placeholder={intl.formatMessage({ id: 'Enter the number' })} value={amount} onChange={onChangeAmount}/>
                </div>
                <div className="depositModalAddresses">
                    <h4>{t('Crypto addresses for payment')}</h4>
                    <div className="depositModalAddressesTable">
                        <div className="depositModalAddressesTableHead">
                            <span style={{ width: '25%' }}>{t('Cryptocurrency')}</span>
                            <span style={{ width: '30%' }}>{t('Payment amount')}</span>
                            <span style={{ width: '45%' }}>{t('Crypto address')}</span>
                        </div>
                        <div className="depositModalAddressesTableBody">
                            {addresses.map((address, index) => {
                                return (
                                    <div key={index} className="depositModalAddressesTableBodyRow">
                                        <span style={{ width: '25%' }}>{address['Currency']}</span>
                                        <span style={{ width: '30%' }}>
                                            {HelperService.FormatCrypto(address['Amount'], address['Currency'] === 'USDT-TRC20' && 2)}
                                        </span>
                                        <div style={{ width: '45%' }}>
                                            <p>{address['Address']}</p>
                                            <Icon type={Icons.COPY} width={16} height={16}/>
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                </div>
            </Modal>
        </div>
    );
};

DepositModal.defaultProps = {
    onClose: () => {}
};

DepositModal.propTypes = {
    onClose: PropTypes.func
};

export default DepositModal;