import React, { useState, useRef, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../../../../../lang/t';
import { withdrawalSlice } from '../../../../../../store/slices';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import Input from '../../../../../../components/Input/Input';
import ListItemButton from '@mui/material/ListItemButton';
import Tooltip from '../../../../../../components/Tooltip/Tooltip';
import Checkbox from '@mui/material/Checkbox';
import { FixedSizeList } from 'react-window';

import './MerchantSelect.scss';

const listOptions = {
    height: 200,
    itemSize: 40,
    className: 'nav',

};

const MerchantSelect = () => {
    const dispatch = useDispatch();
    const intl = useIntl();
    const ref = useRef(null);
    const merchants = useSelector((state) => state?.merchant.merchants);
    const addresses = useSelector((state) => state?.address.addresses);
    const activeMerchant = useSelector((state) => state?.withdrawal?.manualWithdrawal?.merchant);
    const activeCurrency = useSelector((state) => state?.withdrawal?.manualWithdrawal?.currency);
    const [dropdownStatus, setDropdownStatus] = useState(false);
    const [searchValue, setSearchValue] = useState('');

    const [localMerchants, setLocalMerchants] = useState(merchants.map((item, index) => Object.assign(item, activeMerchant ? activeMerchant.some(active => active.Id === item.Id) ? item.isShow = true : item.isShow = false : index !== 0 ? item.isShow = false : item.isShow = true)));

    const onClickOutside = (e) => {
        if (ref.current && ref.current.contains && !ref.current.contains(e.target)) {
            if (dropdownStatus) setDropdownStatus(false);
        }
    };

    useEffect(() => {
        document.addEventListener('click', onClickOutside, true);
        return () => {
            document.removeEventListener('click', onClickOutside, true);
        };
    });

    const getAddressCount = (merchantId) => addresses.reduce((acc, add) => {
        return (add.MerchantId === merchantId && add.CurrencyId === activeCurrency.Id && !add.IsHidden) ? acc + 1 : acc;
    }, 0);

    const onChangeSearch = (e) => {
        const { value = '' } = e.target;
        const RGX = new RegExp(`${value}`, 'i');
        const filteredLocalCurrencies = !!value ? localMerchants.filter((merchant) => RGX.test(merchant.Name)) : merchants;
        setSearchValue(value);
        setLocalMerchants(filteredLocalCurrencies);
    };

    const allSelected = localMerchants.every(item => item.isShow);

    const onSelectAll = () => {
        setLocalMerchants(localMerchants.map(item => allSelected ? {...item, isShow: false} : {...item, isShow: true} ));
    };

    const onSelect = merchant => {
        setLocalMerchants(localMerchants.map(item => item.Id === merchant.Id ? {...item, isShow: !item.isShow} : item))
    };

    useEffect(() => {
        dispatch(withdrawalSlice.actions.setMerchantWithdrawalModel(localMerchants));
    }, [dispatch, localMerchants])

    return (
        <div ref={ref} className="MerchantSelect">
            <div className="click-zone" onClick={() => setDropdownStatus(true)} />
            <div className="MerchantSelect__Title">
                {t('From merchants')}
            </div>
            <div className="MerchantSelect__Placeholder">
                <div className="Placeholder">
                    {activeMerchant.map((item, index) => activeMerchant.length - 1 === index ? item?.Name : item?.Name + ', ')}
                </div>
            </div>
            <Icon className="dropdown-icon" type={Icons.SELECT_ARROW} width={16} height={16}/>
            {dropdownStatus && <div className="MerchantSelect__Dropdown">
                <div className="Dropdown__Search">
                    <div className="Search__Title">
                        {t('Choose a merchant')}
                    </div>
                    <div className="Search__Input">
                        <Input type="search" placeholder={intl.formatMessage({ id: 'Find a merchant' })} value={searchValue} onChange={onChangeSearch}/>
                    </div>
                </div>
                <ListItemButton>
                    <div className="List__Item">
                        <div className="Item__Checkbox">
                            <Checkbox id="address"
                                      checked={allSelected}
                                      onClick={() => onSelectAll()}
                            />
                        </div>
                        <div className="Item__Amount">
                            <div className="value">{allSelected ? t('Unselect all') : t('Select all')}</div>
                        </div>
                    </div>
                </ListItemButton>
                <FixedSizeList
                    {...listOptions}
                    itemData={localMerchants}
                    itemCount={localMerchants.length}
                >
                    {({ data, index, style }) => (<div style={style}>
                        <ListItemButton key={data[index]?.Id}>
                            <div className="List__Item">
                                <div className="Item__Checkbox">
                                    <Checkbox id="address" checked={data[index]?.isShow} onClick={() => onSelect(data[index])}/>
                                </div>
                                <div className="Item__Name">
                                    {data[index]?.Name}
                                </div>
                                <div className="Item__Amount">
                                    <span className="label">{t('Addr.')}:</span> <span className="value">{getAddressCount(data[index]?.Id)}</span>
                                </div>
                            </div>
                        </ListItemButton>
                    </div>)}
                </FixedSizeList>
            </div>}
            <Tooltip id="merchant-select-tooltip" text={t('The name of the merchant, which addresses from the sending will be made')} />
        </div>
    );
};

export default MerchantSelect;
