import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import './Textarea.scss';

const Textarea = ({ id, className, label, placeholder, required, disabled, value, error, onChange }) => {
    return (
        <div className={classNames('textarea', className, error ? 'textareaError' : '')}>
            <textarea id={id} value={value} placeholder={placeholder} required={required} disabled={disabled}
                      onChange={onChange}/>
            <label htmlFor={id}>{label}</label>
        </div>
    );
};

Textarea.defaultProps = {
    id: null,
    className: null,
    label: '',
    placeholder: '',
    required: false,
    disabled: false,
    value: null,
    error: false,
    onChange: () => {}
};

Textarea.propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    placeholder: PropTypes.string,
    required: PropTypes.bool,
    disabled: PropTypes.bool,
    value: PropTypes.string,
    error: PropTypes.bool,
    onChange: PropTypes.func
};

export default Textarea;