import React, { useState } from 'react';
import classNames from 'classnames';
import { Link, NavLink, useLocation } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import t from '../../lang/t';
import config from '../../AppConfig';
import Icon, { Icons } from '../../components/Icon/Icon';
import CalcModal from '../../modals/CalcModal/CalcModal';
import ProfileModal from '../../modals/ProfileModal/ProfileModal';
import NotificationsModal from '../../modals/NotificationsModal/NotificationsModal';
import { appSlice } from '../../store/slices';

import './Menu.scss';
import RequestService from '../../services/RequestService';

const { routes } = config;

const Menu = ({ onLogout, children }) => {
    const dispatch = useDispatch();
    const location = useLocation();

    const [modal, setModal] = useState(null);

    const getTronRootAddress = async () => {
        setModal(modal !== 'profile' ? 'profile' : null);

        // Get Tron Root Account
        const tronRootAccount = await RequestService.GetTronRootAccount();

        if (tronRootAccount['Status'] !== 'Success') {
            return;
        }

        dispatch(appSlice.actions.setTronRootAccount(tronRootAccount));
    };

    return (
        <div className="menuContainer">
            <ProfileModal isOpen={modal === 'profile'} onClose={() => setModal(null)} onLogout={onLogout}/>
            <NotificationsModal isOpen={modal === 'notifications'} onClose={() => setModal(null)}/>
            <div className="menuTop">
                <Link to={routes.main} className="menuTopLeft">
                    <Icon type={Icons.LOGO_MIN} width={40} height={40}/>
                    <p className={location.pathname === '/' || location.pathname.indexOf('profile') !== -1 ? 'active' : ''}>
                        <b>BitHide</b>
                        <span>Business</span>
                    </p>
                </Link>
                <ul className="menuTopCenter">
                    <li><NavLink to={routes.funds}>{t('Funds')}</NavLink></li>
                    <li><NavLink to={routes.merchants}>{t('Merchants')}</NavLink></li>
                    <li><NavLink to={routes.administrators}>{t('Administrators')}</NavLink></li>
                </ul>
                <ul className="menuTopRight">
                    <li className={classNames('menuTopRightItem', modal === 'calc' ? 'active': '')}
                        onClick={() => setModal(modal !== 'calc' ? 'calc' : null)}>
                        <Icon type={Icons.CALC} width={24} height={24}/>
                        {modal === 'calc' && <CalcModal onClose={() => setModal(null)}/>}
                    </li>
                    <li className={classNames('menuTopRightItem', modal === 'profile' ? 'active': '')}
                        onClick={async () => { await getTronRootAddress() }}>
                        <Icon type={Icons.PROFILE} width={24} height={24}/>
                    </li>
                </ul>
            </div>
            <div className="menuBottom">
                {children}
            </div>
        </div>
    );
};
export default Menu;
