import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import t from '../../lang/t';
import Modal from '../../components/Modal/Modal';
import Input from '../../components/Input/Input';
import './TwoFaModal.scss';

const TwoFaModal = ({ onAccept, onClose }) => {
    const [twoFaError, setTwoFaError] = useState(false);
    const [twoFaCode, setTwoFaCode] = useState(['', '', '', '', '', '']);

    const onChangeTwoFaCode = async (key, code) => {
        const prevTwoFactorCode = Object.assign([], twoFaCode);

        if (code === 'Backspace' && key !== 0 && prevTwoFactorCode[key] === '') {
            prevTwoFactorCode[key - 1] = '';
            setTwoFaCode(prevTwoFactorCode);

            document.getElementById('twoFaCode' + key).focus();
            return;
        } else if (code === 'Backspace') {
            prevTwoFactorCode[key] = '';
            setTwoFaCode(prevTwoFactorCode);
        }


        if (+code !== 0 && !+code) {
            return;
        }

        prevTwoFactorCode[key] = code;
        setTwoFaCode(prevTwoFactorCode);

        if (key < 5 && code.length !== 0) {
            document.getElementById('twoFaCode' + (key + 2)).focus();
        } else if (key === 5) {
            const isAccept = await onAccept(prevTwoFactorCode.join(''));

            if (!isAccept) {
                setTwoFaError(true);
                setTwoFaCode(['', '', '', '', '', '']);

                document.getElementById('twoFaCode1').focus();
            }
        }
    };

    useEffect(() => {
        document.getElementById('twoFaCode1').focus();
    }, []);

    return (
        <div className="twoFaModal">
            <Modal title={t('Two-factor authentication')} description={t('Enter your code from the Google Authenticator')}
                   onClose={onClose}>
                {twoFaError && <p className="twoFaModalError">{t('Incorrect code')}</p>}
                <div className="twoFaModalContainer">
                    <Input id="twoFaCode1" type="text" value={twoFaCode[0]} required
                           onKeyDown={(e) => onChangeTwoFaCode(0, e.key)}/>
                    <Input id="twoFaCode2" type="text" value={twoFaCode[1]} required
                           onKeyDown={(e) => onChangeTwoFaCode(1, e.key)}/>
                    <Input id="twoFaCode3" type="text" value={twoFaCode[2]} required
                           onKeyDown={(e) => onChangeTwoFaCode(2, e.key)}/>
                    <Input id="twoFaCode4" type="text" value={twoFaCode[3]} required
                           onKeyDown={(e) => onChangeTwoFaCode(3, e.key)}/>
                    <Input id="twoFaCode5" type="text" value={twoFaCode[4]} required
                           onKeyDown={(e) => onChangeTwoFaCode(4, e.key)}/>
                    <Input id="twoFaCode6" type="text" value={twoFaCode[5]} required
                           onKeyDown={(e) => onChangeTwoFaCode(5, e.key)}/>
                </div>
            </Modal>
        </div>
    );
};

TwoFaModal.defaultProps = {
    onAccept: () => {},
    onClose: () => {}
};

TwoFaModal.propTypes = {
    onAccept: PropTypes.func,
    onClose: PropTypes.func
};

export default TwoFaModal;