import React, { useRef } from 'react';
import { useSelector } from 'react-redux';
import classNames from 'classnames';
import useIntl from 'react-intl/lib/src/components/useIntl';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { PickersDay } from '@mui/x-date-pickers/PickersDay';
import isFirstDayOfMonth from 'date-fns/isFirstDayOfMonth';
import isLastDayOfMonth from 'date-fns/isLastDayOfMonth';
import startOfWeek from 'date-fns/startOfWeek';
import endOfWeek from 'date-fns/endOfWeek';
import isAfter from 'date-fns/isAfter';
import isBefore from 'date-fns/isBefore';
import isSameDay from 'date-fns/isSameDay';
import enLocale from 'date-fns/locale/en-US';
import ruLocale from 'date-fns/locale/ru';
import ukLocale from 'date-fns/locale/uk';
import t from '../../lang/t';
import Icon, { Icons } from '../Icon/Icon';
import './DateRangePicker.scss';

const localeMap = {
    en: enLocale,
    ru: ruLocale,
    uk: ukLocale,
};

const DateRangePicker = ({ datesRange, handleDateFromChange, handleDateToChange, fromLabel, toLabel }) => {
    const intl = useIntl();
    const dateFromPickerOpenFunc = useRef(null);
    const dateToPickerOpenFunc = useRef(null);
    const lang = useSelector((state) => state.app.lang);


    const renderDay = (date, selectedDates, pickersDayProps) => {
        if (pickersDayProps.outsideCurrentMonth) {
            return <div className="empty-cell" key={pickersDayProps.key}><PickersDay {...pickersDayProps} /></div>
        }
        if (!datesRange[0] || ! datesRange[1]) {
            return <div className="cell" key={pickersDayProps.key}><PickersDay {...pickersDayProps} /></div>
        }

        const monthStart = isFirstDayOfMonth(date);
        const monthEnd = isLastDayOfMonth(date);
        const weekStart = isSameDay(date, startOfWeek(date, {weekStartsOn: lang === 'en' ? 0 : 1}));
        const weekEnd = isSameDay(date, endOfWeek(date, {weekStartsOn: lang === 'en' ? 0 : 1}));
        const start = isSameDay(date, datesRange[0]);
        const end = isSameDay(date, datesRange[1]);
        const afterStart = isAfter(date, datesRange[0]);
        const beforeEnd = isBefore(date, datesRange[1]);

        return (
            <div className={classNames('cell', { monthStart, monthEnd, weekStart, weekEnd, start, end, afterStart, beforeEnd })} key={pickersDayProps.key}>
                <div className="day-wrap">
                    <PickersDay disableMargin {...pickersDayProps} />
                </div>
            </div>
        )
    }

    return (
        <div className="tableControlsFiltersDate">
            <Icon
                type={Icons.CALENDAR}
                width={32}
                height={32}
                onClick={() => {(datesRange[0] && !datesRange[1]) ? dateToPickerOpenFunc.current() : dateFromPickerOpenFunc.current()}}
            />
            <LocalizationProvider dateAdapter={AdapterDateFns} locale={localeMap[lang]}>
                <div className="tableControlsFiltersDate__dates">
                    <DatePicker
                        PopperProps={{className: 'dateFromPicker'}}
                        value={datesRange[0]}
                        mask="__.__.____"
                        inputFormat="dd.MM.yyyy"
                        onChange={handleDateFromChange}
                        renderDay={renderDay}
                        renderInput={({inputRef, inputProps, InputProps}) => {
                            dateFromPickerOpenFunc.current = () => {InputProps?.endAdornment?.props?.children?.props?.onClick()}
                            return (
                                <div className="tableControlsFiltersDate__from" onClick={dateFromPickerOpenFunc.current}>
                                    {fromLabel !== undefined ? t(fromLabel) : <span>{t('From')}: </span>}
                                    <input
                                        ref={inputRef}
                                        className="tableControlsFiltersDate__date"
                                        {...inputProps}
                                        placeholder={intl.formatMessage({ id: 'dd.mm.yyyy' })}
                                    />
                                </div>
                            )
                        }}
                    />
                    <DatePicker
                        PopperProps={{className: 'dateToPicker'}}
                        value={datesRange[1]}
                        mask="__.__.____"
                        inputFormat="dd.MM.yyyy"
                        onChange={handleDateToChange}
                        renderDay={renderDay}
                        renderInput={({inputRef, inputProps, InputProps}) => {
                            dateToPickerOpenFunc.current = () => {InputProps?.endAdornment?.props?.children?.props?.onClick()}
                            return (
                                <div className="tableControlsFiltersDate__to" onClick={dateToPickerOpenFunc.current}>
                                    {toLabel !== undefined ? t(toLabel) : <span>{t('To')}: </span>}
                                    <input
                                        ref={inputRef}
                                        className="tableControlsFiltersDate__date"
                                        {...inputProps}
                                        placeholder={intl.formatMessage({ id: 'dd.mm.yyyy' })}
                                    />
                                </div>
                            )
                        }}
                    />
                </div>
            </LocalizationProvider>
        </div>
    );
};

export default DateRangePicker;