import React, { useState, useEffect } from 'react';
import t from '../../lang/t';
import SideModal from '../../components/SideModal/SideModal';
import Icon, { Icons } from '../../components/Icon/Icon';
import RequestService from '../../services/RequestService';
import './NotificationsModal.scss';

const NotificationsModal = ({ isOpen, onClose }) => {
    const [logs, setLogs] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            setLogs(await RequestService.GetNotifications());
        };
        fetchData();
    }, []);

    //todo translate
    return (
        <SideModal className="notificationsModal" isOpen={isOpen} onClose={onClose} title={t('Notifications')}>
            <div className="notificationsModalTop">
                <h5>{t('New')}</h5>
                <Icon type={Icons.TRASH} width={16} height={16}/>
            </div>
            {logs.map((log, index) => {
                return (
                    <div key={index} className="notificationsModalNotification">
                        <div className="notificationsModalNotificationLeft">
                            <label className={log.type === 1 ? 'blue' : 'orange'}>
                                {log.type === 1 ? t('Receipt of funds') : log.type === 2 ? t('Withdrawal') : t('Auto withdrawal')}
                            </label>
                            <p>{log.amount} {log.currency}</p>
                            {(log.type === 2 || log.type === 3) &&
                            <span className={log.error ? 'red' : 'blue'}>
                                        {log.error ? t('Withdrawal error') : t('Completed')}
                                    </span>}
                        </div>
                        <div className="notificationsModalNotificationRight">
                            <span>21.03.21 12:03</span>
                            <span>Мерчант 1</span>
                            {log.type === 2 && <p>Виталий Наливкин</p>}
                        </div>
                    </div>
                );
            })}
        </SideModal>
    );
};
export default NotificationsModal;