import React, { useState, useEffect } from 'react';
import t from '../../lang/t';
import SideModal from '../../components/SideModal/SideModal';
import ProfileModalTabProfile from './ProfileModalTabProfile';
import ProfileModalTabSettings from './ProfileModalTabSettings';
import './ProfileModal.scss';

const ProfileModal = ({ isOpen, onClose, onLogout }) => {
    const [tab, setTab] = useState('profile');

    useEffect(() => {
        setTab('profile')
    }, [isOpen])

    return (
        <SideModal className="profileModal" isOpen={isOpen} onClose={onClose} title={(
            <ul className="profileModalTabs">
                <li className={tab === 'profile' ? 'active' : ''} onClick={() => setTab('profile')}>{t('Profile')}</li>
                <li className={tab === 'settings' ? 'active' : ''} onClick={() => setTab('settings')}>{t('Settings')}</li>
            </ul>
        )}>
            {isOpen &&
            <>
                {tab === 'profile' && <ProfileModalTabProfile onLogout={onLogout}/>}
                {tab === 'settings' && <ProfileModalTabSettings/>}
            </>}
        </SideModal>
    );
};
export default ProfileModal;