import BaseModel from './base';

export default class BackupModel extends BaseModel {
    constructor(initData) {
        super();

        this.encryptionPassword = '';
        this.encryptionPasswordType = 'password'; // password | text
        this.encryptionPasswordError = false;
        this.encryptionPasswordIsTouched = false;
        this.signInStatus = false;
        this.saveStatus = false;
        this.lastBackup = null;
        this.storageAccountEmail = null;
        this.authUrl = null;
        this.opts = {
            length: 20,
            numbers: true,
        };

        this.copyFrom(initData);
        // this.checkDefaultPassword(initData);
    }

    checkDefaultPassword(initData) {
        const { encryptionPassword = '' } = initData;
        const testEXP = /^[A-Za-z0-9]*$/;
        const testLength = 20;
        this.encryptionPasswordError = !testEXP.test(encryptionPassword) || (encryptionPassword.toString().length !== testLength);

    }
}
