import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import classNames from 'classnames';
import { appSlice } from '../../store/slices/app';
import t from '../../lang/t';
import { LOCALES_INFO } from '../../lang';
import Icon, { Icons } from '../Icon/Icon';
import './LanguageSelector.scss';

const { setLanguage: updateLanguage } = appSlice.actions;

const LanguageSelector = () => {
    const [expand, setExpand] = useState(false);

    const bodyRef = React.createRef();

    const lang = useSelector((state) => state.app.lang);
    const dispatch = useDispatch();

    const localesOptions = Object.values(LOCALES_INFO).map((l) => ({ label: l.title, value: l.name }));
    const filteredLocalesOptions = localesOptions.filter((l) => l.value !== lang);
    const currentLocale = localesOptions.find((l) => l.value === lang);

    useEffect(() => {
        const handleClick = (event) => {
            if (expand && bodyRef.current && !bodyRef.current.contains(event.target)) {
               setExpand(false);
            }
        };

        document.body.addEventListener('click', handleClick);

        return () => {
            document.body.removeEventListener('click', handleClick);
        }
    }, [bodyRef, expand]);


    const onToggle = () => setExpand(!expand);

    const onSelect = (value) => {
        setExpand(false);
        dispatch(updateLanguage(value));
    };

    return (
        <div className='LanguageSelector'>
            <div className='LanguageSelector__label' onClick={onToggle}>
                {t(currentLocale.label)}
                <Icon type={Icons.SELECT_ARROW} width={24} height={24} className={classNames('arrow', { reversed: expand })} />
            </div>

            {expand && (
                <div className='LanguageSelector__body' ref={bodyRef}>
                    {filteredLocalesOptions.map((locale, index) => (
                        <React.Fragment key={locale.value}>
                            <div className='locale-item' onClick={() => onSelect(locale.value)}>
                                {t(locale.label)}
                            </div>
                            {(index < (filteredLocalesOptions.length - 1) && (filteredLocalesOptions.length > 1)) && <div className='divider' />}
                        </React.Fragment>
                    ))}
                </div>
            )}
        </div>
    )
};

export default LanguageSelector;
