import React, {useEffect, useState} from 'react';
import { Link, Redirect } from 'react-router-dom';
import t from '../../lang/t';
import useIntl from 'react-intl/lib/src/components/useIntl';
import config from '../../AppConfig';
import HelperService from '../../services/HelperService';
import NotificationService from '../../services/NotificationService';
import RequestService from '../../services/RequestService';
import Form from '../../components/Form/Form';
import Input from '../../components/Input/Input';
import Button from '../../components/Button/Button';
import Icon, { Icons } from '../../components/Icon/Icon';

import './Forgot.scss';

const { routes } = config;

const Forgot = () => {
    const [step, setStep] = useState(1);

    const [isFetching, setIsFetching] = useState(false);
    const [email, setEmail] = useState('');

    const [time, setTime] = useState(0);
    const [code, setCode] = useState(['', '', '', '', '', '']);

    const [password, setPassword] = useState('');
    const [repeatPassword, setRepeatPassword] = useState('');

    const [redirectToMain, setRedirectToMain] = useState(false);

    const intl = useIntl();

    const onForgot = async () => {
        setIsFetching(true);
        await RequestService.Forgot(email);
        setIsFetching(false);

        setTime(59);
        setStep(2);

        document.getElementById('forgotCodeInput1').focus();
    };
    const onChangeCode = async (key, keyCode) => {
        const prevCode = Object.assign([], code);

        if (keyCode === 'Backspace' && key !== 0 && prevCode[key] === '') {
            prevCode[key - 1] = '';
            setCode(prevCode);

            document.getElementById('forgotCodeInput' + key).focus();
            return;
        } else if (keyCode === 'Backspace') {
            prevCode[key] = '';
            setCode(prevCode);
        }


        if (+keyCode !== 0 && !+keyCode) {
            return;
        }

        prevCode[key] = keyCode;
        setCode(prevCode);

        if (key < 5 && keyCode.length !== 0) {
            document.getElementById('forgotCodeInput' + (key + 2)).focus();
        } else if (key === 5) {
            setStep(3);
            setTimeout(() => document.getElementById('forgotPassword').focus(), 1);
        }
    };
    const onForgotUpdate = async () => {
        setIsFetching(true);

        if (password !== repeatPassword)
        {
            setIsFetching(false);
            NotificationService.Push(t('Passwords do not match'), true);

            return;
        }

        const forgotUpdateResponse = await RequestService.ForgotAccept(email, code.join(''), password);

        if (forgotUpdateResponse['Status'] !== 'Success') {
            setIsFetching(false);
            NotificationService.Push(t('Try again later'), true);

            return;
        }

        setIsFetching(false);
        setRedirectToMain(true);
    };

    useEffect(() => {
        setInterval(() => setTime((prev) => prev === 0 ? prev : prev - 1), 1000);
    }, []);

    if (redirectToMain) {
        <Redirect to={routes.main} />
    }
    // todo many translates
    return (
        <div className="forgot">
            <Icon type={Icons.LOGO_MIN} width={72} height={72} className="forgotLogo"/>
            {step === 1 &&
            <Form className="forgotForm" onSubmit={onForgot}>
                <h2 className="forgotTitle">{t('Password recovery')}</h2>
                <p className="forgotDescription">{t('Enter the email address to which the password recovery details will be sent')}</p>
                <Input type="email" value={email} label="Email" placeholder={intl.formatMessage({ id: 'Email' })} required
                       onChange={(e) => setEmail(e.target.value)}/>
                <Button type="submit" disabled={isFetching}>{t('Reset the password')}</Button>
                <Link to={routes.main} className="returnLink">{t('Back to authorization')}</Link>
            </Form>}
            {step === 2 &&
            <Form className="forgotMail" onSubmit={onForgot}>
                <Icon type={Icons.EMAIL} width={84} height={84}/>
                <h2 className="forgotTitle">{t('Check your Email')}</h2>
                <p className="forgotDescription">{t('An email has been sent to the address you provided with a verification code')}</p>
                <div className="forgotCodeInputContainer">
                    <Input id="forgotCodeInput1" type="text" value={code[0]}
                           onKeyDown={(e) => onChangeCode(0, e.key)}/>
                    <Input id="forgotCodeInput2" type="text" value={code[1]}
                           onKeyDown={(e) => onChangeCode(1, e.key)}/>
                    <Input id="forgotCodeInput3" type="text" value={code[2]}
                           onKeyDown={(e) => onChangeCode(2, e.key)}/>
                    <Input id="forgotCodeInput4" type="text" value={code[3]}
                           onKeyDown={(e) => onChangeCode(3, e.key)}/>
                    <Input id="forgotCodeInput5" type="text" value={code[4]}
                           onKeyDown={(e) => onChangeCode(4, e.key)}/>
                    <Input id="forgotCodeInput6" type="text" value={code[5]}
                           onKeyDown={(e) => onChangeCode(5, e.key)}/>
                </div>
                <Button type="submit" disabled={isFetching || time !== 0}>
                    {t('Send an email again')}
                    {time !== 0 && <span>&nbsp;00:{HelperService.FormatTime(time)}</span>}
                </Button>
                <Link to={routes.main} className="returnLink">{t('Back to authorization')}</Link>
            </Form>}
            {step === 3 &&
            <Form className="forgotUpdateForm" onSubmit={onForgotUpdate}>
                <h2 className="forgotTitle">{t('Password recovery')}</h2>
                <p className="forgotDescription">{t('Enter a new password')}</p>
                <Input id="forgotPassword" type="password" label={t('New password')} placeholder={intl.formatMessage({ id: 'New password'})} required
                       value={password} onChange={(e) => setPassword(e.target.value)}/>
                <Input type="password" label={intl.formatMessage({ id: 'New password' })} placeholder={intl.formatMessage({ id: 'New password' })} required
                       value={repeatPassword} onChange={(e) => setRepeatPassword(e.target.value)}/>
                <Button type="submit" disabled={isFetching}>{t('Recover password')}</Button>
                <Link to={routes.main} className="returnLink">{t('Back to authorization')}</Link>
            </Form>}
        </div>
    );
};
export default Forgot;