import React from 'react';
import t from '../../../../../lang/t';
import HelperService from '../../../../../services/HelperService';
import {addressLink} from '../../../../../utils/cryptoLinkGenerator';

const Transactions = ({ transactions }) => {
    if (!transactions) return null;

    return (
        <div className="mainTabMainTransactions">
            <h4>{t('Recent anonymized transactions')}</h4>
            <div className="mainTabMainTransactionsContainer">
                <ul>
                    <li>{t('Crypto address')}</li>
                    <li>{t('Amount')}</li>
                    <li>{t('Date')}</li>
                    {/*<li>IP</li>*/}
                </ul>
                <div className="mainTabMainTransactionsBody">
                    {transactions.map((transaction, index) => {
                        const { DestinationAddress, Amount, Cryptocurrency, Date: TransactionDate } = transaction;
                        return (
                            <div key={index} className="mainTabMainTransactionsBodyItem">
                                <p>
                                    <a href={addressLink(Cryptocurrency, DestinationAddress)} target="_blank" rel="noreferrer">
                                        { DestinationAddress }
                                    </a>
                                </p>
                                <p>
                                    { `${HelperService.FormatCrypto(Amount, Cryptocurrency === 'USDT-TRC20' && 2)} ${Cryptocurrency}` }
                                </p>
                                <p>
                                    { new Date(TransactionDate).toLocaleString() }
                                </p>
                                {/*<p>{transaction['Ip']}</p>*/}
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};

export default Transactions;
