import React from 'react';
import { Route } from 'react-router-dom';
import config from '../AppConfig';

// Components
import Administrators from '../containers/Administrators';
import Forgot from '../containers/Forgot';
import Funds from '../containers/Funds';
import Login from '../containers/Login';
import Main from '../containers/Main';
import Merchant from '../containers/Merchant';
import Merchants from '../containers/Merchants';
import ErrorCatcher from '../components/ErrorCatcher';

const { routes: urls } = config;

const mainRoutes = [
    {
        id: 0,
        path: urls.main,
        component: Main,
        exact: true,
    },
    {
        id: 1,
        path: urls.administrators,
        component: Administrators,
        exact: true,
    },
    {
        id: 2,
        path: urls.merchantsIdTab,
        component: Merchant,
        exact: true,
    },
    {
        id: 3,
        path: urls.merchantsId,
        component: Merchant,
        exact: true,
    },
    {
        id: 4,
        path: urls.merchantsTab,
        component: Merchants,
        exact: true,
    },
    {
        id: 5,
        path: urls.merchants,
        component: Merchants,
        exact: true,
    },
    {
        id: 6,
        path: urls.fundsTabAddresses,
        component: Funds,
        exact: true,
    },
    {
        id: 7,
        path: urls.fundsTab,
        component: Funds,
        exact: true,
    },
    {
        id: 8,
        path: urls.funds,
        component: Funds,
        exact: true,
    },
    {
        id: 9,
        path: urls.profileTab,
        component: Main,
        exact: true,
    },
];

const authRoutes = [
    {
        id: 0,
        path: urls.main,
        component: Login,
        exact: true,
    },
    {
        id: 1,
        path: urls.forgot,
        component: Forgot,
        exact: true,
    },
];


// A special wrapper for <Route> that knows how to
// handle "sub"-routes by passing them in a `routes`
// prop to the component it renders.
function RouteWithSubRoutes(route) {
    return (
        <Route
            path={route.path}
            render={(props) => (
                // pass the sub-routes down to keep nesting
                <ErrorCatcher key={route.id}>
                    <route.component {...props} routes={route.routes} />
                </ErrorCatcher>
            )}
            exact={route.exact}
        />
    );
}

export {
    mainRoutes,
    authRoutes,
    RouteWithSubRoutes,
};
