import React from 'react';
import t from '../../lang/t';
import Icon, { Icons } from '../Icon/Icon';
import NotificationService from '../../services/NotificationService';
import './Copy.scss';

const Copy = ({ text }) => {
    return (
        <div className="copy" onClick={() => navigator.clipboard.writeText(text)
            .then(() => NotificationService.Push(t('Copied'), false))}>
            <span>{text}</span>
            <Icon type={Icons.COPY} width={16} height={16}/>
        </div>
    );
};
export default Copy;