import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Icon, { Icons } from '../Icon/Icon';
import './Tooltip.scss';

const Tooltip = ({ id, className, position, text, children, ...props }) => {
    return (
        <div id={id} className={classNames('tooltip', className)} {...props}>
            <span>{children}</span>
            <div className="tooltipIcon">
                <Icon type={Icons.INFO} width={16} height={16}/>
                <div className={classNames('tooltipIconContainer', position)}>{text}</div>
            </div>
        </div>
    );
};

Tooltip.defaultProps = {
    id: null,
    className: null,
    position: 'left',
    text: '',
    children: null
};

Tooltip.propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    position: PropTypes.oneOf(['left', 'right']),
    text: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    children: PropTypes.node
};

export default Tooltip;