import React, { useState } from 'react';
import { useLocation, Redirect } from 'react-router-dom';
import { ErrorBoundary } from 'react-error-boundary';
import PropTypes from 'prop-types';
import t from '../../lang/t';
import Button from '../../components/Button/Button';
import Background from '../../components/Background/Background';
import config from '../../AppConfig';
import Icon, { Icons } from '../Icon/Icon';
import './ErrorCatcher.scss';

const { routes } = config;

const ErrorCatcher = ({ redirectToAddress, actionBeforeRedirect, children }) => {
    const [redirectAddress, setRedirectAddress] = useState(null);
    
    const currentRoute = useLocation();    

    if (redirectAddress) {

        if (actionBeforeRedirect) {
            actionBeforeRedirect();
        };

        if (redirectAddress !== 'auth') return <Redirect to={redirectAddress} />
    }

    const redirect = () => {
        if (redirectToAddress) {
            setRedirectAddress(redirectToAddress);
        } else if (currentRoute.pathname === routes.main) {
            setRedirectAddress(routes.funds);
        } else {
            setRedirectAddress(routes.main);
        }        
    }

    const ErrorModal = ({error, resetErrorBoundary}) => {
    
        return (
            <div>
                <div className="ErrorCatcher">
                    <div className="ErrorCatcher__modal">
                        <div className="error-animation">
                            <Background/>
                            {/* <Icon type={Icons.ERRORANIMATION} width={120} height={120} /> */}
                            <Icon className="error-animation__cross" type={Icons.ERRORCROSS} width={56} height={56} />
                        </div>
                        <p className="ErrorCatcher__header">{t('Error')}</p>
                        <p className="ErrorCatcher__text">Случилась непредвиденная ошибка,</p>
                        <p className="ErrorCatcher__text">приложение будет перезапущено.</p>
                        <Button className="ErrorCatcher__button" onClick={resetErrorBoundary}>{t('Restart')}</Button>
                    </div>                
                </div>
            </div>
        )
    }

    return (
        <ErrorBoundary FallbackComponent={ErrorModal} onReset={redirect}>
            {children}
        </ErrorBoundary>
    );
};

ErrorCatcher.defaultProps = {
    children: null,
};

ErrorCatcher.propTypes = {
    children: PropTypes.node.isRequired,
};

export default ErrorCatcher;