import React from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import t from '../../../../lang/t';
import config from '../../../../AppConfig';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import Switch from '../../../../components/Switch/Switch';
import PopUp from '../../../../components/PopUp/PopUp';
import Table from '../../../../components/Table/Table';

const { subRoutesGenerator } = config;

const AutoWithdrawalsTable = (props) => {
    const { isLoading, page, count, search, sort, filters, total, data, onPaginate, onSwitch, setAutoWithdrawal, setIsDeleteModalOpen, openModalAutoWithdrawal } = props;

    const columns = [
        {
            Field: 'MerchantName',
            Title: t('Merchant'),
            Description: t('The name of the merchant to which the crypto address belongs to'),
            tooltipPosition: 'right',
            Width: '120px',
            // Filter: { Type: 1 },
            Cell: (data) => (
                <Link to={subRoutesGenerator.merchantsId(data.MerchantId)} className="tableBodyItemCellLink">
                    {data.MerchantName}
                </Link>
            )
        },
        {
            Field: 'Type',
            Title: t('Type'),
            Description: t('Options to withdraw funds to crypto withdrawal addresses. \n\n Looped - the withdrawal will held to each address each time when minimal amount is reached. \n\n Single-time - the withdrawal will start only once to each withdraw address when minimal amount will be reached, then the automatic withdrawal will be turned off.'),
            Width: '90px',
            // Filter: { Type: 3, Items: [ { label: t('By the circle'), value: 1 }, { label: t('By the list'), value: 2 } ] },
            Cell: (data) => <>{data.Type === 1 ? t('Looped') : t('Single-time')}</>
        },
        {
            Title: t('Cryptocurrency'),
            Field: 'CryptoCurrency',
            Width: '140px',
            // Filter: { Type: 3, Items: StorageService.Cryptos.map((crypto) => { return { label: crypto.value, value: crypto.id } }) }
        },
        {
            Field: 'WithdrawalAddresses',
            Title: t('Crypto addresses ver2'),
            Description: t('Crypto addresses to which withdrawals will be made'),
            Width: '170px',
            Cell: (data) => <PopUp values={data.WithdrawalAddresses} CryptoCurrency={data.CryptoCurrency} />
        },
        {
            Field: 'CountCompletedAutoWithdrawals',
            Title: t('Number of perfect withdrawal'),
            Width: '180px',
            // Filter: { Type: 4 }
        },
        {
            Field: 'LastWithdrawalDate',
            Title: t('Date of the last withdrawal'),
            Width: '210px',
            // Filter: { Type: 2 }
            Cell: (data) => <>{data.LastWithdrawalDate ? new Date(data.LastWithdrawalDate).toLocaleString() : '-'}</>
        },
        {
            Field: 'IsEnable',
            Title: t('Status'),
            Width: '110px',
            Cell: (data) => (
                <div className={classNames('tableBodyItemCellSwitch', !data.IsEnable ? 'tableBodyItemCellSwitchDisabled' : '')}>
                    <Switch id={data.Id.toString()} checked={data.IsEnable} onChange={() => onSwitch(data)} />
                    <span>{data.IsEnable ? t('Switched on') : t('Switched off')}</span>
                </div>
            )
        },
        {
            Width: '48px',
            Cell: (data) => (
                <div className="tableBodyItemCellActions">
                    <Icon type={Icons.EDIT_PEN} width={16} height={16}
                        onClick={() => {
                            openModalAutoWithdrawal(data)
                        }} />
                    <Icon className="tableBodyItemCellActionsTrash" type={Icons.TRASH} width={16} height={16}
                        onClick={() => {
                            setAutoWithdrawal(data);
                            setIsDeleteModalOpen(true);
                        }} />
                </div>
            )
        }
    ];

    const tableProps = { isLoading, columns, page, count, search, sort, filters, total, data, onPaginate };

    return (
        <div className="tableWrapper">
            <Table id="autoWithdrawalsTable" {...tableProps} />
        </div>
    );
};

export default AutoWithdrawalsTable;