import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    addresses: [],
    riskAddress: null,
    checkAddressData: null,
    quarantineAddress: null,
};

export const addressSlice = createSlice({
    name: 'address',
    initialState,
    reducers: {
        getAllAddresses: (state, action) => {
            const { payload : addresses = [] } = action;
            state.addresses = addresses;
        },
        setRiskAddress: (state, action) => {
            const { payload : riskAddress = null } = action;
            state.riskAddress = riskAddress;
        },
        setCheckAddressData: (state, action) => {
            const { payload : checkAddressData = null } = action;
            state.checkAddressData = checkAddressData;
        },
        setQuarantineAddress: (state, action) => {
            const { payload : quarantineAddress = null } = action;
            state.quarantineAddress = quarantineAddress;
        },
    },
});
