import React, { useState, useEffect, useCallback } from 'react';
import { Redirect, useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import t from '../../lang/t';
import { addressSlice } from '../../store/slices/address';
import config from '../../AppConfig';
import NotificationService from '../../services/NotificationService';
import RequestService from '../../services/RequestService';
import AddressesTable from './components/AddressesTable';
import DeleteModal from '../../modals/DeleteModal/DeleteModal';
import RiskModal from '../../modals/RiskModal/RiskModal';
import WalletsList from '../../components/WalletsList';

import './Wallets.scss';

const { routes } = config;
const { setQuarantineAddress } = addressSlice.actions;

const Wallets = ({ onCheck, onWithdraw, calculateBalances, needUpdateAddresses, setNeedUpdateAddresses, openModalEditDescription }) => {
    const { addresses } = useParams();

    const [balances, setBalances] = useState([]);
    const [balancesData, setBalancesData] = useState({ sum: 0, list: [] });

    const [isLoading, setLoading] = useState(false);
    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState([]);
    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [selectedItems, setSelectedItems] = useState([]);

    const [redirectToFunds, setRedirectToFunds] = useState(false);

    const cryptocurrencies = useSelector(state => state.cryptocurrency.cryptocurrencies);
    const quotes = useSelector(state => state.cryptocurrency.quotes);
    const quarantineAddress = useSelector((state) => state.address.quarantineAddress);
    const merchants = useSelector((state) => state?.merchant.merchants);
    const dispatch = useDispatch();

    const [selectedWalletIndex, setSelectedWalletIndex] = useState(null);
    const [isHiddeAddresses, setIsHiddeAddresses] = useState(true)

    useEffect(() => {
        if (!addresses) {
            return;
        }

        setPage(1);
        setFilters([{ 'Type': '1', 'Column': 'Address', 'Search': addresses.split(',') }]);


        setRedirectToFunds(true);
    }, [addresses]);

    const updateBalancesData = useCallback(balances => {
        const balancesData = calculateBalances(balances, quotes, cryptocurrencies);
        setBalancesData(balancesData);
    }, [calculateBalances, quotes, cryptocurrencies])

    useEffect(() => {
        updateBalancesData(balances);
    }, [quotes, balances, updateBalancesData]);

    const GetBalances = useCallback(async () => {
        const merchantIds = merchants.map(merchant => merchant.Id);
        const response = await RequestService.GetBalances(merchantIds);

        if (response.Status === 'Success') {
            setBalances(response.Balance);
            updateBalancesData(response.Balance);
        }
    }, [merchants, updateBalancesData]);

    useEffect(() => {
        GetBalances();
    }, [GetBalances]);

    const onPaginate = async (tablePage = page, tableCount = count, tableSearch = search, tableSort = sort, tableFilters = filters) => {
        setLoading(true);
        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        const response = await RequestService.GetAddresses(tablePage, tableCount, tableSearch, tableSort, tableFilters, isHiddeAddresses);

        setTotal(response.Total);
        setData(response.Addresses);
        setSelectedItems(new Array(response.Total).fill(false));

        setLoading(false);
    };

    useEffect(() => {
        onPaginate();
    }, [needUpdateAddresses, isHiddeAddresses]); // eslint-disable-line react-hooks/exhaustive-deps

    const onQuarantine = async () => {
        const response = await RequestService.QuarantineAdd(quarantineAddress.Id);
        dispatch(setQuarantineAddress(null));
        if (response.Status !== 'Success') {
            NotificationService.Push(t('Failed to add to quarantine'), true);
            return;
        }
        NotificationService.Push(t('Successfully quarantined'), false);
        onPaginate();
        GetBalances();
    };

    if (redirectToFunds) {
        return (
            <Redirect to={routes.funds} />
        );
    }

    const walletsListProps = { sourcePage: 'wallets', data: balancesData, onPaginate, onWithdraw, page, count, search, sort, filters, selectedWalletIndex, setSelectedWalletIndex };

    const addressesTableProps = { setNeedUpdateAddresses, isLoading, page, count, search, sort, filters, total, data, selectedItems, setSelectedItems,
        onWithdraw, onCheck, onPaginate, openModalEditDescription, isHiddeAddresses, setIsHiddeAddresses, balancesData, selectedWalletIndex };

    //todo translate
    return (
        <div className="wallets">
            <RiskModal onCheck={onCheck} />
            {quarantineAddress && (
                <DeleteModal
                    title={t('Adding to quarantine')}
                    subtitle={t('Are you sure you want to quarantine the address?')}
                    description={quarantineAddress.Address}
                    currency={quarantineAddress.Currency}
                    onAccept={onQuarantine}
                    onClose={() => dispatch(setQuarantineAddress(null))}
                />
            )}
            <WalletsList {...walletsListProps} />
            <AddressesTable {...addressesTableProps} />
        </div>
    );
};

export default Wallets;
