import React, { useEffect, useRef, useState } from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../../../../../lang/t';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import Input from '../../../../../../components/Input/Input';
import ListItemButton from '@mui/material/ListItemButton';
import Checkbox from '@mui/material/Checkbox';
import RiskIcon from '../RiskIcon';
import { useSelector, useDispatch } from 'react-redux';
import { withdrawalSlice } from '../../../../../../store/slices';
import HelperService from '../../../../../../services/HelperService';
import { FixedSizeList } from 'react-window';

import './AddressesSelect.scss';

const listOptions = {
    height: 160,
    itemSize: 40,
    className: 'nav',

};

const AddressesSelect = () => {
    const dispatch = useDispatch();
    const ref = useRef(null);
    const addresses = useSelector((state) => state?.withdrawal?.withdrawalFilteredList);
    const searchAddresses = useSelector((state) => state?.withdrawal?.withdrawalSearchList);
    const activeCurrency = useSelector((state) => state?.withdrawal?.manualWithdrawal?.currency);
    const checkCost = useSelector((state) => state?.app?.license?.CheckAddressCost);
    const modal = useSelector((state) => state?.app?.modal);
    const [localAddresses, setLocalAddresses] = useState(addresses);
    const [dropdownStatus, setDropdownStatus] = useState(false);
    const [searchValue, setSearchValue] = useState('');
    const [isSearchEnabled, setSearchStatus] = useState(false);
    const selectedAmount = localAddresses.filter((add) => add.isShow).length;

    const intl = useIntl();

    const onClickOutside = (e) => {
        if (ref.current && ref.current.contains && !ref.current.contains(e.target)) {
            if (dropdownStatus) setDropdownStatus(false);
        }
    };

    useEffect(() => {
        if (!isSearchEnabled) setLocalAddresses(addresses);
    }, [isSearchEnabled, addresses]);

    useEffect(() => {
        document.addEventListener('click', onClickOutside, true);
        return () => {
            document.removeEventListener('click', onClickOutside, true);
        };
    });

    const getRiskPercent = (score = 0) => {
        const percent = parseInt((score * 100).toString());
        return percent > 0 ? `${percent}%` : `-`;

    };

    const onSelect = (address) => dispatch(withdrawalSlice.actions.selectAddressesWithdrawalModel(address));

    const onChangeSearch = (e) => {
        const { value = '' } = e.target;
        setSearchValue(value);
        dispatch(withdrawalSlice.actions.searchAddressesWithdrawalModel(value));
    };

    const doCheck = (address) => {
        const { onCheck } = modal?.outsideParams;
        if (onCheck && typeof onCheck === 'function') onCheck(address);
    };

    const addressItem = (address) => {
        return (
            <ListItemButton key={address.Id}>
                <div className="List__Item">
                    <div className="Item__Checkbox">
                        <Checkbox id="address" checked={address.isShow} onClick={() => onSelect(address)}/>
                    </div>
                    <div className="Item__Amount">
                        <div
                            className="value">{HelperService.FormatCrypto(address.Balance, activeCurrency.Symbol === 'USDT-TRC20' && 2)}</div>
                        <div className="currency">{activeCurrency.Symbol}</div>
                    </div>
                    <div className="Item__Risk">
                        {address.riskName === 'unchecked' && false &&
                        <div className="icon-tooltip" onClick={() => doCheck(address.Address)}>
                            <Icon className="check-icon" type={Icons.CHECK} width={18} height={18}/>
                            <div className="icon-tooltip-text">
                                {t('{amount} will be deducted from your BitHide account for verification', {
                                    amount: HelperService.FormatFiat(checkCost),
                                })}
                            </div>
                        </div>}
                        <div className="icon">
                            <RiskIcon type={address.riskName}/>
                        </div>
                        <div className="percent">{getRiskPercent(address.RiskScore)}</div>
                    </div>
                </div>
            </ListItemButton>);
    };

    const [selectAll, setSellectAll] = useState(null);

    const onSelectAll = () => {
        dispatch(withdrawalSlice.actions.selectAllAddressesWithdrawalModel());
    };

    useEffect(() => {
        searchAddresses.some(add => !add.isShow) ? setSellectAll(false) : setSellectAll(true)
    }, [searchAddresses]);

    return (
        <div className="AddressesSelect" onClick={() => setDropdownStatus(true)}>
            <div className="AddressesSelect__Title">
                {t('From crypto addresses')}
            </div>
            <div className="AddressesSelect__Placeholder">
                <div className="Placeholder">
                    {t('Number of selected addresses')}: <span className="value">{selectedAmount}</span>
                </div>
            </div>
            <Icon className="dropdown-icon" type={Icons.SELECT_ARROW} width={16} height={16}/>
            {dropdownStatus && <div ref={ref} className="AddressesSelect__Dropdown">
                <div className="Dropdown__Search">
                    <div className="Search__Title">
                        {t('Number of selected addresses')}: {selectedAmount}
                    </div>
                    <div className="Search__Input">
                        <Input type="search" placeholder={intl.formatMessage({ id: 'Find an address' })}
                               value={searchValue} onChange={onChangeSearch}
                               onBlur={() => setSearchStatus(false)} onFocus={() => setSearchStatus(true)}/>
                    </div>
                </div>
                {(searchAddresses.length > 0) && <ListItemButton>
                    <div className="List__Item">
                        <div className="Item__Checkbox">
                            <Checkbox id="address" checked={selectAll} onClick={() => onSelectAll()}/>
                        </div>
                        <div className="Item__Amount">
                            <div className="value">{selectAll ? t('Unselect all') : t('Select all')}</div>
                        </div>
                    </div>
                </ListItemButton>}
                <FixedSizeList
                    {...listOptions}
                    itemData={searchAddresses}
                    itemCount={searchAddresses.length}
                >
                    {({ data, index, style }) => (<div style={style}>{addressItem(data[index])}</div>)}
                </FixedSizeList>
            </div>}
        </div>
    );
};

export default AddressesSelect;
