import React, { useState } from 'react';
import PropTypes from 'prop-types';
import t from '../../lang/t';
import useIntl from 'react-intl/lib/src/components/useIntl';
import Modal from '../../components/Modal/Modal';
import TwoFaModal from '../TwoFaModal/TwoFaModal';
import Form from '../../components/Form/Form';
import Input from '../../components/Input/Input';
import Button from '../../components/Button/Button';
import NotificationService from '../../services/NotificationService';
import RequestService from '../../services/RequestService';
import './ChangePasswordModal.scss';

const ChangePasswordModal = ({ onClose }) => {
    const [isNewPasswordError, setIsNewPasswordError] = useState(false);
    const [isCurrentPasswordError, setIsCurrentPasswordError] = useState(false);
    const [isTwoFaModal, setIsTwoFaModal] = useState(false);

    const [currentPassword, setCurrentPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [newRePassword, setNewRePassword] = useState('');

    const intl = useIntl();

    const onChangePassword = async (twoFaCode = '') => {
        setIsCurrentPasswordError(false);
        setIsNewPasswordError(false);

        if (!newPassword || !newRePassword || newPassword !== newRePassword) {
            setIsNewPasswordError(true);
            return false;
        }

        const changePasswordResponse = await RequestService.ChangePassword(currentPassword, newPassword, twoFaCode);

        if (changePasswordResponse['Status'] !== 'Success') {
            if (changePasswordResponse['ErrorMessage'] === 'Wrong password.') {
                setIsCurrentPasswordError(true);
            } else if (changePasswordResponse['ErrorMessage'] === 'Incorrect two factor code') {
                setIsTwoFaModal(true);
            } else if(changePasswordResponse['ErrorCode'] === 'EntityNotFound') {
                setIsCurrentPasswordError(true);
            }

            return false;
        }

        onClose();
        NotificationService.Push(t('Password changed'), false);

        return true;
    };

    return (
        <Modal className="changePasswordModal" title={t('Change Password')} onClose={onClose}>
            {isTwoFaModal &&
            <TwoFaModal onAccept={(code) => onChangePassword(code)} onClose={() => setIsTwoFaModal(false)}/>}
            <Form onSubmit={onChangePassword}>
                <Input type="password" label={isCurrentPasswordError ? t('Incorrect password') : t('Current Password')}
                       placeholder={intl.formatMessage({ id: 'Current Password' })} value={currentPassword} error={isCurrentPasswordError}
                       onChange={(e) => setCurrentPassword(e.target.value)}/>
                <Input type="password" label={isNewPasswordError ? t('Passwords do not match') : t('New password')}
                       placeholder={intl.formatMessage({ id: 'New password' })} value={newPassword} error={isNewPasswordError}
                       onChange={(e) => setNewPassword(e.target.value)}/>
                <Input type="password" label={isNewPasswordError ? t('Passwords do not match') : t('Confirm new password')}
                       placeholder={intl.formatMessage({ id: 'Confirm new password' })} value={newRePassword} error={isNewPasswordError}
                       onChange={(e) => setNewRePassword(e.target.value)}/>
                <Button type="submit">{t('Apply Changes')}</Button>
            </Form>
        </Modal>
    );
};

ChangePasswordModal.defaultProps = {
    onClose: () => {}
};

ChangePasswordModal.propTypes = {
    onClose: PropTypes.func
};

export default ChangePasswordModal;
