import React from 'react';
import { useParams } from 'react-router-dom';
import { tabTypes } from './fundsMainTypes';
import AddressesTab from './components/AddressesTab';
import HistoryTab from './components/HistoryTab';
import QuarantineTab from './components/QuarantineTab';
import RiskModal from '../../../modals/RiskModal/RiskModal';

import './FundsMain.scss';


const FundsMain = ({ onCheck, onWithdraw }) => {
    const { tab } = useParams();

    return (
        <div className="fundsMain">
            <RiskModal onCheck={onCheck} />
            <div className="fundsMainTables">
                {(tab === tabTypes.addresses.key) && (
                    <AddressesTab
                        onWithdraw={onWithdraw}
                        onCheck={onCheck}
                    />
                )}
                {(tab === tabTypes.quarantine.key) && (
                    <QuarantineTab
                        onWithdraw={onWithdraw}
                        onCheck={onCheck}
                    />
                )}
                {(tab === tabTypes.history.key) && (
                    <HistoryTab />
                )}
            </div>
        </div>
    );
};
export default FundsMain;