import React, { useState, useEffect, useMemo } from 'react';
import { useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import t from '../../../../../lang/t';
import config from '../../../../../AppConfig';
import Types from '../../../../../classes/Types';
import HelperService from '../../../../../services/HelperService';
import RequestService from '../../../../../services/RequestService';
import PopUp from '../../../../../components/PopUp/PopUp';
import Table from '../../../../../components/Table/Table';
import {addressLink, transactionLink} from '../../../../../utils/cryptoLinkGenerator';

const { subRoutesGenerator } = config;
const { statusesById } = Types;

const TABLE_ID = 'historyTable';

const HistoryTab = () => {
    const [isLoading, setLoading] = useState(false);
    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState([]);

    const today = new Date(new Date().setHours(0, 0, 0, 0));
    const mounthAgo = new Date(today - 1000*60*60*24*30);
    const [datesRange, setDatesRange] = useState([mounthAgo, today]);

    const history = useHistory();

    const cryptocurrencies = useSelector(state => state.cryptocurrency.cryptocurrencies);

    const currencyByIds = cryptocurrencies.reduce((acc, currency) => {acc[currency.Symbol] = currency.Name; return acc;}, {});

    const columns = useMemo(() => ([
        {
            Field: 'Date',
            Title: t('Date'),
            Width: '140px',
            Cell: (data) => (
                <>
                    <span>{data.Date.slice(0, data.Date.indexOf('T'))}</span>
                    <span className="text-color-darkGray"> {data.Date.slice(data.Date.indexOf('T') + 1)}</span>
                </>
            )
        },
        {
            Field: 'Cryptocurrency',
            Title: t('Currency'),
            Width: '120px',
            Cell: (data) => currencyByIds[data.Cryptocurrency].includes('(') ? (
                <>
                    <p>{currencyByIds[data.Cryptocurrency].slice(0, currencyByIds[data.Cryptocurrency].lastIndexOf('('))}</p>
                    <p className="text-color-darkGray">{currencyByIds[data.Cryptocurrency].slice(currencyByIds[data.Cryptocurrency].lastIndexOf('('))}</p>
                </>
            ) : (
                <p>{currencyByIds[data.Cryptocurrency]}</p>
            )
        },
        {
            Field: 'Transaction status',
            Title: t('Transaction status'),
            Width: '120px',
            Cell: (data) => {
                return data.Status ? <p className={`text-color-${statusesById[data.Status].color}`}>{t(statusesById[data.Status].name)}</p> : <p>-</p>
            }
        },
        {
            Field: 'Amount without comission',
            Title: t('Transaction amount\n(minus commissions)'),
            Width: '200px',
            Cell: (data) => (
                <>
                    {(data.Type === 2 || data.Type === 4) && '- '}
                    {data.Cryptocurrency === 'USDT-TRC20' ?
                        HelperService.FormatCrypto(data.Amount, 2) :
                        HelperService.FormatCrypto(data.Amount - data.Commission)
                    }
                    &nbsp;
                    {data.Cryptocurrency}
                </>
            ),
        },
        {
            Field: 'Type',
            Title: t('Type'),
            Width: '140px',
            Cell: (data) => (
                <>
                    {(data.Type === 1) ? t('Receipt of funds') : (data.Type === 2) ? t('Withdrawal') : (data.Type === 3) ? t('Odd money Receipt') : t('Odd money Withdrawal')}
                </>
            ),
        },
        {
            Field: 'TxId',
            Title: t('Transaction link'),
            Width: '500px',
            Cell: (data) => {
                return(
                    <a href={transactionLink(data.Cryptocurrency, data.TxId)} target="_blank" rel="noreferrer">
                        {data.TxId}
                    </a>
                )
            }
        },
        {
            Field: 'MerchantName',
            Title: t('Merchant'),
            Description: t('The name of the merchant to which the crypto address belongs to'),
            Width: '120px',
            Cell: (data) => (
                <div
                    className="tableBodyItemCellLink"
                    onClick={() => history.push(subRoutesGenerator.merchantsId(data.MerchantId))}
                >
                    {data.MerchantName}
                </div>
            ),
        },
        {
            Field: 'Initiator',
            Title: t('Initiator'),
            Description: t('The user, administrator, or automatic withdrawal that made the transaction. For the transaction type "Received" - always the user\'s email. For the "Withdrawal" transaction type, the name of the administrator or the name of the automatic withdrawal'),
            Width: '140px',
            Cell: (data) => (<>{t(data.InitiatorType)} {data.InitiatorId}</>)
        },
        {
            Field: 'Commission',
            Title: t('Transaction fee'),
            Width: '160px',
            Cell: (data) => (
                <>
                    {data.Commission > 0 && '- '}
                    {HelperService.FormatCrypto(data.Commission, data.Cryptocurrency === 'USDT-TRC20' && 2)}
                    &nbsp;
                    {data.CommissionCurrency}
                </>
            ),
        },
        {
            Field: 'Amount',
            Title: t('Transaction amount\n(without commission deduction)'),
            Width: '200px',
            Cell: (data) => (
                <>
                    {(data.Type === 2 || data.Type === 4) && '- '}
                    {HelperService.FormatCrypto(data.Amount, data.Cryptocurrency === 'USDT-TRC20' && 2)}
                    &nbsp;
                    {data.Cryptocurrency}
                </>
            ),
        },
        {
            Field: 'SenderAddresses',
            Title: t('Sender\'s address'),
            Width: '300px',
            Cell: (data) => (
                <>
                    {
                        data.SenderAddresses.length === 0 ?
                            <span>-</span> : data.SenderAddresses.length === 1 ?
                                <a href={addressLink(data.Cryptocurrency, data.SenderAddresses[0])} target="_blank" rel="noreferrer">{data.SenderAddresses[0]}</a> :
                                <div className="flex-table-cell">
                                    {t('Number of addresses')}:&nbsp;<PopUp values={data.SenderAddresses} CryptoCurrency={data.Cryptocurrency} />
                                </div>
                    }
                </>
            ),
        },
        {
            Field: 'DestinationAddress',
            Title: t('Recipient address'),
            Width: '340px',
            Cell: (data) => {
                return(
                    <a href={addressLink(data.Cryptocurrency, data.DestinationAddress)} target="_blank" rel="noreferrer">
                        {data.DestinationAddress}
                    </a>
                )
            }
        },
        {
            Field: 'AddressAdditionalInfo',
            Title: t('System description'),
            Description: t('The description that was written when creating the crypto address.'),
            Width: '300px',
        },
        {
            Field: 'Comment',
            Title: t('User comment'),
            Description: t('Comment, specified when the transaction was created'),
            Width: '220px',
            Cell: (data) => {
                const opts = {
                    start: 0,
                    maxLength: 22,
                    middleIndex: 10,
                    separator: ' ... ',
                    defaultValue: '-',
                };
                let { Comment: comment, FailReason: failReason } = data;
                comment = (comment?.length > opts.maxLength) ? (comment.slice(opts.start, opts.middleIndex) + opts.separator + comment.slice(-opts.middleIndex)) : comment;
                const errorCode = JSON.parse(failReason)?.Code || '';
                return (!comment && !failReason) ? opts.defaultValue : `${comment} ${errorCode}`.trimEnd().trimStart();
            }
        },
    ]), [currencyByIds, history]);

    useEffect(() => {
        let tableSettings = JSON.parse(localStorage.getItem(TABLE_ID));

        if (tableSettings === null) {
            const tableSettings = columns.filter(column => column.Title).map((column, index) => ({ title: column.Title.props.id, isVisible: index < 6 }));
            localStorage.setItem(TABLE_ID, JSON.stringify(tableSettings));
        }
    }, [columns]);

    const onPaginate = async (tablePage = page, tableCount = count, tableSearch = search, tableSort = sort, tableFilters = filters, tableDatesRange = datesRange) => {
        setLoading(true);

        setDatesRange(tableDatesRange);
        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        const frommDate = HelperService.formatDate(tableDatesRange?.[0], '-');
        const toDate = HelperService.formatDate(tableDatesRange?.[1], '-', 10, 'T23:59:59.99999');

        const response = await RequestService.GetTransactions(tablePage, tableCount, tableSearch, tableSort, tableFilters, frommDate, toDate);

        setTotal(response.Total);
        setData(response.Transactions || []);

        setLoading(false);
    };

    const tableProps = { isLoading, columns, page, count, search, sort, filters, datesRange, total, data, onPaginate };

    return (
        <Table id={TABLE_ID} { ...tableProps } />
    );
};

export default HistoryTab;
