import RequestService from '../../../services/RequestService';
import { appSlice } from '../../slices';

const getWalletSettings = async (store, next, action) => {
    const { type, payload } = action;
    if (type === 'app/updateModalStructure' && payload?.id === "Backup" && payload?.isOpen) {
        const responseSettings = await RequestService.GetWalletBackupSettings();
        const responseStorageAuthUrl = await RequestService.GetWalletStorageAuthUrl();
        const backupModel = store.getState()?.app?.backup;

        if (responseSettings && responseSettings?.Status === 'Success') {
            let encryptionPassword = responseSettings?.EncryptionKey;
            let storageAccountEmail = responseSettings?.StorageAccountEmail;
            let signInStatus = storageAccountEmail !== null;
            let saveStatus = encryptionPassword !== null;
            let authUrl = responseStorageAuthUrl?.AuthUrl;

            encryptionPassword = encryptionPassword === null ? '' : encryptionPassword;
            storageAccountEmail = storageAccountEmail === null ? '' : storageAccountEmail;

            store.dispatch(appSlice.actions.setBackupModel({
                ...backupModel,
                encryptionPassword,
                storageAccountEmail,
                signInStatus,
                authUrl,
                saveStatus,
                encryptionPasswordIsTouched: saveStatus,
            }));

        }
    }
    return next(action);
};

export const backupMiddleware = (store) => (next) => (action) => {
    getWalletSettings(store, next, action).then();
};
