import React, { useState, useRef, useEffect } from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import Input from '../../../../../../components/Input/Input';
import ListItemButton from '@mui/material/ListItemButton';
import classNames from 'classnames';
import t from '../../../../../../lang/t';
import { getRiskLevelByRiskScore } from '../../../../../../utils';
import HelperService from '../../../../../../services/HelperService';
import { FixedSizeList } from 'react-window';

import './ReceivingAddressesSelect.scss';

const listOptions = {
    height: 200,
    itemSize: 40,
    className: 'nav dropdown__list',

};

const ReceivingAddressesSelect = ({ selectedAddress, setSelectedAddress, addresses, placeholder }) => {
    const ref = useRef(null);
    const intl = useIntl();

    const [dropdownStatus, setDropdownStatus] = useState(false);
    const [searchValue, setSearchValue] = useState('');
    const [filteredAddresses, setFilteredAddresses] = useState(addresses);

    const onClickOutside = (e) => {
        if (ref.current && ref.current.contains && !ref.current.contains(e.target)) {
            if (dropdownStatus) setDropdownStatus(false);
        }
    };

    useEffect(() => {
        document.addEventListener('click', onClickOutside, true);
        return () => {
            document.removeEventListener('click', onClickOutside, true);
        };
    });

    useEffect(() => {
        setFilteredAddresses(addresses);
    }, [addresses]);

    const onSelectItem = (address) => {
        setSelectedAddress(address);
        setDropdownStatus(false);
    };

    const onChangeSearch = (e) => {
        const { value = '' } = e.target;
        const RGX = new RegExp(`${value}`, 'i');
        const newFilteredAddresses = !!value ? addresses.filter(address => RGX.test(address.Balance)) : addresses;
        setSearchValue(value);
        setFilteredAddresses(newFilteredAddresses);
    };

    return (
        <div ref={ref} className="ReceivingAddressesSelect">
            <div className="click-zone" onClick={() => setDropdownStatus(!dropdownStatus)} />
            {selectedAddress && <div className="ReceivingAddressesSelect__title">
                {t('Crypto address')}
            </div>}
            <div className="ReceivingAddressesSelect__placeholder">
                {selectedAddress
                    ? <div className="ReceivingAddressesSelect__value">
                        {selectedAddress?.Address}
                    </div>
                    : <div className="placeholder">
                        {t(placeholder || 'Crypto address')}
                    </div>
                }
            </div>
            <Icon className="dropdown-icon" type={Icons.SELECT_ARROW} width={16} height={16} />
            {dropdownStatus && <div className="ReceivingAddressesSelect__dropdown dropdown">
                <div className="dropdown__search">
                    <div className="dropdown__search-title">
                        {t('Select crypto address')}
                    </div>
                    <Input type="search" placeholder={intl.formatMessage({ id: 'Find crypto address' })} value={searchValue} onChange={onChangeSearch} />
                </div>
                <FixedSizeList
                    {...listOptions}
                    itemData={filteredAddresses}
                    itemCount={filteredAddresses.length}
                >
                    {({ data, index, style }) => (<div style={style}>
                        <ListItemButton key={data[index]?.Id} className={classNames({ active: selectedAddress?.Id === data[index]?.Id })} onClick={() => onSelectItem(data[index])}>
                            <div className="dropdown__item">
                                <div className="dropdown__item-name">
                                    {HelperService.FormatCrypto(data[index]?.Balance, data[index]?.Currency === 'USDT-TRC20' && 2)} {data[index]?.Currency}
                                </div>
                                <div className="dropdown__item-value">
                                    <Icon className={getRiskLevelByRiskScore(data[index]?.RiskScore, 1).color} type={Icons.RISK} width={16} height={16} />
                                    <span>{data[index]?.RiskScore != null ? (data[index]?.RiskScore * 100 + '%') : '-'}</span>
                                </div>
                            </div>
                        </ListItemButton>
                    </div>)}
                </FixedSizeList>
            </div>}
        </div>
    );
};

export default ReceivingAddressesSelect;
