import React, { useState } from 'react';
import classNames from 'classnames';
import t from '../../../../../../lang/t';
import AutoScalingInput from '../AutoScalingInput';
import HelperService from '../../../../../../services/HelperService';
import { useSelector, useDispatch } from 'react-redux';
import RequestService from '../../../../../../services/RequestService';
import { withdrawalSlice } from '../../../../../../store/slices';

import './AmountInputs.scss';

function debounce(func, timeout = 500) {
    let timer;
    return (...args) => {
        clearTimeout(timer);
        timer = setTimeout(() => {
            func.apply(this, args);
        }, timeout);
    };
}

const SwitchIcon = ({ onClick }) => (<svg
    xmlns="http://www.w3.org/2000/svg"
    width="24"
    height="24"
    fill="none"
    viewBox="0 0 24 24"
    className="SwitchIcon"
    onClick={onClick}
>
    <circle cx="12" cy="12" r="12" fill="#F5F7FB"/>
    <path
        fill="#0455A3"
        d="M9 18l-.354.354.354.353.354-.353L9 18zm0-6h-.5.5zm1.5-1a.5.5 0 000-1v1zm-4.854 4.354l3 3 .708-.708-3-3-.708.708zm3.708 3l3-3-.708-.708-3 3 .708.708zM9.5 18v-6h-1v6h1zm0-6a1 1 0 011-1v-1a2 2 0 00-2 2h1zM15 6l-.354-.354.354-.353.354.353L15 6zm0 6h-.5.5zm-1.5 2a.5.5 0 010-1v1zm-1.854-5.354l3-3 .708.708-3 3-.708-.708zm3.708-3l3 3-.708.708-3-3 .708-.708zM15.5 6v6h-1V6h1zm0 6a2 2 0 01-2 2v-1a1 1 0 001-1h1z"
    />
</svg>);

const AmountInputs = () => {
    const dispatch = useDispatch();
    const roundCrypto = (value = 0) => Math.round(+value * 1e8) / 1e8;
    const [inputToggle, setInputToggle] = useState(false);
    const activeCurrency = useSelector((state) => state?.withdrawal?.manualWithdrawal?.currency);
    const fiatAmount = useSelector((state) => state?.withdrawal?.manualWithdrawal?.fiatAmount);
    const cryptoAmount = useSelector((state) => state?.withdrawal?.manualWithdrawal?.cryptoAmount);
    const availableBalance = useSelector((state) => state?.withdrawal?.manualWithdrawal?.availableBalance);
    const isLowBalance = ((+cryptoAmount > 0) && (roundCrypto(availableBalance) < roundCrypto(cryptoAmount)));

    const setFiatValue = (value) => dispatch(withdrawalSlice.actions.setFiatAmountWithdrawalModel(value));
    const setCryptoValue = (value) => dispatch(withdrawalSlice.actions.setCryptoAmountWithdrawalModel(value));

    const getQuotes = async (value, type = 'fiat') => {
        const quotes = await RequestService.GetQuotes();
        const currentQuote = quotes?.Quotes?.find((quote) => quote?.Cryptocurrency === activeCurrency.Symbol);
        const price = currentQuote?.Price;

        if(type === 'fiat') {
            setCryptoValue((+value / price).toFixed(8));
        } else {
            setFiatValue((+value * price).toFixed(2));
        }
    };

    const getQuotesWithDebounce = debounce(getQuotes);

    const onChangeUSD = (e) => {
        setFiatValue(e.target.value);
        getQuotesWithDebounce(e.target.value);
    };

    const onChangeCrypto = (e) => {
        setCryptoValue(e.target.value);
        getQuotesWithDebounce(e.target.value, 'crypto');
    };

    const toggleInput = () => {
        setInputToggle((prev) => (!prev));
    };

    const maxBalance = () => {
        setCryptoValue(availableBalance)
        getQuotesWithDebounce(availableBalance, 'crypto');
    }

    return (
        <div className="AmountInputs">
            <div className={classNames('AmountInputs__Title', { 'low-balance': isLowBalance })}>
                {isLowBalance ? t('Insufficient funds') : t('Enter the amount')}
            </div>
            <div className="AmountInputs__Body">
                {inputToggle ? (
                    <div className="field-group">
                        <div className="input-field">
                            <div className="currency">&#36;</div>
                            <AutoScalingInput
                                onChange={onChangeUSD}
                                value={fiatAmount}
                                name="fiat"
                            />
                            <div className="button-max" onClick={maxBalance}>Max</div>
                        </div>
                        <div className="converter">
                            <div className="value">{HelperService.FormatCrypto(cryptoAmount, activeCurrency.Symbol === 'USDT-TRC20' && 2)}</div>
                            <div className="currency">{activeCurrency.Symbol}</div>
                        </div>
                        <SwitchIcon onClick={toggleInput}/>
                    </div>
                ) : (
                    <div className="field-group">
                        <div className="input-field">
                            <AutoScalingInput
                                onChange={onChangeCrypto}
                                value={cryptoAmount}
                                name="crypto"
                            />
                            <div className="currency">{activeCurrency.Symbol}</div>
                            <div className="button-max" onClick={maxBalance}>Max</div>
                        </div>
                        <div className="converter">
                            <div className="currency">&#36;</div>
                            <div className="value">{HelperService.FormatFiatWithoutCurrency(fiatAmount)}</div>
                        </div>
                        <SwitchIcon onClick={toggleInput}/>
                    </div>
                )}
            </div>
        </div>
    );
};

export default AmountInputs;
