import React from 'react';
import PropTypes from 'prop-types';
import Icon, { Icons } from '../Icon/Icon';
import './PopUp.scss';
import {addressLink} from '../../utils/cryptoLinkGenerator';

const PopUp = ({ values, CryptoCurrency = '' }) => {
    return (
        <div className="popup">
            <div className="popupIcon">
                <span>{values.length}</span>
                <Icon type={Icons.SELECT_ARROW} width={16} height={16}/>
            </div>
            <div className="popupWindow">
                <div>
                    {values.map((item, index) => {
                        return (
                            <p key={index}><a href={addressLink(CryptoCurrency, item)} target="_blank" rel="noreferrer">{item}</a></p>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};

PopUp.defaultProps = {
    values: []
};

PopUp.propTypes = {
    values: PropTypes.arrayOf(PropTypes.string)
};

export default PopUp;