import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import t from '../../../../lang/t';
import HelperService from '../../../../services/HelperService';
import RequestService from '../../../../services/RequestService';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import Table from '../../../../components/Table/Table';
import DateRangePicker from '../../../../components/DateRangePicker';
import Select from '../../../../components/Select/Select';
import './BalanceSheetTab.scss';

const BalanceSheetTab = () => {
    const [isLoading, setLoading] = useState(false);
    const [data, setData] = useState([]);
    const [filteredData, setFilteredData] = useState([]);
    const [sort, setSort] = useState({ by: '', desc: true });

    const today = new Date(new Date().setHours(0, 0, 0, 0));
    const mounthAgo = new Date(today - 1000*60*60*24*30);
    const [datesRange, setDatesRange] = useState([mounthAgo, today]);
    const [selectedMerchant, setSelectedMerchant] = useState({label: t('All merchants'), value: null});
    const [selectedCurrency, setSelectedCurrency] = useState({label: t('All cryptocurrencies'), value: null});

    const merchants = useSelector((state) => state?.merchant.merchants);
    const cryptocurrencies = useSelector(state => state?.cryptocurrency.cryptocurrencies);

    const merchantsOptions = [{label: t('All merchants'), value: null}].concat(merchants.map(merchant => {
        return {id: merchant.Id, label: merchant.Name, value: merchant.Name}
    }));
    const currenciesOptions = [{label: t('All cryptocurrencies'), value: null}].concat(cryptocurrencies.map(currency => {
        return {id: currency.Id, label: currency.Name, value: currency.Symbol}
    }));

    const columns = [
        {
            Field: 'name',
            Title:  t('Cryptocurrency'),
            Width: '203px',
            Cell: (data) => (
                <div className="currency-column">
                    <Icon type={Icons[data.symbol]} width={24} height={24} />
                    {data.name.includes('(') ?
                        <p>
                            <span>{data.name.slice(0, data.name.lastIndexOf('('))}</span>
                            <span className="currency-postfix">{data.name.slice(data.name.lastIndexOf('('))}</span>
                        </p> : 
                        <p>{data.name}</p>
                    }
                </div>
            )
        },
        {
            Field: 'balanceBegin',
            Title:  t('Start of period'),
            Width: '180px',
            Cell: (data) => <span>{HelperService.FormatCrypto(data.balanceBegin, data.symbol === 'USDT-TRC20' && 2)}&nbsp;{data.symbol}</span>
        },
        {
            Field: 'totalIn',
            Title:  t('Income'),
            Width: '180px',
            Cell: (data) => <span>{HelperService.FormatCrypto(data.totalIn, data.symbol === 'USDT-TRC20' && 2)}&nbsp;{data.symbol}</span>
        },
        {
            Field: 'totalOut',
            Title:  t('Outcome'),
            Width: '180px',
            Cell: (data) => <span>{HelperService.FormatCrypto(data.totalOut, data.symbol === 'USDT-TRC20' && 2)}&nbsp;{data.symbol}</span>
        },
        {
            Field: 'balanceEnd',
            Title:  t('End of period'),
            Width: '240px',
            Cell: (data) => <span>{HelperService.FormatCrypto(data.balanceEnd, data.symbol === 'USDT-TRC20' && 2)}&nbsp;{data.symbol}</span>
        },
    ];

    const getFilteredData = (data, merchant, currency) => {
        return data.filter(item => (!merchant.value || item.MerchantId === merchant.id) &&  (!currency.value || item.Currency === currency.value));
    }

    const formatToTable = (data) => {
        const balancesByCurrency = cryptocurrencies.filter(currency => {
            return !selectedCurrency.value || currency.Symbol === selectedCurrency.value
        }).reduce((acc, currency) => {
            acc[currency.Symbol] = {
                id: currency.Id,
                name: currency.Name,
                symbol: currency.Symbol,
                balanceBegin: 0,
                balanceEnd: 0,
                totalIn: 0,
                totalOut: 0,
            }
            return acc;
        }, {});
        data.forEach(balance => {
            balancesByCurrency[balance.Currency].balanceBegin += balance.BalanceBegin;
            balancesByCurrency[balance.Currency].balanceEnd += balance.BalanceEnd;
            balancesByCurrency[balance.Currency].totalIn += balance.TotalIn;
            balancesByCurrency[balance.Currency].totalOut += balance.TotalOut;
        });
        
        const sortedTableData = Object.values(balancesByCurrency).sort((a,b) => {
                if (!sort.by) return false;

                const factor = sort.desc ? 1 : -1;
                if (sort.by === 'name') return a[sort.by].localeCompare(b[sort.by]) * factor;
                return (a[sort.by] - b[sort.by]) * factor;
            });

        return sortedTableData;
    }

    const onPaginate = async (page, count, search, tableSort = sort, filters, tableDatesRange = datesRange) => {
        setSort(tableSort);
        setDatesRange(tableDatesRange);

        const frommDate = HelperService.formatDate(tableDatesRange?.[0], '-');
        const toDate = HelperService.formatDate(tableDatesRange?.[1], '-', 10, 'T23:59:59.99999');
        const merchantIds = merchants.map(merchant => merchant.Id);

        if (!data || data.length === 0 || filters === true) {
            setLoading(true);
            const response = await RequestService.GetReportBalance(merchantIds, frommDate, toDate);
            setLoading(false);

            if (response.Status === 'Success') {
                setData(response.Balance);
                setFilteredData(getFilteredData(response.Balance, selectedMerchant, selectedCurrency));
            }
        } else {
            setFilteredData(getFilteredData(data, selectedMerchant, selectedCurrency));
        }
    };

    const handleDateFromChange = (from) => {
        if (HelperService.formatDate(from) === HelperService.formatDate(datesRange[0])) return;
        const to = (datesRange[1] && from > datesRange[1]) ? from : datesRange[1];
        onPaginate(null, null, null, sort, true, [from, to]);
    }

    const handleDateToChange = (to) => {
        if (HelperService.formatDate(to) === HelperService.formatDate(datesRange[1])) return;
        const from = (datesRange[0] && datesRange[0] > to) ? to : datesRange[0];
        onPaginate(null, null, null, sort, true, [from, to]);
    }

    const selectMerchant = (merchant) => {
        if (selectedMerchant.value === merchant.value) return;

        setSelectedMerchant(merchant);
        setFilteredData(getFilteredData(data, merchant, selectedCurrency));
    }
    
    const selectCurrency = (currency) => {
        if (selectedCurrency.value === currency.value) return;
        setSelectedCurrency(currency);
        setFilteredData(getFilteredData(data, selectedMerchant, currency));
    }

    const tableProps = {isLoading, columns, sort, datesRange, data: formatToTable(filteredData), onPaginate, noFilters: true, noPagitations: true, uploadedData: filteredData};

    return (
        <div className="BalanceSheetTab">
            <div className="control-block">
                <div className="control-block__merchant-select">
                    <label className="control-block__select-label">{t('Choose a merchant')}</label>
                    <Select options={merchantsOptions} value={selectedMerchant} onChange={selectMerchant} />
                </div>
                <div className="control-block__currency-select">
                    <label className="control-block__select-label">{t('Choose a cryptocurrency')}</label>
                    <Select options={currenciesOptions} value={selectedCurrency} onChange={selectCurrency} />
                </div>
                <div className="control-block__dates-select">
                    <DateRangePicker
                        datesRange={datesRange}
                        handleDateFromChange={handleDateFromChange}
                        handleDateToChange={handleDateToChange}
                        fromLabel="Date from"
                        toLabel="Date to"
                    />
                </div>
            </div>
            <div className="tableWrapper">
                <Table id="balanceSheetTable" {...tableProps} />
            </div>
        </div>
    );
};

export default BalanceSheetTab;