import React, { useState, useRef, useEffect } from 'react';
import { useSelector } from 'react-redux';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../../../../../lang/t';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import Input from '../../../../../../components/Input/Input';
import ListItemButton from '@mui/material/ListItemButton';
import classNames from 'classnames';
import { FixedSizeList } from 'react-window';

import './ReceivingMerchantSelect.scss';

const listOptions = {
    height: 200,
    itemSize: 40,
    className: 'dropdown__list',

};

const ReceivingMerchantSelect = ({ selectedMerchant, setSelectedMerchant, addressesCountInMerchants }) => {
    const ref = useRef(null);
    const intl = useIntl();

    const [dropdownStatus, setDropdownStatus] = useState(false);
    const [searchValue, setSearchValue] = useState('');

    const merchants = useSelector((state) => state?.merchant.merchants);
    const [filteredMerchants, setFilteredMerchants] = useState(merchants);

    const onClickOutside = (e) => {
        if (ref.current && ref.current.contains && !ref.current.contains(e.target)) {
            if (dropdownStatus) setDropdownStatus(false);
        }
    };

    useEffect(() => {
        document.addEventListener('click', onClickOutside, true);
        return () => {
            document.removeEventListener('click', onClickOutside, true);
        };
    });

    const onSelectItem = (merchant) => {
        setSelectedMerchant(merchant);
        setDropdownStatus(false);
    };

    const onChangeSearch = (e) => {
        const { value = '' } = e.target;
        const RGX = new RegExp(`${value}`, 'i');
        const filteredMerchants = !!value ? merchants.filter(merchant => RGX.test(merchant.Name)) : merchants;
        setSearchValue(value);
        setFilteredMerchants(filteredMerchants);
    };

    return (
        <div ref={ref} className="ReceivingMerchantSelect">
            <div className="click-zone" onClick={() => setDropdownStatus(!dropdownStatus)}/>
            <div className="ReceivingMerchantSelect__title">
                {t('Merchant')}
            </div>
            <div className="ReceivingMerchantSelect__placeholder">
                <div className="ReceivingMerchantSelect__value">
                    {selectedMerchant?.Name}
                </div>
            </div>
            <Icon className="dropdown-icon" type={Icons.SELECT_ARROW} width={16} height={16}/>
            {dropdownStatus && <div className="ReceivingMerchantSelect__dropdown dropdown">
                <div className="dropdown__search">
                    <div className="dropdown__search-title">
                        {t('Choose a merchant')}
                    </div>
                    <Input type="search" placeholder={intl.formatMessage({ id: 'Find a merchant' })} value={searchValue}
                           onChange={onChangeSearch}/>
                </div>
                <FixedSizeList
                    {...listOptions}
                    itemData={filteredMerchants}
                    itemCount={filteredMerchants.length}
                >
                    {({ data, index, style }) => (<div style={style}>
                        <ListItemButton key={data[index]?.Id}
                                        className={classNames({ active: selectedMerchant?.Id === data[index]?.Id })}
                                        onClick={() => onSelectItem(data[index])}>
                            <div className="dropdown__item">
                                <div className="dropdown__item-name">
                                    {data[index]?.Name}
                                </div>
                                <div className="dropdown__item-amount">
                                    <span className="label">{t('Addr.')}:</span> <span
                                    className="value">{addressesCountInMerchants[data[index]?.Id]}</span>
                                </div>
                            </div>
                        </ListItemButton>
                    </div>)}
                </FixedSizeList>
            </div>}
        </div>
    );
};

export default ReceivingMerchantSelect;
