import React from 'react';
import Tooltip from '../../../../../../components/Tooltip/Tooltip';
import t from '../../../../../../lang/t';

import './Switcher.scss';

const Switcher = ({arrayButton, data, dataType, onChange, tooltip}) => {

    const transactionTypes = {
        3: t('Transaction time up to 24 hours'),
        2: t('Transaction time up to 8 hours'),
        1: t('Transaction time up to 20 minutes')
    }

    return (
        <div className="Switcher__Body">
            <div className={data.CryptoCurrency.Symbol === "USDT-TRC20" && dataType === 'CommissionType' ? "Switcher__Tabs disabled" : "Switcher__Tabs"}>
                {arrayButton.map((type, i) => ( 
                <button key={i} disabled={data.CryptoCurrency.Symbol === "USDT-TRC20" && dataType === 'CommissionType' ? true : false}
                    className={type.value === data[dataType].value ? 'commission-tab active' : 'commission-tab'}
                    onClick={() => onChange(dataType, type)}
                >{type.label}</button>
                ))}
            </div>
            {transactionTypes[data[dataType].value] && dataType === 'CommissionType'
                ? <div className="switcher-title">{transactionTypes[data[dataType].value]}</div>
                : null
            }
            {
                tooltip ? <Tooltip className="switcher-tooltip" text={tooltip} /> : null
            }
        </div>
    )

};

export default Switcher;