import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import classNames from 'classnames';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../lang/t';
import HelperService from '../../services/HelperService';
import { appSlice } from '../../store/slices';
import RootModalModel from '../../models/root-modal';
import Button from '../../components/Button/Button';
import Icon, { Icons } from '../Icon/Icon';

import './WalletsList.scss';


const WalletsList = ({ sourcePage, data, onPaginate, onWithdraw, page, count, search, sort, filters, merchantId, selectedWalletIndex, setSelectedWalletIndex }) => {
    const dispatch = useDispatch();
    const [filterText, setFilterText] = useState('');

    const intl = useIntl();

    const selectWallet = (index) => {
        if (index === selectedWalletIndex) return;

        setSelectedWalletIndex(index);
        const newFilters = filters.filter(item => item.Type !== '3');
        if (index !== null) {
            newFilters.push({ 'Type': '3', 'Column': 'Currency', 'Items': [data.list[index].Id] });
        }
        onPaginate(page, count, search, sort, newFilters)
    };

    const send = ({ index }) => {
        onWithdraw(data.list[index].Symbol)
    };

    const receive = ({ index }) => {
        openReceiveModal(data.list[index].Symbol)
    };

    const openReceiveModal = (currency = null) => {
        const currencyBalances = data.list.reduce((acc, currency) => {
            acc[currency.Symbol] = currency.Amount
            return acc
        }, {})
        dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
            id: 'Receiving',
            className: 'ReceivingModal',
            title: t('To receive'),
            tag: 'receiving',
            isOpen: true,
            outsideParams: {
                currencyBalances,
                currency,
                merchantId,
                onPaginate,
            },
        })))
    };

    const WalletControls = (index) => (
        <div className="walletControls">
            <Button
                className={classNames('small', 'light', 'walletControls__send')}
                onClick={(e) => send(index)}
            >
                <Icon
                    type={Icons.SEND}
                    width={24} height={24}
                />
                <span>{t('Send')}</span>
            </Button>
            {sourcePage === 'wallets' &&
                <Button className="small walletControls__receive" onClick={() => { receive(index) }}>
                    <Icon type={Icons.RECEIVE} width={24} height={24} />
                    <span>{t('To receive')}</span>
                </Button>
            }
        </div>
    );
    // todo translate
    return (
        <div className="walletsList">
            <div className="searchInput">
                <Icon
                    className="searchInput__searchIcon"
                    type={Icons.SEARCH}
                    width={24}
                    height={24}
                />
                <input
                    type="searchInput__text"
                    placeholder={intl.formatMessage({ id: 'Search' })}
                    value={filterText}
                    onChange={(e) => setFilterText(e.target.value)}
                />
                <Icon
                    className="searchInput__clear"
                    type={Icons.CROSS}
                    width={16}
                    height={16}
                    onClick={() => setFilterText('')}
                />
            </div>
            <div
                className={classNames('walletSumBlock', (sourcePage === 'quarantine') && 'red', (selectedWalletIndex === null) && 'walletSumBlock__active')}
                onClick={() => selectWallet(null)}
            >
                <div className="walletSumBlock__content">
                    <Icon
                        className="walletSumBlock__currency"
                        type={Icons.USD}
                        width={32}
                        height={32}
                    />
                    <div className="walletSum">
                        <div className="walletSum__text">
                            {t('Total')} {(sourcePage === 'quarantine') ? t('risky funds') : ''}
                        </div>
                        <div className="walletSum__number">
                            {HelperService.FormatFiatWithoutCurrency(data.sum)}
                        </div>
                    </div>
                </div>
            </div>
            <div className="walletListItems">
                {data.list.filter(({ Name }) => Name.toLowerCase().includes(filterText.toLocaleLowerCase())).map((item, index) => (
                    <div
                        className={classNames('wallet', (sourcePage === 'quarantine') && 'red', (selectedWalletIndex === index) && 'wallet__active')}
                        key={item.Id}
                        onClick={() => selectWallet(index)}
                    >
                        <div className="wallet__content">
                            <div className="walletInfo">
                                <div className="currency">
                                    <Icon
                                        className="currency__icon"
                                        type={Icons[item.Symbol]}
                                        width={32}
                                        height={32}
                                    />
                                    <div>
                                        <div className="currency__name">
                                            {item.Name}
                                        </div>
                                        <div className="currency__rate">
                                            {HelperService.FormatFiat(item.quotation, item.Symbol === 'USDT-TRC20' && 6)}
                                        </div>
                                    </div>
                                </div>
                                <div className="amount">
                                    <div className="amount__currencyAmount">
                                        {HelperService.FormatCrypto(item.Amount, item.Symbol === 'USDT-TRC20' && 2)}
                                        {item.Symbol === 'USDT-TRC20' ? <br/> : <>&nbsp;</>}
                                        <span className="amount__currency">
                                            {item.Symbol}
                                        </span>
                                    </div>
                                    <div className="amount__fiatAmount">
                                        {HelperService.FormatFiat(item.AmountUSD)}
                                    </div>
                                </div>
                            </div>
                            {(index === selectedWalletIndex) && (
                                <WalletControls index={index} />
                            )}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default WalletsList;
