import React, { useState } from 'react';
import Modal from '@mui/material/Modal';
import { useSelector, useDispatch } from 'react-redux';
import { appSlice } from '../../store/slices';
import RootModalModel from '../../models/root-modal';
import classNames from 'classnames';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Icon, { Icons } from '../../components/Icon/Icon';
import BodyRenderer from './components/BodyRenderer';
import DataLoader from '../../components/DataLoader';

import './RootModal.scss';

const RootModal = () => {
    const dispatch = useDispatch();
    const modal = useSelector((state) => state?.app?.modal);
    const { isOpen: open } = modal;

    const [close, setClose] = useState(false);

    const handleClose = () => {
        setClose(true);

        setTimeout(() => {
            setClose(false);

            dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
                ...modal,
                isOpen: !modal.isOpen,
            })))
        }, 290);
    };
    const { id, className, title, tag, isShowLoader } = modal;
    return (
        <Modal
            id="RootModal"
            className={close ? "RootModal close" : "RootModal"}
            open={open}
            onClose={handleClose}
        >
            <Box id={id} className={classNames('RootModal__Container', { [className]: !!className })}>
                <div className="Container__Header">
                    <div className="Container__Header__Close">
                        <Icon type={Icons.CROSS} width={32} height={32} onClick={handleClose}/>
                    </div>
                    <Typography variant="h2">{ title }</Typography>
                </div>
                <div className="Container__Body">
                    <BodyRenderer tag={tag} handleClose={handleClose}/>
                </div>
                {isShowLoader && <DataLoader />}
            </Box>
        </Modal>
    );
};

export default RootModal;