import React from 'react';
import { Link } from 'react-router-dom';
import t from '../../../../lang/t';
import config from '../../../../AppConfig';
import HelperService from '../../../../services/HelperService';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import Table from '../../../../components/Table/Table';
import { useDispatch } from 'react-redux';
import { appSlice } from '../../../../store/slices';
import RootModalModel from '../../../../models/root-modal';

const { subRoutesGenerator } = config;

const MerchantsTable = (props) => {
    const { isLoading, page, count, search, sort, filters, total, data, onPaginate, onEditModalOpen, onDeleteModalOpen } = props;
    const dispatch = useDispatch();

    //todo translate
    const columns = [
        {
            Field: 'Name',
            Title: t('Name ver2'),
            Width: '200px',
            Filter: {
                Type: 1
            },
            Cell: (data) => (
                <Link
                    to={subRoutesGenerator.merchantsId(data.Id)}
                    className="tableBodyItemCellLink"
                >
                    { data['Name'] }
                </Link>
            ),
        },
        {
            Field: 'BalanceBTC',
            Title: t('BTC balance'),
            Width: '140px',
            Cell: (data) => {

                return (
                <>
                    { HelperService.FormatCrypto(data?.BalanceBTC) }
                </>
                )
            },
        },
        {
            Field: 'BalanceUSDT',
            Title: t('USDT balance'),
            Width: '140px',
            Cell: (data) => {

                return (
                <>
                    { HelperService.FormatCrypto(data?.BalanceUSDT, 2) }
                </>
                )
            },
        },
        {
            Field: 'IsAutoWithdrawals',
            Title: t('Auto Withdrawals'),
            Width: '230px',
            Cell: (data) => {
                return (
                    <div>
                        {data['IsAutoWithdrawals']
                            ? (
                                <Link
                                    to={subRoutesGenerator.fundsTabWithParams('withdrawals', 'merchant', data['Id'])}
                                    className="tableBodyItemCellButton"
                                >
                                    <Icon type={Icons.EYE} width={16} height={16}/>
                                    <span>{ t('View Auto Withdrawals') }</span>
                                </Link>
                            )
                            : (
                                <button
                                    className="tableBodyItemCellButton"
                                    onClick={() => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
                                        id: 'AutoWithdrawal',
                                        className: 'AutoWithdrawalModal',
                                        title: t('Create auto withdrawal'),
                                        tag: 'autoWithdrawal',
                                        isOpen: true,
                                        isShowLoader: true,
                                        outsideParams: {
                                            data: null,
                                            MerchantId: {Name: data.Name, Id: data.Id}
                                        },
                                    })))}
                                >
                                    <Icon type={Icons.AUTO_WITHDRAW} width={16} height={16} />
                                    <span>{ t('Create an auto withdrawal') }</span>
                                </button>
                            )
                        }
                    </div>
                )
            },
        },
        {
            Field: 'Created',
            Title: t('Last Transaction Date'),
            Width: '180px',
            Cell: (data) => (
                <>
                    { !!data['LastTransactionDate'] ? new Date(data['LastTransactionDate']).toLocaleString() : '-' }
                </>
            ),
        },
        {
            Field: 'CallBackLink',
            Title: t('Backlink'),
            Description: t('The link to which notifications will be sent when funds are received'),
            Width: '240px',
            Filter: {
                Type: 1
            },
        },
        {
            Field: 'PublicKey',
            Title: t('Public Key'),
            Description: t('The key that is used to access the public api of the service'),
            Width: '200px',
            Filter: {
                Type: 1
            },
        },
        {
            Width: '80px',
            Cell: (data) => (
                <div className="tableBodyItemCellActions">
                    <Link to={subRoutesGenerator.merchantsId(data.Id)}>
                        <Icon
                            type={Icons.EYE}
                            width={16}
                            height={16}
                        />
                    </Link>
                    <Icon
                        type={Icons.EDIT_PEN}
                        width={16}
                        height={16}
                        onClick={() => onEditModalOpen(data)}
                    />
                    <Icon
                        className="tableBodyItemCellActionsTrash"
                        type={Icons.TRASH}
                        width={16}
                        height={16}
                        onClick={() => onDeleteModalOpen(data)}
                    />
                </div>
            ),
        },
    ];

    const tableProps = { isLoading, columns, page, count, search, sort, filters, total, data, onPaginate };

    return (
        <Table id="merchantsTable" { ...tableProps } />
    );
};

export default MerchantsTable;
