import React, { useState, useEffect } from 'react';
import RequestService from '../../services/RequestService';
import t from '../../lang/t';
import { tabTypes } from './checkModalTypes';
import Button from '../../components/Button/Button';
import CheckDescription from './components/CheckDescription';
import CheckError from './components/CheckError';
import CheckSuccess from './components/CheckSuccess';
import Modal from '../../components/Modal/Modal';
import MultiCheckTab from './components/MultiCheckTab';
import SingleCheckTab from './components/SingleCheckTab';
import TabSwitcher from './components/TabSwitcher';
import { addressSlice } from '../../store/slices';
import { useDispatch } from 'react-redux';
import './CheckModal.scss';

const CheckModal = ({ checkAddress, onClose, setNeedUpdate }) => {
    const [tab, setTab] = useState(tabTypes.singleCheck.key);

    const [isChecking, setIsChecking] = useState(false);
    const [isCheckingError, setIsCheckingError] = useState(false);
    const [isChecked, setIsChecked] = useState(false);

    const [address, setAddress] = useState(checkAddress?.Address ?? '');
    const [addressPercent, setAddressPercent] = useState(0);

    const [merchants, setMerchants] = useState([]);
    const [addresses, setAddresses] = useState([]);
    const dispatch = useDispatch()

    useEffect(() => {
        const fetchData = async () => {
            const addressesResponse = await RequestService.GetUncheckedAddresses();
            setMerchants(addressesResponse['Data'].filter((item) => item['Addresses'].length !== 0));
        };

        if (tabTypes.multiCheck.key) {
            fetchData().then();
        }
    }, [tab]);

    const onChangeTab = (tab) => {
        setTab(tab);

        setAddress('');
        setAddresses([]);

        setIsCheckingError(false);
        setIsChecked(false);
    };

    const onDeposit = () => onClose();

    const onSingleCheck = async () => {
        setIsChecking(true);

        const response = await RequestService.CheckAddresses([address]);
        setIsChecking(false);

        if (response['Status'] !== 'Success') {
            setIsCheckingError(true);
            return;
        }

        setAddressPercent(response['Results'][0]['Riskscore'] * 100);
        setIsChecked(true);
        setNeedUpdate(true);
    };

    const onSingleView = async () => {
        const { setRiskAddress } = addressSlice.actions;

        let newCheckAddress = Object.assign({}, checkAddress)
        newCheckAddress.RiskScore = addressPercent / 100;

        dispatch(setRiskAddress(newCheckAddress));
        onClose();
    };

    const onMultiCheck = async () => {
        setIsChecking(true);

        const response = await RequestService.CheckAddresses(addresses.map((item) => item['Address']));
        setIsChecking(false);

        if (response['Status'] !== 'Success') {
            setIsCheckingError(true);
            return;
        }

        const newAddresses = Object.assign([], addresses);

        for (let i = 0; i < newAddresses.length; i++) {
            const resultIndex = response['Results'].findIndex((item) => item['Address'] === newAddresses[i]['Address']);
            newAddresses[i].Percent = response['Results'][resultIndex]['Riskscore'] * 100;
        }

        setAddresses(newAddresses);
        setIsChecked(true);
        setNeedUpdate(true);
    };

    const singleCheckTabProps = { address, setAddress, addressPercent, isChecked };
    const multiCheckTabProps = { isChecked, addresses, setAddresses, merchants };

    const isSingleCheck = tab === tabTypes.singleCheck.key;
    const isMultiCheck = tab === tabTypes.multiCheck.key;

    return (
        <div className="checkModal">
            <Modal title={t('Check crypto addresses')} onClose={onClose}>
                <TabSwitcher tab={tab} onChangeTab={onChangeTab} />
                {isSingleCheck && (
                    <SingleCheckTab { ... singleCheckTabProps } />
                )}
                {isMultiCheck && (
                    <MultiCheckTab { ...multiCheckTabProps } />
                )}
                {(!isCheckingError && !isChecked) && (
                    <CheckDescription
                        isSingleCheck={isSingleCheck}
                        addresses={addresses}
                    />
                )}
                {isCheckingError && (
                    <CheckError />
                )}
                {isChecked && (
                    <CheckSuccess/>
                )}
                {(!isCheckingError && !isChecked) && (
                    <Button
                        color="green"
                        disabled={(!address && addresses.length === 0) || isChecking}
                        onClick={isSingleCheck ? onSingleCheck : onMultiCheck}
                    >
                        {t('Check')}
                    </Button>
                )}
                {isCheckingError && (
                    <Button color="green" onClick={onDeposit}>{t('Close')}</Button>
                )}
                {isSingleCheck && isChecked && (
                    <Button color="green" onClick={onSingleView}>{t('View Details')}</Button>
                )}
            </Modal>
        </div>
    );
};

export default CheckModal;
