import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import * as PropTypes from 'prop-types';
import t from '../../../../../lang/t';
import { addressSlice } from '../../../../../store/slices/address';
import config from '../../../../../AppConfig';
import HelperService from '../../../../../services/HelperService';
import NotificationService from '../../../../../services/NotificationService';
import RequestService from '../../../../../services/RequestService';
import StorageService from '../../../../../services/StorageService';
import DeleteModal from '../../../../../modals/DeleteModal/DeleteModal';
import Icon, { Icons } from '../../../../../components/Icon/Icon';
import RiskScoreCell from '../../../../../components/RiskScoreCell/RiskScoreCell';
import Table from '../../../../../components/Table/Table';

const { subRoutesGenerator } = config;
const { setQuarantineAddress } = addressSlice.actions;

const QuarantineTab = ({ onCheck, onWithdraw }) => {
    const [isLoading, setLoading] = useState(false);
    const [total, setTotal] = useState(0);
    const [data, setData] = useState([]);

    const [page, setPage] = useState(1);
    const [count, setCount] = useState(10);
    const [search, setSearch] = useState('');
    const [sort, setSort] = useState({ by: 'Id', desc: true });
    const [filters, setFilters] = useState([]);

    const riskAddress = useSelector((state) => state['address'].riskAddress);
    const quarantineAddress = useSelector((state) => state['address'].quarantineAddress);
    const dispatch = useDispatch();
    const history = useHistory();

    const onQuarantine = async () => {
        const response = await RequestService.QuarantineRemove(quarantineAddress['Id']);
        dispatch(setQuarantineAddress(null));

        if (response['Status'] !== 'Success') {
            NotificationService.Push(t('Unable to remove from quarantine'), true);
            return;
        }

        NotificationService.Push(t('Successfully removed from quarantine'), false);
        onPaginate().then();
    };

    const columns = [
        {
            Field: 'Balance',
            Title: t('Balance'),
            Width: '140px',
            Filter: {
                Type: 2
            },
            Cell: (data) => (
                <div className="tableBodyItemCellBalance">
                    <Icon
                        type={Icons[data['Currency']]}
                        width={16}
                        height={16}
                    />
                    <p>
                        <b>
                            {HelperService.FormatCrypto(data['Balance'], data['Currency'] === 'USDT-TRC20' && 2)}
                            {data['Currency'] === 'USDT-TRC20' ? <br/> : <>&nbsp;</>}
                            {data['Currency']}
                        </b>
                        <span>
                            {HelperService.FormatFiat(data['BalanceUSD'])}
                        </span>
                    </p>
                </div>
            ),
        },
        {
            Field: 'RiskScore',
            Title: t('Risk level'),
            Description: t('The indicator of the legality of funds on the crypto address. The higher the percentage, the worth.'),
            Width: '180px',
            Cell: (data) => (
                <RiskScoreCell
                    address={data}
                    isQuarantine
                    riskScore={data['RiskScore']}
                    lastCheckDate={data['LastCheckDate']}
                    onCheck={() => onCheck(data)}
                    onQuarantine={() => dispatch(setQuarantineAddress(data))}
                />
            ),
        },
        {
            Field: 'MerchantId',
            Title: t('Merchant'),
            Width: '120px',
            Filter: {
                Type: 3,
                Items: StorageService.Merchants
            },
            Cell: (data) => (
                <div
                    className="tableBodyItemCellLink"
                    onClick={() => history.push(subRoutesGenerator.merchantsId(data['MerchantId']))}
                >
                    { data['MerchantName']}
                </div>
            ),
        },
        {
            Field: 'AdditionalInfo',
            Title: t('Description'),
            Description: t('The description that was written when creating the crypto address.'),
            Width: '300px',
            Filter: {
                Type: 1
            },
        },
        {
            Field: 'Address',
            Title: t('Crypto address'),
            Width: '340px',
            Filter: {
                Type: 1
            },
        },
        {
            Width: '16px',
            Cell: (data) => (
                <div className="tableBodyItemCellActions">
                    <Icon
                        type={Icons.WITHDRAW}
                        width={16}
                        height={16}
                        onClick={() => onWithdraw(data['Currency'], data['MerchantId'], data['Address'])}
                    />
                </div>
            ),
        }
    ];

    const onPaginate = async (tablePage = page, tableCount = count, tableSearch = search, tableSort = sort, tableFilters = filters) => {
        setLoading(true);

        setPage(tablePage);
        setCount(tableCount);
        setSearch(tableSearch);
        setSort(tableSort);
        setFilters(tableFilters);

        const response = await RequestService.GetQuarantine(tablePage, tableCount, tableSearch, tableSort, tableFilters);

        setTotal(response['Total']);
        setData(response['Addresses']);

        setLoading(false);
    };

    const isActiveRowCallback = (row) => riskAddress ? riskAddress.Id === row.Id : false;

    const tableProps = { isLoading, columns, page, count, search, sort, filters, total, data, onPaginate, isActiveRowCallback };

    return (
        <>
            <Table id="quarantineTable" {...tableProps} />
            {quarantineAddress && (
                <DeleteModal
                    title={t('Remove from quarantine')}
                    subtitle={t('Are you sure you want to remove the address from quarantine?')}
                    description={quarantineAddress['Address']}
                    onAccept={onQuarantine}
                    onClose={() => dispatch(setQuarantineAddress(null))}
                />
            )}
        </>
    );
};

QuarantineTab.propTypes = {
    onCheck: PropTypes.func.isRequired,
    onWithdraw: PropTypes.func.isRequired,
};

export default QuarantineTab;