import React from 'react';
import * as PropTypes from 'prop-types';
import CurrencyIcon from '../../../../../../containers/RootModal/components/Withdrawal/components/CurrencyIcon';
import HelperService from '../../../../../../services/HelperService';
import Types from '../../../../../../classes/Types';
import './GasStation.scss';

const precisions = Types.gasStationPrecisions;

const GasStation = ({ currencyName, currencySymbol, balance, onOpenEditModal }) => {
    const isEnableEditButton = false;
    const onEditStation = () => onOpenEditModal();

    const EditButton = () => {
        return (
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                fill="none"
                viewBox="0 0 16 16"
                className="EditButton"
                onClick={onEditStation}
            >
                <path
                    fill="#0455A3"
                    d="M1 12.084V15h2.916l8.601-8.601-2.916-2.916L1 12.083zm13.773-7.94a.774.774 0 000-1.097l-1.82-1.82a.774.774 0 00-1.097 0l-1.423 1.424 2.916 2.916 1.424-1.423z"
                    opacity="0.5"
                />
            </svg>
        );
    };

    return (
        <div className="GasStation">
            <div className="GasStation__Name">
                <div className="Icon"><CurrencyIcon name={currencyName}/></div>
                <div className="Name">{currencyName}</div>
            </div>
            <div className="GasStation__Details">
                <div className="Amount">
                    <div
                        className="Value">{HelperService.FormatCrypto(balance, precisions[currencySymbol] || precisions.BASE)}</div>
                    <div className="Currency">{currencySymbol}</div>
                </div>
                {isEnableEditButton && <EditButton/>}
            </div>
        </div>
    );
};

GasStation.defaultProps = {
    currencyName: 'Tron',
    currencySymbol: 'TRX',
    balance: 0,
    onOpenEditModal: () => {},
};

GasStation.propTypes = {
    currencyName: PropTypes.oneOf(['Tron']),
    currencySymbol: PropTypes.oneOf(['TRX']),
    balance: PropTypes.number,
    onOpenEditModal: PropTypes.func,
};

export default GasStation;
