import React from 'react';
import * as PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import t from '../../../../lang/t';
import config from '../../../../AppConfig';
import ButtonIcon from '../../../../components/ButtonIcon';
import PageHeadTabs from '../../../../components/PageHeadTabs';

const { routes, subRoutesGenerator } = config;

const MerchantsHead = ({ onCreateModalOpen, tab }) => {
    const tabOptions = [
        { to: routes.merchants, value: t('Merchants'), isActive: () => (tab === undefined) },
        { to: subRoutesGenerator.merchantsTab('balance-sheet'), value: t('Balance sheet') },
    ];

    return (
        <div className="merchantsHead">
            <div className="merchantsHead__header">
                <PageHeadTabs tabOptions={tabOptions} />
            </div>
            {tab === undefined &&
                <div className="merchantsHeadContainer">
                    <Button
                        variant="contained"
                        className="merchantsHeadContainerAdd text-initial text-padding"
                        onClick={onCreateModalOpen}
                    >
                        <ButtonIcon type="merchant" />
                        <span>
                            {t('Add merchant')}
                        </span>
                    </Button>
                </div>
            }
        </div>
    )
};

MerchantsHead.propTypes = {
    onCreateModalOpen: PropTypes.func.isRequired,
};

export default MerchantsHead;
