import React, { useState, useEffect, useCallback } from 'react';
import classNames from 'classnames';
import t from '../../../../lang/t';
import Filter from '../../../Filter/Filter';
import Tooltip from '../../../Tooltip/Tooltip';
import Icon, { Icons } from '../../../Icon/Icon';
import Checkbox from '../../../../components/Checkbox/Checkbox';
import './TableContent.scss';
import RequestService from '../../../../services/RequestService';
import HelperService from '../../../../services/HelperService';

const TableContent = ({ setNeedUpdateAddresses, columns, data, page, count, search, sort, filters, total, isLoading, tableSettings, onPaginate, 
    selectedItems, setSelectedItems, isActiveRowCallback, balancesData, selectedWalletIndex }) => {
    const [isInitialized, setIsInitialized] = useState(false);
    const [leastOneHidden, setLeastOneHidden] = useState(false)

    const allSelected = selectedItems && selectedItems.every(item => item)
    const someSelected = selectedItems && selectedItems.some(item => item)
    const selectItem = index => {
        const itemIndex = (page - 1) * count + index;
        setSelectedItems([...selectedItems.slice(0, itemIndex), !selectedItems[itemIndex], ...selectedItems.slice(itemIndex + 1)]);
    }

    const selectAll = () => {
        setSelectedItems(new Array(total).fill(!allSelected))
    }

    useEffect(() => {
        if (selectedItems) {
            setLeastOneHidden(false)
            data.map((item, index) => {
                if (selectedItems[(page - 1) * count + index] && item?.IsHidden) return setLeastOneHidden(true);
                return null;
            })
        }
    }, [selectedItems]) // eslint-disable-line react-hooks/exhaustive-deps

    const hiddeAddresses = async () => {
        let arraySend = [];

        data.map((item, index) => {
            if(selectedItems[(page - 1) * count + index]) return arraySend.push({Id: item.Id, IsHidden: leastOneHidden ? false : true});
            return null;
        });

        const result = arraySend.map(async item => {
            await RequestService.UpdateVisibility(item.Id, item.IsHidden);
        });

        await Promise.all(result);

        setSelectedItems(new Array(total).fill(false));
        setNeedUpdateAddresses(prev => !prev);
    };

    useEffect(() => {
        if (isInitialized) {
            return;
        }

        const params = new URLSearchParams(document.location.search);

        const requestPage = params.get('page');
        const requestCount = params.get('count');

        if (requestPage && requestCount) {
            onPaginate(Number(requestPage), Number(requestCount));
        } else {
            onPaginate(page, count);
        }

        setIsInitialized(true);
    }, [isInitialized, page, count, onPaginate]);

    const isActiveRow = (row) => isActiveRowCallback(row);

    const onTableFilter = useCallback((filter) => {
        const filtersData = Object.assign([], filters);
        const filterIndex = filtersData.findIndex((item) => item.Column === filter.Column);

        if (filterIndex !== -1 && filter.Type) {
            filtersData[filterIndex] = filter;
        } else if (filterIndex !== -1) {
            filtersData.splice(filterIndex, 1);
        } else {
            filtersData.push(filter);
        }

        onPaginate(1, count, search, sort, filtersData);
    }, [count, search, sort, filters, onPaginate]);

    const onTableSort = useCallback((column) => {
        const sortData = Object.assign({}, sort);

        if (sortData.by === column.Field) {
            sortData.desc = !sortData.desc;
        } else {
            sortData.by = column.Field;
            sortData.desc = true;
        }

        onPaginate(page, count, search, sortData, filters);
    }, [page, count, search, sort, filters, onPaginate]);

    //todo translate
    return (
        <div className="tableContent">
            <div className="tableHead">
                {selectedItems && selectedItems.length > 0 &&
                    <div className="block-select-all">
                        <Checkbox id="selectAll" className="tableCheckBox" checked={allSelected} onChange={selectAll} />
                        <div className="tooltip-select-all">{allSelected ? t('Unselect all') : t('Select all')}</div>
                    </div>
                }
                {
                    someSelected
                        ?   <div className="selecdedItemsActions">
                                <div className="selecdedItemsActions__hiddeAddresses" onClick={hiddeAddresses}>{leastOneHidden ? t('Show addresses') : t('Hide addresses')}</div>
                                {/* <div className="selecdedItemsActions__checkAdresses">{t('Check crypto addresses')}</div>
                                <div className="selecdedItemsActions__sendToQuarantine">{t('Send to Quarantine')}</div> */}
                            </div> 
                        :
                    tableSettings.map((tableSetting, tableSettingIndex) => {
                        if (!tableSetting.isVisible) {
                            return null;
                        }

                        const columnIndex = columns.findIndex((item) => item.Title?.props?.id === tableSetting.title);

                        if (columnIndex === -1) {
                            return null;
                        }

                        const column = columns[columnIndex];

                        return (
                            <div key={tableSettingIndex} className="tableHeadColumn"
                                style={{ minWidth: column.Width, width: column.Width }}>
                                {column.Title &&
                                    <>
                                        {column.Filter &&
                                            <Filter type={column.Filter.Type} items={column.Filter.Items} field={column.Field}
                                                onFilter={onTableFilter}
                                                value={filters.find((filter) => filter.Column === column.Field) ?? {}} />}
                                        {column.Field !== 'BalanceBTC' && column.Field !== 'BalanceUSDT' 
                                            ?
                                            <>
                                                <span onClick={() => onTableSort(column)}>{column.Title}</span>
                                                <Icon type={Icons.SORT} width={10} height={20} onClick={() => onTableSort(column)}
                                                    className={classNames('tableHeadColumnSort', sort.by === column.Field ? sort.desc ? 'tableHeadColumnSortDesc' : 'tableHeadColumnSortAsc' : '')} 
                                                />
                                            </>
                                            : <span className="withoutCursor">{column.Title}</span>
                                        }
                                        {column.Description && <Tooltip position={column.tooltipPosition ? column.tooltipPosition : "left"} text={column.Description} />}
                                    </>}
                            </div>
                        );
                    })}
                {tableSettings.length !== columns.length &&
                    <div className="tableHeadColumn" style={{
                        minWidth: columns[columns.length - 1].Width,
                        width: columns[columns.length - 1].Width
                    }} />}
            </div>
            <div className="tableBody">
                {isInitialized && !isLoading && data.length === 0 &&
                    <div className="tableBodyEmpty">
                        <Icon type={Icons.TABLE_EMPTY} width={200} height={200} />
                        <p>{t('No information available at the moment')}</p>
                    </div>}

                {data.map((item, itemIndex) => {
                    return (
                        <div key={itemIndex} className={classNames('tableBodyItemWrapper', { 'tableBodyItemWrapper-active': isActiveRow(item), 'hidden': item.IsHidden})}>
                            <div className="tableBodyItem">
                                {selectedItems &&
                                    <Checkbox id={'select' + itemIndex} className="tableCheckBox"
                                        checked={selectedItems[(page - 1) * count + itemIndex]} onChange={() => { selectItem(itemIndex) }} />}

                                {tableSettings.map((tableSetting, tableSettingIndex) => {
                                    if (!tableSetting.isVisible) {
                                        return null;
                                    }

                                    const columnIndex = columns.findIndex((item) => item.Title?.props?.id === tableSetting.title);

                                    if (columnIndex === -1) {
                                        return null;
                                    }

                                    const column = columns[columnIndex];

                                    return (
                                        <div key={itemIndex + '' + tableSettingIndex} className='tableBodyItemCell'
                                            style={{ minWidth: column.Width, width: column.Width }}>
                                            {column.Cell ? column.Cell(item) : item[column.Field] ? item[column.Field] : '-'}
                                        </div>
                                    );
                                })}
                                {tableSettings.length !== columns.length &&
                                    <div className="tableBodyItemCell" style={{
                                        minWidth: columns[columns.length - 1].Width,
                                        width: columns[columns.length - 1].Width
                                    }}>
                                        {columns[columns.length - 1].Cell(item)}
                                    </div>}
                            </div>
                        </div>
                    );
                })}
            </div>
            {balancesData 
                &&
                <div className="tableFooter">
                    <div>{t('Total:')}</div>
                    {balancesData.list.map((item, index) => {
                        if (selectedWalletIndex === null || selectedWalletIndex === index) {
                            return ( 
                                <div className="item" key={index}>
                                    <Icon
                                        type={Icons[item.Symbol]}
                                        width={16}
                                        height={16}
                                    />
                                    <span>{HelperService.FormatCrypto(item.Amount, item.Symbol === 'USDT-TRC20' && 2)}</span>
                                    <span>{item.Symbol}</span>
                                </div>
                            )
                        }
                        return null;
                    })}
                </div>
            }
        </div>
    );
};

export default TableContent;