import BaseModel from './base';

export default class RootModalModel extends BaseModel {
    constructor(initData) {
        super();

        this.id = '';
        this.className = '';
        this.isOpen = false;
        this.isShowLoader = false;
        this.title = '';
        // one of ['withdrawal', 'receiving']
        this.tag = '';
        this.outsideParams = {};

        this.copyFrom(initData);
    }
}
