import React from 'react';
import t from '../../../../lang/t';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import Table from '../../../../components/Table/Table';

const AdministratorsTable = (props) => {
    const { isLoading, page, count, search, sort, filters, total, data, onPaginate, onEditModalOpen, onDeleteModalOpen } = props;

    const columns = [
        {
            Field: 'Name',
            Title: t('Name'),
            Width: '120px',
            Filter: { Type: 1 }
        },
        {
            Field: 'Email',
            Title: t('Email'),
            Width: '180px',
            Filter: { Type: 1 }
        },
        /*{
            Field: 'Rights',
            Title: t('Permissions'),
            Width: '400px',
            // Filter: { Type: 3, Items: StorageService.Rights },
            Cell: (data) => (
                <>
                    {data.Rights.length === 0 && '-'}
                    {data.Rights.map((right, index) => <span key={index}>{right.Name}{index + 1 < data.Rights.length ? ', ' : ''}</span>)}
                </>
            )
        },*/
        {
            Width: '48px',
            Cell: (data) => (
                <div className="tableBodyItemCellActions">
                    <Icon type={Icons.EDIT_PEN} width={16} height={16} onClick={() => onEditModalOpen(data)} />
                    <Icon className="tableBodyItemCellActionsTrash" type={Icons.TRASH} width={16} height={16}
                        onClick={() => onDeleteModalOpen(data)} />
                </div>
            )
        }
    ];

    const tableProps = { isLoading, columns, page, count, search, sort, filters, total, data, onPaginate };

    return (
        <div className="administratorsTable">
            <div className="tableWrapper">
                <Table id="administratorsTable" {...tableProps} />
            </div>
        </div>
    );
};

export default React.memo(AdministratorsTable, (props, nextProps) => {
    if(JSON.stringify(props.data) === JSON.stringify(nextProps.data)) {
        return true;
    }
});
