import React from 'react';
import { Link } from 'react-router-dom';
import { QRCodeSVG } from 'qrcode.react';
import Button from '@mui/material/Button';
import t from '../../../../../../lang/t';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import ReceivingAddressField from '../ReceivingAddressField';

import './ReceivingResults.scss';

const ReceivingResults = ({ createdAddress, isNew, finishReceiving }) => {

    return (
        <div className="ReceivingResults">
            <div className="ReceivingResults__qr">
                <QRCodeSVG value={createdAddress.Address} size={190} level="Q" imageSettings={{ src: '', height: 55, width: 55, excavate: true }} />
                <Icon className="ReceivingResults__qr-currnecy" type={Icons[createdAddress.Currency]} width={48} height={48} />
            </div>
            <ReceivingAddressField createdAddress={createdAddress} />
            <div className="ReceivingResults__merchant">
                <span>{isNew ? t('Crypto address created in') : t('Crypto address is in')} </span>
                <Link to={`/merchants/${createdAddress.MerchantId}`} className="ReceivingResults__merchant-name" onClick={finishReceiving}>
                    {createdAddress.MerchantName}
                </Link>
            </div>
            <div>
                <Button className="ReceivingResults__finish-button" variant="contained" onClick={finishReceiving}>{t('Finish')}</Button>
            </div>
        </div>
    );
};

export default ReceivingResults;