import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    transactions: [],
};

export const transactionSlice = createSlice({
    name: 'transaction',
    initialState,
    reducers: {
        getAllTransactions: (state, action) => {
            const { payload: transactions = [] } = action;
            state.transactions = transactions;
        },
    },
});
