import React from 'react';
import PropTypes from 'prop-types';
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd';

export const Reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);

    result.splice(endIndex, 0, removed);
    return result;
};

export const DragAndDrop = ({ children, onDragEnd }) => {
    return (
        <DragDropContext onDragEnd={onDragEnd}>
            <Droppable droppableId="droppable">
                {(provided) => (
                    <div ref={provided.innerRef} {...provided.droppableProps}>
                        {children}
                        {provided.placeholder}
                    </div>
                )}
            </Droppable>
        </DragDropContext>
    );
};

DragAndDrop.defaultProps = {
    children: null,
    onDragEnd: () => {}
};

DragAndDrop.propTypes = {
    children: PropTypes.node.isRequired,
    onDragEnd: PropTypes.func.isRequired
};

export const DragAndDropItem = ({ index, children }) => {
    return (
        <Draggable index={index} draggableId={'draggable-' + index}>
            {(provided) => (
                <div ref={provided.innerRef} style={{ ...provided.draggableProps.style }} {...provided.draggableProps}
                     {...provided.dragHandleProps}>
                    {children}
                </div>
            )}
        </Draggable>
    );
};

DragAndDropItem.defaultProps = {
    index: 0,
    children: null
};

DragAndDropItem.propTypes = {
    index: PropTypes.number.isRequired,
    children: PropTypes.node.isRequired
};