import classNames from "classnames";
import { useState } from "react";
import Icon, {Icons} from "../../../Icon/Icon";

const NotificationButton = ({data, text}) => {
    const [visibleNotice, setVisibleNotice] = useState(false)

    return (
        <div className="block-notification">
            <Icon
                type={Icons.COPY}
                width={16}
                height={16}
                onClick={() => navigator.clipboard.writeText(data).then(() => {
                    setVisibleNotice(true); 
                    setTimeout(() => setVisibleNotice(false), 3000)
                })}
            />
            <div className={ classNames("copy-notice", {'visible' : visibleNotice})}>{text}</div>
        </div>
    )
};
export default NotificationButton;