import React, { useState, useEffect, useCallback } from 'react';
import { Switch, Redirect } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { I18nPropvider } from './lang';
import { appSlice } from './store/slices';
import { authRoutes, mainRoutes, RouteWithSubRoutes } from './routes';
import config from './AppConfig';
import StorageService from './services/StorageService';
import RequestService from './services/RequestService';
import Background from './components/Background/Background';
import Loader from './components/Loader/Loader';
import Menu from './containers/Menu/Menu';
import Notifications from './components/Notifications/Notifications';
import RootModal from './containers/RootModal';
import ErrorCatcher from './components/ErrorCatcher';

import './App.scss';
import HelperService from './services/HelperService';

const { setToken } = appSlice.actions;
const { routes } = config;

const App = () => {
    const [isFetched, setIsFetched] = useState(false);

    const token = useSelector((state) => state.app.token);
    const lang = useSelector((state) => state.app.lang);
    const dispatch = useDispatch();

    const onLogout = useCallback(() => {
        HelperService.RemoveTokenFromStorage();
        dispatch(setToken(null));
    }, [dispatch]);

    useEffect(() => {
        const fetchData = async () => {
            const token = HelperService.GetTokenFromStorage();

            if (token) {
                const checkTokenResponse = await RequestService.CheckToken(token);

                if (checkTokenResponse['Status'] === 'Success') {
                    RequestService.Token = token;
                    RequestService.OnLogout = onLogout;
                    dispatch(setToken(token));

                    setToken(token);
                    await StorageService.Initialize();
                } else {
                    HelperService.RemoveTokenFromStorage();
                }
            }

            setTimeout(() => setIsFetched(true), 2000);
        };
        fetchData();
    }, [dispatch, onLogout]);

    return (
        <div className="app">
            <I18nPropvider locale={lang}>
                <Notifications isAuth={token ?? false}/>
                {(!isFetched || !token) && (<Background/>)}
                {!isFetched && (<Loader/>)}
                {(isFetched && !token) && (
                    <Switch>
                        {authRoutes.map((route) => (
                            <RouteWithSubRoutes key={route.id} {...route} />
                        ))}
                        <Redirect from="*" to={routes.main} />
                    </Switch>
                )}
                {(isFetched && token) && (
                    <ErrorCatcher redirectToAddress={'auth'} actionBeforeRedirect={onLogout}>
                        <Menu onLogout={onLogout}>
                            <Switch>
                                {mainRoutes.map((route) => (
                                    <RouteWithSubRoutes key={route.id} {...route} />
                                ))}
                                <Redirect from="*" to={routes.main} />
                            </Switch>
                        </Menu>
                    </ErrorCatcher>
                )}
                <RootModal/>
            </I18nPropvider>
        </div>
    );
};
export default App;
