import React from 'react';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../../../../../lang/t'
import TransactionIcon from '../TransactionIcon';
import './TransactionError.scss';
import { useSelector } from 'react-redux';

const TransactionError = () => {
    const modal = useSelector((state) => state?.app?.modal);
    const errorObject = modal.outsideParams.error ? modal.outsideParams.error : {Code: 'Error'};
    const errorCode = errorObject.EntityName ? errorObject.Code + errorObject.EntityName : errorObject.Code;
    const intl = useIntl()

    return (
        <div className="TransactionError">
            <div className="TransactionError__Icon">
                <TransactionIcon type="no" />
            </div>
            <div className="TransactionError__Title">
                {t('An error occurred while the transaction creation')}
            </div>
            <div className="TransactionError__Label">
                { intl.formatMessage({ id: errorCode }) !== errorCode ? t(errorCode) : t('Try again')}
            </div>
        </div>
    );
};

export default TransactionError;