import React, { useState, useRef } from 'react';
import Input from '../../../../components/Input/Input';
import Button from '../../../../components/Button/Button';
import useIntl from 'react-intl/lib/src/components/useIntl';
import CurrencyIcon from '../Withdrawal/components/CurrencyIcon';
import t from '../../../../lang/t';
import { useOnClickOutside } from 'use-hooks';
import { useSelector, useDispatch } from 'react-redux';
import Icon, { Icons } from '../../../../components/Icon/Icon';
import RequestService from '../../../../services/RequestService';
import NotificationService from '../../../../services/NotificationService';
import { appSlice } from '../../../../store/slices';
import Types from '../../../../classes/Types';

import './GasStationModal.scss';

const GasStationModal = () => {
    const intl = useIntl();
    const dispatch = useDispatch();
    const dropdownRef = useRef(null);

    const currencies = Types.gasStationCurrencies;
    const tronRootAccount = useSelector((state) => state?.app?.tronRootAccount);
    const modal = useSelector((state) => state?.app?.modal);
    const [currency, setCurrency] = useState(currencies[0]);
    const [currencyDropdownStatus, setCurrencyDropdownStatus] = useState(false);
    const [isValuesTouched, setValuesStatus] = useState(false);
    const [values, setValues] = useState({
        address: tronRootAccount?.OwnerAddress || '',
        privateKey: tronRootAccount?.OwnerPrivateKey || '',
    });
    const [submitStatus, setSubmitStatus] = useState(false);
    const [serverError, setServerError] = useState(false);
    const addressError = values?.address === '';
    const privateKeyError = values?.privateKey === '';
    const submitButtonDisabled = addressError || privateKeyError || submitStatus;

    const toggleCurrencyDropdown = () => setCurrencyDropdownStatus(!currencyDropdownStatus);

    useOnClickOutside(dropdownRef, () => setCurrencyDropdownStatus(false));

    const onSelectCurrency = (currency) => {
        setCurrency(currency);
        setCurrencyDropdownStatus(false);
    };

    const onChange = (e, name = null) => {
        if (!name) return;
        const { value } = e.target;
        setValues({
            ...values,
            [name]: value,
        });
        setValuesStatus(true);
    };

    const onToggleLoader = (loaderStatus = false) => {
        setSubmitStatus(loaderStatus);
        dispatch(appSlice.actions.updateModalStructure({
            ...modal,
            isShowLoader: loaderStatus,
        }));
    };

    const onSubmit = async (e) => {
        setServerError(false);
        onToggleLoader(true);

        e.preventDefault();
        const tronRootAddress = await RequestService.SetTronRootAccount(values?.address, values?.privateKey);

        if (tronRootAddress?.Status === Types.requestStatuses.success) {

            // Get Tron Root Account
            const tronRootAccount = await RequestService.GetTronRootAccount();

            if (tronRootAccount?.Status !== Types.requestStatuses.success) {
                return;
            }

            dispatch(appSlice.actions.setTronRootAccount(tronRootAccount));

            NotificationService.Push(t('Saved'), false);
        } else {
            setServerError(true);
            NotificationService.Push(t('Incorrect crypto address or private key'), true);
        }

        onToggleLoader(false);
    };

    return (
        <form
            id="gasStationForm"
            name="gasStationForm"
            autoComplete="off"
            onSubmit={onSubmit}
        >
            <div className="input-group">
                <Input
                    id="currency"
                    name="currency"
                    type="text"
                    label={intl.formatMessage({ id: 'Select gas currency' })}
                    value=" "
                />
                <div className="currency-value" onClick={toggleCurrencyDropdown}>
                    <CurrencyIcon name={currency && currency?.Name}/>
                    <span className="currency-symbol">{currency && currency?.Symbol}</span>
                    <Icon className="dropdown-icon" type={Icons.SELECT_ARROW} width={16} height={16}/>
                </div>
                {currencyDropdownStatus && <div className="currency-dropdown" ref={dropdownRef}>
                    <ul className="currency-list">
                        {Array.isArray(currencies) && currencies.length > 0 && currencies.map((currency) => (
                            <li className="currency-item" key={currency?.Id} onClick={() => onSelectCurrency(currency)}>
                                <span className="item-icon"><CurrencyIcon name={currency?.Name}/></span>
                                <span className="item-symbol">{currency?.Symbol}</span>
                            </li>
                        ))}
                    </ul>
                </div>}
            </div>
            <div className="input-group">
                <Input
                    id="address"
                    name="address"
                    type="text"
                    label={intl.formatMessage({ id: 'Crypto address' })}
                    placeholder={intl.formatMessage({ id: 'Enter crypto address' })}
                    value={values?.address}
                    error={(addressError || serverError) && isValuesTouched}
                    onChange={(e) => onChange(e, 'address')}
                />
            </div>
            <div className="input-group">
                <Input
                    id="privateKey"
                    name="privateKey"
                    type="password"
                    label={intl.formatMessage({ id: 'Private key' })}
                    placeholder={intl.formatMessage({ id: 'Private key' })}
                    value={values?.privateKey}
                    error={(privateKeyError || serverError) && isValuesTouched}
                    autoComplete="new-password"
                    onChange={(e) => onChange(e, 'privateKey')}
                />
            </div>
            <Button type="submit" disabled={submitButtonDisabled}>{t('Save')}</Button>
        </form>
    );
};

export default GasStationModal;
