import React, { useState, useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../lang/t';
import Modal from '../../components/Modal/Modal';
import Icon, { Icons } from '../../components/Icon/Icon';
import Tooltip from '../../components/Tooltip/Tooltip';
import Select from '../../components/Select/Select';
import Input from '../../components/Input/Input';
import Checkbox from '../../components/Checkbox/Checkbox';
import Button from '../../components/Button/Button';
import StorageService from '../../services/StorageService';
import RequestService from '../../services/RequestService';
import './WithdrawModal.scss';

const WithdrawModal = ({ withdrawCurrency, withdrawMerchant, withdrawAddress, onClose }) => {
    const [stage, setStage] = useState(0);
    const [quotesTimeout, setQuotesTimeout] = useState(0);

    const [isInit, setIsInit] = useState(false);
    const [isAccept, setIsAccept] = useState(false);

    const [currency, setCurrency] = useState({});
    const [merchant, setMerchant] = useState({});

    const [sendersData, setSendersData] = useState([]);
    const [senders, setSenders] = useState([]);
    const [allowAmount, setAllowAmount] = useState('');

    const [recipient, setRecipient] = useState('');
    const [cryptoAmount, setCryptoAmount] = useState('0');
    const [fiatAmount, setFiatAmount] = useState('0');
    const [commission, setCommission] = useState('0');

    const [isProcess, setIsProcess] = useState(false);
    const [transactionId, setTransactionId] = useState('');

    const intl = useIntl();

    const onChangeSenders = useCallback((addresses) => {
        let addressesBalance = 0;

        if (addresses.length !== 0) {
            addresses.map((address) => {
                addressesBalance += address['balance'];
                return address;
            });
        } else {
            sendersData.map((address) => {
                addressesBalance += address['balance'];
                return address;
            });
        }

        setAllowAmount(addressesBalance.toFixed(8));
        setSenders(addresses);
    }, [sendersData]);
    const onChangeAmount = useCallback(async (cAmount, fAmount) => {
        cAmount !== cryptoAmount ? setCryptoAmount(cAmount) : setFiatAmount(fAmount);

        clearTimeout(quotesTimeout);
        const timeout = setTimeout(async () => {
            const quotes = await RequestService.GetQuotes();
            const quote = quotes['Quotes'].find((quote) => quote['Cryptocurrency'] === currency.value)['Price'];

            cAmount !== cryptoAmount
                ? setFiatAmount((cAmount * quote).toFixed(2))
                : setCryptoAmount((fAmount / quote).toFixed(8));
        }, 500);
        setQuotesTimeout(timeout);
    }, [quotesTimeout, currency, cryptoAmount]);

    const onChange = useCallback(async (cryptoCurrency, merchant) => {
        setCurrency(cryptoCurrency);
        setMerchant(merchant);

        const response = await RequestService.GetAddresses(1, 999999, '', { by: 'Id', desc: true }, [
            {
                'Type': 3,
                'Column': 'Currency',
                'Items': [cryptoCurrency.id]
            },
            {
                'Type': 3,
                'Column': 'MerchantId',
                'Items': [merchant.value]
            }
        ]);

        const addresses = response['Addresses'].map((address) => {
            return { label: address.Address, value: address.Id, balance: address['Balance'] };
        });

        setSendersData(addresses);
    }, []);

    const isValid = () => {
        return !!recipient && cryptoAmount > 0 && !!commission && cryptoAmount + commission < allowAmount;
    };
    const onAccept = async () => {
        setIsProcess(true);
        const response = await RequestService.Send(merchant.value, currency.value,
            senders.map((sender) => sender.label), recipient, cryptoAmount, commission);

        if (response['Status'] !== 'Success') {
            setStage(3);
            return;
        }

        setTransactionId(response['TxId']);
        setStage(2);
    };

    useEffect(() => { onChangeSenders(sendersData) }, [sendersData, onChangeSenders]);
    useEffect(() => {
        const currency = StorageService.Cryptos.find((item) => item.value === withdrawCurrency)
            ?? StorageService.Cryptos[0];

        const merchant = StorageService.Merchants.find((item) => item.value === withdrawMerchant)
            ?? StorageService.Merchants[0];

        onChange(currency, merchant);
    }, [withdrawCurrency, withdrawMerchant, onChange]);

    if (withdrawAddress && !isInit && senders.length !== 0) {
        onChangeSenders(sendersData.filter((item) => item.label === withdrawAddress));
        setIsInit(true);
    }

    //todo translate
    return (
        <div className="withdrawModal">
            <Modal title={stage === 0 ? t('Send') : stage === 1 ? t('Confirm sending') : null} onClose={onClose}>
                {stage === 0 &&
                <div className="withdrawModalForm">
                    <Select label={t('Cryptocurrency')} options={StorageService.Cryptos} value={currency}
                            onChange={(option) => onChange(option, merchant)}/>
                    <Select label={t('Merchant')} options={StorageService.Merchants} value={merchant}
                            onChange={(option) => onChange(currency, option)}/>
                    <Select label={t('Sender\'s Crypto adress')} options={sendersData} value={senders} isMulti={true}
                            onChange={(options) => onChangeSenders(options)}/>
                    <Input type="text" label={t('Recipient\'s crypto address')} placeholder={intl.formatMessage({ id: 'Recipient\'s crypto address' })}
                           value={recipient} onChange={(e) => setRecipient(e.target.value)}/>
                    <Input type="number" label={`${t('Available amount for withdrawal')} ${currency.label}` }
                           placeholder={intl.formatMessage({ id: 'Available amount for withdrawal' })} value={allowAmount} disabled={true}
                           onChange={(e) => setAllowAmount(e.target.value)}/>
                    <Input type="number" label={`${t('Amount in')} ${currency.label}`}
                           placeholder={intl.formatMessage({ id: 'Amount in' }) + currency.label} value={cryptoAmount}
                           onChange={(e) => onChangeAmount(e.target.value, fiatAmount)}/>
                    <Input type="number" label={t('Amount in USD')} placeholder={intl.formatMessage({ id: 'Amount in USD' })}
                           value={fiatAmount} onChange={(e) => onChangeAmount(cryptoAmount, e.target.value)}/>
                    <div className="withdrawModalCommission">
                        <p>{t('Fee settings')}</p>
                        <Input type="number" label={`${t('Fee')} ${currency.label}`}
                               placeholder={intl.formatMessage({ id: 'Fee' }) + currency.label} value={commission}
                               onChange={(e) => setCommission(e.target.value)}/>
                    </div>
                    {/*<div className="withdrawModalRecommendedCommission">*/}
                    {/*    <Tooltip position="left" text="Рекоммендуемая комиссия">Рекоммендуемая комиссия</Tooltip>*/}
                    {/*    <div className="withdrawModalRecommendedCommissionContainer">*/}
                    {/*        <div className="withdrawModalRecommendedCommissionContainerItem">*/}
                    {/*            <Checkbox id="1" label="Быстро (5-30 мин)"/>*/}
                    {/*            <span>0.0004 {cryptoCurrency.label}</span>*/}
                    {/*        </div>*/}
                    {/*        <div className="withdrawModalRecommendedCommissionContainerItem">*/}
                    {/*            <Checkbox id="2" label="Средне (30-60 мин)"/>*/}
                    {/*            <span>0.0002 {cryptoCurrency.label}</span>*/}
                    {/*        </div>*/}
                    {/*        <div className="withdrawModalRecommendedCommissionContainerItem">*/}
                    {/*            <Checkbox id="3" label="Медленно (2-3 часа)"/>*/}
                    {/*            <span>0.0001 {cryptoCurrency.label}</span>*/}
                    {/*        </div>*/}
                    {/*    </div>*/}
                    {/*</div>*/}
                    <Button disabled={!isValid()} onClick={() => setStage(1)}>{t('Withdraw')}</Button>
                </div>}
                {stage === 1 &&
                <div className="withdrawModalAccept">
                    <div className="withdrawModalInfo">
                        <label>{t('Cryptocurrency')}:</label>
                        <span>{currency.label}</span>
                    </div>
                    <div className="withdrawModalInfo">
                        <label>{t('Merchant')}:</label>
                        <span>{merchant.label}</span>
                    </div>
                    {senders.length !== 0 &&
                    <div className="withdrawModalInfo">
                        <label>{t('Sender\'s Crypto address')}:</label>
                        <div className="withdrawModalInfoContainer">
                            {senders.map((address, index) => {
                                return (
                                    <p key={index} className="withdrawModalInfoAddress">{address.label}</p>
                                );
                            })}
                        </div>
                    </div>}
                    <div className="withdrawModalInfo">
                        <label>{t('Recipient\'s crypto address')}:</label>
                        <p className="withdrawModalInfoAddress">{recipient}</p>
                    </div>
                    <div className="withdrawModalInfoContainer">
                        <div className="withdrawModalInfo">
                            <label>{t('Amount')} {currency.label}:</label>
                            <span>{cryptoAmount} {currency.label}</span>
                        </div>
                        <div className="withdrawModalInfo">
                            <label>{t('Amount in USD')}:</label>
                            <span>{fiatAmount} USD</span>
                        </div>
                    </div>
                    <div className="withdrawModalInfo">
                        <label>{t('Fee')} {currency.label}:</label>
                        <Tooltip text={t('The commission amount that will be paid to the blockchain for conducting a transaction. The speed of the transaction depends on the size of the amount')} position="right">
                            {commission <= 0 ? t('Automatically') : commission + ' ' + currency.label}
                        </Tooltip>
                    </div>
                    <Checkbox id="isAccept" className="withdrawModalAcceptCheckbox"
                              label={t('I am sure that the information is correct')} checked={isAccept}
                              onChange={() => setIsAccept((prev) => !prev)}/>
                    <p className="withdrawModalNotice">{t('Withdraw - irreversible operation')}</p>
                    <Button disabled={!isAccept || isProcess} onClick={onAccept}>{t('Confirm')}</Button>
                </div>}
                {stage === 2 &&
                <div className="withdrawModalSuccess">
                    <Icon type={Icons.YES} width={84} height={84}/>
                    <h2>{t('Withdraw created successfully')}</h2>
                    <p>{t('Transaction link:')}</p>
                    <span>{transactionId}</span>
                </div>}
                {stage === 3 &&
                <div className="withdrawModalError">
                    <Icon type={Icons.NO} width={84} height={84}/>
                    <h2>{t('An error occurred while creating the withdrawal')}</h2>
                    <p>{t('Try again')}</p>
                </div>}
            </Modal>
        </div>
    );
};

WithdrawModal.defaultProps = {
    onClose: () => {}
};

WithdrawModal.propTypes = {
    onClose: PropTypes.func
};

export default WithdrawModal;