import React, { useEffect, useRef, useState } from 'react';
import './RiskSelect.scss';
import t from '../../../../../../lang/t';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import Tooltip from '../../../../../../components/Tooltip/Tooltip';
import RiskIcon from '../RiskIcon';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import { riskTypes } from './riskTypes';
import classNames from 'classnames';
import { useSelector, useDispatch } from 'react-redux';
import { withdrawalSlice } from '../../../../../../store/slices';
import HelperService from '../../../../../../services/HelperService';

const RiskSelect = ({ disabled }) => {
    const dispatch = useDispatch();
    const ref = useRef(null);
    const [dropdownStatus, setDropdownStatus] = useState(false);
    const activeCurrency = useSelector((state) => state?.withdrawal?.manualWithdrawal?.currency);
    const activeRisk = useSelector((state) => state?.withdrawal?.manualWithdrawal?.riskLevel);
    const addresses = useSelector((state) => state?.withdrawal?.manualWithdrawal?.addresses);
    const activeMerchant = useSelector((state) => state?.withdrawal?.manualWithdrawal?.merchant);
    const activeAddresses = addresses.filter((add) => (add.Currency === activeCurrency.Symbol && add.MerchantId === activeMerchant?.Id));
    const riskArray = Object.values(riskTypes);


    const onClickOutside = (e) => {
        if (ref.current && ref.current.contains && !ref.current.contains(e.target)) {
            if (dropdownStatus) setDropdownStatus(false);
        }
    };

    useEffect(() => {
        document.addEventListener('click', onClickOutside, true);
        return () => {
            document.removeEventListener('click', onClickOutside, true);
        };
    });

    const onSelectItem = (risk) => {
        dispatch(withdrawalSlice.actions.setRiskWithdrawalModel(risk));
        setDropdownStatus(false)
    };

    // const riskPlaceHolder = (risk = riskTypes.all) => {
    //     return (<div className="Placeholder">
    //         {risk.name !== riskTypes.all.name && <span className="risk-icon"><RiskIcon type={risk.name}/></span>}
    //         {risk.label}
    //     </div>);
    // };

    const getAvailableAmountByRisk = (risk = 'all') => activeAddresses.reduce((acc, add) => {
        if(risk === 'all') {
            return acc + add.Balance;
        } else {
            return add.riskName === risk ? acc + add.Balance : acc;
        }
    }, 0);

    const riskItem = (risk = riskTypes.all) => {
        return (
            <ListItemButton key={risk.id} className={classNames({active: risk.id === activeRisk.id})} onClick={() => onSelectItem(risk)}>
                <div className="List__Item">
                    <div className="Item__Name">
                        {risk.name !== riskTypes.all.name && <span className="risk-icon"><RiskIcon type={risk.name}/></span>}
                        {risk.label}
                    </div>
                    <div className="Item__Amount">
                        <span className="value">{HelperService.FormatCrypto(getAvailableAmountByRisk(risk.name), activeCurrency.Symbol === 'USDT-TRC20' && 2)}</span>
                        <span className="currency">{activeCurrency.Symbol}</span>
                    </div>
                </div>
            </ListItemButton>);
    };

    return (
        <div ref={ref} className={classNames('RiskSelect', { disabled } )}>
            <div className="click-zone" onClick={() => !disabled && setDropdownStatus(true)}/>
            <div className="RiskSelect__Title">
                {t('By the risk level')}
            </div>
            <div className="RiskSelect__Placeholder">
                {/* {riskPlaceHolder(activeRisk)} */}
                {t('All addresses')}
            </div>
            <Icon className="dropdown-icon" type={Icons.SELECT_ARROW} width={16} height={16}/>
            {dropdownStatus && <div className="RiskSelect__Dropdown">
                <List component="nav" className="Dropdown__List">
                    {(Array.isArray(riskArray) && riskArray.length > 0) && riskArray.map((risk) => (riskItem(risk)))}
                </List>
            </div>}
            <Tooltip id="risk-select-tooltip"
                     text={t('Filtering crypto addresses by risk level, from which sending will be made')}/>
        </div>
    );
};

export default RiskSelect;
