import SubscribeService from './SubscribeService';

const NotificationService = {
    Notifications: [],

    Push: (text, isError, className) => {
        const notifications = Object.assign([], NotificationService.Notifications);
        const notification = { text, isError, className };

        notifications.push(notification);
        NotificationService.Notifications = notifications;

        SubscribeService.Action(SubscribeService.Actions.NOTIFICATION, NotificationService.Notifications);
        setTimeout(() => NotificationService.Remove(notification), 5000);
    },
    Remove: (item) => {
        // const notifications = Object.assign([], NotificationService.Notifications);
        // notifications.splice(notifications.indexOf(item), 1);
        // NotificationService.Notifications = notifications;

        NotificationService.Notifications = [];
        SubscribeService.Action(SubscribeService.Actions.NOTIFICATION, NotificationService.Notifications);
    }
};
export default NotificationService;