import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    payments: [],
};

export const paymentSlice = createSlice({
    name: 'payment',
    initialState,
    reducers: {
        getPaymentHistory: (state, action) => {
            const { payload: payments = [] } = action;
            state.payments = payments;
        },
    },
});
