import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import t from '../../lang/t';
import Tooltip from '../Tooltip/Tooltip';
import './Checkbox.scss';

const Checkbox = ({ id, className, label, checked, disabled, onChange, tooltip }) => {
    return (
        <div id={id} className={classNames('checkbox', className)}>
            <input id={'checkbox-' + id} type="checkbox" checked={checked} disabled={disabled} onChange={onChange}/>
            <label htmlFor={'checkbox-' + id}>
                <div>
                    <svg width="9" height="6" viewBox="0 0 9 6" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M3.18518 4.2553C3.23356 4.19247 3.26259 4.15381 3.29646 4.11997C4.59309 2.81989 5.89456 1.52463 7.19603 0.224547C7.60243 -0.181428 8.2701 -0.0122712 8.42008 0.533862C8.49749 0.819011 8.41041 1.06066 8.20236 1.26848C7.32666 2.14326 6.45095 3.01804 5.57524 3.88799C4.98014 4.48245 4.38021 5.06242 3.79963 5.66655C3.49482 5.99036 2.86102 5.97586 2.56106 5.66655C1.79663 4.87393 1.00317 4.10064 0.224221 3.32252C-0.177347 2.92138 -0.0176868 2.26409 0.529027 2.1046C0.814479 2.02244 1.06122 2.1046 1.26927 2.31242C1.87404 2.91655 2.47881 3.52068 3.08358 4.12481C3.10777 4.15864 3.13196 4.19247 3.18518 4.2553Z" fill="white"/>
                    </svg>
                </div>
                {label && <span>{t(label)}</span>}
            </label>
            {tooltip && <Tooltip id={id + '-tooltip'} className="checkbox-tooltip" text={t(tooltip)} />}
        </div>
    );
};

Checkbox.defaultProps = {
    id: null,
    className: null,
    label: '',
    checked: false,
    disabled: false,
    onChange: () => {}
};

Checkbox.propTypes = {
    id: PropTypes.string.isRequired,
    className: PropTypes.string,
    label: PropTypes.string,
    checked: PropTypes.bool.isRequired,
    disabled: PropTypes.bool,
    onChange: PropTypes.func
};

export default Checkbox;