import React from 'react';
import { useSelector } from 'react-redux';
import './AppVersion.scss';

const AppVersion = () => {
    const version = useSelector((state) => state?.app?.versionInfo?.formatedVersion);

    return (
        <div className="AppVersion">
            <div className="AppVersion__Content">{version}</div>
        </div>
    );
};

export default AppVersion;
