import React from 'react';
import * as PropTypes from 'prop-types';
import Withdrawal from '../Withdrawal';
import AutoWithdrawal from '../AutoWithdrawal';
import Receiving from '../Receiving';
import Backup from '../Backup';
import EditDescription from '../EditDescription';
import GasStationModal from '../GasStationModal';

const BodyRenderer = ({ tag, handleClose }) => {
    const components = {
        withdrawal: Withdrawal,
        autoWithdrawal: AutoWithdrawal,
        receiving: Receiving,
        backup: Backup,
        editDescription: EditDescription,
        gasStation: GasStationModal,
    };
    const TagName = components[tag];

    if (!tag) return null;
    if (tag && TagName) return <TagName handleClose={handleClose}/>;
};

BodyRenderer.propTypes = {
    tag: PropTypes.oneOf([
        'withdrawal',
        'receiving',
        'backup',
        'autoWithdrawal',
        'editDescription',
        'gasStation',
        '']).isRequired,
};

export default BodyRenderer;
