import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { LOCALES_INFO } from '../../lang';
import { appSlice } from '../../store/slices/app';
import useIntl from 'react-intl/lib/src/components/useIntl';
import t from '../../lang/t';
import ChangePasswordModal from '../ChangePasswordModal/ChangePasswordModal';
import TwoFaActivateModal from '../TwoFaActivateModal/TwoFaActivateModal';
import TwoFaModal from '../TwoFaModal/TwoFaModal';
import ImportModal from '../ImportModal/ImportModal';
import Form from '../../components/Form/Form';
import Icon, { Icons } from '../../components/Icon/Icon';
import Input from '../../components/Input/Input';
import Select from '../../components/Select/Select';
import Checkbox from '../../components/Checkbox/Checkbox';
import Switch from '../../components/Switch/Switch';
import Button from '../../components/Button/Button';
import StorageService from '../../services/StorageService';
import NotificationService from '../../services/NotificationService';
import RequestService from '../../services/RequestService';
import { AppVersion, BackupSettings, GasStationSettings } from './components';
import './ProfileModalTabSettings.scss';

const { setLanguage } = appSlice.actions;

const ProfileModalTabSettings = () => {
    const [isEditInfo, setIsEditInfo] = useState(false);

    const [name, setName] = useState(StorageService.Self['Name']);
    const [email, setEmail] = useState(StorageService.Self['Email']);

    const [isChangePasswordModal, setIsChangePasswordModal] = useState(false);

    const [isTwoFa, setIsTwoFa] = useState(StorageService.Self['Is2FaEnabled']);
    const [isTwoFaActivateModal, setIsTwoFaActivateModal] = useState(false);
    const [isTwoFaModal, setIsTwoFaModal] = useState(false);

    const [isNotificationIncome, setIsNotificationIncome] = useState(false);
    const [isNotificationWithdrawal, setIsNotificationWithdrawal] = useState(false);
    const [isNotificationAutoWithdrawal, setIsNotificationAutoWithdrawal] = useState(false);

    const [isImportModal, setIsImportModal] = useState(false);

    const lang = useSelector((state) => state.app.lang);
    const dispatch = useDispatch();

    const intl = useIntl();

    const localesOptions = Object.values(LOCALES_INFO).map((l) => ({ label: intl.formatMessage({ id: l.title }), value: l.name }));
    const currentLocale = localesOptions.find((l) => l.value === lang);

    const onChangeInfo = async (lang) => {
        await RequestService.UpdateSelf(name, email, {
            income: isNotificationIncome,
            withdrawal: isNotificationWithdrawal,
            autoWithdrawal: isNotificationAutoWithdrawal
        }, lang);

        StorageService.Self['Name'] = name;
        StorageService.Self['Email'] = email;
        StorageService.Self['Language'] = lang;

        setIsEditInfo(false);
        NotificationService.Push(t('Personal information changed'), false);
    };

    const onChangeLanguage = (option) => {
        const { value } = option;

        if (value !== lang) dispatch(setLanguage(value));
    };

    const onAcceptTwoFa = () => {
        setIsTwoFa(true);
        setIsTwoFaActivateModal(false);

        NotificationService.Push(t('Two-factor authentication enabled'), false);
        StorageService.Self['Is2FaEnabled'] = false;
    };
    const onDisableTwoFa = async (twoFaCode) => {
        const response = await RequestService.DisableTwoFa(twoFaCode);

        if (response['Status'] !== 'Success') {
            return false;
        }

        setIsTwoFa(false);
        setIsTwoFaModal(false);

        StorageService.Self['Is2FaEnabled'] = false;
        NotificationService.Push(t('Two-factor authentication disabled'), false);

        return true;
    };

    const isNotifications = () => {
        return isNotificationIncome || isNotificationAutoWithdrawal || isNotificationWithdrawal;
    };
    const onChangeNotifications = () => {
        if (isNotifications()) {
            setIsNotificationIncome(false);
            setIsNotificationAutoWithdrawal(false);
            setIsNotificationWithdrawal(false);
        } else {
            setIsNotificationIncome(true);
            setIsNotificationAutoWithdrawal(true);
            setIsNotificationWithdrawal(true);
        }
    };

    const onExportSystem = () => {

    };

    return (
        <div className="profileModalTabSettings">
            {isChangePasswordModal &&
            <ChangePasswordModal onClose={() => setIsChangePasswordModal(false)}/>}
            {isTwoFaActivateModal &&
            <TwoFaActivateModal onAccept={onAcceptTwoFa} onClose={() => setIsTwoFaActivateModal(false)}/>}
            {isTwoFaModal &&
            <TwoFaModal onAccept={onDisableTwoFa} onClose={() => setIsTwoFaModal(false)}/>}
            {isImportModal &&
            <ImportModal onClose={() => setIsImportModal(false)}/>}
            <div className="profileModalTabSettingsInfo">
                <h4>
                    <Icon type={Icons.PERSONAL_INFO} width={21} height={21}/>
                    <span>{t('Personal information')}</span>
                </h4>
                <Form onSubmit={() => onChangeInfo(lang)}>
                    <Input type="name" label={intl.formatMessage({ id: 'Name' })} placeholder={intl.formatMessage({ id: 'Name' })} disabled={!isEditInfo} required value={name}
                           onChange={(e) => setName(e.target.value)}/>
                    <Input type="email" label={intl.formatMessage({ id: 'Email' })} placeholder={intl.formatMessage({ id: 'Email' })} disabled={!isEditInfo} required value={email}
                           onChange={(e) => setEmail(e.target.value)}/>
                    <Button className={isEditInfo ? 'd-none' : null} onClick={() => setIsEditInfo(true)}>{t('Edit')}</Button>
                    <div className={isEditInfo ? "profileModalTabSettingsInfoButtons" : 'd-none'}>
                        <Button color="red" onClick={() => setIsEditInfo(false)}>{t('Cancel')}</Button>
                        <Button type="submit">{t('Save')}</Button>
                    </div>
                </Form>
            </div>
            <div className="profileModalTabSettingsSeparator"/>
            <div className="profileModalTabSettingsSecurity">
                <h4>
                    <Icon type={Icons.SECURITY} width={21} height={21}/>
                    <span>{t('Security')}</span>
                </h4>
                <Button color="light" onClick={() => setIsChangePasswordModal(true)}>{t('Change Password')}</Button>
            </div>
            <div className="profileModalTabSettingsSeparator"/>
            <div className="profileModalTabSettingsTwoFa">
                <h4>
                    <Icon type={Icons.TWOFACTOR} width={21} height={21}/>
                    <span>{t('Two-factor authentication')}</span>
                </h4>
                <p>{t('Turn on additional Google Autentificator protection , and be sure that your data is protected!')}</p>
                {!isTwoFa &&
                <Button color="light" onClick={() => setIsTwoFaActivateModal(true)}>
                    {t('Enable additional protection!')}
                </Button>}
                {isTwoFa &&
                <Button color="red" onClick={() => setIsTwoFaModal(true)}>
                    {t('Disable additional protection!')}
                </Button>}
            </div>
            <div className="profileModalTabSettingsSeparator"/>
            {false &&
            <>
                <div className="profileModalTabSettingsNotification">
                    <div className="profileModalTabSettingsNotificationHead">
                        <h4>
                            <Icon type={Icons.NOTIFICATION} width={21} height={21}/>
                            <span>{t('Notifications')}</span>
                        </h4>
                        <Switch id="n" checked={isNotifications()} onChange={() => onChangeNotifications()}/>
                    </div>
                    <Checkbox id="ni" label={t('Receipt of funds ver2')} checked={isNotificationIncome}
                              onChange={() => setIsNotificationIncome(value => !value)}/>
                    <Checkbox id="naw" label={t('Execution of auto withdrawals')} checked={isNotificationAutoWithdrawal}
                              onChange={() => setIsNotificationAutoWithdrawal(value => !value)}/>
                    <Checkbox id="nw" label={t('Withdrawals')} checked={isNotificationWithdrawal}
                              onChange={() => setIsNotificationWithdrawal(value => !value)}/>
                </div>
                <div className="profileModalTabSettingsSeparator"/>
            </>}
            <GasStationSettings />
            <BackupSettings />
            <div className="profileModalTabSettingsLanguage">
                <h4>
                    <Icon type={Icons.LANGUAGE} width={21} height={21}/>
                    <span>{t('Interface language')}</span>
                </h4>
                <Select
                    label={intl.formatMessage({ id: 'Select interface language' })}
                    options={localesOptions}
                    value={currentLocale}
                    onChange={onChangeLanguage}
                    excludeActiveOption
                />
            </div>
            <div className="profileModalTabSettingsSeparator"/>
            {false &&
            <>
                <div className="profileModalTabSettingsOther">
                    <h4>
                        <Icon type={Icons.MORE} width={21} height={21}/>
                        <span>{t('Backup')}</span>
                    </h4>
                    <Button onClick={onExportSystem}>{t('Create a backup')}</Button>
                    <Button onClick={() => setIsImportModal(true)}>{t('Restore from backup')}</Button>
                </div>
                <div className="profileModalTabSettingsSeparator"/>
            </>}
            <AppVersion />
        </div>
    );
};
export default ProfileModalTabSettings;
