import React, { useState, useRef } from 'react';
import classNames from 'classnames';
import { useOnClickOutside } from 'use-hooks';
import { UploadIcon } from './components';
import * as XLSX from 'xlsx';
import * as FileSaver from 'file-saver';
import { settings } from './settings';
import RequestService from '../../../../services/RequestService';
import HelperService from '../../../../services/HelperService';
import NotificationService from '../../../../services/NotificationService';
import t from '../../../../lang/t';

import './TableUpload.scss';

const { uploadSettings, enableUpload: availableTables } = settings;

const TableUpload = ({ tableId, datesRange, uploadedData, filters = [] }) => {
    const settings = uploadSettings[tableId];
    const [data, setData] = useState(null);
    const [isFetchData, setFetchStatus] = useState(false);
    const uploadRef = useRef(null);
    const [toggleStatus, setToggleStatus] = useState(false);

    const toggle = () => setToggleStatus(!toggleStatus);
    const close = () => setToggleStatus(false);

    const upload = ({
                        data,
                        type = 'xlsx',
                        fileName = 'transactions',
                    }) => {
        const options = {
            xlsx: {
                type,
                fileType: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8',
                fileExtension: '.xlsx',
                fileName,
                generateFile() {
                    if (!data) return;

                    const { fileName, fileType, fileExtension } = this;
                    const ws = XLSX.utils.json_to_sheet(data);
                    const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
                    const buffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
                    const exportData = new Blob([buffer], { type: fileType });

                    FileSaver.saveAs(exportData, `${fileName}${fileExtension}`);
                },
            },
            csv: {
                type,
                fileType: 'text/csv;charset=UTF-8',
                fileExtension: '.csv',
                fileName,
                generateFile() {
                    if (!data) return;

                    const { fileName, fileType, fileExtension } = this;
                    const ws = XLSX.utils.json_to_sheet(data);
                    const csvOutput = XLSX.utils.sheet_to_csv(ws);
                    const exportData = new Blob([csvOutput], { type: fileType });

                    FileSaver.saveAs(exportData, `${fileName}${fileExtension}`);
                },
            },
        };

        options[type]?.generateFile();
    };

    const parseDate = (dates = []) => {
        const fromDate = HelperService.formatDate(dates?.[0], '-');
        const toDate = HelperService.formatDate(dates?.[1], '-', 10, 'T23:59:59.99999');
        return [fromDate, toDate];
    };

    const replacer = (value = '', replace = /-/g, replaceTo = '_') => (value.replace(replace, replaceTo));

    const generateFileName = (name = '', dates = [], type = 'xlsx') => {
        let start = dates[0] || '';
        let end = dates[1] || '';
        end = end.split('T')[0] || '';
        start = start ? `-${replacer(start)}` : '';
        end = end ? `-${replacer(end)}` : '';

        return `${name}${start}${end}.${type}`;
    };

    const uploadFileFromBlobData = ({ data, name }) => {
        const link = document.createElement("a");
        const blob = new Blob([data]);

        link.href = URL.createObjectURL(blob);
        link.download = name;
        link.click();
    };

    const uploadFromServer = async ({ type, fileName, datesRange, filters }) => {
        setFetchStatus(true);
        NotificationService.Push(t('Please wait, report is generating.'), false);

        const dates = parseDate(datesRange);
        const report = await RequestService.GetTransactionsReport(dates[0], dates[1], type, filters);

        if (!report || report?.constructor?.name !== 'Blob') {
            NotificationService.Push(t('Error occurred while report generating, please, try again later.'), true);
            setFetchStatus(false);
            return;
        }

        const fullFileName = generateFileName(fileName, dates, type);
        uploadFileFromBlobData({ data: report, name: fullFileName });
        setFetchStatus(false);
    };

    useOnClickOutside(uploadRef, close);
    settings?.hook({ setData, datesRange, uploadedData });


    const uploadTypes = [
        {
            id: 0,
            icon: (<UploadIcon type="csv" width={28} height={28} color="#ffffff"/>),
            title: 'CSV',
            handler: () => {
                settings?.serverReporting ? uploadFromServer({
                    type: 'csv',
                    fileName: settings?.fileName,
                    datesRange,
                    filters
                }) : upload({ data, type: 'csv', fileName: settings?.fileName });
                close();
            },
            isActive: true
        },
        {
            id: 1,
            icon: (<UploadIcon type="excel" width={28} height={28} color="#ffffff"/>),
            title: 'Excel',
            handler: () => {
                settings?.serverReporting ? uploadFromServer({
                    type: 'xlsx',
                    fileName: settings?.fileName,
                    datesRange,
                    filters
                }) : upload({ data, type: 'xlsx', fileName: settings?.fileName });
                close();
            },
            isActive: true
        },
        {
            id: 2,
            icon: (<UploadIcon type="pdf" width={28} height={28} color="#ffffff"/>),
            title: 'pdf',
            handler: () => {
                close();
            },
            isActive: false
        },
    ];
    const uploadTypesFiltered = uploadTypes.filter((type) => type.isActive);

    if (!settings || Object.keys(settings).length === 0 || !availableTables.includes(tableId)) return null;

    return (
        <div className="TableUpload" ref={uploadRef}>
            <div className={classNames('TableUpload__Switcher', { active: toggleStatus })} onClick={toggle}>
                <UploadIcon width={30} height={30} onClick={toggle}/>
            </div>
            {toggleStatus && (<div className="TableUpload__Dropdown">
                {uploadTypesFiltered.map((type) => (
                    <button disabled={isFetchData} className="Dropdown__Button" key={type?.id} onClick={type?.handler}>
                        <span className="Button__Icon">
                            {type?.icon}
                        </span>
                        <span className="Button__Title">
                            {type?.title}
                        </span>
                    </button>))}
            </div>)}
        </div>
    );
};

export default React.memo(TableUpload, (props, nextProps) => {
    if (JSON.stringify(props.datesRange) === JSON.stringify(nextProps.datesRange) &&
        JSON.stringify(props.uploadedData) === JSON.stringify(nextProps.uploadedData)) {
        return true;
    }
});
