import React from 'react';
import Checkbox from '@mui/material/Checkbox';
import Button from '@mui/material/Button';
import { useDispatch, useSelector } from 'react-redux';
import t from '../../../../../../lang/t';
import { appSlice, withdrawalSlice } from '../../../../../../store/slices';
import RootModalModel from '../../../../../../models/root-modal';
import { stepTypes } from '../WithdrawalStepper/step-types';
import RequestService from '../../../../../../services/RequestService';
import './ConfirmationButtonsGroup.scss';

const ConfirmationButtonsGroup = () => {
    const dispatch = useDispatch();
    const modal = useSelector((state) => state?.app?.modal);
    const confirmation = useSelector((state) => state?.withdrawal?.manualWithdrawal?.confirmation);
    const withdrawalModel = useSelector((state) => state?.withdrawal?.manualWithdrawal);
    const filteredAddresses = useSelector((state) => state?.withdrawal?.withdrawalFilteredList);

    const setWithdrawalStep = () => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        ...modal,
        title: t('Send'),
        outsideParams: {
            step: stepTypes.send.name,
        },
    })));

    const setSuccessStep = () => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        ...modal,
        title: '',
        className: 'withdrawal-success',
        outsideParams: {
            step: stepTypes.success.name,
        },
    })));

    const setLoaderStatus = (status = false) => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({ ...modal, isShowLoader: status })));

    const doSendWithdrawal = async () => {

        setLoaderStatus(true);
        const response = await RequestService.Send(
            withdrawalModel.currency.Symbol,
            filteredAddresses.filter((sender) => sender.isShow).map((sender) => sender.Address),
            withdrawalModel.receiverAddress,
            withdrawalModel.cryptoAmount,
            withdrawalModel.commission,
            withdrawalModel.comment,
        );

        if (response['Status'] !== 'Success') {
            setLoaderStatus(false);
            setErrorStep(response.Error);
        } else {
            dispatch(withdrawalSlice.actions.setLastTransaction([response['TxId'], withdrawalModel.currency.Symbol]));
            setLoaderStatus(false);
            setSuccessStep();
        }
    };

    const setErrorStep = (error) => dispatch(appSlice.actions.updateModalStructure(new RootModalModel({
        ...modal,
        title: '',
        className: 'withdrawal-error',
        outsideParams: {
            step: stepTypes.error.name,
            error: error
        },
    })));

    const toggleConfirmation = () => dispatch(withdrawalSlice.actions.toggleConfirmationWithdrawalModel(null));

    return (
        <div className="ConfirmationButtonsGroup">
            <div className="ConfirmationButtonsGroup__DataConfirm">
                <div className="field-group">
                    <div className="confirm-data-checkbox" onClick={toggleConfirmation}>
                        <Checkbox checked={confirmation}/>
                        <div className="confirm-data-label">{t('I am sure that the information is correct')}</div>
                    </div>
                </div>
                <div className="confirm-data-additional">{t('Withdrawal - irreversible operation')}</div>
            </div>
            <div className="ConfirmationButtonsGroup__Actions">
                <Button variant="outlined" className="back-button" onClick={setWithdrawalStep}>{t('Back')}</Button>
                <Button variant="contained" className="confirm-button" onClick={doSendWithdrawal}
                        disabled={!confirmation}>{t('I confirm')}</Button>
            </div>
        </div>
    );
};

export default ConfirmationButtonsGroup;
