import React from 'react';
import t from '../../../../lang/t';
import StorageService from '../../../../services/StorageService';

const CheckDescription = ({ isSingleCheck, addresses }) => (
    <p className="checkModalDescription">
        {t('The cost of checking')}
        &nbsp;
        <span>
            {isSingleCheck ? StorageService.System['CheckAddressCost'] : addresses.length * StorageService.System['CheckAddressCost']}
            {` USD`}
        </span>
    </p>
);

export default CheckDescription;
