import React, { useState } from 'react';
import classNames from 'classnames';
import t from '../../../../../../lang/t';
import Icon, { Icons } from '../../../../../../components/Icon/Icon';
import Switcher from '../Switcher/Switcher';

import './AdditionalDropdown.scss';

const AdditionalDropdown = ({arrayButton, data, dataType, onChange, tooltip}) => {
    const [showDropdown, setDropdownStatus] = useState(false);
    const toggleDropdown = () => setDropdownStatus((prev) => (!prev));
    return (
        <div className={classNames('AdditionalDropdown', { open: showDropdown })}>
            <div className="AdditionalDropdown__Button">
                <button className="toggle-button" onClick={toggleDropdown}>
                    <span className="toggle-button-text">
                        {t('Additionally')}
                        <span className="toggle-button-icon">
                            <Icon type={Icons.SELECT_ARROW} width={18} height={18}/>
                        </span>
                    </span>
                </button>
            </div>
            {showDropdown && <div className="AdditionalDropdown__Body">
                <p className="fee">{t('Fee settings')}</p>
                <Switcher
                    arrayButton={arrayButton}
                    data={data}
                    dataType={dataType}
                    onChange={onChange}
                    tooltip={tooltip}
                />
            </div>}
        </div>
    );
};

export default AdditionalDropdown;